playtotv.define('playtotv.storage.MockStorage', [], function() {

	'use strict';

	return new playtotv.Class({
		/** @lends playtotv.storage.MockStorage.prototype */

		/**
		 * DOM Storage (e.g. localStorage) compatible class for mocking.
		 *
		 * Note: Data is stored in a plain object, so it will not be persisted beyond reloads etc.
		 *
		 * Note: This does not support Storage events (yet).
		 *
		 * @constructs
		 */
		initialize: function() {
			/**
			 * The number of key/value pairs currently present in the list associated with the object.
			 * @type number
			 */
			this.length = 0;

			this._container = {};
		},

		/** @lends playtotv.storage.MockStorage.prototype */

		/**
		 * Returns the name of the key with the given index.
		 *
		 * @param {number} index
		 * @return {string} key
		 */
		key: function(index) {
			return Object.keys(this._container)[index];
		},

		/**
		 * Returns the current value associated with the given key.
		 *
		 * @param {string} key
		 * @returns {string} value
		 */
		getItem: function(key) {
			return Object.keys(this._container).indexOf(key) === -1 ? null : this._container[key];
		},

		/**
		 * Adds or updates value of key/value pair to given value.
		 *
		 * @param {string} key
		 * @param {string} value
		 */
		setItem: function(key, value) {
			this._container[key] = value;
			this._updateLength();
		},

		/**
		 * Causes the key/value pair with the given key to be removed.
		 *
		 * @param {string} key
		 */
		removeItem: function(key) {
			delete this._container[key];
			this._updateLength();
		},

		/**
		 * Causes the list associated with the object to be emptied of all key/value pairs.
		 */
		clear: function() {
			this.length = 0;
			this._container = {};
		},

		_updateLength: function() {
			this.length = Object.keys(this._container).length;
		}
	});
});
