(function() {

	'use strict';

	// TODO: Replace this by using ES6 import and export (nBob will need to support that)

	// Reliably get a global reference on all platforms by looking at a non-strict function's "this"
	var global = /*jshint evil:true*/ new Function('return this')(); /*jshint evil:false*/

	// Variables

	var pkgURL = playtotv.pkgURL = {};
	var loadPromises = {};
	var definePromises = playtotv.definePromises = {};
	var modules = playtotv.modules = {};

	// Private functions

	function getModuleURL(id) {
		var base = '';
		playtotv.each(pkgURL, function(url, pkg) {
			if (!base && id.indexOf(pkg) === 0) {
				base = url;
				id = id.replace(pkg, '');
			}
		});
		// Replace camel case by lower case dash separated and replace dots by slashes, then add .js extension
		var path = id.replace(/([a-z0-9])([A-Z])/g, '$1-$2').toLowerCase().replace(/\./g, '/') + '.js';
		return base + path;
	}

	function registerModule(id, module) {
		var space = global;
		var spaces = id.split('.');
		var i, name, current, l = spaces.length, last = l - 1;
		for (i = 0; i < l; i++) {
			name = spaces[i];
			current = space[name];
			if (i < last) {
				space = current || (space[name] = {});
			} else {
				// a.b.c may be defined before a.b, if so, extend a.b with c
				space[name] = current ? playtotv.extend(module, current) : module;
			}
		}
	}

	// Public functions

	/**
	 * Returns a Promise of the module with specified id, loading the related script file if necessary.
	 *
	 * @param {string} id Module id
	 * @return {Promise} Module promise
	 */
	playtotv.getModule = function(id) {
		var url = getModuleURL(id) + '?nocache=' + Date.now(); // bust cache to ease development
		return definePromises[id] || loadPromises[id] || (loadPromises[id] = playtotv.loadScript(url).then(function() {
			if (!definePromises[id]) {
				throw new Error(id + ' was not defined by ' + url);
			}
			return definePromises[id];
		}));
	};

	/**
	 * Defines a module using the given id.
	 * An array of dependencies may be passed, which will be loaded dynamically if not already defined.
	 * The factory function will be called after all depencies are loaded to create the module.
	 * The module will also be registered in the global namespace to facilitate usage by third party JavaScript.
	 *
	 * A define/require implementation inspired by CommonJS Asynchronous Definition.
	 * <p>
	 * Main differences:
	 * <ul>
	 * <li>Module id's are dot separated and map to URLs (dots replaced by slashes and camel case by dashed lower case)
	 * <li>Module dependencies are strings like the module id and can not be relative
	 * <li>Module dependencies will be dynamically loaded if not already defined
	 * <li>Module dependencies that are dynamically loaded must use playtotv.define to complete their definition
	 * </ul>
	 * </p>
	 *
	 * @param {string} id Module id
	 * @param {Array} dependencies Module dependency id's
	 * @param {Function} factory Module factory function
	 * @return {Promise} A Promise of the defined module
	 */
	playtotv.define = function(id, deps, factory) {
		if (arguments.length !== 3) {
			throw new Error('Three arguments required');
		}

		// TODO: Enable module factory functions to return a promise
		// Now, when a module depends on such a module it will wait for that promise to resolve instead of just waiting
		// for the promise of the factory function execution around it to resolve.
		return definePromises[id] ||
			(
				definePromises[id] = Promise.all(deps.map(playtotv.getModule)).then(function(dependencies) {
					var thisArg = { id: id, dependencies: deps };
					var module = (factory instanceof Function) ? factory.apply(thisArg, dependencies) : factory;
					modules[id] = module;
					registerModule(id, module);
					return module;
				})
			);
	};

	/**
	 * Returns the module with specified id if it has been defined.
	 *
	 * @param {string} id Module id
	 * @returns {Object} Module
	 */
	playtotv.require = function(id) {
		return modules[id];
	};

	/**
	 * Returns the id of specified module.
	 *
	 * @param {Object} module Module
	 * @return {string} Module id
	 */
	playtotv.getModuleId = function(module) {
		return playtotv.find(Object.keys(modules), function(id) {
			return modules[id] === module;
		});
	};

	/**
	 * Returns an object mapping the names of the specified module ids without their 'package prefixes' to their
	 * corresponding modules, similar to Java's import statement.
	 *
	 * @param {Array} ids Module id's
	 * @returns {Object} Module map
	 */
	playtotv.importModules = function(ids) {
		var nameReg = /[^.]+$/i;
		var importModules = {};
		var i, id, name, l = ids.length;
		for (i = 0; i < l; i++) {
			id = ids[i];
			name = nameReg.exec(id)[0];
			importModules[name] = modules[id];
		}
		return importModules;
	};

}());
