/**
 * EcmaScript 5 shim, mostly for IE8.
 * Note: Does not provide complete ES5 compatibility.
 */
(function() {
	/*jshint maxstatements: 100, maxcomplexity:100*/

	'use strict';

	var nativeSlice = [].slice;
	var nativeConcat = [].concat;

	/**
	 * Function prototype
	 */

	var funcProto = Function.prototype;

	if (!funcProto.bind) {
		funcProto.bind = function(scope) {
			var method = this;

			if (arguments.length > 1) {
				// any arguments in addition to scope are prepended when the method is called
				var prepend = nativeSlice.call(arguments, 1);
				return function() {
					var params = nativeConcat.apply(prepend, arguments);
					return method.apply(scope, params);
				};

			} else {
				// conditional for performance, don't want to concat empty arrays
				return function() {
					return method.apply(scope, arguments);
				};
			}
		};
	}

	/**
	 * String prototype
	 */

	var stringProto = String.prototype;

	if (!stringProto.trim) {
		var trimmer = /(^\s+)|(\s+)$/g;
		stringProto.trim = function() {
			return this.replace(trimmer, '');
		};
	}

	/**
	 * Date
	 */

	if (!Date.now) {
		Date.now = function() {
			return (new Date()).getTime();
		};
	}

	// Ensure Date.parse properly supports ISO8601
	(function() {
		// Based on: http://stackoverflow.com/questions/11020658/javascript-json-date-parse-in-ie7-ie8-returns-nan
		var d1 = Date.parse('2011-06-02T09:34:29+02:00');
		var d2 = Date.parse('2011-06-02T09:34:29+0200');
		if (d1 !== d2 || d1 !== 1307000069000) {
			// ISO8601 support needs to be improved
			var _parse = Date.parse;
			Date.parse = function(s) {
				var day, tz;
				var rx = /^(\d{4}\-\d\d\-\d\d([tT][\d:\.]*)?)([zZ]|([+\-])(\d\d):?(\d\d))?$/;
				var p = rx.exec(s) || [];
				if (p[1]) {
					day = p[1].split(/\D/);
					for (var i = 0, L = day.length; i < L; i++) {
						day[i] = parseInt(day[i], 10) || 0;
					}
					day[1] -= 1;
					day = new Date(Date.UTC.apply(Date, day));
					if (!day.getDate()) { return NaN; }
					if (p[5]) {
						tz = parseInt(p[5], 10) * 60;
						if (p[6]) { tz += parseInt(p[6], 10); }
						if (p[4] === '+') { tz *= -1; }
						if (tz) { day.setUTCMinutes(day.getUTCMinutes() + tz); }
					}
					return day.getTime();
				} else {
					// Regex failed, not ISO8601? Fallback to native Date.parse().
					return _parse(s);
				}
			};
		}
	})();

	/**
	 * Array prototype
	 */

	var arrayProto = Array.prototype;

	// 1.6

	if (!arrayProto.indexOf) {
		arrayProto.indexOf = function(value, start) {
			/*jshint newcap:false*/
			var i, obj = Object(this), l = obj.length;
			for (i = start || 0; i < l; i++) {
				if (i in obj && obj[i] === value) {
					return i;
				}
			}
			return -1;
		};
	}

	if (!arrayProto.lastIndexOf) {
		arrayProto.lastIndexOf = function(value, start) {
			/*jshint newcap:false*/
			var i, obj = Object(this), l = obj.length;
			for (i = start || (l - 1); i >= 0; i--) {
				if (i in obj && obj[i] === value) {
					return i;
				}
			}
			return -1;
		};
	}

	if (!arrayProto.forEach) {
		arrayProto.forEach = function(callback, scope) {
			/*jshint newcap:false*/
			var i, obj = Object(this), l = obj.length;
			for (i = 0; i < l; i++) {
				if (i in obj) {
					callback.call(scope, obj[i], i, obj);
				}
			}
		};
	}

	if (!arrayProto.map) {
		arrayProto.map = function(callback, scope) {
			/*jshint newcap:false*/
			var i, result = [], obj = Object(this), l = obj.length;
			for (i = 0; i < l; i++) {
				if (i in obj) {
					result[i] = callback.call(scope, obj[i], i, obj);
				}
			}
			return result;
		};
	}

	if (!arrayProto.filter) {
		arrayProto.filter = function(callback, scope) {
			/*jshint newcap:false*/
			var i, result = [], value, obj = Object(this), l = obj.length;
			for (i = 0; i < l; i++) {
				if (i in obj) {
					value = obj[i];
					if (callback.call(scope, value, i, obj)) {
						result.push(value);
					}
				}
			}
			return result;
		};
	}

	if (!arrayProto.some) {
		arrayProto.some = function(callback, scope) {
			/*jshint newcap:false*/
			var i, obj = Object(this), l = obj.length;
			for (i = 0; i < l; i++) {
				if (i in obj && callback.call(scope, obj[i], i, obj)) {
					return true;
				}
			}
			return false;
		};
	}

	if (!arrayProto.every) {
		arrayProto.every = function(callback, scope) {
			/*jshint newcap:false*/
			var i, obj = Object(this), l = obj.length;
			for (i = 0; i < l; i++) {
				if (i in obj && !callback.call(scope, obj[i], i, obj)) {
					return false;
				}
			}
			return true;
		};
	}

	// 1.8

	if (!arrayProto.reduce) {
		arrayProto.reduce = function(callback, current) {
			/*jshint newcap:false*/
			var i = 0, obj = Object(this), l = obj.length;
			if (arguments.length <= 1) {
				current = obj[0];
				i = 1;
			}

			for (; i < l; i++) {
				if (i in obj) {
					current = callback(current, obj[i], i, obj);
				}
			}
			return current;
		};
	}

	if (!arrayProto.reduceRight) {
		arrayProto.reduceRight = function(callback, current) {
			/*jshint newcap:false*/
			var obj = Object(this), l = obj.length, i = l - 1;
			if (arguments.length <= 1) {
				current = obj[i];
				i = l - 2;
			}

			for (; i >= 0; i--) {
				if (i in obj) {
					current = callback(current, obj[i], i, obj);
				}
			}
			return current;
		};
	}

	/**
	 * Object
	 */

	if (!Object.keys) {
		Object.keys = function(obj) {
			var key, keys = [];
			var has = Object.prototype.hasOwnProperty; // for `window` on <=IE8
			for (key in obj) {
				if (has.call(obj, key)) {
					keys.push(key);
				}
			}
			return keys;
		};
	}

}());
