playtotv.define('playtotv.analytics.AnalyticsController', [
	'playtotv.analytics.AnalyticsWidget',
	'playtotv.analytics.ComScoreAnalytics',
	'playtotv.analytics.GoogleAnalytics',
	'playtotv.mvc.Controller',
	'playtotv.net.request'
], function(
	AnalyticsWidget,
	ComScoreAnalytics,
	GoogleAnalytics,
	Controller,
	request
) {

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;
	var event = ptv.event;
	var log = new ptv.Logger(this.id);

	return class AnalyticsController extends Controller {
		/** @lends playtotv.analytics.AnalyticsController.prototype */

		/**
		 * TODO: JSDOC
		 *
		 * @extends playtotv.mvc.Controller
		 * @constructs
		 */
		constructor(context, options) {
			options = ptv.params(options, {
				// privateGoogle is required but a default config is provided to facilitate it's use
				privateGoogle: { name: 'private', id: 'UA-20746434-1', fields: { sampleRate: 1 } },
				googles: [],
				comScore: null
			});

			super(context);

			this.addMethods([
				'privateTime',
				'privateError',
				'privateEvent',
				'view',
				'event',
				'setFields'
			]);

			this.addWidget('track', AnalyticsWidget);

			var privateGoogle = this.privateGoogle = new GoogleAnalytics(options.privateGoogle);

			var googles = this.googles = [ privateGoogle ];

			options.googles.forEach(googleOptions => {
				// Googles can be easily disabled by providing a falsy id option
				if (googleOptions.id) {
					googles.push(new GoogleAnalytics(googleOptions));
				}
			});

			// ComScore can be easily disabled by providing a falsy url option
			var comScoreOptions = options.comScore;
			if (comScoreOptions && comScoreOptions.url) {
				this.comScore = new ComScoreAnalytics(comScoreOptions);
			}

			event.addWindowOnError(this.privateError.bind(this));

			this.hookRequestTap();
		}

		/**
		 * Called by constructor to hook playtotv.net.request.tap to log request timings and rejections to
		 * our private Google tracker.
		 */
		hookRequestTap() {
			request.taps.push((url, label, promise) => {
				var cleanUrl = url.replace(/.nocache=\d+/, '');

				// Time all but long poll requests, recognizable by timeout URL param
				if (!util.getURLParams(url).timeout) {
					this.privateTime(promise, 'request', label, {
						timingLabel: cleanUrl
					});
				}

				// Rejections are not always errors, so log them as events
				promise.catch(reason => {
					this.privateEvent('request', 'reject ' + label + ' ' + reason.status, {
						eventLabel: cleanUrl,
						nonInteraction: true
					});
				});
			});
		}

		/**
		 * Calls and returns this.privateGoogle.time(promise, category, variable, fields).
		 */
		privateTime(promise, category, variable, fields) {
			return this.privateGoogle.time(promise, category, variable, fields);
		}

		/**
		 * Calls and returns this.privateGoogle.sendError(error, isFatal, fields).
		 */
		privateError(error, isFatal, fields) {
			return this.privateGoogle.sendError(error, isFatal, fields);
		}

		/**
		 * Calls and returns this.privateGoogle.sendEvent(category, action, fields).
		 */
		privateEvent(category, action, fields) {
			return this.privateGoogle.sendEvent(category, action, fields);
		}

		/**
		 * Calls this.googles sendView(name) and this.udm(name).
		 */
		view(name, fields) {
			log.d('view: ' + name, fields);

			this.lastView = name;

			this.googles.forEach(google => google.sendView(name, fields));

			this.udm(name);
		}

		/**
		 * Calls this.googles sendEvent(category, action, fields) and
		 * this.udm('[category].[action].[label]', { type: 'hidden' }).
		 */
		event(category, action, fields) {
			log.d('event: ' + category + ', ' + action, fields);

			this.googles.forEach(google => google.sendEvent(category, action, fields));

			var label = fields && fields.eventLabel;
			this.udm(category + '.' + action + (label ? '.' + label : ''), { type: 'hidden' });
		}

		/**
		 * Calls this.googles setFields(fields).
		 */
		setFields(fields) {
			this.googles.forEach(google => google.setFields(fields));
		}

		/**
		 * Calls this.comScore.udm(tag, params), if available.
		 * Note: Override this to add project specific and runtime params.
		 */
		udm(tag, params) {
			var comScore = this.comScore;
			if (comScore) {
				comScore.udm(tag, params);
			}
		}
	};
});
