/*jshint maxparams:100*/
playtotv.define('playtotv.widget.DraggableWidget', [
	'playtotv.widget.Widget',
	'playtotv.css.Vector',
	'playtotv.fx.Transition',
	'playtotv.animationFrame',
	'playtotv.css.style',
	'playtotv.touch.touchManager'
], function(
	Widget,
	Vector,
	Transition,
	animationFrame,
	style,
	touchManager
) {

	'use strict';

	/**
	 * Shorthands
	 *
	 */

	var dom = playtotv.dom;
	var event = playtotv.event;

	var STATE_PASSIVE = 1;
	var STATE_ACTIVE = 2;

	var nativeTouch = 'ontouchstart' in window;

	/**
	 * DraggableWidget element, used or extended by various UI components.
	 * This widget requires em:touch[type] events to be enabled either on its own node, or an ascendant, using
	 * {@link playtotv.touch.touchManager}.
	 *
	 * @name DraggableWidget
	 * @class
	 * @memberOf playtotv.widget
	 * @extends playtotv.widget.Widget
	 * @param {Node} node
	 * @param {Object} [options]
	 * @param {number} [options.orientation=Vector.VERTICAL] dragging orientation
	 * @param {string} [options.restrict='inside'] Movement restriction, based on element size in respect to container
	 * @param {boolean} [options.elastic=false] Let element be dragged beyond its limits (although heavily reduced)
	 * @param {boolean} [options.inertia=false] Let element have inertia and be flung on release.
	 * @param {boolean} [options.friction=30] Inertia friction, a lower value flings farther and longer.
	 * @param {boolean} [options.priority=false] Let element claim events regardless of orientation, stops propagation.
	 * @param {string} [options.handle] Selector for a child node by which the draggable node is actually dragged
	 */
	var DraggableWidget = new playtotv.Class({
		extend: Widget,

		initialize: function(node, options) {
			this._super(node);

			if (!touchManager.isCaptured(node)) {
				// TODO: capture locally with release on dispose? nesting IS possible ...
				throw new Error('Required events not captured via playtotv.touch');
			}

			options = playtotv.params(options, {
				orientation: Vector.VERTICAL,
				restrict: 'inside',
				elastic: false,
				inertia: false,
				friction: 50,
				priority: false,
				handle: null
			});

			this.start = Vector.ZERO;
			this.position = Vector.ZERO;
			this.transition = new Transition({
				callback: this.handleAnim,
				duration: 1000,
				easing: 'cubicout'
			});

			this.orientation = options.orientation;
			this.elastic = options.elastic;
			this.priority = options.priority;
			this.inertia = options.inertia;
			this.friction = options.friction;

			this.outside = /outside/i.test(options.restrict) ? true : false;
			this.limiter = new Vector(
				this.orientation === Vector.VERTICAL ? 0 : 1,
				this.orientation === Vector.HORIZONTAL ? 0 : 1
			);

			this.restricter = this.limiter.multiplyBy(0.75);

			var handle = options.handle;
			if (handle) {
				node = dom.first(handle, node) || node;
			}

			this.addEventListener(node, 'em:touchstart', this.dragStart);
			this.addEventListener(node, 'em:touchmove', this.dragMove);
			this.addEventListener(node, 'em:touchend', this.dragEnd);
			this.addEventListener(node, 'em:swipe', this.handleSwipe);

			this.setup();
			this.moveTo(this.start);
		},

		/** @lends playtotv.widget.DraggableWidget.prototype */

		/**
		 * Checks various element properties on drag start for limiting, swiping, etc. Must set this.space, this.min
		 * and this.max members when redefined in subclasses.
		 */
		setup: function() {
			var node = this.node;

			this.content = style.getSize(node);
			this.view = style.getInnerSize(node.parentNode);
			this.space = this.view.subtract(this.content).multiply(this.limiter);
			this.min = this.space.min(Vector.ZERO);
			this.max = this.outside ? Vector.ZERO : this.space.max(Vector.ZERO);
		},

		setState: function(state) {
			this.state = state;
		},

		getState: function() {
			return this.state;
		},

		requestFrame: function() {
			this.request = animationFrame.request(this.handleFrame);
		},

		cancelFrame: function() {
			if (this.request) {
				animationFrame.cancel(this.request);
				delete this.request;
			}
		},

		/**
		 * Internal dragstart handler, initialized a draggable sequence, and fires a 'em:dragstart' event.
		 * @param {Event|playtotv.event.Event} event
		 *
		 */
		dragStart: function(e) {
			// prevent start event for native mouse and pointer implementations to prevent native drag behavior.
			// don't prevent for native touch implementations, as this prevents a native click on touchend, which may
			// not be fired by the fastClick option depending on device and app properties.
			if (!nativeTouch) {
				e.preventDefault();
			}

			// follow only this touch, in case more than one finger enters the fray
			this.touchId = e.detail.sequence.id;

			// disallow propagation only if instance has priority
			if (this.priority) {
				e.stopPropagation();
			}

			if (this.transition.playing) {
				this.transition.stop();
				this.setState(STATE_PASSIVE);
			}

			this.start = this.position;
			this.lastOrientation = null;
			this.setup();

			event.dispatchEvent(this.node, 'em:dragstart', false, false, { offset: this.position });
		},

		/**
		 * internal dragmove handler, checks movement, and sends a restricted position to this.moveTo. Prevents event
		 * propagation if the event's orientation matches this instance's orientation, for nested components.
		 * @param {Event|playtotv.event.Event} event
		 */
		dragMove: function(e) {
			if (this.hasPriority(e)) {
				e.preventDefault();
				e.stopPropagation();

				var sequence = e.detail.sequence;
				var target = this.start.add(sequence.offset);
				var restricted = this.restrict(target, this.elastic);
				this.position = restricted;

				// start animating when actual dragging starts
				if (this.getState() !== STATE_ACTIVE) {
					this.setState(STATE_ACTIVE);
					this.requestFrame();
				}
			}
		},

		/**
		 * Internal dragend handler. Checks event orientation and inertia to fire an optional swipe.
		 *
		 * @param {Event|playtotv.event.Event} event
		 */
		dragEnd: function(e) {
			if (this.hasPriority(e)) {
				if (!nativeTouch) {
					e.preventDefault();
				}

				e.stopPropagation();

				delete this.touchId;

				if (this.inertia) {
					var sequence = e.detail.sequence;
					var velocity = sequence.velocity;
					var factor = Math.sqrt(velocity.magnitude()) / this.friction;
					var time = this.pulling ? 300 : (factor * 1000);
					this.swipe(velocity.multiplyBy(factor), time);
				} else {
					this.handleStopped();
				}
			}
		},

		/**
		 * Internal swipe event handler. Prevents event propagation if the event's orientation matches this instance's
		 * orientation, for nested components.
		 *
		 * @param {Event|playtotv.event.Event} event
		 */
		handleSwipe: function(e) {
			if (this.hasPriority(e)) {
				e.stopPropagation();
			}
		},

		/**
		 * Checks the event and returns a boolean whether the event may be claimed, and its propagation stopped.
		 *
		 * @param {Event|playtotv.event.Event} event
		 * @returns {boolean} priority
		 */
		hasPriority: function(e) {
			var requested = this.orientation;
			var sequence = e.detail.sequence;
			return (sequence.id === this.touchId) &&
				(this.priority || !requested || (this.getOrientation(sequence) === requested));
		},

		/**
		 * Updates the on screen position of the node while dragging,
		 *
		 */
		handleFrame: function() {
			this.moveTo(this.position);
			this.requestFrame();
		},

		/**
		 * Internal end handler when the draggable node has come to a full stop.
		 *
		 */
		handleStopped: function() {
			this.cancelFrame();
			this.setState(STATE_PASSIVE);
			event.dispatchEvent(this.node, 'em:dragend', false, false, { offset: this.position });
		},

		/**
		 * Moves this.node to the given position (unrestricted) and fires a 'em:dragmove' event.
		 * @param {playtotv.css.Vector} position
		 */
		moveTo: function(position) {
			this.position = position;
			style.setTranslate(this.node, position);
			event.dispatchEvent(this.node, 'em:dragmove', false, false, { offset: position });
		},

		/**
		 * Moves this.node by the given vector (unrestricted) via this.moveTo
		 * @param {playtotv.css.Vector} vector
		 */
		moveBy: function(vector) {
			var position = this.position.add(vector);
			this.moveTo(position);
		},

		/**
		 * Animates a target node into view, aligning it with the top-left edge of the view. The target node
		 * must be a childnode of this instance's one, and content should exceed viewsize for best effect.
		 *
		 * @param {Node} target
		 * @param {number} [time=1000]
		 */
		moveIntoView: function(target, time) {
			if (!this.node.contains(target)) {
				return;
			}

			this.setup();
			this.swipeTo(Vector.ZERO.subtract(
				style.getOffset(target)
			).add(
				style.getOffset(this.node)
			), time);
		},

		/**
		 * Moves this.node to the given relative position, e.g. (0.5, 0.5) for half way.
		 * @param {playtotv.css.Vector} position
		 */
		setRelative: function(position) {
			this.moveTo(this.space.multiply(position));
		},

		/**
		 * Returns this.node's relative position in regards to its available space. Essentially this is a normalized
		 * vector with x and y properties between 0 and 1, though elasticity may lead to values exceeding this.
		 *
		 * @returns {playtotv.css.Vector} relative position
		 */
		getRelative: function() {
			var position = this.position;
			var space = this.space;

			// prevent NaNs if x or y space is 0
			return new Vector(
				space.x ? position.x / space.x : 0,
				space.y ? position.y / space.y : 0
			);
		},

		/**
		 * Returns the orientation of the current dragging sequence. Once set, the orientation is locked until the
		 * next drag sequence starts. This allows nested components to properly handle orientation preferences /
		 * propagation; e.g. vertical scrolling within horizontal pages.
		 *
		 * @param {playtotv.touch.Sequence} sequence
		 * @returns {number} orientation Vector.HORIZONTAL or Vector.VERTICAL
		 */
		getOrientation: function(sequence) {
			if (!this.lastOrientation) {
				this.lastOrientation = touchManager.getOrientation(sequence);
			}

			return this.lastOrientation;
		},

		/**
		 * Returns the current position.
		 *
		 * @returns {playtotv.css.Vector} position
		 */
		getPosition: function() {
			return this.position;
		},

		/**
		 * Swipes the node across the given vector over the given time.
		 *
		 * @param {playtotv.css.Vector} vector
		 * @param {number} [time=1000]
		 */
		swipe: function(vector, time) {
			var position = this.position;
			var target = this.restrict(position.add(vector), false);
			this.vector = target.subtract(position);
			this.start = position;

			this.cancelFrame(); // if any

			this.transition.play({
				duration: time || 1000
			});
		},

		/**
		 * Swipes the node to the given position over the given time.
		 *
		 * @param {playtotv.css.Vector} position
		 * @param {number} [time=1000]
		 */
		swipeTo: function(position, time) {
			position = position.subtract(this.position);
			this.swipe(position, time);
		},

		/**
		 * swipe transition frame callback
		 * @private
		 */
		handleAnim: function(n) {
			var moved = this.vector.multiplyBy(n);
			this.moveTo(this.start.add(moved));

			if (n === 1) {
				this.handleStopped();
			}
		},

		/**
		 * Restricts the given position to the instance's orientation, and may optionally apply elasticity to
		 * positions outside of min and max values.
		 *
		 * @param {playtotv.css.Vector} position
		 * @param {boolean} elastic
		 * @returns {playtotv.css.Vector} restricted
		 */
		restrict: function(position, elastic) {
			var pos = this.limiter.multiply(position);
			var min = this.min;
			var max = this.max;

			if (elastic && this.content.exceeds(this.view)) {
				var ex = (pos.x < min.x) ? pos.x - min.x : (pos.x > max.x) ? pos.x - max.x : 0;
				var ey = (pos.y < min.y) ? pos.y - min.y : (pos.y > max.y) ? pos.y - max.y : 0;

				this.pulling = (ex || ey) ? true : false;

				var res = this.restricter;
				return new Vector(
					pos.x - (ex * res.x),
					pos.y - (ey * res.y)
				);
			} else {
				return pos.limit(min, max);
			}
		},

		/**
		 * Dispose handler, can automatically be called when this.node is removed from the DOM,
		 * but ONLY if this instance is managed by the {@link playtotv.widget.widgetManager}.
		 */
		dispose: function() {
			if (this.transition.playing) {
				this.transition.stop();
			}

			this.cancelFrame();

			Widget.prototype.dispose.call(this);
		},

		callbacks: [
			'handleSwipe',
			'handleFrame',
			'handleAnim',
			'dragStart',
			'dragMove',
			'dragEnd'
		]
	});

	return DraggableWidget;
});
