playtotv.define('playtotv.net.request', [
	'playtotv.net.xhrProxy'
], function(
	xhrProxy
) {

	// TODO: Use Promise.Cancellable instead of Promise and have it xhr.abort() on cancellation
	// TODO: Replace by use of fetch shim (https://github.com/github/fetch) or improve this based on that

	'use strict';

	var ptv = playtotv;
	var log = ptv.Logger.get(this.id);
	var util = ptv.util;

	/**
	 * Performs an HTTP request, optionally using {@link playtotv.net.xhrProxy} for improved cross-origin support.
	 *
	 * @function
	 * @name request
	 * @memberOf playtotv
	 *
	 * @param {string} url URL to request
	 * @param {Object} [settings] Request settings
	 * @param {string} [settings.label=type + ' ' + url] Optional label for debugging
	 * @param {string} [settings.type='GET'] HTTP request type (GET/POST/PUT/DELETE)
	 * @param {Object} [settings.urlParams={}] Parameters to add to url
	 * @param {Object} [settings.headers={}] HTTP request headers
	 * @param {Object} [settings.username] HTTP Basic Authentication username
	 * @param {Object} [settings.password] HTTP Basic Authentication password
	 * @param {Object} [settings.data={}] HTTP request data
	 * @param {Function} [settings.onProgressDown] Download progress callback
	 * @param {Function} [settings.onProgressUp] Upload progress callback
	 * @param {string} [settings.dataType='text'] If 'json' then response text will be processed through JSON.parse()
	 * @param {boolean} [settings.xhrProxy] Whether to use {@link playtotv.net.xhrProxy}, default true if cross-origin
	 * @param {boolean} [settings.cache=true] If false, will prevent caching by adding a cache buster query to url
	 * @param {number} [settings.timeout=0] If set, will timeout request after specified time in ms
	 * @property {[Function]} taps Array of tap functions to call on each request with url, label and promise arguments
	 * @returns {Promise} Response data promise, resolves if status code is in: 2xx, 304, 1223, rejects otherwise
	 */
	var request = function(url, settings) {
		/*jshint maxstatements:100, maxcomplexity:100*/

		var windowOrigin = util.getOrigin();
		var urlOrigin = util.getOrigin(url);

		settings = ptv.params(settings, {
			label: ((settings && settings.type) || 'GET') + ' ' + url,
			type: 'GET',
			urlParams: {},
			headers: {},
			username: undefined,
			password: undefined,
			data: {},
			onProgressDown: undefined,
			onProgressUp: undefined,
			dataType: 'text',
			xhrProxy: urlOrigin !== windowOrigin,
			cache: true,
			timeout: 0
		});

		var xhr;
		// TODO: Rewrite without use of Promise.defer()
		var deferred = Promise.defer();
		var finished = false;
		var timeoutRef = null;

		log.s(settings.label);

		function finish(status) {
			if (finished) {
				return; // already finished
			}

			if (timeoutRef) {
				clearTimeout(timeoutRef);
			}

			var responseData = xhr.responseText;
			if (settings.dataType === 'json') {
				try {
					responseData = responseData ? JSON.parse(responseData) : {};
				} catch (e) {}
			}

			// IE8 and IE9 mangle status 204 to 1223 sometimes, so we have to accept that as well
			if ((status >= 200 && status < 300) || [ 304, 1223 ].indexOf(status) !== -1) {
				deferred.resolve(responseData);
			} else {
				var error = new Error(
					// Use error message from server response if available
					responseData && responseData.error && responseData.error.message ||
					'Request ' + settings.label + ' failed with status ' + status
				);
				deferred.reject(ptv.extend(error, {
					label: settings.label,
					status: status,
					xhr: xhr,
					data: responseData
				}));
			}

			finished = true;
		}

		// Use xhrProxy for cross origin requests
		xhr = settings.xhrProxy ? xhrProxy.create(urlOrigin) : new XMLHttpRequest();

		var loadEventSupported = 'onload' in xhr;
		var errorEventSupported = 'onerror' in xhr;
		var abortEventSupported = 'onabort' in xhr;
		var timeoutEventSupported = 'ontimeout' in xhr;

		url = util.addURLParams(url, settings.urlParams);
		if (!settings.cache) {
			url = util.addURLParam(url, 'nocache', Date.now());
		}

		xhr.open(settings.type, url, true, settings.username, settings.password);

		if (loadEventSupported) {
			xhr.addEventListener('load', function() {
				// Note (pp): Chrome fires 'load' event with 404 on my localhost Apache so have to double check status
				finish(xhr.status);
			}, false);
		} else {
			xhr.onreadystatechange = function() {
				if (xhr.readyState !== 4) {
					return; // not yet DONE
				}
				finish(xhr.status);
			};
		}

		if (errorEventSupported) {
			xhr.addEventListener('error', function() {
				finish(xhr.status);
			}, false);
		}

		if (abortEventSupported) {
			xhr.addEventListener('abort', function() {
				finish('abort');
			}, false);
		}

		if (settings.timeout) {
			if (timeoutEventSupported) {
				xhr.addEventListener('timeout', function() {
					finish('timeout');
				}, false);
				xhr.timeout = settings.timeout;
			} else {
				timeoutRef = setTimeout(function() {
					finish('timeout');
					xhr.abort();
				}, settings.timeout);
			}
		}

		var onProgressDown = settings.onProgressDown;
		if (onProgressDown) {
			xhr.addEventListener('progress', function(e) {
				if (e.lengthComputable) {
					onProgressDown(e);
				}
			}, false);
		}

		var onProgressUp = settings.onProgressUp;
		if (onProgressUp) {
			xhr.upload.addEventListener('progress', function(e) {
				if (e.lengthComputable) {
					onProgressUp(e);
				}
			}, false);
		}

		ptv.each(settings.headers, function(value, key) {
			xhr.setRequestHeader(key, value);
		});

		if (settings.type === 'PUT' || settings.type === 'POST') {
			xhr.setRequestHeader('Content-type', 'application/json');
			xhr.send(JSON.stringify(settings.data));
		} else {
			xhr.send();
		}

		request.taps.forEach(function(tap) {
			tap(url, settings.label, deferred.promise);
		});

		return deferred.promise;
	};

	request.taps = [];

	return request;
});
