playtotv.define('playtotv.user.UserController', [
	'playtotv.mvc.Controller',
	'playtotv.user.usersAPI'
], function(
	Controller,
	usersAPI
) {

	// Note: This just does automatic registerAsGuest and loginByAuthenticationToken for now
	// TODO: Complete this with manual (email, ..) login support and then remove UserManager, User and LocalUser
	// TODO: Add support for 463
	// TODO: Add support for 401 if necessary

	'use strict';

	var ptv = playtotv;
	var log = ptv.Logger.get(this.id);

	return class UserController extends Controller {
		constructor(context) {
			var stored = context.stored || {};
			super(context, {
				model: {
					current: stored.current || null,
					online: false
				}
			});

			this.addMethods([
				'logout',
				'getPrivateProperty',
				'setPrivateProperty',
				'updateProfile',
				'extendPrivateProfile'
			]);

			this.login();
		}

		loginBy(method, params) {
			return this.scope.call('server.call', method, params).then(user => {
				this.set('current', user);

				// Note: current user model property is persisted in storage for fast resume and offline support
				// Things depending on user being logged in for sure should wait for this
				this.set('online', true);

				return user;
			});
		}

		loginByToken() {
			var user = this.model.current;
			var authenticationToken = user && user.authenticationToken;
			return !authenticationToken ? Promise.reject() :
				this.loginBy(usersAPI.loginByAuthenticationToken, {
					authenticationToken: authenticationToken
				});
		}

		registerAsGuest() {
			return this.loginBy(usersAPI.registerAsGuest, {
				optionalValues: null
			});
		}

		login() {
			return this.loginByToken().catch(() => {
				// TODO: if mode is guest then registerAsGuest else set waiting true and wait for manual login
				return this.registerAsGuest().catch(error => {
					log.e('login(): registerAsGuest failed, retrying in 5s', error);
					return Promise.delay(5000).then(() => this.login());
				});
			});
		}

		logout() {
			return this.scope.call('server.call', usersAPI.logout).finally(() => {
				// Reset (and reload) is a rough but simple solution to a rarely used feature
				// Makes the dealing with this easier for all controllers
				this.scope.reset();
			});
		}

		getPrivateProperty(name) {
			var user = this.model.current;
			return user && user.profile.privateProperties[name];
		}

		setPrivateProperty(name, value) {
			var user = this.model.current;
			return this.scope.call('server.call', usersAPI.setPrivateProperty, {
				propertyName: name,
				value: value
			}).then(() => {
				user.profile.privateProperties[name] = value;
				this.publish('set-private-property-' + name, value);
			});
		}

		updateProfile(data) {
			var user = this.model.current;
			data = ptv.params(data, {
				displayName: user.displayName || null,
				avatarUrl: user.avatarUrl || null,
				properties: user.profile.profileProperties,
				privateProperties: user.profile.privateProperties
			});
			return this.scope.call('server.call', usersAPI.updateProfile, data).then(() => {
				user.displayName = data.displayName;
				user.avatarUrl = data.avatarUrl;
				user.profile.profileProperties = data.properties;
				user.profile.privateProperties = data.privateProperties;
				this.publish('update-profile', data);
			});
		}

		extendPrivateProfile(properties) {
			var user = this.model.current;
			return this.updateProfile({
				privateProperties: ptv.extend({}, user.profile.privateProperties, properties)
			});
		}
	};
});
