playtotv.define('playtotv.storage.Storage', [
	'playtotv.storage.MockStorage'
], function(
	MockStorage
) {

	'use strict';

	var log = playtotv.Logger.get(this.id);

	var Storage = new playtotv.Class({
		/** @lends playtotv.storage.Storage.prototype */

		/**
		 * Wraps localStorage or sessionStorage, providing support for storage scoping through prefixing and getting
		 * and setting non-string values through JSON encoding.
		 *
		 * This depends on the storage object's setItem, getItem, removeItem and key methods and length property.
		 *
		 * If window.localStorage is falsy then a new {@link playtotv.storage.MockStorage} instance is used by default.
		 *
		 * In any case, storage should be considered unreliable. For reliable persistence of data, use our servers.
		 *
		 * @constructs
		 * @param {Object} [storage=window.localStorage] Storage object to use
		 * @param {string} [prefix=''] Key prefix for this instance
		 */
		initialize: function(storage, prefix) {
			this.storage = storage || window.localStorage || new MockStorage();
			this.prefix = prefix || '';
		},

		/** @lends playtotv.storage.Storage.prototype */

		/**
		 * Returns the stored value for a given key. Returns defaultValue if no value is found.
		 *
		 * @param {string} key
		 * @param {mixed} defaultValue
		 * @returns {mixed} value
		 */
		get: function(key, defaultValue) {
			var value = this.storage.getItem(this.prefix + key);

			if (value === null || value === undefined) {
				return defaultValue; // always return defaultValue/undefined if not found (Firefox returns null)
			} else {
				try { value = JSON.parse(value); } catch (e) {} // catch for backwards compatibility (e.g. strings)
				return value;
			}
		},

		/**
		 * Stores a value with the given key. If value equals null or undefined then stored value is removed instead.
		 *
		 * Note: This should not be considered to be reliable let alone secure, always provide fallbacks.
		 *
		 * @param {string} key
		 * @param {mixed} value
		 * @param {Function} [quotaHandler=this.quotaHandler]
		 */
		set: function(key, value, quotaHandler) {
			quotaHandler = quotaHandler || this.quotaHandler;

			if (value === null || value === undefined) {
				this.remove(key); // setting undefined is not supported, remove it instead with likewise result
			} else {
				// Catch quota exceptions in case we run out or have none like in Safari Private Browsing
				try {
					this.storage.setItem(this.prefix + key, JSON.stringify(value));
				} catch (e) {
					if (e instanceof window.DOMException && e.code === e.QUOTA_EXCEEDED_ERR) {
						quotaHandler.call(this, e, key, value);
					} else {
						throw e; // pass on any other exceptions
					}
				}
			}
		},

		/**
		 * Default quota exceeded exception handler that simply logs the exception.
		 *
		 * @param {Exception} e
		 * @param {string} key
		 * @param {mixed} value
		 */
		quotaHandler: function(e, key, value) {
			log.e('Caught quota exception: ' + e.name + ', while setting: ' + key, value, e);
		},

		/**
		 * Removes a key
		 *
		 * @param {string} key
		 * @returns {mixed} value
		 */
		remove: function(key) {
			this.storage.removeItem(this.prefix + key);
		},

		/**
		 * Removes all items with this instance's prefix.
		 */
		clear: function() {
			var storage = this.storage;
			var i = 0;
			while (i < storage.length) {
				var key = storage.key(i);
				if (key.indexOf(this.prefix) === 0) {
					storage.removeItem(key);
				} else {
					i++;
				}
			}
		},

		/**
		 * Returns a sub storage instance with combined prefixes.
		 *
		 * @param {string} subprefix
		 * @returns {playtotv.storage.Storage} storage
		 */
		subStorage: function(subPrefix) {
			return new Storage(this.storage, this.prefix + subPrefix);
		}
	});

	return Storage;
});
