playtotv.define('playtotv.css.Vector', [], function() {

	'use strict';

	/**
	 * Shorthands
	 *
	 */

	var _min = Math.min;
	var _max = Math.max;
	var _sqrt = Math.sqrt;

	/**
	 * 2D Vector
	 *
	 * @name playtotv.css.Vector
	 * @class
	 * @constructor
	 * @param {number} x
	 * @param {number} y
	 */
	var Vector = new playtotv.Class({
		initialize: function(x, y) {
			this.x = x;
			this.y = y;
		},

		/** @lends playtotv.css.Vector.prototype */

		/**
		 * Adds a vector and returns the result.
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {playtotv.css.Vector} result
		 */
		add: function(v) {
			return new Vector(this.x + v.x, this.y + v.y);
		},

		/**
		 * Subtracts a vector and returns the result.
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {playtotv.css.Vector} result
		 */
		subtract: function(v) {
			return new Vector(this.x - v.x, this.y - v.y);
		},

		/**
		 * Multiplies with a vector and returns the result.
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {playtotv.css.Vector} result
		 */
		multiply: function(v) {
			return new Vector(this.x * v.x, this.y * v.y);
		},

		/**
		 * Returns a vector multiplied by a single number
		 *
		 * @param {number} value
		 */
		multiplyBy: function(n) {
			return new Vector(this.x * n, this.y * n);
		},

		/**
		 * Divides by a vector and returns the result.
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {playtotv.css.Vector} result
		 */
		divide: function(v) {
			return new Vector(this.x / v.x, this.y / v.y);
		},

		/**
		 * Returns a vector divided by a single number
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {playtotv.css.Vector} result
		 */
		divideBy: function(n) {
			return new Vector(this.x / n, this.y / n);
		},

		/**
		 * Returns the dot product with another vector
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {Float} dot
		 */
		dot: function(b) {
			var d = this.multiply(b);
			return (d.x + d.y);
		},

		/**
		 * Returns the vector's magnitude
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {Float} magnitude
		 */
		magnitude: function() {
			var x = this.x;
			var y = this.y;
			return _sqrt(x * x + y * y);
		},

		/**
		 * Returns a normalized version of this vector
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {playtotv.css.Vector} result
		 */
		normalize: function() {
			var m = this.magnitude();
			return new Vector(this.x / m, this.y / m);
		},

		/**
		 * Returns the inversed version of this vector
		 *
		 */
		inverse: function() {
			return new Vector(-this.x, -this.y);
		},

		/**
		 * Returns modulo result with another vector
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {playtotv.css.Vector} result
		 */
		modulo: function(v) {
			return new Vector(
				v.x ? this.x % v.x : this.x,
				v.y ? this.y % v.y : this.y
			);
		},

		/**
		 * Indicates whether a vector exceeds another one (in any 1 direction)
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {boolean} result
		 */
		exceeds: function(v) {
			return this.x > v.x || this.y > v.y || false;
		},

		/**
		 * Returns a vector with the smallest x and y values
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {playtotv.css.Vector} result
		 */
		min: function(v) {
			return new Vector(
				_min(this.x, v.x),
				_min(this.y, v.y)
			);
		},

		/**
		 * Returns a vector with the largest x and y values
		 *
		 * @param {playtotv.css.Vector} vector
		 * @returns {playtotv.css.Vector} result
		 */
		max: function(v) {
			return new Vector(
				_max(this.x, v.x),
				_max(this.y, v.y)
			);
		},

		/**
		 * Limits a vector to x and y values that are within the given minimal and maximal boundary vectors.
		 *
		 * @param {playtotv.css.Vector} min
		 * @param {playtotv.css.Vector} max
		 * @returns {playtotv.css.Vector} limited
		 */
		limit: function(min, max) {
			// return this.max(min).min(max); // creates 2 Vector instances
			return new Vector(
				_min(_max(this.x, min.x), max.x),
				_min(_max(this.y, min.y), max.y)
			);
		},

		/**
		 * Returns a comma separated string of the vector's x and y properties. An optional unit
		 * type may be given (px, %, etc), which defaults to px for CSS purposes.
		 *
		 * @param {string} [unit='px']
		 * @returns {string} position
		 */
		toString: function(unit) {
			unit = unit || 'px';
			return this.x + unit + ',' + this.y + unit;
		}
	});

	// static Vector members
	playtotv.extend(Vector, {

		/** @lends playtotv.css.Vector */

		/**
		 * @property
		 * @type number
		 */
		FREE: 0,

		/**
		 * @property
		 * @type number
		 */
		VERTICAL: 1,

		/**
		 * @property
		 * @type number
		 */
		HORIZONTAL: 2,

		/**
		 * @property
		 * @type number
		 */
		UP: 3,

		/**
		 * @property
		 * @type number
		 */
		RIGHT: 4,

		/**
		 * @property
		 * @type number
		 */
		DOWN: 5,

		/**
		 * @property
		 * @type number
		 */
		LEFT: 6,

		/**
		 * Zero vector
		 * @property
		 * @type Vector
		 */
		ZERO: new Vector(0, 0),

		/**
		 * Parses values into a Vector. Uses x for y when not specified.
		 *
		 * @static
		 * @param {string|number} x
		 * @param {string|number} [y]
		 * @returns {playtotv.css.Vector} vector
		 */
		parseFloat: function(x, y) {
			x = parseFloat(x);
			y = arguments.length < 2 ? x : parseFloat(y);
			return new Vector(x, y);
		}
	});

	return Vector;
});
