playtotv.define('playtotv.facebook.FacebookPostWidget', [ 'playtotv.widget.Widget' ], function(Widget) {

	'use strict';

	var ATTR_PREFIX = 'data-facebook-';

	var log = playtotv.Logger.get(this.id);

	return new playtotv.Class({
		/** @lends playtotv.facebook.FacebookPostWidget.prototype */

		extend: Widget,

		/**
		 * Widget that facilitates making a Facebook post when a user clicks on a node.
		 *
		 * Content parameters:
		 * <ul>
		 * <li><a href="http://developers.facebook.com/docs/reference/dialogs/feed/">Facebook documentation</a>
		 * <li>Can be defined using options.content and overridden using data-facebook-* attributes
		 * <li>Values will be processed at post time by options.process, allowing for substitutions etc.
		 * </ul>
		 *
		 * Note: If you do not post interactively you should use onPost to show a confirmation to the user!
		 *
		 * @constructs
		 * @extends playtotv.widget.Widget
		 * @param {Node} node
		 * @param {Options} options
		 * @param {playtotv.facebook.Facebook} options.facebook Facebook instance to use
		 * @param {boolean} [options.interactive=true] {@link playtotv.facebook.Facebook#post} interactive
		 * @param {number} [options.timeout] {@link playtotv.facebook.Facebook#post} timeout
		 * @param {string} [options.content={}] Default post content params (e.g. name, link, etc.)
		 * @param {Function} [options.process] Content processing function which is passed (value, key)
		 * @param {Function} [options.onResolve] Function to be called each time a post successfully resolves
		 * @param {Function} [options.onReject] Function to be called each time a post is rejected (e.g. cancelled)
		 */
		initialize: function(node, options) {
			this._super(node);
			this.options = playtotv.params(options, {
				facebook: playtotv.REQUIRED,
				interactive: true,
				timeout: undefined,
				content: {},
				process: function(value /*, key*/) { return value; }, // leave as-is by default
				onResolve: function(postId) { log.d('onResolve, post id:', postId); },
				onReject: function(reason) { log.d('onReject', reason); }
			});
			this.addEventListener(node, 'click', this.onClick);
		},

		onClick: function(e) {
			e.preventDefault();
			this.post();
			return false;
		},

		post: function() {
			var node = this.node;
			var options = this.options;
			var facebook = options.facebook;

			var content = playtotv.extend({}, options.content);

			playtotv.toArray(node.attributes).forEach(function(attr) {
				if (attr.name.indexOf(ATTR_PREFIX) === 0) {
					content[attr.name.substr(ATTR_PREFIX.length)] = attr.value;
				}
			});

			playtotv.each(content, function(value, key) {
				content[key] = options.process(content[key], key);
			});

			facebook.post(content, options.interactive, options.timeout).then(options.onResolve, options.onReject);
		},

		callbacks: [
			'onClick'
		]
	});
});
