/**
 * Facilitates making cross origin requests by creating a mock XMLHttpRequest that proxies function calls on to a real
 * XMLHttpRequest created in an iframe of the target origin. These iframes require the 'xhr-proxy.html' file to be
 * available from the target server's root to complete the proxy functionality.
 *
 * @function
 * @name xhrProxy
 * @memberOf playtotv
 * @namespace
 */
playtotv.define('playtotv.net.xhrProxy', [], function() {

	'use strict';

	// Abort if we are not running in a browser (e.g: in Node)
	if (typeof window === 'undefined') {
		return;
	}

	var log = playtotv.Logger.get(this.id);
	var dom = playtotv.dom;
	var event = playtotv.event;

	var xhrMethods = [ 'open', 'setRequestHeader', 'send', 'abort' ]; // to be proxied
	var deferredWindows = {};
	var xhrs = {};
	var xhrCounter = 0;
	var supportsObject = false; // start without object support for messaging
	var rpcAbort = { method: 'abort', args: [] }; // special instance for easy detection with indexOf

	// Test to see if we can skip data stringification
	function supportsObjectTestHandler(msg) {
		supportsObject = typeof msg.data === 'object';
		event.removeListener(window, 'message', supportsObjectTestHandler);
	}
	event.addListener(window, 'message', supportsObjectTestHandler);
	window.postMessage({}, '*');

	event.addListener(window, 'message', function(msg) {
		/*jshint maxcomplexity:100*/
		var deferredWindow = deferredWindows[msg.origin];
		if (deferredWindow) { // ignore messages from other origins (Facebook, Hyves, ..)
			var data = typeof msg.data === 'string' ? JSON.parse(msg.data) : msg.data;
			var type = data.type;
			var xhr = xhrs[data.id];
			var label = 'onMessage: ' + msg.origin + ': ' + type;

			// Note: We use immediate to prevent Facebook's window message handler from whining
			(msg.stopImmediatePropagation || msg.stopPropagation).call(msg);

			if (type === 'initialized') {
				log.s(label);
				// Resolving with a direct iframe window reference would result in 'when' examining the value to see
				// if it is a Promise later and in doing so throwing a cross origin DOM Exception 18.
				// We circumvent this by wrapping the window reference in another object.
				deferredWindow.resolve({ win: msg.source });
			} else if (!xhr) {
				log.d(label + ': xhr: ' + data.id + ' is undefined');
			} else if (type === 'event') {
				var uploadEvent = data.upload;
				var e = data.event;
				var eventType = e.type;
				log.s(label + ': ' + (uploadEvent ? 'upload: ' : '') + eventType + ': ' + e.loaded);
				var obj = uploadEvent ? xhr.upload : xhr;
				var listeners = obj._eventListenersMap[eventType];
				listeners.forEach(function(listener) {
					listener(e);
				});
			} else if (type === 'readyStateChange') {
				xhr.status = data.status;
				xhr.responseText = data.responseText;
				xhr._readyStateChange(data.readyState);
			} else {
				log.w(label + ': unsupported type');
			}
		}
	});

	function createIFrame(url) {
		var iframe = dom.create('iframe', {
			src: url
		});
		playtotv.extend(iframe.style, {
			width: 0,
			height: 0,
			visibility: 'hidden',
			overflow: 'hidden',
			position: 'absolute',
			display: 'none'
		});
		return iframe;
	}

	function getWindow(origin) {
		var deferredWindow = deferredWindows[origin];
		if (deferredWindow) {
			return deferredWindow.promise;
		}

		// Create an iframe, then check to see if that has successfully initialized.
		log.d('getWindow(): ' + origin);

		// TODO: Rewrite without use of Promise.defer()
		var deferred = deferredWindows[origin] = Promise.defer();

		var initialized = false;
		deferred.promise.then(function() {
			initialized = true;
		});

		var url = origin + '/xhr-proxy.html';
		var iframe = createIFrame(url);

		// Note: Listening to 'error' event does not seem to be necessary
		// A 'load' event will always fire, any errors will be returned as iframe contents

		event.addListener(iframe, 'load', function() {
			// The initialization confirmation is sent back as a window message by xhr-proxy.html so the check also
			// has to be deferred through a window message to ensure that it is processed after the initialization.
			// Note: I could not find a good way to defer this compatible with IE browsers, hence the setTimeout
			setTimeout(function() {
				if (!initialized) {
					// Clean up to allow for future retries
					deferredWindows[origin] = null;
					dom.remove(iframe);
					deferred.reject(new Error('Failed to initialize: ' + url));
				}
			}, 1000);
		});

		dom.appendTo(iframe, document.body);

		return deferred.promise; // will be resolved by window initialized callback eventlistener
	}

	function execBatch(origin, originWindow, id) {
		var xhr = xhrs[id];
		if (!xhr) {
			return; // batch with abort has been processed already
		}

		var batch = xhr._batch;
		if (batch.length === 0) {
			return; // batch has been processed already
		}

		if (batch.indexOf(rpcAbort) >= 0) {
			delete xhrs[id]; // clean up (prevent leak)
			if (batch.length > 1) {
				batch = [ rpcAbort ]; // no use to send anything but abort
			}
		}

		var data = { id: id, rpcs: batch };
		originWindow.postMessage(supportsObject ? data : JSON.stringify(data), origin);

		xhr._batch = []; // batch has been processed
	}

	function exec(id, method, args) {
		var xhr = xhrs[id];

		if (!xhr) {
			throw new Error('xhr undefined: ' + id); // note: xhr can no longer be used after abort() or readyState 4
		}

		xhr._batch.push(method === 'abort' ? rpcAbort : { // special abort instance for easy detection with indexOf
			method: method,
			args: [].slice.call(args, 0) // turn arguments into array so Safari's JSON.stringify works properly
		});

		xhr._execBatch();
	}

	function addEventSupport(id, upload) {
		var xhr = xhrs[id];
		var obj = upload ? xhr.upload : xhr;
		obj._eventListenersMap = {};
		obj.addEventListener = function(type, listener) {
			var eventListeners = obj._eventListenersMap[type];
			if (!eventListeners) {
				eventListeners = obj._eventListenersMap[type] = [];
				exec(id, 'addEventListener', [ upload, type ]);
			}
			eventListeners.push(listener);
		};
	}

	return {
		/** @lends playtotv.net.xhrProxy */

		/**
		 * Creates a mock XMLHttpRequest instance.
		 *
		 * @param {string} origin The request origin to proxy to.
		 * @return {Object} Mock XMLHttpRequest instance.
		 */
		create: function(origin) {
			var id = xhrCounter++;
			var xhr = xhrs[id] = {
				_batch: [], // private field
				_readyStateChange: function(readyState) {
					xhr.readyState = readyState;
					if (xhr.onreadystatechange) {
						xhr.onreadystatechange();
					}
					if (readyState === 4) {
						delete xhrs[id]; // clean up (prevent leak)
					}
				},
				_execBatch: playtotv.unstack(function() { // private function, unstack to optimize batching
					getWindow(origin).then(function(originWindow) {
						execBatch(origin, originWindow.win, id);
					}, function(reason) {
						xhr.status = 'no-xhr-proxy';
						xhr.reason = reason;
						xhr._readyStateChange(4);
					});
				}),
				upload: {},
				onreadystatechange: null,
				readyState: 0,
				status: 0,
				responseText: ''
			};

			xhrMethods.forEach(function(method) {
				xhr[method] = function() {
					exec(id, method, arguments);
				};
			});

			addEventSupport(id, false); // main xhr events
			addEventSupport(id, true); // upload events

			return xhr;
		},

		/** For debugging purposes */
		debug: {
			deferredWindows: deferredWindows,
			xhrs: xhrs
		}
	};
});
