(function() {

	// TODO: Replace all uses of playtotv.Class and remove this file

	'use strict';

	var objectCreate = Object.create || function(proto) {
		function Proto() {}
		Proto.prototype = proto;
		return new Proto();
	};

	/**
	 * Creates a class of which instances can be created at a later stage. The definition object defines a few
	 * predefined properties for class construction. Any other properties and/or methods are added to the class'
	 * prototype.
	 *
	 * @class
	 * @param {Object} definition
	 * @param {Function} definition.initialize constructor function
	 * @param {playtotv.Class} [defition.extend] Class to extend on
	 * @param {Array} [definition.callbacks] Array of method names that are bound to each instance (e.g. for callbacks)
	 * @returns {Class} the created class
	 */
	playtotv.Class = function(definition) {
		/*jshint maxcomplexity:100*/

		var Construct;
		var Base;
		var proto = {};
		var callbacks = [];

		var entity, type;
		for (type in definition) {
			if (definition.hasOwnProperty(type)) {
				entity = definition[type];
				switch (type) {

				// constructor
				case 'initialize':
					Construct = entity;
					break;

				// class to extend
				case 'extend':
					Base = entity;
					break;

				// callbacks to bind
				case 'callbacks':
					callbacks = entity;
					break;

				// prototype methods
				default:
					proto[type] = entity;
					break;
				}
			}
		}

		// Base must be a function
		if (Base && !(Base instanceof Function)) {
			throw new TypeError('Function expected using \'extend\' with playtotv.Class');
		}

		// Allow omitting the constructor, also when extending
		if (!Construct) {
			Construct = Base ? function() {
				// in case of extending, auto super
				this._super.apply(this, arguments);
			} : function() {
				// otherwise, do nothing
			};
		}

		// wrap the constructor function if necessary
		if (Base || callbacks.length > 0) {
			Construct = this.wrap(Construct, Base, callbacks);
		}

		// extend with our class prototype (can override base and mixin prototypes)
		playtotv.extend(Construct.prototype, proto);

		// and finally return it.
		return Construct;
	};

	playtotv.Class.prototype = {

		/**
		 * Wraps a constructor function with another function that can extend a class with a base class, mix in more
		 * classes and have callback methods be bound to their instances.
		 *
		 * @param {Function} Construct The class to extend
		 * @param {Function} [Base] The base class
		 * @param {Array} callbacks Array of callback method names
		 * @returns {Function} combined constructor
		 */
		wrap: function(Construct, Base, callbacks) {

			var Wrapped = function() {

				// hardwire callbacks to the current instance
				callbacks.forEach(function(name) {
					var entity = this[name];
					if (entity instanceof Function) {
						this[name] = entity.bind(this);
					} else {
						// callbacks MUST exist, leniency here might cause really obscure behaviour or errors.
						throw new ReferenceError('Callback "' + name + '" is undefined or not a Function');
					}
				}, this);

				// set a super reference during instanciation only, end with undefined
				// TODO: rename to super when IE8 no longer needs to be supported
				this._super = Base || undefined;

				// and run the actual constructor
				return Construct.apply(this, arguments);
			};

			// Maintain the prototype chain for the instanceof operator.
			if (Base) {
				Wrapped.prototype = objectCreate(Base.prototype);
				Wrapped.prototype.constructor = Wrapped;
			}

			return Wrapped;
		}
	};

	/**
	 * Immediately returns an instance of the given definition, for singletons / static classes.
	 *
	 * @static
	 * @param {Object} definition
	 * @returns {Object} instance
	 */
	playtotv.Class.getInstance = function(definition) {
		var Type = new playtotv.Class(definition);
		return new Type();
	};

}());
