playtotv.define('playtotv.pubsub.PubSub', [
	'playtotv.pubsub.Subscription'
], function(
	Subscription
) {

	'use strict';

	var ptv = playtotv;

	function remove(list, item) {
		var index = list.indexOf(item);
		if (index !== -1) {
			list.splice(index, 1);
			return true;
		}
		return false;
	}

	return new ptv.Class({
		/** @lends playtotv.pubsub.PubSub.prototype */

		/**
		 * Class for managing publishing of data to handlers subscribed to channels.
		 *
		 * @constructs
		 *
		 * @property {[playtotv.pubsub.Subscription]} subscriptions Subscriptions container array
		 * @property {[Promise.Cancellable]} cancellables List of active cancellables
		 */
		initialize: function() {
			this.subscriptions = [];
			this.cancellables = [];
		},

		/**
		 * Creates and adds a subscription.
		 *
		 * @param {string|RegExp} selector Channel selector
		 * @param {Function} handler Subscription callback
		 * @throws {TypeError} If selector or handler is invalid
		 * @returns {playtotv.pubsub.Subscription} Added subscription
		 */
		subscribe: function(selector, handler) {
			var subscription = new Subscription(selector, handler);
			this.subscriptions.push(subscription);
			return subscription;
		},

		/**
		 * Removes and disposes a subscription.
		 *
		 * @param {playtotv.pubsub.Subscription} subscription Subscription to remove
		 * @returns {boolean} True if subscription was successfully removed
		 */
		unsubscribe: function(subscription) {
			var subscriptions = this.subscriptions;
			subscription.dispose();
			return subscriptions ? remove(subscriptions, subscription) : false;
		},

		/**
		 * Convenience method to make a number of subscriptions at once.
		 *
		 * @param {Array} args Array of argument arrays to apply to {@link playtotv.pubsub.PubSub#subscribe}
		 * @returns {[playtotv.pubsub.Subscription]} Array of subscriptions
		 */
		subscribeAll: function(args) {
			var i, len = args.length;
			var subscriptions = [];
			for (i = 0; i < len; i++) {
				subscriptions.push(this.subscribe.apply(this, args[i]));
			}
			return subscriptions;
		},

		/**
		 * Convenience method to remove a number of subscriptions at once.
		 *
		 * @param {[playtotv.pubsub.Subscription]} [subscriptions=this.subscriptions] Subscriptions to unsubscribe
		 */
		unsubscribeAll: function(subscriptions) {
			subscriptions = subscriptions || this.subscriptions;
			var i, len = subscriptions.length;
			for (i = 0; i < len; i++) {
				this.unsubscribe(subscriptions[i]);
			}
		},

		/**
		 * Creates a one-time subscription and returns a cancellable promise that resolves on it's first match.
		 *
		 * A reference is also added to cancellables so that it can be cancelled on dispose.
		 *
		 * @param {string} selector Selector to match
		 * @returns {Promise.Cancellable} Cancellable promise which will resolve when selector first matches
		 */
		when: function(selector) {
			var cancellables = this.cancellables;
			var pubsub = this;
			return new Promise.Cancellable(function(resolve) {
				var cancellable = this;
				cancellables.push(cancellable);

				var subscription = pubsub.subscribe(selector, function(data) {
					resolve(data);
				});

				cancellable.finally(function() {
					pubsub.unsubscribe(subscription);
					remove(cancellables, cancellable);
				});
			});
		},

		/**
		 * Publish data to specified channel and notify all subscribers with matching selectors.
		 *
		 * The handler is called asynchronously to provide consistent code execution order.
		 * As an added benefit this also prevents an exception in one callback from interrupting any other callbacks.
		 *
		 * @param {string} channel Channel to publish to
		 * @param {mixed} [data] Data to publish
		 * @param {boolean} [sync=false] Publish synchronously (beware of exceptions!)
		 */
		publish: function(channel, data, sync) {
			// Code below has been optimized for performance, hence no pretty use of forEach etc.
			var subscriptions = this.subscriptions;
			var len = subscriptions.length;
			var i, subscription, match;
			for (i = 0; i < len; i++) {
				subscription = subscriptions[i];
				match = subscription.match(channel);
				if (match) {
					if (sync) {
						subscription.deliver(data, channel);
					} else {
						// TODO: Add matches as third argument in case you specified a RegExp as selector
						// And with wildcard selector strings
						// Or as second argument (instead of full channel)
						// That way you can easily subscribe to 'foo-*' and then deal with the value of '*'
						nextTick(subscription.deliver, subscription, [ data, channel ]);
					}
				}
			}
		},

		/**
		 * Dispose.
		 */
		dispose: function() {
			// TODO: Extract this (and eventListeners etc. from Widget) into generic Disposable class
			// And/or automatically detect any properties that are (arrays of) instances of Disposable?
			// And/or just keep track of disposables and make Cancellable etc. Disposable
			this.subscriptions.forEach(function(subscription) {
				subscription.dispose();
			});

			this.cancellables.forEach(function(cancellable) {
				cancellable.cancel(new Promise.CancellationError('Disposed'));
			});

			ptv.dispose(this);
		}
	});
});
