playtotv.define('playtotv.cron', [], function() {

	'use strict';

	var log = playtotv.Logger.get(this.id);

	var tickTimeout = null;
	var tick;
	var jobs = {};
	var timeoutJobs = {};
	var promisedJobs = {};
	var id = 1;

	function now() {
		return Date.now();
	}

	function labelledLog(msg, job) {
		if (!job.quiet) {
			log.s('job #' + job.id + ' "' + job.label + '": ' + msg);
		}
	}

	function repeat(job) {
		labelledLog('Scheduling repeat @ ' + job.time, job);
		job.timeout = undefined;
		job.time = now() + job.repeat;
		jobs[job.id] = job;
		if (!tickTimeout) {
			tick();
		}
	}

	function exec(id) {
		var job;
		if ((job = timeoutJobs[id]) !== undefined) {
			delete timeoutJobs[id];
		} else if ((job = jobs[id]) !== undefined) {
			delete jobs[id];
		} else {
			return;
		}

		var _now = now();
		var delta = job.time - _now;
		delta = delta < 0 ? delta : '+' + delta;
		labelledLog('Executing @ ' + _now + ' (' + delta + 'ms)', job);

		promisedJobs[id] = job;
		var returnValueOrPromise = job.func.apply(job.context, job.args);
		Promise.resolve(returnValueOrPromise).then(function(returnValue) {
			if (promisedJobs[id]) { // ensure job still exists (e.g. has not been cancelled)
				delete promisedJobs[id];
				if (job.repeat > 0 && returnValue !== false) {
					repeat(job);
				}
			}
		}, function() {
			delete promisedJobs[id];
		});
	}

	function process(id) {
		var job = jobs[id];
		if (!job) {
			return;
		}

		var timeLeft = job.time - now();

		if (timeLeft <= 0) {
			exec(id);
		} else if (timeLeft < cron.tickInterval) {
			delete jobs[id];
			timeoutJobs[id] = job;
			job.timeout = setTimeout(function() {
				exec(id);
			}, timeLeft);
		} // else execute in a future tick
	}

	tick = function() {
		if (tickTimeout) {
			clearTimeout(tickTimeout);
			tickTimeout = null;
		}

		// TODO: process jobs in order of job.time and then id
		Object.keys(jobs).forEach(process);

		// must check again, process deletes keys
		if (Object.keys(jobs).length > 0) {
			tickTimeout = setTimeout(tick, cron.tickInterval);
		}
	};

	function at(time, func, options) {
		options = playtotv.extend({
			context: func,
			args: [],
			repeat: 0, // repeat job after specified amount of ms (cancel job to stop repeating)
			label: 'unlabeled', // to facilitate debugging
			quiet: false
		}, options);

		var job = playtotv.extend({
			id: id,
			time: time,
			func: func
		}, options);

		jobs[id++] = job;
		log.s('at(): #' + job.id + ' "' + job.label + '"', job);

		if (options.repeat > 0 && options.repeat < cron.tickInterval) {
			cron.tickInterval = options.repeat;
			log.d('Setting tick interval to ' + cron.tickInterval + ' to keep up with job #' + job.id + ' (' +
				job.label + ')');
		}

		// Process as soon as possible, but still asynchronously (do not wait for next scheduled timeout)
		nextTick(tick);

		return job.id;
	}

	function get(id) {
		return jobs[id] || timeoutJobs[id] || promisedJobs[id];
	}

	function cancel(id) {
		var job;
		if ((job = timeoutJobs[id]) !== undefined) {
			clearTimeout(job.timeout);
			delete timeoutJobs[id];
		} else if ((job = promisedJobs[id]) !== undefined) {
			delete promisedJobs[id];
		} else if ((job = jobs[id]) !== undefined) {
			delete jobs[id];
		} else {
			return false;
		}
		labelledLog('Cancelled', job);
		return true;
	}

	function halt() {
		if (tickTimeout) {
			log.s('Halting..');

			Object.keys(jobs).forEach(cancel);
			Object.keys(timeoutJobs).forEach(cancel);
			Object.keys(promisedJobs).forEach(cancel);

			clearTimeout(tickTimeout);
			tickTimeout = null;
			return true;
		}
		return false;
	}

	var cron = {
		tickInterval: 1000, // subject to change
		at: at,
		get: get,
		cancel: cancel,
		halt: halt,
		debug: { // exposed for debugging purposes, do not abuse
			jobs: jobs,
			timeoutJobs: timeoutJobs,
			promisedJobs: promisedJobs
		}
	};

	return cron;
});
