playtotv.define('playtotv.Timer', [], function() {

	'use strict';

	/**
	 * A simple class for tracking timings and producing a summarizing string for those.
	 *
	 * Parts of the total timing interval can be marked using the start() and end() methods.
	 * Note: Those parts should not overlap, otherwise the unaccounted time will be incorrect.
	 */
	return class Timer {
		constructor() {
			this.t0 = Date.now();
			this.accounted = 0;
			this.timings = [];
		}

		now() {
			// Note: You can also use window.performance.now() for increased accuracy, but it quickly becomes spammy
			return Date.now();
		}

		start() {
			this.tx = Date.now();
		}

		end(label) {
			var time = Date.now() - this.tx;
			delete this.tx;
			this.accounted += time;
			this.timings.push(label + ': ' + time);
		}

		toString() {
			var total = Date.now() - this.t0;
			var timings = this.timings.join(', ');
			var unaccounted = total - this.accounted;
			return `${total}ms (${timings}) +${unaccounted}`;
		}
	};
});
