playtotv.define('playtotv.model.AbstractModel', [
	'playtotv.model.ModelSubscription',
	'playtotv.pubsub.PubSub'
], function(
	ModelSubscription,
	PubSub
) {

	'use strict';

	return new playtotv.Class({
		/** @lends playtotv.model.AbstractModel.prototype */

		extend: PubSub,

		/**
		 * Abstract model class that facilitates subscribing to mutations of data with handlers.
		 *
		 * Subscriptions are defined by selector strings which look like this: "[action]:[key]=[value]".
		 * <ul>
		 * <li>Both "[action]" and "[key]" can be replaced by "*" wildcards, matching all actions/key mutations.
		 * <li>The special global wildcard selector "*" will match all mutations, also those of nested data.
		 * <li>The optional "=[value]" will perform a simple value comparison casting value to string if necessary.
		 * </ul>
		 *
		 * Possible actions are: "add", "remove", "change" and "init".
		 *
		 * Example selectors:
		 * <ul>
		 * <li>"change:status=full"
		 * <li>"change:status"
		 * <li>"add:*"
		 * <li>"*:status"
		 * <li>"*"
		 * </ul>
		 *
		 * Handler callbacks take place asynchronously on the next 'tick', see {@link playtotv.pubsub.PubSub#publish}.
		 * This should in general result with your model being in a more consistent state during callbacks.
		 * Do note however that the handler value might not be in sync with the model state anymore during that time.
		 * Depending on your use you will want to work with the published value or lookup the current model value.
		 *
		 * While a property is set to an AbstractModel value that child model's publishes will bubble up to this model.
		 * It's key will be prefixed with that property's key and a dot.
		 *
		 * Note: This talks about object, property and key. For ArrayModels imagine array, item and index instead.
		 *
		 * @extends playtotv.pubsub.PubSub
		 * @constructs
		 */
		initialize: function() {
			this._super();
		},

		/**
		 * Returns a shallowly de-modeled plain JavaScript Object copy of the model data.
		 *
		 * If any child properties are models, they will still be models in the copy.
		 * To recursively de-model a model use {@link playtotv.model.modelUtil.toObject}.
		 *
		 * Note: This is an {@link playtotv.AbstractFunction}
		 *
		 * @function
		 * @returns {Object} Plain JavaScript Object copy of the model data
		 */
		toObject: playtotv.AbstractFunction,

		/**
		 * Updates model data to match that of specified plain JavaScript source Object.
		 *
		 * If any child properties are models, this model will be updated to refer to those models.
		 * To recursively update a model use {@link playtotv.model.modelUtil.update}.
		 *
		 * Note: This is an {@link playtotv.AbstractFunction}
		 *
		 * @function
		 * @param {Object} source Source data to update model to
		 * @returns {[mixed]} Array with any removed values for garbage collection etc.
		 */
		updateModel: playtotv.AbstractFunction,

		/**
		 * Returns true if this model or any of our child models equals specified root model.
		 *
		 * This can be used to test for and prevent creation of cyclic models leading to infinite publishes.
		 *
		 * Note: This is an {@link playtotv.AbstractFunction}
		 *
		 * @param {playtotv.model.AbstractModel} root Root model to compare to
		 * @returns {boolean} True if this model has a cyclic relation to root
		 */
		isRelatedModel: playtotv.AbstractFunction,

		/**
		 * Returns the action from specified selector.
		 *
		 * @param {string} selector Model subscription selector
		 * @return {string} The action from specified selector
		 */
		getSelectorAction: function(selector) {
			var colonIndex = selector.indexOf(':');
			return colonIndex === -1 ? undefined : selector.substr(0, colonIndex);
		},

		/**
		 * Returns the key from specified selector.
		 *
		 * @param {string} selector Model subscription selector
		 * @return {string} The key from specified selector
		 */
		getSelectorKey: function(selector) {
			var colonIndex = selector.indexOf(':');
			if (colonIndex === -1) {
				return undefined;
			}
			var key = selector.substring(colonIndex + 1);
			var eqIndex = key.indexOf('=');
			return eqIndex === -1 ? key : key.substring(0, eqIndex);
		},

		/**
		 * Returns the value from specified selector.
		 *
		 * @param {string} selector Model subscription selector
		 * @return {string} The value from specified selector
		 */
		getSelectorValue: function(selector) {
			var eqIndex = selector.indexOf('=');
			return eqIndex === -1 ? undefined : selector.substring(eqIndex + 1);
		},

		/**
		 * Returns an Array of {key, value} property tuples that match specified selector.
		 *
		 * <ul>
		 * <li>"[add/change/*]:[key]=[value]": property with matching [key] and [value]
		 * <li>"[add/change/*]:[*]=[value]": property with matching [value]
		 * <li>"[add/change/*]:[key]": property with matching [key]
		 * <li>"[add/change/*]:*": all of this object's properties
		 * <li>"*": is equal to "*:*" for the sake of this type of matching
		 * </ul>
		 *
		 * @param {string} selector Model subscription selector
		 * @throws {Error} If selector matching is not supported
		 * @returns {[Object]} Array of matching {key, value} tuples
		 */
		getSelectorMatches: function(selector) {
			var action = this.getSelectorAction(selector) || '*';
			var key = this.getSelectorKey(selector) || '*';
			var val = this.getSelectorValue(selector);

			if (action === 'remove') {
				throw new Error('Matching with remove action selector is not supported: ' + selector);
			}
			if (!key || key.indexOf('.') !== -1) {
				throw new Error('Only basic key matching is supported: ' + selector);
			}

			var matches = [];
			playtotv.each(this.toObject(), function(v, k) {
				if (key !== '*' && k !== key) {
					return;
				}
				if (val !== undefined && String(v) !== val) { // includes 'undefined'
					return;
				}
				matches.push({ key: k, value: v });
			});
			return matches;
		},

		/**
		 * Subscribe handler to specified selector.
		 *
		 * This overrides {@link playtotv.pubsub.PubSub#subscribe}.
		 *
		 * If init is true then handler will be called with action "init" on nextTick for each match returned by
		 * {@link playtotv.model.AbstractModel#getSelectorMatches}.
		 *
		 * @param {string} selector Selector to match
		 * @param {Function} handler Handler to call on mutation with value, action and key arguments
		 * @param {boolean} [init=false] If true then initialize by publishing current value(s)
		 * @throws {Error} If init is true but selector matching is not supported
		 * @returns {playtotv.model.ModelSubscription} Subscription
		 */
		subscribe: function(selector, handler, init) {
			var subscription = new ModelSubscription(selector, handler);

			if (init) {
				this.getSelectorMatches(selector).forEach(function(match) {
					var key = match.key;
					var value = match.value;
					var channel = 'init:' + key + '=' + value;
					var data = { key: key, action: 'init', value: value };
					nextTick(subscription.deliver, subscription, [ data, channel ]);
				});
			}

			this.subscriptions.push(subscription);
			return subscription;
		},

		/**
		 * Creates a one-time subscription and returns a promise that resolves on it's first match.
		 *
		 * This extends {@link playtotv.pubsub.PubSub#when} with initialization support.
		 * If selector value is defined and specified selector matches the model's current value
		 * then this will immediately return a Cancellable that is resolved with the first matching value.
		 *
		 * @param {string} selector Selector to match
		 * @returns {Promise.Cancellable} Cancellable promise which will resolve when selector first matches
		 */
		when: function(selector) {
			if (this.getSelectorValue(selector) !== undefined) {
				var matches = this.getSelectorMatches(selector);
				if (matches.length > 0) {
					return Promise.Cancellable.resolve(matches[0].value);
				}
			}

			return PubSub.prototype.when.call(this, selector);
		},

		/**
		 * Publish an action with value on property specified by key.
		 *
		 * This overrides and delegates to {@link playtotv.pubsub.PubSub#publish}.
		 *
		 * @param {string} key
		 * @param {string} action
		 * @param {mixed} value
		 */
		publish: function(key, action, value) {
			var channel = action + ':' + key + '=' + value;
			var data = { key: key, action: action, value: value };
			PubSub.prototype.publish.call(this, channel, data);
		}
	});
});
