(function() {
	/*global console*/

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;

	var loggers = {};
	var numericLevelMap = {
		SPAM: 4,
		DEBUG: 3,
		INFO: 2,
		WARNING: 1,
		ERROR: 0,
		NONE: -1
	};
	var logFuncMap = {
		SPAM: 'log',
		DEBUG: 'debug',
		INFO: 'info',
		WARNING: 'warn',
		ERROR: 'error'
	};
	var urlParams = typeof window !== 'undefined' ? util.getURLParams(window.location.href) : {};

	// If requested, include firebug lite
	if (urlParams.fbl) {
		ptv.loadScript('//getfirebug.com/firebug-lite.js#startOpened', false);
	}

	// If requested, include weinre
	var weinre = urlParams.weinre;
	if (weinre !== undefined) {
		ptv.loadScript('https://weinre.exmachina.nl/target/target-script-min.js#' + (weinre || 'anonymous'), false);
	}

	var Logger = new ptv.Class({
		/** @lends playtotv.Logger.prototype */

		/**
		 * Logger enables named loggers throughout different classes.
		 * The level parameter is optional, and defaults to the static playtotv.Logger.level.
		 * Both instance logger levels and static default level can be changed at runtime.
		 *
		 * Note: It is recommended to use {@link playtotv.Logger.get} to create Logger instances.
		 *
		 * Default log level and log join flag values can be controlled by URL parameters, see:
		 * {@link playtotv.Logger.level} and {@link playtotv.Logger.join}.
		 *
		 * Specifying a truthy "fbl" URL parameter (e.g: ?fbl=1) will result in injection of a Firebug Lite inspector.
		 *
		 * Specifying a "weinre" URL parameter (e.g: ?weinre=lan-pp.playtotv.com:8080) will result in injection of
		 * Weinre Javascript from specified host and port.
		 *
		 * @constructs
		 * @param {string} [name=logger-[uid]]
		 * @param {string} [level]
		 */
		initialize: function(name, level) {
			this.name = name || ptv.getUID('logger-');
			this.level = level;
		},

		/**
		 * Indicates whether the logger is enabled for the given level.
		 *
		 * @param {string} level Logger level
		 * @return {boolean} True if logger is enabled
		 */
		isEnabled: function(level) {
			var myLevel = this.level || Logger.level; // default to global level
			return numericLevelMap[myLevel] >= numericLevelMap[level];
		},

		/**
		 * Logs arguments to console if possible and specified level is enabled.
		 *
		 * Each argument that is a Promise will be substituted by an array of it's status and value.
		 * When the Promise resolves or rejects log will be called again with the updated Promise status and value.
		 *
		 * If an argument is an Array it will be flattened to result in more readable console log output in most cases.
		 *
		 * Note: This is a shallow flatten, e.g: if that Array contains other Arrays, those will not be flattened.
		 *
		 * Finally, the log statement is prefixed with call time, call level and logger name.
		 *
		 * TODO: Update example below with Cancellable.setTimeout instead of when.timeout.
		 *
		 * @example
		 * logger = playtotv.Logger.get('example');
		 * logger.log('INFO', [
		 *   'Test', 1, [2], [[3,4]],
		 *   when.timeout(Promise.delay('yup', 1000), 2000),
		 *   when.timeout(Promise.delay('yup', 3000), 2000)
		 * ]);
		 * // Output:
		 * // 16:21:55 [INFO] example | Test 1 2 [3, 4] [promise] [promise]
		 * // 16:21:56 [INFO] example | Test 1 2 [3, 4] [resolved] yup [promise]
		 * // 16:21:57 [INFO] example | Test 1 2 [3, 4] [resolved] yup [rejected] timeout
		 *
		 * @param {string} level Logger level
		 * @param {arguments} args Arguments to log
		 */
		log: function(level, args) {
			if (console && this.isEnabled(level)) {
				args = ptv.toArray(args);

				// Log again once a Promise resolves or rejects, deferred by Promise until after logger.apply below
				args = args.map(function(arg) {
					if (arg instanceof Promise) {
						var p = [ '[promise]' ];
						arg.then(function(data) {
							p.push('[resolved]', data);
							this.log(level, args);
						}.bind(this), function(reason) {
							p.push('[rejected]', reason);
							this.log(level, args);
						}.bind(this));
						return p;
					} else {
						return arg;
					}
				}, this);

				// Prefix and flatten args
				var prefix = util.timeStr(new Date(), true) + ' [' + level + '] ' + this.name + ' |';
				var processed = Array.prototype.concat.apply([ prefix ], args);

				// Determine logger function at runtime to allow for effective Firebug Lite etc. injection
				var logger = console[logFuncMap[level]] || console.log;
				// IE8 does not allow logger.apply and has type 'unknown'
				if (typeof logger.apply === 'function') {
					logger.apply(console, Logger.join ? [ processed.join(' ') ] : processed);
				} else {
					logger(processed.join(' '));
				}
			}
		},

		/**
		 * Outputs a stacktrace to console if possible and specified level is enabled.
		 *
		 * @param {string} [level] Logger level
		 */
		trace: function(level) {
			if (console && console.trace && (!level || this.isEnabled(level))) {
				console.trace();
			}
		},

		/**
		 * Shortcut to {@link playtotv.Logger#log}('SPAM', arguments)
		 */
		s: function() {
			this.log('SPAM', arguments);
		},

		/**
		 * Shortcut to {@link playtotv.Logger#log}('DEBUG', arguments)
		 */
		d: function() {
			this.log('DEBUG', arguments);
		},

		/**
		 * Shortcut to {@link playtotv.Logger#log}('INFO', arguments)
		 */
		i: function() {
			this.log('INFO', arguments);
		},

		/**
		 * Shortcut to {@link playtotv.Logger#log}('WARNING', arguments)
		 */
		w: function() {
			this.log('WARNING', arguments);
		},

		/**
		 * Shortcut to {@link playtotv.Logger#log}('ERROR', arguments)
		 */
		e: function() {
			this.log('ERROR', arguments);
		}
	});

	ptv.extend(Logger, {
		/** @lends playtotv.Logger */

		/**
		 * Default Logger level.
		 * Legal values are: SPAM, DEBUG, INFO, WARNING, ERROR, NONE.
		 *
		 * If you specify a logLevel URL parameter then that value will be used as default (e.g: ?logLevel=DEBUG).
		 *
		 * @type string
		 * @default 'INFO'
		 */
		level: urlParams.logLevel || 'INFO',

		/**
		 * Whether to join arguments into a single string before logging to console.
		 * Useful for iOS and Android log implementations that only display the first argument.
		 * Downside is that it does poor string conversion (e.g. you see [object Object]).
		 *
		 * If you specify a truthy logJoin URL parameter then that value will be used as default (e.g: ?logJoin=1).
		 *
		 * @type boolean
		 * @default false
		 */
		join: urlParams.logJoin ? true : false,

		/**
		 * Returns a Logger instance with the given name and level.
		 *
		 * If a Logger with the same name was previously created by this function then that will be returned.
		 * If a level is specified then the level of that instance will be updated.
		 *
		 * Otherwise a new Logger instance will be created with specified name and level and stored for future re-use.
		 *
		 * @param {string} [name]
		 * @param {string} [level]
		 * @returns {playtotv.Logger}
		 */
		get: function(name, level) {
			var logger = name && loggers[name];
			if (logger) {
				if (level) {
					logger.level = level;
				}
				return logger;
			}

			logger = new Logger(name, level);
			loggers[name] = logger;
			return logger;
		}
	});

	ptv.Logger = Logger;

}());
