playtotv.define('playtotv.facebook.Facebook', [
	'playtotv.model.ObjectModel'
], function(
	ObjectModel
) {

	'use strict';

	////////// Private variables

	var ptv = playtotv;
	var util = ptv.util;
	var log = ptv.Logger.get(this.id);
	var REQUIRED = ptv.REQUIRED;

	////////// Class declaration

	return new ptv.Class({
		/** @lends playtotv.facebook.Facebook.prototype */

		extend: ObjectModel,

		/**
		 * Class facilitating use of Facebook's JavaScript API.
		 *
		 * Note: Use redirect when popups are unusual or even unusable, e.g: on touch devices or within other apps.
		 *
		 * TODO: Rewrite to use and support Promise.Cancellable and have apps set and deal with timeouts themselves.
		 * Also try to improve the ability of this code to recover from issues automatically (e.g. reload script).
		 *
		 * @extends playtotv.model.ObjectModel
		 * @constructs
		 * @param {Options} options
		 * @param {string} options.appId Facebook App Id to use
		 * @param {string} [options.version='v2.3'] API version to use
		 * @param {string} [options.cookie=false] Create session cookie or not
		 * @param {string} [options.status=true] Refresh user login status on init
		 * @param {string} [options.xfbml=false] Parse social plugin XFBML tags
		 * @param {string} [options.frictionlessRequests=false] Enable Frictionless Requests for games
		 * @param {string} [options.hideFlashCallback=null] Callback to hide any Adobe Flash objects
		 * @param {string} [options.locale='en_US'] Facebook API locale
		 * @param {string} [options.redirect] If defined, redirect using this URL instead of showing popup/.. dialogs
		 * @param {string} [options.scope] Comma separated list of extended permissions to require when you login
		 * @param {number} [options.timeout=20000] Initialization timeout in ms
		 *
		 * @property {string} status Subscribable: Current Facebook API status
		 * @property {string} id Subscribable: If connected then your Facebook user id, otherwise null
		 * @property {string} accessToken Subscribable: If connected then your accessToken, otherwise null
		 * @property {string} signedRequest Subscribable: If connected then your signedRequest, otherwise null
		 */
		initialize: function(options) {
			this._super({
				status: null,
				id: null,
				accessToken: null,
				signedRequest: null
			});

			options = ptv.params(options, {
				appId: REQUIRED,
				version: 'v2.3',
				cookie: false,
				status: true,
				xfbml: false, // seems pointless with dynamic html
				frictionlessRequests: false,
				hideFlashCallback: null,

				locale: 'en_US',
				redirect: undefined,
				scope: undefined,
				timeout: 20000
			});

			// TODO: Rewrite without use of Promise.defer()
			var deferred = Promise.defer();

			this.appId = options.appId;
			this.version = options.version;
			this.locale = options.locale;
			this.redirect = options.redirect;
			this.scope = options.scope;

			////////// Documented properties

			/**
			 * Promise of initialization, rejected in case of timeout.
			 * @type Promise
			 */
			this.initialized = new Promise.Cancellable(function(resolve, reject) {
				deferred.promise.then(resolve, reject);
				if (options.timeout) {
					this.setTimeout(options.timeout);
				}
			});

			////////// Initialize
			window.fbAsyncInit = function() {
				var FB = window.FB;

				FB.init(ptv.pick(options, [
					'appId', 'version', 'cookie', 'status', 'xfbml', 'frictionlessRequests', 'hideFlashCallback'
				]));

				FB.Event.subscribe('auth.authResponseChange', this.onStatusResponse);

				// Get login status now to get started with the current session
				FB.getLoginStatus(function(response) {
					this.onStatusResponse(response);
					deferred.resolve(FB);
				}.bind(this));
			}.bind(this);

			ptv.loadScript('//connect.facebook.net/' + this.locale + '/sdk.js', true);
		},

		////////// Undocumented methods

		onStatusResponse: function(response) {
			var authResponse = response.authResponse || {};
			this.copyProperties({
				status: response.status,
				id: authResponse.userID || null,
				accessToken: authResponse.accessToken || null,
				signedRequest: authResponse.signedRequest || null
			});
		},

		////////// Documented methods

		/**
		 * Generic FB function wrapper adding support for timeout, error reporting and promise.
		 *
		 * Any further arguments past the timeout argument are passed to the FB function being called.
		 *
		 * Note: This assumes that the last argument of the FB.[what] function is a response callback function.
		 *
		 * @param {string} what Name of function to call on FB (e.g: 'api' for FB.api())
		 * @param {number} [timeout=20000] Timeout in ms (beware of user interaction!) (falsy to disable)
		 * @return {Promise} Promise of FB call response, rejected if response.error is set or call times out
		 */
		call: function(what, timeout) {
			timeout = timeout !== undefined ? timeout : 20000;

			var args = ptv.toArray(arguments).slice(2);
			var details = { what: what, timeout: timeout, args: args };

			log.s('FB.' + what + '(', args, '), timeout: ' + timeout);

			var cancellable = this.initialized.then(function(FB) {
				// TODO: Rewrite without use of Promise.defer()
				var deferred = Promise.defer();

				args.push(function(response) {
					if (response && response.error) {
						// Note: usual properties: message, code, error_subcode, error_user_msg, error_user_title
						// See: https://developers.facebook.com/docs/graph-api/using-graph-api/v2.3#errors
						deferred.reject(ptv.extend(new Error('Call failed'), details, response.error, {
							message: 'Request error: ' + response.error.message
						}));
					} else {
						deferred.resolve(response);
					}
				});

				FB[what].apply(FB, args);

				return deferred.promise;
			});

			// TODO: Remove timeout arg here and leave it to calling code to add timeout on returned Cancellable
			if (timeout) {
				var timeoutDetails = ptv.extend({ message: 'Request timeout' }, details);
				cancellable.setTimeout(timeout, timeoutDetails);
			}

			return cancellable;
		},

		/**
		 * Shortcut to {@link playtotv.facebook.Facebook#call}('api', ..).
		 *
		 * TODO: Add support for paging.
		 *
		 * @return {Promise} As returned by {@link playtotv.facebook.Facebook#call}
		 */
		api: function() {
			var args = ptv.toArray(arguments);
			args.unshift('api');
			return this.call.apply(this, args);
		},

		/**
		 * Shortcut to {@link playtotv.facebook.Facebook#call}('ui', false, ..).
		 *
		 * @return {Promise} As returned by {@link playtotv.facebook.Facebook#call}
		 */
		ui: function() {
			var args = ptv.toArray(arguments);
			args.unshift('ui', false);
			return this.call.apply(this, args);
		},

		/**
		 * Returns friends who have used (via Facebook Login) the app making the request.
		 *
		 * Requires permission: user_friends
		 *
		 * @see <a href="https://developers.facebook.com/docs/graph-api/reference/v2.3/user/friends">Graph API</a>
		 *
		 * @param {number} [timeout] {@link playtotv.facebook.Facebook#call} timeout
		 * @return {Promise} Promise of array of friends
		 */
		getFriends: function(timeout) {
			return this.api(timeout, '/me/friends').then(function(data) {
				return data.data;
			});
		},

		/**
		 * Facility function for logging into Facebook.
		 *
		 * <ul>
		 * <li>If you are already logged in this will resolve immediately.
		 * <li>Otherwise if not interactive (e.g. asking for user input is disabled) this will reject.
		 * <li>Otherwise if options.redirect is set this will use {@link playtotv.facebook.Facebook#loginByRedirect}.
		 * <li>Otherwise this will use {@link playtotv.facebook.Facebook#loginByDialog}.
		 * </ul>
		 *
		 * @param {boolean} [interactive=false] Whether to allow user interaction through a popup or redirect
		 * @return {Promise} Promise of Facebook user id, rejected if cancelled by user
		 */
		login: function(interactive) {
			return this.initialized.then(function() {
				if (this.id) {
					return Promise.resolve(this.id); // already logged in
				} else if (!interactive) {
					return Promise.reject(new Error('Login requires user interaction'));
				} else if (this.redirect) {
					this.loginByRedirect();
					// TODO: Rewrite without use of Promise.defer()
					return Promise.defer().promise; // redirect does not resolve
				} else {
					return this.loginByDialog();
				}
			}.bind(this));
		},

		/**
		 * Login to Facebook using FB popup dialog.
		 *
		 * TODO: Add support for options besides scope and move that out of Facebook options to login and loginByX
		 *
		 * @see <a href="https://developers.facebook.com/docs/reference/javascript/FB.login/v2.3">SDK</a>
		 *
		 * @return {Promise} Promise of Facebook user id, rejected if cancelled by user
		 */
		loginByDialog: function() {
			return this.initialized.then(function(FB) {
				// Note: we can not use this.call() since the last argument is not the callback function with FB.login
				// TODO: Rewrite without use of Promise.defer()
				var deferred = Promise.defer();
				FB.login(function(response) {
					if (response.authResponse && response.authResponse.userID) {
						deferred.resolve(response.authResponse.userID);
					} else {
						deferred.reject(ptv.extend(new Error('Login failed'), response)); // e.g. cancelled by user
					}
				}, { scope: this.scope });
				return deferred.promise;
			}.bind(this));
		},

		/**
		 * Login to Facebook by redirecting to Facebook and then having Facebook redirect back to us with a token.
		 *
		 * @see <a href="https://developers.facebook.com/docs/facebook-login/manually-build-a-login-flow/v2.3">Docs</a>
		 *
		 * @param {string} [redirect=options.redirect] URL that Facebook should redirect back to once done
		 */
		loginByRedirect: function(redirect) {
			/*jshint camelcase:false*/
			/*jscs:disable requireCamelCaseOrUpperCaseIdentifiers*/
			window.top.location = util.addURLParams('https://www.facebook.com/' + this.version + '/dialog/oauth', {
				client_id: this.appId,
				response_type: 'token',
				scope: this.scope || '',
				redirect_uri: redirect || this.redirect
			});
			/*jscs:enable requireCamelCaseOrUpperCaseIdentifiers*/
		},

		/**
		 * Logout of Facebook using FB API.
		 *
		 * @param {number} [timeout] {@link playtotv.facebook.Facebook#call} timeout for logout
		 * @return {Promise} Promise of an object with a status property
		 */
		logout: function(timeout) {
			return this.initialized.then(function() {
				return this.id ? this.call('logout', timeout) : Promise.resolve({ status: this.status });
			}.bind(this));
		},

		/**
		 * Facility function for posting to Facebook.
		 *
		 * <ul>
		 * <li>If interactive and options.redirect is set then use {@link playtotv.facebook.Facebook#postByRedirect}.
		 * <li>Otherwise login using {@link playtotv.facebook.Facebook#login}.<br>
		 * Note: The login will reject if not interactive and not already logged in.
		 * <li>Then if interactive use {@link playtotv.facebook.Facebook#postByDialog}.
		 * <li>Otherwise use {@link playtotv.facebook.Facebook#postByAPI}.
		 * </ul>
		 *
		 *
		 * @param {Object} params <a href="http://developers.facebook.com/docs/reference/dialogs/feed/">FB Doc</a>
		 * @param {boolean} [interactive=false] Whether to let user review and confirm message before posting
		 * @param {number} [timeout] {@link playtotv.facebook.Facebook#postByAPI} timeout (not used while interactive)
		 * @return {Promise} Promise of post_id
		 */
		post: function(params, interactive, timeout) {
			if (interactive && this.redirect) {
				this.postByRedirect(params, this.redirect);
				// TODO: Rewrite without use of Promise.defer()
				return Promise.defer().promise; // redirect does not resolve
			} else {
				return this.login(interactive).then(function() {
					return interactive ? this.postByDialog(params) : this.postByAPI(params, timeout);
				}.bind(this));
			}
		},

		/**
		 * Post to Facebook wall using FB API, e.g: in the background, without asking for user input.
		 *
		 * TODO: Updated this to v2.3 (this is still legacy v1.0 code which probably does not work anymore)
		 *
		 * Note: This requires the publish_stream extended permission; get it using options.scope.
		 *
		 * @param {Object} params <a href="http://developers.facebook.com/docs/reference/dialogs/feed/">FB Doc</a>
		 * @param {[type]} [timeout] {@link playtotv.facebook.Facebook#call} timeout for api feed post
		 * @return {Promise} Promise of post_id
		 */
		postByAPI: function(params, timeout) {
			return this.call('api', timeout, '/me/feed', 'post', params).then(function(response) {
				/*jshint camelcase:false*/
				return response.id;
			});
		},

		/**
		 * Post to Facebook wall using FB UI dialog.
		 *
		 * @param {Object} params <a href="http://developers.facebook.com/docs/reference/dialogs/feed/">FB Doc</a>
		 * @return {Promise} Promise of post_id
		 */
		postByDialog: function(params) {
			return this.ui(ptv.extend({
				method: 'feed'
			}, params)).then(function(response) {
				/*jshint camelcase:false*/
				/*jscs:disable requireCamelCaseOrUpperCaseIdentifiers*/
				return response && response.post_id || Promise.reject(ptv.extend(new Error('Post failed'), response));
				/*jscs:enable requireCamelCaseOrUpperCaseIdentifiers*/
			});
		},

		/**
		 * Post to Facebook wall by redirecting to Facebook and then having Facebook redirect back to us.
		 *
		 * @param {Object} params <a href="http://developers.facebook.com/docs/reference/dialogs/feed/">FB Doc</a>
		 * @param {string} [redirect=options.redirect] URL that Facebook should redirect back to once done
		 * @return {Promise} Promise of post_id
		 */
		postByRedirect: function(params, redirect) {
			/*jshint camelcase:false*/
			/*jscs:disable requireCamelCaseOrUpperCaseIdentifiers*/
			window.top.location = util.addURLParams('https://www.facebook.com/dialog/feed', ptv.extend({
				app_id: this.appId,
				redirect_uri: redirect || this.redirect
			}, params));
			/*jscs:enable requireCamelCaseOrUpperCaseIdentifiers*/
		},

		////////// Callback methods

		callbacks: [
			'onStatusResponse'
		]
	});
});
