playtotv.define('playtotv.widget.WidgetManager', [
	// 'playtotv.MutationObserver'
], function(
	// MutationObserver
) {

	'use strict';

	var ptv = playtotv;
	var dom = ptv.dom;

	/**
	 * Monitors childList mutations to the DOM within a given node, and creates and removes widget instances for
	 * encountered nodes matching registered widget types. Widgets are registered either using CSS selectors, or if
	 * specified to the constructor, by attribute word value of the given attribute.
	 *
	 * @name WidgetManager
	 * @class
	 * @memberOf playtotv.widget
	 * @param {Node} node Node to monitor and manage instances for
	 * @param {string} [attr] Optional attribute to register widgets under rather than CSS selectors
	 */
	var WidgetManager = new ptv.Class({

		initialize: function(node, attr) {
			this.node = node || document;
			this.attr = attr;
			this.definitions = [];
			this.instances = [];

			// TODO: Replace this by something simpler and better integrated with App
			// TODO: Replace data-widget=".." data-...="[json]" by widget="[json]" like with on-event="[json]"
			//
			// this.observer = new MutationObserver(this.onMutations);
			// this.observer.observe(this.node, {
			// 	childList: true,
			// 	subtree: true
			// });
		},

		/** @lends playtotv.widget.WidgetManager.prototype */

		/**
		 * Registers a class under a given selector or attribute value with options for future class instances.
		 *
		 * @param {string} selector
		 * @param {Function} Class
		 * @param {Object|Function} [options]
		 * @returns {Object} definition
		 */
		register: function(selector, Class, options) {
			var proto = Class.prototype;
			if (!proto.getNode || !proto.dispose) {
				throw new ReferenceError('Class must define getNode() and dispose() methods');
			}

			var definition = {
				selector: this.attr ? '[' + this.attr + '~="' + selector + '"]' : selector,
				Class: Class,
				options: options
			};

			this.definitions.push(definition);
			this.createInstances(this.node, definition);

			return definition;
		},

		/**
		 * Unregisters a previously registered definition and disposes any existing instances
		 *
		 * @param {Object} definition
		 */
		unregister: function(definition) {
			if (typeof definition !== 'object') {
				throw new TypeError('Object expected');
			}

			var definitions = this.definitions;
			var index = definitions.indexOf(definition);
			if (index > -1) {
				this.disposeInstances(this.node, definition);
				definitions.splice(index, 1);
			}
		},

		/**
		 * Mutation observer callback, splits added and removed nodes to onNodeAdded and -removed.
		 * @param {Array} mutations Array of mutation records, see {@link playtotv.MutationRecord}.
		 */
		onMutations: function(mutations) {
			ptv.each(mutations, function(mutation) {
				var removed = mutation.removedNodes;
				if (removed.length) {
					// onMutations > onNodeRemoved > disposeInstances
					ptv.each(removed, this.onNodeRemoved, this);
				}

				var added = mutation.addedNodes;
				if (added.length) {
					// onMutations > onNodeAdded > createInstances
					ptv.each(added, this.onNodeAdded, this);
				}
			}, this);
		},

		/**
		 * Mutation handler for added nodes
		 */
		onNodeAdded: function(node) {
			if (dom.isNode(node)) {
				this.definitions.forEach(function(definition) {
					this.createInstances(node, definition);
				}, this);
			}
		},

		/**
		 * Mutation handler for removed nodes.
		 */
		onNodeRemoved: function(node) {
			if (dom.isNode(node)) {
				// disposal checks current instances rather than known definitions, no need to forEach them.
				this.disposeInstances(node);
			}
		},

		/**
		 * Creates instances for and within the given node using the given definition's selector, Class and options.
		 */
		createInstances: function(node, definition) {
			var nodes = dom.any(definition.selector, node);
			nodes.forEach(function(item) {
				var Class = definition.Class;
				if (!this.getWidget(item, Class)) {
					var options = definition.options;
					options = options instanceof Function ? options(item) : options;
					this.instances.push(
						new Class(item, options)
					);
				}
			}, this);
		},

		/**
		 * Disposes any instances on and within the given node that were created by this WidgetManager.
		 * A definition may be given to dispose only a subset of instances.
		 */
		disposeInstances: function(node, definition) {
			if (definition && !definition.Class) {
				throw new TypeError('Illegal definition');
			}

			var instances = this.instances;
			var last = instances.length - 1;
			var global = arguments.length < 2;

			for (var instance, target, i = last; i >= 0; i--) {
				instance = instances[i];
				target = instance.getNode();
				if (
					target &&
					(node.contains(target) || node === target) &&
					(global || instance.constructor === definition.Class)
				) {
					instance.dispose();
					instances.splice(i, 1);
				}
			}
		},

		/**
		 * Returns the instance of the given class on the given node.
		 */
		getWidget: function(node, Class) {
			if (!Class) {
				throw new ReferenceError('Class expected');
			}

			return ptv.find(this.instances, function(instance) {
				return (instance.getNode() === node && instance.constructor === Class);
			});
		},

		/**
		 * Disposes this widgetManager and exits all instances for memory and -leaking purposes, and should be called
		 * beforeUnload of the document, or at the time the widgetManager's own node is removed.
		 */
		dispose: function() {
			// this.observer.disconnect();
			this.disposeInstances(this.node);

			ptv.dispose(this);
		},

		callbacks: [
			'onMutations',
			'onNodeAdded',
			'onNodeRemoved'
		]
	});

	return WidgetManager;
});
