playtotv.define('playtotv.push.PushController', [
	'playtotv.mvc.Controller'
], function(
	Controller
) {

	'use strict';

	var ptv = playtotv;
	var log = ptv.Logger.get(this.id);

	// Not published as a normal resource api definition, so hard coded here
	var pushPollAPI = {
		getEvents: {
			label: 'pushPoll.getEvents',
			type: 'GET',
			url: '1/events',
			queryKeys: [
				'stream',
				'timeout'
			]
		}
	};

	return class PushController extends Controller {
		constructor(context, options) {
			options = ptv.params(options, {
				pollTimeout: 10000
			});

			super(context);

			this.pollTimeout = options.pollTimeout;

			this.scope.observe('user.online', this.start.bind(this));
		}

		// Note: Currently no support for going offline and online again, but that does not seem necessary
		start(online) {
			if (this.hasStarted || !online) {
				return;
			}
			this.poll();
			this.hasStarted = true;
		}

		/**
		 * Performs a single getEvents call and then recurses.
		 * Received events are published to their corresponding channels: '[event.subject]-[event.action]'.
		 * To stop polling dispose this controller.
		 */
		poll() {
			if (this.disposed) {
				return;
			}

			this.getEvents().then(events => {
				events.forEach(event => this.publish(event.subject + '-' + event.action, event));
			}).catch(error => {
				// Brute force sturdiness; catch rejection and continue polling after a delay
				log.i('poll: getEvents rejected, delaying next poll', error);
				return Promise.delay(5000);
			}).then(() => this.poll());
		}

		/**
		 * Gets events from server and updates stream id in model for following requests.
		 *
		 * @returns {Promise} Promise of an array of event data objects
		 */
		getEvents() {
			var model = this.model;
			var stream = model.stream;
			var pollTimeout = this.pollTimeout;

			return this.scope.call('server.call', pushPollAPI.getEvents, {
				stream: stream,
				timeout: pollTimeout
			}, {
				// Extend server call timeout with long poll timeout
				timeout: 20000 + pollTimeout
			}).then(data => {
				this.set('stream', data.stream);
				return data.events;
			});
		}

		serialize() {
			return false;
		}
	};
});
