(function() {

	// TODO: Update and cleanup (e.g: replace toArray and find by use of ES Array shims etc)

	// TODO: Remove things that can be done with destructuring:
	// http://untangled.io/in-depth-es6-destructuring-with-assembled-avengers/
	// http://untangled.io/advanced-es6-destructuring-techniques/
	// http://untangled.io/es6-destructuring-recipes/

	'use strict';

	// Reliably get a global reference on all platforms by looking at a non-strict function's "this"
	var global = /*jshint evil:true*/ new Function('return this')(); /*jshint evil:false*/

	/**
	 * Global playtotv namespace, contains everything.
	 *
	 * @name playtotv
	 * @namespace
	 */
	var ptv = global.playtotv = {};

	var nativeEach = [].forEach;
	var uid = 1;

	/**
	 * Object literal extender, extends a target object with the properties of 1 or more other
	 * object literals. Because this method is called - a lot - it contains next to no checks on
	 * the validity of the arguments passed to it.
	 *
	 * TODO: Replace by use of ES6 Object.assign()?
	 * Better yet skip straight ahead to Babel transform-object-rest-spread { ...src, key: value } (ES7 stage 2?)
	 *
	 * @name extend
	 * @memberOf playtotv
	 * @function
	 * @param {Object} target
	 * @param {Object} literal
	 */
	ptv.extend = function(target) {
		var literal, name, i, l = arguments.length;
		for (i = 1; i < l; i++) {
			literal = arguments[i];
			for (name in literal) {
				if (literal.hasOwnProperty(name)) {
					target[name] = literal[name];
				}
			}
		}

		return target;
	};

	/**
	 * Core playtotv features
	 *
	 */
	ptv.extend(ptv, {

		/** @lends playtotv */

		/**
		 * Turns an array like object (arguments, nodelists) into a real array, enabling use of Array prototype
		 * functions.
		 *
		 * @deprecated Use Array.from() method instead
		 *
		 * @param {Object} list
		 * @returns {Array} array
		 */
		toArray: function(list) {
			var array;

			// if the object has a toArray, call that
			if (list.toArray) {
				array = list.toArray();
			} else {
				var i = list.length;
				array = new Array(i);

				while (i--) {
					array[i] = list[i];
				}
			}

			return array;
		},

		/**
		 * Gets a unique ID.
		 *
		 * @param {string} [prefix]
		 * @returns {number} uid
		 */
		getUID: function(prefix) {
			uid += 1;
			return prefix ? (prefix + uid) : uid;
		},

		/**
		 * Slightly smarter each, does nodelists and object literals too.
		 *
		 * @param {Object} list
		 * @param {Function} callback
		 * @param {Object} [thisArg]
		 */
		each: function(list, callback, thisArg) {
			if (!list) {
				return;
			} else if (list.forEach) {
				list.forEach(callback, thisArg);
			} else if (list.length === +list.length) {
				nativeEach.call(list, callback, thisArg);
			} else {
				var key;
				for (key in list) {
					if (list.hasOwnProperty(key)) {
						callback.call(thisArg, list[key], key, list);
					}
				}
			}
		},

		/**
		 * Returns the first value of an array or object literal for which the callback returns true.
		 *
		 * @deprecated Use Array find() prototype method instead
		 *
		 * @param {Array|Object} list
		 * @param {Function} callback
		 * @param {Object} [thisArg]
		 * @returns {mixed} value
		 */
		find: function(list, callback, thisArg) {
			/*jshint maxdepth:4*/
			var value;
			if (list instanceof Array) {
				var i, l = list.length;
				for (i = 0; i < l; i++) {
					value = list[i];
					if (callback.call(thisArg, value, i, list)) {
						return value;
					}
				}
			} else {
				var key;
				for (key in list) {
					if (list.hasOwnProperty(key)) {
						value = list[key];
						if (callback.call(thisArg, value, key, list)) {
							return value;
						}
					}
				}
			}
		},

		/**
		 * Returns an array of property values from an array of objects.
		 *
		 * @param {Array} list
		 * @param {string} key
		 * @returns {Array}
		 */
		pluck: function(list, key) {
			return list.map(function(item) {
				return item[key];
			});
		},

		/**
		 * Creates an array of unique values, in order, from all of the provided arrays.
		 *
		 * @returns {Array} The new array of combined values
		 */
		union: function() {
			var union = [];
			this.toArray(arguments).forEach(function(array) {
				array.forEach(function(item) {
					if (union.indexOf(item) < 0) {
						union.push(item);
					}
				});
			});
			return union;
		},

		/**
		 * Returns a copy of the object only containing the whitelisted properties.
		 *
		 * @param {Object} obj
		 * @param {[string]} keys
		 * @returns {Object}
		 */
		pick: function(obj, keys) {
			var copy = {};
			keys.forEach(function(key) {
				if (key in obj) {
					copy[key] = obj[key];
				}
			});
			return copy;
		},

		/**
		 * Returns a copy of the object without the blacklisted properties.
		 *
		 * @param {Object} obj
		 * @param {[string]} keys
		 * @returns {Object}
		 */
		omit: function(obj, keys) {
			var copy = {};
			for (var key in obj) {
				if (keys.indexOf(key) === -1) {
					copy[key] = obj[key];
				}
			}
			return copy;
		},

		// TODO: Move these (also shuffle?) into util?
		randomNr: function(max, min = 0) {
			var delta = max - min;
			return min + Math.floor(Math.random() * delta);
		},

		randomItem: function(array) {
			return array[this.randomNr(array.length)];
		},

		/**
		 * Returns a copy of specified array with it's items shuffled randomly.
		 *
		 * @param {Array} array Array of items to shuffle
		 * @returns {Array} Shuffled array
		 */
		shuffle: function(array) {
			var cursor = array.length;
			var shuffled = array.slice(0); // start with a copy
			var temp, rand;
			while (cursor > 0) { // go from tail to head
				rand = Math.floor(Math.random() * cursor--); // pick a random preceding index
				temp = shuffled[cursor]; // remember value at cursor
				shuffled[cursor] = shuffled[rand]; // swap cursor value with random index value
				shuffled[rand] = temp; // swap random index value with cursor value
			}
			return shuffled;
		},

		/**
		 * Returns a function that will unstack calls using {@link nextTick} and then call callback with at least the
		 * specified time in ms between callbacks.
		 * When callback is called it will use the arguments of the most recent call.
		 * Any extra calls made in the meantime are ignored; they will not cause callback to be called and their
		 * arguments will be discarded.
		 *
		 * @param {Function} callback
		 * @param {number} ms
		 * @returns {Function}
		 */
		throttle: function(callback, ms) {
			var thisArg, args, timeout;

			function call() {
				if (args && !timeout) {
					callback.apply(thisArg, args);
					thisArg = null;
					args = null;

					timeout = setTimeout(function() {
						timeout = null;
						call();
					}, ms);
				} // else wait for timeout to call when it is finished
			}

			return function() {
				thisArg = this;
				if (args || timeout) { // already waiting for tick or timeout
					args = arguments;
				} else {
					args = arguments;
					nextTick(call);
				}
			};
		},

		/**
		 * Returns a function that will call the callback function after it has not been called for ms. If it is called
		 * again within that time the previously scheduled callback will be cancelled and another period of waiting
		 * will be started. This will effectively result in the callback only being called after a period of quiet.
		 *
		 * @param {Function} callback
		 * @param {number} ms
		 * @returns {Function}
		 */
		debounce: function(callback, ms) {
			var timeout;

			return function() {
				var thisArg = this;
				var args = arguments;

				clearTimeout(timeout);

				timeout = setTimeout(function() {
					timeout = null;
					callback.apply(thisArg, args);
					thisArg = null;
					args = null;
				}, ms);
			};
		},

		/**
		 * Returns a function that will call the callback function once after the stack has cleared (using
		 * {@link nextTick}).
		 * This effectively consolidates multiple calls from a single stack into one.
		 *
		 * @param {Function} callback
		 * @return {Function}
		 */
		unstack: function(callback) {
			var thisArg, args;

			function call() {
				callback.apply(thisArg, args);
				thisArg = null;
				args = null;
			}

			return function() {
				thisArg = this;
				if (args) { // already waiting for tick
					args = arguments;
				} else {
					args = arguments;
					nextTick(call);
				}
			};
		},

		/**
		 * Returns a function that will only call the callback function if it has not been called already.
		 *
		 * @param {Function} callback
		 * @returns {Function}
		 */
		once: function(callback) {
			var ran = false, memo;
			return function() {
				if (ran) {
					return memo;
				}
				ran = true;
				memo = callback.apply(this, arguments);
				return memo;
			};
		},

		/**
		 * Returns a function that will defer calling the callback function untill the current call stack is empty
		 * (using {@link nextTick}).
		 *
		 * @param {Function} callback
		 * @param {Object} [thisArg]
		 * @returns {Function}
		 */
		defer: function(callback, thisArg) {
			return function() {
				nextTick(callback, thisArg || this, arguments);
			};
		},

		/**
		 * Abstract function for mixins or superclasses. Throws an error if not implemented by the
		 * class that uses it.
		 *
		 */
		AbstractFunction: function() {
			throw new ReferenceError('Abstract function not implemented.');
		},

		/**
		 * May be used to make a param required for playtotv.params and the playtotv.Options mixin.
		 * @property REQUIRED
		 * @type String
		 * @default "__required__"
		 */
		REQUIRED: '__required__',

		/**
		 * Augments an options object with properties from a defaults object if undefined. If a default
		 * property is required to have a value, and is not stated in the options, an error is thrown.
		 * Likewise, options that are not present on the defaults throw an error. Note that the original
		 * options parameter is modified, not cloned or used as a reference.
		 *
		 * @param {Object} options
		 * @param {Object} defaults
		 * @returns {Object}
		 */
		params: function(options, defaults) {
			options = options || {};

			// assign options only via keys from defaults, and error on missing required params
			ptv.each(defaults, function(value, key) {
				var hasKey = options.hasOwnProperty(key);
				if (!hasKey) {
					if (value === ptv.REQUIRED) {
						throw new Error('Missing required parameter: ' + key);
					}

					options[key] = value;
				}
			});

			// check options for any unrecognized params
			ptv.each(options, function(value, key) {
				if (!defaults.hasOwnProperty(key)) {
					throw new Error('Invalid parameter: ' + key + ' = ' + value); // error or warn?
				}
			});

			return options;
		},

		/**
		 * Removes a selection of options derived from defaults from specified options (by reference!)
		 * and returns the selection augmented and validated by the playtotv.params function.
		 *
		 * @param {Object} options
		 * @param {Object} defaults
		 * @returns {Object}
		 */
		spliceParams: function(options, defaults) {
			options = options || {};

			var keys = Object.keys(defaults);
			var spliced = ptv.pick(options, keys);

			keys.forEach(function(key) {
				delete options[key];
			});

			return ptv.params(spliced, defaults);
		},

		/**
		 * Creates a script node and adds it to the head of the DOM.
		 *
		 * @param {string} src URL of script to load
		 * @param {boolean} [async=true] Whether to load asynchronously
		 * @return {Promise} Script node promise which will be resolved when script has finished loading
		 */
		loadScript: function(src, async) {
			// TODO: Rewrite without use of Promise.defer()
			var deferred = Promise.defer();
			var script = document.createElement('script');
			script.type = 'text/javascript';
			script.async = async === false ? false : true;
			script.src = src;

			var loaded = false;
			script.onload = script.onreadystatechange = function() {
				var rs = this.readyState;
				if (!loaded && (rs === undefined || rs === 'complete' || rs === 'loaded')) {
					loaded = true;
					deferred.resolve(script);
				}
			};
			script.onerror = function() {
				deferred.reject(new Error('Error loading script: ' + src));
			};

			document.head.appendChild(script);
			return deferred.promise;
		},

		/**
		 * Class instance dispose helper.
		 *
		 * Deletes all instance properties, overrides dispose method with a noop function and adds a disposed property.
		 *
		 * @param {Object} instance
		 */
		dispose: function(instance) {
			Object.keys(instance).forEach(function(key) { delete instance[key]; });
			instance.dispose = function() {};
			instance.disposed = true;
		}
	});

}());
