/**
 * Dollar shortcuts to querySelector() and querySelectorAll() like commonly provided by browser console API's.
 */
(function() {

	'use strict';

	window.$ = window.$ || function(selector, context = document) {
		return context.querySelector(selector);
	};

	window.$$ = window.$$ || function(selector, context = document) {
		return Array.from(context.querySelectorAll(selector));
	};
}());
