playtotv.define('playtotv.fx.EffectsController', [
	'playtotv.mvc.Controller'
], function(
	Controller
) {

	'use strict';

	var ptv = playtotv;
	var dom = ptv.dom;

	/**
	 * Monitors DOM mutations and triggers transitions on nodes matching selectors from transitionMap.
	 * Transitions are objects with enter and leave properties that are class names that start animations.
	 */
	return class EffectsController extends Controller {
		constructor(context, options) {
			options = ptv.params(options, {
				transitions: {}, // name to transition object with enter and leave properties (animation class names)
				defaults: {},
				fxDelay: 250
			});

			super(context);

			this.addMethods([
				'once'
			]);

			this.transitionMap = options.transitions;
			this.defaultMap = options.defaults;
			this.onceMap = {};
			this.delay = options.fxDelay;

			var view = this.scope.view;
			view.nodeAddedListeners.push(this.onNodeAdded.bind(this));
			view.nodeRemoveListeners.push(this.onNodeRemove.bind(this));
		}

		/**
		 * Queues a transition to be played on the next DOM mutation that matches the given selector.
		 * May be used to override default transitions.
		 * Once played, the once entry is removed.
		 */
		once(selector, transitionName) {
			if (!this.transitionMap[transitionName]) {
				throw new Error('Unknown transition: ' + transitionName);
			}
			this.onceMap[selector] = transitionName;
		}

		getSelectors() {
			return ptv.union(Object.keys(this.defaultMap), Object.keys(this.onceMap));
		}

		/**
		 * Returns any transition that should be applied to nodes matching specified selector.
		 */
		getTransition(selector) {
			var transitionName = this.onceMap[selector] || this.defaultMap[selector];
			return this.transitionMap[transitionName];
		}

		/**
		 * Called by the view before nodes are removed from DOM.
		 * Returns a delay to allow for any leave animations to finish before the node is removed.
		 *
		 * TODO: Enable overriding default delay, e.g: with a data attribute
		 */
		onNodeRemove(node) {
			var affected = false;

			this.getSelectors().
				filter(selector => dom.matches(selector, node)).
				forEach(selector => {
					var transition = this.getTransition(selector);
					this.playAnimation(node, transition.leave);
					affected = true;
				});

			return affected ? this.delay : 0;
		}

		/**
		 * Checks the added node and triggers animations for matching selectors of registered transitions.
		 */
		onNodeAdded(node) {
			this.getSelectors().
				filter(selector => dom.matches(selector, node)).
				forEach(selector => {
					var transition = this.getTransition(selector);
					this.playAnimation(node, transition.enter);

					// Clear any onced entry here to ensure that both leave and enter animations are played
					// since in transitions the node remove (leave) precedes the node add (enter)
					delete this.onceMap[selector];
				});
		}

		/**
		 * Starts animation by adding it as a class and storing it for later removal.
		 */
		playAnimation(node, animation) {
			if (!animation) {
				return;
			}

			// Clean up any previous animation class
			var prevAnimation = dom.getData(node, 'fx-animation');
			if (prevAnimation) {
				dom.removeData(node, 'fx-animation');
				node.classList.remove(prevAnimation);
			}

			dom.setData(node, 'fx-animation', animation);
			node.classList.add(animation);
		}
	};
});
