playtotv.define('playtotv.server.ServerController', [
	'playtotv.mvc.Controller',
	'playtotv.server.Server'
], function(
	Controller,
	Server
) {

	// TODO: Merge Server into this
	// TODO: JSDoc

	'use strict';

	var ptv = playtotv;
	var callAsyncCounter = 0;

	return class ServerController extends Controller {
		constructor(context, options) {
			super(context);

			var server = this.getServer(options);

			// Proxy to Server
			var properties = [ 'status', 'timeOffset', 'clientMinimalVersion' ];
			properties.forEach(key => {
				server.subscribe('change:' + key, value => this.set(key, value));
			});
			server.urls.subscribe('*:API', url => this.set('url', url));

			var methods = [ 'call', 'toClientTime', 'toServerTime', 'log' ];
			methods.forEach(name => {
				this[name] = server[name].bind(server);
				this.addMethod(name);
			});

			this.rejectTaps = server.rejectTaps;

			this.addMethods([
				'addRejectTap',
				'callAsync'
			]);

			// Work with push controller to support slow async calls
			this.asyncCalls = [];
			context.scope.subscribe('push.ASYNC-RESPONSE', this.onAsyncResponse.bind(this));
		}

		getServer(options) {
			return new Server(options);
		}

		addRejectTap(tapFn, filterFn) {
			filterFn = filterFn || function() { return true; };
			this.rejectTaps.push(function(error) {
				var method = this;
				if (filterFn(error, method)) {
					tapFn(error, method);
				}
			});
		}

		// Use this instead of call() to get server to send response back later through a push event
		// This is useful in case server might take longer then timeout (20s) to respond
		// Also alleviates server load when responses are likely to be slow in general
		callAsync(method, params, settings) {
			return new Promise((resolve, reject) => {
				// Note: callId is passed as a query param so it has to be a string
				var callId = '' + (++callAsyncCounter);

				this.call(method, params, ptv.extend({}, settings, {
					urlParams: { callId: callId }
				})).catch(reject);

				// TODO: Retry after async response event timeout in case we miss the event somehow?
				// Since this will be particularly used for potentially slow calls, I will leave this for now

				this.asyncCalls.push({
					callId: callId,
					label: method.label,
					resolve: resolve,
					reject: reject
				});
			});
		}

		onAsyncResponse(event) {
			var payload = event.payload;
			var callId = payload.callId;

			var asyncCalls = this.asyncCalls;
			var call = asyncCalls.find(call => call.callId === callId);
			if (!call) {
				return;
			}
			asyncCalls.splice(asyncCalls.indexOf(call), 1);

			var status = payload.responseStatus;
			var data = payload.responseBody;
			if ((status >= 200 && status < 300) || status === 304) {
				call.resolve(data);
			} else {
				var error = new Error('Async call ' + call.label + ' failed with status ' + status);
				call.reject(ptv.extend(error, {
					label: call.label,
					status: status,
					xhr: null,
					data: data
				}));
			}
		}
	};
});
