playtotv.define('playtotv.widget.Widget', [], function() {

	'use strict';

	var ptv = playtotv;
	var event = ptv.event;

	return new ptv.Class({
		/** @lends playtotv.widget.Widget.prototype */

		/**
		 * Widget Prototype, used as a super class to create specific widgets from.
		 *
		 * @constructs
		 * @param {Node} node Node this widget is being instantiated on
		 *
		 * @property {Node} node Widget node
		 * @property {Array} eventListeners Array of event listeners to automatically remove on dispose
		 * @property {Array} subscriptionRecords Array of subscription records to automatically unsubscribe on dispose
		 * @property {Array} timeoutIds Array of timeout id's to automatically remove on dispose
		 */
		initialize: function(node) {
			this.node = node;
			this.eventListeners = [];
			this.subscriptionRecords = [];
			this.timeoutIds = [];
		},

		/**
		 * Returns the node represented by this widget instance
		 *
		 * @returns {Node} node
		 */
		getNode: function() {
			return this.node;
		},

		/**
		 * Adds an event listener of the given type to a Node and returns an {@link playtotv.event.EventListener} for
		 * removal with {@link playtotv.widget.Widget#removeEventListener}. Listeners are added to the instance's
		 * {@link playtotv.widget.Widget#eventListeners} array for automatic removal when widgets are disposed by
		 * {@link playtotv.widget.Widget#dispose}.
		 *
		 * @param {Node} node Node to listen to
		 * @param {string} type Type of events to listen to
		 * @param {Function} listener Listener function to call on event
		 * @returns {playtotv.event.EventListener} Object with node, type and listener references
		 */
		addEventListener: function(node, type, listener) {
			var eventListener = event.addListener(node, type, listener);
			this.eventListeners.push(eventListener);
			return eventListener;
		},

		/**
		 * Removes an {@link playtotv.event.EventListener} listener from this widget provided it was added and
		 * returned by {@link playtotv.widget.Widget#addEventListener}
		 *
		 * @param {playtotv.event.EventListener} eventListener
		 * @returns {boolean} True if listener was successfully removed
		 */
		removeEventListener: function(eventListener) {
			var eventListeners = this.eventListeners;
			var index = eventListeners.indexOf(eventListener);
			var found = index >= 0;
			if (found) {
				event.removeListener(eventListener);
				eventListeners.splice(index, 1);
			}
			return found;
		},

		/**
		 * Subscribe and add pubsub subscription record for automatic unsubscription on dispose.
		 *
		 * Note: All arguments beyond the first (e.g: selector and handler) are passed to the call made to
		 * {@link playtotv.pubsub.PubSub#subscribe}, so calls like {@link playtotv.model.AbstractModel#subscribe}
		 * with extra arguments can also be made through this.
		 *
		 * @param {playtotv.pubsub.PubSub} pubsub PubSub to subscribe to
		 * @param {string} selector
		 * @param {Function} handler
		 * @returns {Object} Subscription record object with pubsub and subscription references
		 */
		subscribe: function(pubsub) {
			var args = ptv.toArray(arguments).slice(1);
			var record = {
				pubsub: pubsub,
				subscription: pubsub.subscribe.apply(pubsub, args)
			};
			this.subscriptionRecords.push(record);
			return record;
		},

		/**
		 * Unsubscribe and remove pubsub subscription.
		 *
		 * @param {Object} record Object with pubsub and subscription references
		 * @returns {boolean} True if subscription was successfully removed
		 */
		unsubscribe: function(record) {
			var records = this.subscriptionRecords;
			var index = records ? records.lastIndexOf(record) : -1;
			if (index === -1) {
				return false;
			}
			records.splice(index, 1);
			record.pubsub.unsubscribe(record.subscription);
			delete record.pubsub;
			delete record.subscription;
			return true;
		},

		/**
		 * Set a timeout for callback to called and remember the id for automatic clearing on dispose.
		 *
		 * @param {Function} callback Function to call
		 * @param {number} ms Time in ms after which to call callback
		 * @returns {number} Timeout id
		 */
		setTimeout: function(callback, ms) {
			var id = setTimeout(callback, ms);
			this.timeoutIds.push(id);
			return id;
		},

		/**
		 * Clear and remove timeout id.
		 *
		 * @param {number} id Object with pubsub and subscription references
		 * @returns {boolean} True if subscription was successfully removed
		 */
		clearTimeout: function(id) {
			var timeoutIds = this.timeoutIds;
			var index = timeoutIds.indexOf(id);
			var found = index >= 0;
			if (found) {
				clearTimeout(id);
				timeoutIds.splice(index, 1);
			}
			return found;
		},

		/**
		 * If a widget is created by a {@link playtotv.widget.WidgetManager} then the dispose method is called
		 * before the widget's containing node is removed from the DOM. Dispose may be used to clean up event
		 * listeners, and/or any other references that could potentially lead to memory leaks etc.
		 *
		 * This cleans up all event listeners, subscriptions and timeouts added by
		 * {@link playtotv.widget.Widget#addEventListener}, {@link playtotv.widget.Widget#subscribe} and
		 * {@link playtotv.widget.Widget#setTimeout}.
		 */
		dispose: function() {
			this.eventListeners.forEach(event.removeListener);

			this.subscriptionRecords.forEach(function(record) {
				record.pubsub.unsubscribe(record.subscription);
				delete record.pubsub;
				delete record.subscription;
			});

			this.timeoutIds.forEach(clearTimeout);

			ptv.dispose(this);
		}
	});
});
