playtotv.define('playtotv.fx.easing', [], function() {

	'use strict';

	/**
	 * Easing, provides callbacks that transform a 0 - 1 range to their eased equivalents.
	 *
	 * @name easing
	 * @memberOf playtotv.fx
	 * @namespace Easing equations, returns eased value from a 0 - 1 range.
	 */
	var easing = {

		/** @lends playtotv.fx.easing */

		/**
		 * Returns the requested easing equation, or linear if not found.
		 *
		 * @param {string} name
		 * @returns {Function} easing
		 */
		get: function(name) {
			return this[name] || this.linear;
		},

		/**
		 * Adds an easing equation under the given name.
		 *
		 * @param {string} name
		 * @param {Function} easing
		 * @returns {Function} easing
		 */
		set: function(name, easing) {
			if (this.hasOwnProperty(name)) {
				throw new Error('"' + name + '" easing is already defined.');
			}

			this[name] = easing;
			return easing;
		},

		/**
		 * Linear easing, input equals output.
		 *
		 * @param {number} value
		 * @returns {number} eased
		 */
		linear: function(i) {
			return i;
		},

		/**
		 * Quadratic ease-in
		 *
		 * @param {number} value
		 * @returns {number} eased
		 */
		quadin: function(i) {
			return i * i;
		},

		/**
		 * Quadratic ease-out
		 *
		 * @param {number} value
		 * @returns {number} eased
		 */
		quadout: function(i) {
			return i * (2 - i);
		},

		/**
		 * Quadratic ease-in-out
		 *
		 * @param {number} value
		 * @returns {number} eased
		 */
		quadinout: function(i) {
			return (i < 0.5) ?
				2 * i * i :
				1 - (2 * (--i) * i);
		},

		/**
		 * Cubic ease-in
		 *
		 * @param {number} value
		 * @returns {number} eased
		 */
		cubicin: function(i) {
			return i * i * i;
		},

		/**
		 * Cubic ease-out
		 *
		 * @param {number} value
		 * @returns {number} eased
		 */
		cubicout: function(i) {
			return ((--i) * i * i + 1);
		},

		/**
		 * Cubic ease-in-out
		 *
		 * @param {number} value
		 * @returns {number} eased
		 */
		cubicinout: function(i) {
			return (i < 0.5) ?
				4 * i * i * i :
				1 + (4 * (--i) * i * i);
		},

		/**
		 * Quartic ease-in
		 *
		 * @param {number} value
		 * @returns {number} eased
		 */
		quartin: function(i) {
			return i * i * i * i;
		},

		/**
		 * Quartic ease-out
		 *
		 * @param {number} value
		 * @returns {number} eased
		 */
		quartout: function(i) {
			return -((--i) * i * i * i - 1);
		},

		/**
		 * Quartic ease-in-out
		 *
		 * @param {number} value
		 * @returns {number} eased
		 */
		quartinout: function(i) {
			return (i < 0.5) ?
				8 * i * i * i * i :
				1 - (8 * (--i) * i * i * i);
		}
	};

	return easing;
});
