playtotv.define('playtotv.mvc.UpdateWidget', [
	'playtotv.widget.Widget',
	'playtotv.cron'
], function(
	Widget,
	cron
) {

	'use strict';

	var ptv = playtotv;

	/**
	 * Callback defaults to controller.update() but can be made to point to another function in the options.
	 *
	 * The repeat option value defaults to 10 minutes.
	 * If you pause your app on your phone and resume it the next day the callback will fire almost immediately.
	 * Also, if you walk away from your desktop and come back after lunch it will be roughly kept up-to-date.
	 */
	return class UpdateWidget extends Widget {

		constructor(node, options) {
			options = ptv.params(options, {
				app: null,
				controller: ptv.REQUIRED,
				callback: null,
				repeat: 10 * 60 * 1000
			});

			super(node);

			var controller = options.controller;
			var callback = options.callback || controller.update.bind(controller);

			// TODO: Add support for starting at or after a specified time (default: now)
			this.job = cron.at(0, callback, { repeat: options.repeat });
		}

		dispose() {
			cron.cancel(this.job);
			super();
		}
	};
});
