playtotv.define('playtotv.storage.AsyncStorage', [
	'playtotv.storage.Storage'
], function(
	Storage
) {
	/* global openDatabase:false*/

	'use strict';

	var ptv = playtotv;
	var moduleId = this.id;
	var log = ptv.Logger.get(moduleId);
	var TABLE = 'keyvaluepairs';

	function sql(db, query, args) {
		args = args || [];

		log.s(query, args);

		return new Promise(function(resolve, reject) {
			db.transaction(function(transaction) {
				transaction.executeSql(query, args, function(transaction, results) {
					resolve(results);
				}, function(transaction, error) {
					reject(error);
				});
			}, reject);
		});
	}

	return new ptv.Class({
		/** @lends playtotv.storage.AsyncStorage.prototype */

		/**
		 * Async key value storage using Web SQL as backend or falling back to localStorage if necessary.
		 *
		 * The interface is similar to {@link playtotv.storage.Storage} (which is also used for localStorage fallback)
		 * but async using Promises.
		 *
		 * Code is inspired by localForage which is excellent but slightly on the heavy side
		 * (localforage.nopromises.min.js is 17.5KB minified vs 2.7KB for Storage and AsyncStorage).
		 * Note: IndexedDB still has limited support and issues so it has been left out for now.
		 *
		 * @constructs
		 * @param {string} [prefix=''] Key prefix for this instance
		 */
		initialize: function(prefix) {
			this.prefix = prefix || '';

			this.ready = Promise.resolve().then(function() {
				// Default DB size is _JUST UNDER_ 5MB, as it's the highest size we can use without a prompt
				var db = openDatabase(moduleId, '1.0', '', 4980736);
				var query = 'CREATE TABLE IF NOT EXISTS ' + TABLE + ' (id INTEGER PRIMARY KEY, key unique, value)';
				return sql(db, query).then(function() {
					return { db: db };
				});
			}).catch(function(error) {
				log.d('Falling back to localStorage', error);
				return { storage: new Storage(window.localStorage, prefix) };
			});
		},

		/**
		 * Returns Promise of stored value for a given key. Returns defaultValue if no value is found.
		 *
		 * @param {string} key
		 * @param {mixed} defaultValue
		 * @returns {Promise} Promise of value
		 */
		get: function(key, defaultValue) {
			var prefix = this.prefix;

			return this.ready.then(function(ref) {
				var db = ref.db;
				if (!db) {
					return ref.storage.get(key, defaultValue);
				}

				var query = 'SELECT * FROM ' + TABLE + ' WHERE key = ? LIMIT 1';
				return sql(db, query, [ prefix + key ]).then(function(results) {
					var rows = results.rows;
					return rows.length ? JSON.parse(rows.item(0).value) : defaultValue;
				});
			});
		},

		/**
		 * Stores a value with the given key. If value equals null or undefined then stored value is removed instead.
		 *
		 * Note: This should not be considered to be reliable let alone secure, always provide fallbacks.
		 *
		 * @param {string} key
		 * @param {mixed} value
		 * @returns {Promise} Promise
		 */
		set: function(key, value) {
			// Remove instead of setting null/undefined so as to match behavior of Storage class
			if (value === null || value === undefined) {
				return this.remove(key);
			}

			var prefix = this.prefix;

			return this.ready.then(function(ref) {
				var db = ref.db;
				if (!db) {
					return ref.storage.set(key, value, function(error) {
						throw new Error('Quota error: ' + error.name);
					});
				}

				var query = 'INSERT OR REPLACE INTO ' + TABLE + ' (key, value) VALUES (?, ?)';
				return sql(db, query, [ prefix + key, JSON.stringify(value) ]);
			});
		},

		/**
		 * Removes a key
		 *
		 * @param {string} key
		 * @returns {Promise} Promise
		 */
		remove: function(key) {
			var prefix = this.prefix;

			return this.ready.then(function(ref) {
				var db = ref.db;
				if (!db) {
					return ref.storage.remove(key);
				}

				return sql(db, 'DELETE FROM ' + TABLE + ' WHERE key = ?', [ prefix + key ]);
			});
		},

		/**
		 * Removes all items with this instance's prefix.
		 *
		 * @returns {Promise} Promise
		 */
		clear: function() {
			var prefix = this.prefix;

			return this.ready.then(function(ref) {
				var db = ref.db;
				if (!db) {
					return ref.storage.clear();
				}

				// Would be prettier to use ? instead of directly injecting prefix here, but don't know how
				return sql(db, 'DELETE FROM ' + TABLE + ' WHERE key LIKE "' + prefix + '%"');
			});
		},

		/**
		 * Returns a sub storage instance with combined prefixes.
		 *
		 * @param {string} subPrefix
		 * @returns {playtotv.storage.AsyncStorage} storage
		 */
		subStorage: function(subPrefix) {
			return new this.constructor(this.prefix + subPrefix);
		},

		/**
		 * Debug method for directly executing a query on Web SQL database.

		 * @param {string} query
		 * @param {Array} args
		 * @returns {Promise} Promise
		 */
		sql: function(query, args) {
			return this.ready.then(function(ref) {
				return sql(ref.db, query, args);
			});
		}
	});
});
