playtotv.define('playtotv.question.QuestionController', [
	'playtotv.mvc.Controller',
	'playtotv.question.questionAPI'
], function(
	Controller,
	questionAPI
) {

	'use strict';

	var ptv = playtotv;

	return class QuestionController extends Controller {
		constructor(context) {
			super(context);

			this.addMethods([
				'submitAnswer',
				'decorateAnswers'
			]);
		}

		submitAnswer(id, code, options) {
			options = ptv.params(options, {
				delay: 0
			});

			var idCode = id + '-' + code;
			this.submitting = idCode;
			this.publish('submitting', this.submitting);

			return Promise.delay(options.delay).then(() => {
				return this.scope.call('episode.submitInteraction', questionAPI.submitAnswer, id, {
					questionId: id,
					answerCodes: [ code ],
					clientAnswerTime: true
				}).finally(() => {
					if (this.submitting === idCode) {
						delete this.submitting;
					}
				});
			});
		}

		decorateAnswers(question) {
			var stats = question.stats;
			var data = question.data;
			var statsAnswers = (stats && stats.answers) || [];
			var submittedAnswerCodes = (data && data.submittedAnswers) || [];

			return question.content.answers.map(answer => {
				var answerCode = answer.code;
				var answerStats = statsAnswers.find(answerStats => answerStats.code === answerCode);
				return ptv.extend({
					question: question,
					submitting: this.submitting === question.id + '-' + answerCode,
					submitted: submittedAnswerCodes.indexOf(answerCode) >= 0
				}, answer, answerStats);
			});
		}
	};
});
