playtotv.define('playtotv.global.GlobalController', [
	'playtotv.global.genericContentAPI',
	'playtotv.global.globalDataAPI',
	'playtotv.mvc.Controller',
	'playtotv.cron'
], function(
	genericContentAPI,
	globalDataAPI,
	Controller,
	cron
) {

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;

	return class GlobalController extends Controller {
		constructor(context, options) {
			options = ptv.params(options, {
				timeZone: util.getTimeZone()
			});

			// Note: model with global data is implicitly persisted
			super(context);

			this.getContent = Promise.limit(this.getContentNow, 10 * 60 * 1000);

			this.addMethods([
				'update',
				'getContent'
			]);

			this.options = options;

			// Note: Using slow polling as a reliable method for staying up-to-date
			var update = this.update.bind(this);
			cron.at(Date.now(), update, { repeat: 5 * 60 * 1000 });

			// Fast, but slightly unreliable, ways of staying up-to-date
			this.scope.subscribe('push.GLOBALDATA-REPLACED', event => this.onData(event.payload));
			this.scope.subscribe('push.GLOBALDATA-UPDATED', event => this.onUpdate(event.payload));
		}

		update() {
			this.scope.call('server.call', globalDataAPI.get, this.options).then(this.onData.bind(this));
		}

		getContentNow(category) {
			return this.scope.call('server.call', genericContentAPI.getCategory, {
				category: category
			});
		}

		onData(data) {
			ptv.each(data, (value, key) => {
				// Note: set() will only trigger model changes when value has actually changed
				this.set(key, value);
			});
		}

		onUpdate(payload) {
			var { category, object, uniqueIdField } = payload;
			var items = this.model[category] || [];
			var item = items.find(candidate => candidate[uniqueIdField] === object[uniqueIdField]);

			if (item) {
				ptv.extend(item, object);
			} else {
				items.push(object);
			}

			// Force change publish in case we just changed an existing item in the existing array
			this.set(category, items, { force: true });
		}
	};
});
