playtotv.define('playtotv.model.ObjectModel', [
	'playtotv.model.AbstractModel'
], function(
	AbstractModel
) {

	'use strict';

	return new playtotv.Class({
		/** @lends playtotv.model.ObjectModel.prototype */

		extend: AbstractModel,

		/**
		 * Concrete implementation of {@link playtotv.model.AbstractModel} for storing Object properties.
		 *
		 * If a source object is specified then it's properties will be copied using
		 * {@link playtotv.model.ObjectModel#copyProperties}.
		 *
		 * Reading and iterating:
		 * <ul>
		 * <li>For basic read access you can simply access the properties of this model
		 * <li>This model does however have some extra Class properties and methods
		 * </ul>
		 *
		 * Modify and publish methods:
		 * <ul>
		 * <li>{@link playtotv.model.ObjectModel#addProperty}
		 * <li>{@link playtotv.model.ObjectModel#removeProperty}
		 * <li>{@link playtotv.model.ObjectModel#changeProperty}
		 * <li>{@link playtotv.model.ObjectModel#setProperty}
		 * <li>{@link playtotv.model.ObjectModel#copyProperties}
		 * </ul>
		 *
		 * Note: Only modify this model's properties through the above methods!
		 *
		 * @extends playtotv.model.AbstractModel
		 * @constructs
		 * @param {Object} [source] Source object to copy into this model
		 *
		 * @property {[string]} propertyKeys This model's property keys
		 * @property {Object} childSubscriptionMap Map of property keys to child model subscriptions
		 */
		initialize: function(source) {
			this._super();

			this.propertyKeys = [];
			this.childSubscriptionMap = {};

			if (source) {
				this.copyProperties(source);
			}
		},

		/**
		 * Implementation of {@link playtotv.model.AbstractModel#toObject}.
		 *
		 * @returns {Object} Plain JavaScript Object copy of the model data
		 */
		toObject: function() {
			var obj = {};
			var keys = this.propertyKeys;
			var keysLength = keys.length;
			var i, key;
			for (i = 0; i < keysLength; i++) {
				key = keys[i];
				obj[key] = this[key];
			}
			return obj;
		},

		/**
		 * Implementation of {@link playtotv.model.AbstractModel#updateModel}.
		 *
		 * Properties are {@link playtotv.model.ObjectModel#removeProperty}d, then
		 * {@link playtotv.model.ObjectModel#addProperty}ed and then {@link playtotv.model.ObjectModel#changeProperty}d.
		 *
		 * @param {Object} source Source data to update model to
		 * @returns {[mixed]} Array with any removed values for garbage collection etc.
		 */
		updateModel: function(source) {
			var keys = this.propertyKeys;
			var sourceKeys = Object.keys(source);
			var removedValues = [];

			keys.filter(function(key) {
				return sourceKeys.indexOf(key) === -1;
			}).forEach(function(key) {
				removedValues.push(this.removeProperty(key));
			}.bind(this));

			sourceKeys.filter(function(sourceKey) {
				return keys.indexOf(sourceKey) === -1;
			}).forEach(function(sourceKey) {
				this.addProperty(sourceKey, source[sourceKey]);
			}.bind(this));

			sourceKeys.filter(function(sourceKey) {
				return keys.indexOf(sourceKey) !== -1;
			}).forEach(function(sourceKey) {
				removedValues.push(this.changeProperty(sourceKey, source[sourceKey]));
			}.bind(this));

			return removedValues;
		},

		/**
		 * Implementation of {@link playtotv.model.AbstractModel#isRelatedModel}.
		 *
		 * @param {playtotv.model.AbstractModel} root Root model to compare to
		 * @returns {boolean} True if this model has a cyclic relation to root
		 */
		isRelatedModel: function(root) {
			if (this === root) {
				return true;
			}

			var childSubscriptionMap = this.childSubscriptionMap;
			var keys = Object.keys(childSubscriptionMap);
			var keysLength = keys.length;
			var i, childModel;

			for (i = 0; i < keysLength; i++) {
				childModel = childSubscriptionMap[keys[i]].model;
				if (childModel instanceof AbstractModel) {
					if (childModel.isRelatedModel(root)) {
						return true;
					}
				}
			}

			return false;
		},

		/**
		 * Subscribe to child model to have it's publishes bubble up to this one.
		 *
		 * Note: It is required that child model is not in a cyclic relationship with this model.
		 *
		 * @param {string} key Child model key
		 * @param {playtotv.model.AbstractModel} child Child model to subscribe to
		 */
		subscribeChild: function(key, child) {
			var subscription = child.subscribe('*', function(childValue, childAction, childKey) {
				this.publish(key + '.' + childKey, childAction, childValue);
			}.bind(this));

			this.childSubscriptionMap[key] = {
				model: child,
				subscription: subscription
			};
		},

		/**
		 * Unsubscribe child model so it's publishes no longer bubble up to this one.
		 *
		 * @param {string} key Child model key
		 */
		unsubscribeChild: function(key) {
			var childSubscriptionMap = this.childSubscriptionMap;
			var childSubscription = childSubscriptionMap[key];
			childSubscription.model.unsubscribe(childSubscription.subscription);
			delete childSubscriptionMap[key];
		},

		/**
		 * Add property with specified key and value to this model.
		 *
		 * @param {string} key
		 * @param {mixed} value
		 * @throws {Error} If property with specified key already exists
		 * @throws {Error} If specified key is reserved
		 * @throws {Error} If value is a model in a cyclic relationship with this model
		 */
		addProperty: function(key, value) {
			var keys = this.propertyKeys;
			var valueIsModel = value instanceof AbstractModel;

			if (keys.indexOf(key) !== -1) {
				throw new Error('Property with specified key already exists: ' + key);
			}
			if (key in this) {
				throw new Error('Specified key is reserved: ' + key);
			}
			if (valueIsModel && value.isRelatedModel(this)) {
				throw new Error('Value is a model in a cyclic relationship with this model');
			}

			keys.push(key);
			this[key] = value;

			if (valueIsModel) {
				this.subscribeChild(key, value);
			}

			this.publish(key, 'add', value);
		},

		/**
		 * Change property specified by key to specified value.
		 *
		 * @param {string} key
		 * @param {mixed} value
		 * @param {Object} [options]
		 * @param {boolean} [options.force] If true then force change even if value is equal to old value
		 * @param {boolean} [options.json] If true then use JSON.stringify to compare value with old value
		 * @throws {Error} If property with specified key does not exist
		 * @throws {Error} If value is a model in a cyclic relationship with this model
		 * @returns {mixed} Removed value (if any) for garbage collection etc.
		 */
		changeProperty: function(key, value, options) {
			/*jshint maxcomplexity:10*/
			options = options || {};
			var oldValue = this[key];
			var valueIsModel = value instanceof AbstractModel;

			if (this.propertyKeys.indexOf(key) === -1) {
				throw new Error('Property with specified key does not exist: ' + key);
			}

			if (!options.force) {
				if (value === oldValue || options.json && JSON.stringify(value) === JSON.stringify(oldValue)) {
					return; // no change
				}
				if (valueIsModel && value.isRelatedModel(this)) {
					throw new Error('Value is a model in a cyclic relationship with this model');
				}
			}

			if (oldValue instanceof AbstractModel) {
				this.unsubscribeChild(key);
			}

			this[key] = value;

			if (valueIsModel) {
				this.subscribeChild(key, value);
			}

			this.publish(key, 'change', value);

			return oldValue;
		},

		/**
		 * Remove property with specified key from this model.
		 *
		 * @param {string} key
		 * @throws {Error} If property with specified key does not exist
		 * @returns {mixed} Removed value for garbage collection etc.
		 */
		removeProperty: function(key) {
			var keys = this.propertyKeys;
			var index = keys.indexOf(key);
			var oldValue = this[key];

			if (index === -1) {
				throw new Error('Property with specified key does not exist: ' + key);
			}

			if (oldValue instanceof AbstractModel) {
				this.unsubscribeChild(key);
			}

			keys.splice(index, 1);
			delete this[key];

			this.publish(key, 'remove', oldValue);

			return oldValue;
		},

		/**
		 * Either calls {@link playtotv.model.ObjectModel#addProperty} or
		 * {@link playtotv.model.ObjectModel#changeProperty} depending on property existence.
		 *
		 * @param {string} key Argument to addProperty or changeProperty
		 * @param {mixed} value Argument to addProperty or changeProperty
		 * @param {Object} [options] Options to pass to changeProperty
		 * @returns {mixed} Removed value (if any) for garbage collection etc.
		 */
		setProperty: function(key, value, options) {
			if (this.propertyKeys.indexOf(key) === -1) {
				this.addProperty(key, value);
			} else {
				return this.changeProperty(key, value, options);
			}
		},

		/**
		 * Copy all properties from specified source object to this model using
		 * {@link playtotv.model.ObjectModel#setProperty}.
		 *
		 * This will change existing properties and add them if they were missing, but it will not remove them.
		 * To have this model data match source use {@link playtotv.model.ObjectModel#updateModel}.
		 *
		 * @param {Object} source Source to copy from
		 * @returns {[mixed]} Array with any removed values for garbage collection etc.
		 */
		copyProperties: function(source) {
			var removedValues = [];
			for (var key in source) {
				if (source.hasOwnProperty(key)) {
					removedValues.push(this.setProperty(key, source[key]));
				}
			}
			return removedValues;
		},

		/**
		 * Dispose keys and child subscriptions.
		 *
		 * Note: Do not forget to also dispose any child models that are no longer used.
		 */
		dispose: function() {
			playtotv.each(this.childSubscriptionMap, function(childSubscription) {
				childSubscription.model.unsubscribe(childSubscription.subscription);
			});

			AbstractModel.prototype.dispose.call(this);
		}
	});
});
