playtotv.define('playtotv.touch.touchManager', [
	'playtotv.css.Vector',
	'playtotv.touch.TouchProxy'
], function(
	Vector,
	TouchProxy
) {

	'use strict';

	var dom = playtotv.dom;
	var TOUCH_ENABLED = 'TouchEnabled';

	/**
	 * Touch controller for managing em:touch[type] events for use with both mouse and touch. Typically, only the root
	 * node of an app is enabled via the captureEvents method, but an approach where individual nodes are captured and
	 * released during their lifecycle is a possible one.
	 * Various widgets like {@link playtotv.widget.ScrollableWidget} rely on a captured node (or ascendant) to
	 * function properly, and will throw an error if none is found.
	 *
	 * Available events are em:touchstart, em:touchmove, em:touchend (which are fired from their native down, move and
	 * up counterparts) and em:swipe, which (if at all) is fired immediately after em:touchend. The event object that
	 * is passed to handlers contains a detail object with the following properties:
	 *
	 * - proxy, the {@link playtotv.touch.TouchProxy} that fired the event
	 * - sequence, the {@link playtotv.touch.Sequence} of this event, exposing offset and velocity vectors etc.
	 * - nativeEvent, the underlying native event
	 *
	 * Calling preventDefault on em:touchstart, -move and -end will prevent native mouse and touch behavior to trigger
	 * (e.g. native scrolling, dragging, zooming) depending on preventNative option passed to the captureEvents
	 * method. In general, handlers SHOULD call preventDefault, as custom event handling often implies
	 * that native behavior is not desired at that time.
	 *
	 * Calling stopPropagation will prevent events to reach handlers higher up in the DOM, and MAY be called by
	 * handlers that wish to claim the event. A horizontal slider could stop propagation of horizontally orientented
	 * em:touchmove events to prevent them from reaching a vertically scrollable ascendant, which might otherwise
	 * result in sliding and scrolling simultaneously.
	 *
	 * @name touchManager
	 * @namespace Touch controller for managing em:touch[type] events.
	 * @memberOf playtotv.touch
	 */
	var touchManager = {

		/** @lends playtotv.touch */

		proxies: [],

		/**
		 * Enables custom em:touch[type] events for a node and its decendants via {@link playtotv.touch.TouchProxy}.
		 * These events are required by {@link playtotv.widget.DraggableWidget} and any widgets that use it.
		 *
		 * Set the fastClick option to true (default) to allow a faster click event to be fired on touch enabled
		 * devices, which may have an aproximately 300ms delay before a native click is fired. Some devices remove this
		 * delay themselves if the viewport meta tag content contains 'user-scalable=no', and/or when the target node's
		 * style has a touch-action css property with a value of 'none' or 'manipulation'. In this case, the fastClick
		 * option will not fire clicks.
		 *
		 * When preventDefault is called on a custom event, the underlying native (pointer, touch or mouse) event is
		 * also prevented. Set the em>preventNative</em> option to false (default true) to disallow this. This may be
		 * required in special cases, for instance when combining custom horizontal scrolling with native vertical
		 * scrolling etc.
		 *
		 * Set the propagateNative option to true to allow underlying native events to bubble unhindered.
		 * This has no effect on native browser behavior, but might be needed when using other third party scripts that
		 * need to handle these events on nodes higher up in the DOM. This may also apply to the preventNative option.
		 *
		 * @param {Node} node
		 * @param {Object} [options]
		 * @param {boolean} [options.fastClick=true]
		 * @param {boolean} [options.preventNative=true]
		 * @param {boolean} [options.propagateNative=false]
		 * @returns {playtotv.touch.TouchProxy} proxy
		 */
		captureEvents: function(node, options) {
			options = playtotv.params(options, {
				fastClick: true,
				preventNative: true,
				propagateNative: false
			});

			if (this.isCaptured(node)) {
				throw new Error('Node or ascendant is already captured by playtotv.touch');
			}

			if (options.fastClick && !options.preventNative) {
				throw new Error('Ambiguous options: preventNative (false) is overruled by fastClick (true)');
			}

			// flag as enabled
			dom.setData(node, TOUCH_ENABLED, true);

			// create the proxy
			var proxy = new TouchProxy(node, options);
			this.proxies.push(proxy);

			// and return it
			return proxy;
		},

		/**
		 * Removes custom em:touch[type] events of a previously enabled node. Returns a boolean whether the node
		 * had its events removed successfully.
		 *
		 * @param {Node} node
		 * @returns {boolean} success
		 */
		releaseEvents: function(node) {
			var i, proxy, l = this.proxies.length;
			for (i = 0; i < l; i++) {
				proxy = this.proxies[i];
				if (proxy.node === node) {
					dom.removeData(node, TOUCH_ENABLED);
					this.proxies.splice(i, 1);
					proxy.dispose();
					return true;
				}
			}

			return false;
		},

		/**
		 * Returns a boolean whether em:touch[type] events are enabled for this node (or via an ascendant).
		 *
		 * @param {Node} node
		 * @returns {boolean} captured
		 */
		isCaptured: function(node) {
			while (node) {
				if (dom.getData(node, TOUCH_ENABLED)) {
					return true;
				}
				node = node.parentNode;
			}
			return false;
		},

		/**
		 * Returns the angle (in radians) of a sequence's current velocity vector
		 *
		 * @param {playtotv.touch.Sequence} sequence
		 * @return {number} angle
		 */
		getAngle: function(sequence) {
			var v = sequence.velocity;
			return Math.atan2(v.y, v.x);
		},

		/**
		 * Returns the orientation of a sequence's current velocity, either Vector.HORIZONTAL or Vector.VERTICAL
		 *
		 * @param {playtotv.touch.Sequence} sequence
		 * @return {number} orientation
		 */
		getOrientation: function(sequence) {
			var v = sequence.velocity;
			return Math.abs(v.x) > Math.abs(v.y) ?
				Vector.HORIZONTAL : Vector.VERTICAL;
		},

		/**
		 * Returns a given event's direction, as Vector property UP, DOWN, LEFT or RIGHT
		 *
		 * @param {playtotv.touch.Sequence} sequence
		 * @return {number} direction
		 */
		getDirection: function(sequence) {
			var v = sequence.velocity;
			return this.getOrientation(sequence) === Vector.HORIZONTAL ?
				(v.x > 0 ? Vector.RIGHT : Vector.LEFT) : (v.y > 0 ? Vector.DOWN : Vector.UP);
		}
	};

	/**
	 * return the module
	 *
	 */

	return touchManager;
});
