playtotv.define('playtotv.twitter.TwitterController', [
	'playtotv.mvc.Controller',
	'playtotv.twitter.twitterAPI'
], function(
	Controller,
	twitterAPI
) {

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;

	return class TwitterController extends Controller {
		/** @lends playtotv.twitter.TwitterController.prototype */

		/**
		 * Generic controller that facilitates displaying a stream of tweets and tweeting etc.
		 *
		 * Uses Controller storage and then {@link playtotv.twitter.twitterAPI.getRecentTweets} to get tweets.
		 * Also see that method for a specification of tweet object properties.
		 *
		 * Uses Twitter Streaming API tweets being distributed as PlayToTV events by our servers to update tweets.
		 * This subscribes to push.TWITTER-POST events on scope that can be provided by
		 * {@link playtotv.push.PushController}.
		 *
		 * @extends playtotv.mvc.Controller
		 * @constructs
		 * @param {Object} context
		 * @param {number} [options.maxItems=25] Maximum number of items to keep in tweets list
		 */
		constructor(context, options) {
			options = ptv.params(options, {
				maxItems: 25
			});

			var stored = context.stored || {};
			super(context, {
				model: {
					tweets: stored.tweets || []
				}
			});

			this.addMethods([
				'getTweetParts',
				'intent'
			]);

			this.maxItems = options.maxItems;

			// Fetch current list of Tweets
			this.scope.call('server.call', twitterAPI.getRecentTweets, {
				// max: maxItems // TODO: not yet supported by server
			}).then(tweets => {
				this.cap(tweets);
				this.set('tweets', tweets);

				// And from this point on keep up-to-date by push events
				this.scope.subscribe('push.TWITTER-POST', event => this.addTweet(event.payload));
			});
		}

		cap(tweets) {
			tweets.length = Math.min(tweets.length, this.maxItems);
		}

		addTweet(tweet) {
			var tweets = this.model.tweets;
			tweets.unshift(tweet);
			this.cap(tweets);
			// Note: We forego publishing here since we got here from a push.TWITTER-POST publish
			// this.publish('tweets-add', tweet);
		}

		/**
		 * Returns array of parts of text with different types.
		 *
		 * Each part is an object with properties:
		 * <ul>
		 * <li>type - text/url/mention/hash
		 * <li>text - text of part
		 * <li>[match] - specific match of part text (e.g: without hash # or mention @ prefix)
		 * </ul>
		 *
		 * @param {string} text Tweet text to split up
		 * @returns {[Object]} Tweet text parts
		 */
		getTweetParts(text) {
			var part = [
				{ type: 'url', regex: new RegExp(util.URL_REGEXP.source) }, // non-global copy of URL_REGEXP
				{ type: 'mention', regex: /@(\w+)/ },
				{ type: 'hash', regex: /#(\w+)/ }
			].reduce(function(part, test) {
				var matches = test.regex.exec(text);
				return matches && (part.type === 'text' || matches.index < part.index) ? {
					index: matches.index,
					type: test.type,
					text: matches[0],
					match: matches[1]
				} : part;
			}, { index: 0, type: 'text', text: text });

			var parts = [ part ];

			var index = part.index;
			if (index > 0) {
				parts.unshift({ type: 'text', text: text.substr(0, index) });
			}

			var tail = text.substr(index + part.text.length);
			return tail ? parts.concat(this.getTweetParts(tail)) : parts;
		}

		/**
		 * Opens intent with specified type and options in a fitting popup through scope.
		 *
		 * @example
		 * <a href="#" {{rels (rel "twitter-intent" "tweet" text="Tweet tweet!")}}>
		 * Will open: https://twitter.com/intent/tweet?text=Tweet%20tweet!
		 *
		 * @param {string} type
		 * @param {Object} [options]
		 */
		intent(type, options) {
			var url = util.addURLParams('https://twitter.com/intent/' + type, options);
			this.scope.open(url, 'twitter-intent', { width: 550, height: 420 });
		}
	};
});
