playtotv.define('playtotv.css.Point', [
	'playtotv.css.Vector'
], function(
	Vector
) {

	'use strict';

	/**
	 * 3D point, as returned by {@link playtotv.css.Matrix#transformPoint}.
	 *
	 * @name playtotv.css.Point
	 * @class
	 * @constructor
	 * @param {number} x
	 * @param {number} y
	 * @param {number} z
	 */
	var Point = new playtotv.Class({
		initialize: function(x, y, z) {
			this.x = x;
			this.y = y;
			this.z = z;
		},

		/** @lends playtotv.css.Point.prototype */

		/**
		 * Returns a 2D vector representation of this point, discarding the z.
		 *
		 * @returns {playtotv.css.Vector} vector
		 */
		toVector: function() {
			return new Vector(this.x, this.y);
		}
	});

	/**
	 * Parses values into a point. Uses x for y and/or z when not specified.
	 *
	 * @static
	 * @function
	 * @name parseFloat
	 * @memberOf playtotv.css.Point
	 * @param {string|number} x
	 * @param {string|number} [y]
	 * @param {string|number} [z]
	 * @returns {playtotv.css.Point} point
	 */
	Point.parseFloat = function(x, y, z) {
		x = parseFloat(x);
		y = arguments.length < 2 ? x : parseFloat(y);
		z = arguments.length < 3 ? x : parseFloat(z);
		return new Point(x, y, z);
	};

	return Point;
});
