playtotv.define('playtotv.touch.Sequence', [
	'playtotv.css.Vector'
], function(
	Vector
) {

	'use strict';

	var _max = Math.max;

	var SWIPE_DELAY = 300;
	var SENSITIVITY = 20;

	/**
	 * Follows a mouse or touch pointer from start to end, and is continuously updated with properties like the
	 * current point, offset and velocity vector. Updated automatically by the {@link playtotv.touch.TouchProxy} that
	 * created it, and passed along with 'em:touch[type]' events as 'sequence' property on the event's details.
	 *
	 * @name Sequence
	 * @class
	 * @memberOf playtotv.touch
	 * @constructor
	 * @param {Node} target
	 * @param {Event|Touch} e
	 */
	var Sequence = new playtotv.Class({
		initialize: function(target, e) {
			this.target = target;
			this.id = e.pointerId || e.identifier;
			this.start = new Vector(e.clientX, e.clientY);
			this.screen = new Vector(e.screenX, e.screenY);
			this.point = this.start;
			this.offset = Vector.ZERO;
			this.velocity = Vector.ZERO;
			this.time = Date.now();

			this.isTap = true;
		},

		/** @lends playtotv.touch.Sequence.prototype */

		/**
		 * Updates this sequence's point, offset and velocity vectors with the given native event or touch, and sets
		 * the isTap boolean to false when the offset's magnitude exceeds tap sensitivity.
		 *
		 * @param {Event|Touch} e
		 */
		update: function(e) {
			var t = Date.now();
			var c = new Vector(e.clientX, e.clientY);
			var w = new Vector(e.screenX, e.screenY);
			var s = this.start;
			var p = this.point;
			var ms = t - this.time;
			var v = 1000 / _max(ms, 40); // TODO: average over multiple updates more accurate?

			this.velocity = new Vector(
				(c.x - p.x) * v,
				(c.y - p.y) * v
			);

			this.offset = new Vector(
				c.x - s.x,
				c.y - s.y
			);

			if (this.isTap && (this.offset.magnitude() > SENSITIVITY)) {
				delete this.isTap;
				this.isSwipe = setTimeout(this.clearSwipe, SWIPE_DELAY);
			}

			this.time = t;
			this.point = c;
			this.screen = w;
		},

		/**
		 * Deletes the isSwipe property when swipe time has been exceeded. Used by the TouchProxy (in combination with
		 * isTap) to determine whether a swipe event will be fired.
		 */
		clearSwipe: function() {
			clearTimeout(this.isSwipe);
			delete this.isSwipe;
		},

		callbacks: [
			'clearSwipe'
		]
	});

	return Sequence;
});
