playtotv.define('playtotv.model.ArrayModel', [
	'playtotv.model.AbstractModel'
], function(
	AbstractModel
) {

	'use strict';

	function countEqualItems(array, item) {
		var count = 0;
		var len = array.length;
		var i;
		for (i = 0; i < len; i++) {
			if (array[i] === item) {
				count++;
			}
		}
		return count;
	}

	return new playtotv.Class({
		/** @lends playtotv.model.ArrayModel.prototype */

		extend: AbstractModel,

		/**
		 * Concrete implementation of {@link playtotv.model.AbstractModel} for storing an Array of items.
		 *
		 * If a source array is specified then it's items will be copied using {@link playtotv.model.ArrayModel#add}.
		 *
		 * Model items can be read and iterated etc. by using this instance's {@link playtotv.model.ArrayModel#array}
		 * property.
		 *
		 * @extends playtotv.model.AbstractModel
		 * @constructs
		 * @param {Array} [source] Source array to copy into this model
		 *
		 * @property {Array} array Container of this model's items
		 * @property {Array} childSubscriptions Array mapping item indexes to child model subscriptions
		 */
		initialize: function(source) {
			this._super();

			this.array = [];
			this.childSubscriptions = [];

			if (source) {
				source.forEach(function(item) {
					this.add(item);
				}.bind(this));
			}
		},

		/**
		 * Implementation of {@link playtotv.model.AbstractModel#toObject}.
		 *
		 * @returns {Array} Plain JavaScript Array Object copy of the model data
		 */
		toObject: function() {
			return this.array.slice(0);
		},

		/**
		 * Implementation of {@link playtotv.model.AbstractModel#updateModel}.
		 *
		 * First tries to check whether JSON stringified versions of source and array are equal and aborts if so.
		 *
		 * Items are {@link playtotv.model.ArrayModel#remove}d from right to left then
		 * {@link playtotv.model.ArrayModel#add}ed and then {@link playtotv.model.ArrayModel#change}d from left to
		 * right.
		 *
		 * @param {Array} source Source data array to update model to
		 * @returns {[mixed]} Array with any removed items for garbage collection etc.
		 */
		updateModel: function(source) {
			var array = this.array;
			var sourceLength = source.length;
			var removedItems = [];
			var i, item;

			// TODO: JSON stringify compare items individually to try to minimize number of removes, adds and changes
			try {
				if (JSON.stringify(source) === JSON.stringify(array)) {
					return [];
				}
			} catch (e) {}

			for (i = array.length - 1; i >= 0; i--) {
				item = array[i];
				if (countEqualItems(source, item) < countEqualItems(array, item)) {
					removedItems.push(this.remove(i));
				}
			}

			for (i = 0; i < sourceLength; i++) {
				item = source[i];
				if (countEqualItems(source, item) > countEqualItems(array, item)) {
					this.add(item, i);
				}
			}

			for (i = 0; i < sourceLength; i++) {
				removedItems.push(this.change(i, source[i]));
			}

			return removedItems;
		},

		/**
		 * Implementation of {@link playtotv.model.AbstractModel#isRelatedModel}.
		 *
		 * @param {playtotv.model.AbstractModel} root Root model to compare to
		 * @returns {boolean} True if this model has a cyclic relation to root
		 */
		isRelatedModel: function(root) {
			/*jshint maxdepth:4*/

			if (this === root) {
				return true;
			}

			var childSubscriptions = this.childSubscriptions;
			var length = childSubscriptions.length;
			var i, childSubscription, childModel;

			for (i = 0; i < length; i++) {
				childSubscription = childSubscriptions[i];
				if (childSubscription) {
					childModel = childSubscription.model;
					if (childModel instanceof AbstractModel) {
						if (childModel.isRelatedModel(root)) {
							return true;
						}
					}
				}
			}

			return false;
		},

		/**
		 * Subscribe to child model to have it's publishes bubble up to this one.
		 *
		 * Note: It is required that child model is not in a cyclic relationship with this model.
		 *
		 * @param {number} index Child model index
		 * @param {playtotv.model.AbstractModel} child Child model to subscribe to
		 */
		subscribeChild: function(index, child) {
			var childSubscriptions = this.childSubscriptions;

			var subscription = child.subscribe('*', function(childValue, childAction, childKey) {
				// Index could have changed
				index = childSubscriptions.indexOf(childSubscription);
				this.publish(index + '.' + childKey, childAction, childValue);
			}.bind(this));

			var childSubscription = {
				model: child,
				subscription: subscription
			};

			childSubscriptions[index] = childSubscription;
		},

		/**
		 * Unsubscribe child model so it's publishes no longer bubble up to this one.
		 *
		 * @param {number} index Child model index
		 */
		unsubscribeChild: function(index) {
			var childSubscriptions = this.childSubscriptions;
			var childSubscription = childSubscriptions[index];
			childSubscription.model.unsubscribe(childSubscription.subscription);
			childSubscriptions[index] = null;
		},

		/**
		 * Add item to this model.
		 *
		 * @param {mixed} item Item to add
		 * @param {number} [index=array.length] Index at which to add item
		 * @throws {RangeError} If index is out of bounds
		 * @throws {Error} If item is a model in a cyclic relationship with this model
		 */
		add: function(item, index) {
			var array = this.array;
			var arrayLength = array.length;
			var childSubscriptions = this.childSubscriptions;
			var itemIsModel = item instanceof AbstractModel;

			if (arguments.length < 2) {
				index = arrayLength;
			}

			if (index < 0) {
				throw new RangeError('Index out of bounds: ' + index);
			}
			if (itemIsModel && item.isRelatedModel(this)) {
				throw new Error('Item is a model in a cyclic relationship with this model');
			}

			if (index < arrayLength) {
				array.splice(index, 0, item);
				childSubscriptions.splice(index, 0, null);
			} else {
				array[index] = item;
				childSubscriptions[index] = null;
			}

			if (itemIsModel) {
				this.subscribeChild(index, item);
			}

			this.publish(index, 'add', item);
		},

		/**
		 * Change item at specified index in this model.
		 *
		 * @param {number} index Index at which to change item
		 * @param {mixed} item Item to change to
		 * @throws {RangeError} If index is out of bounds
		 * @throws {Error} If item is a model in a cyclic relationship with this model
		 * @returns {mixed} Removed item (if any) for garbage collection etc.
		 */
		change: function(index, item) {
			var array = this.array;
			var itemIsModel = item instanceof AbstractModel;

			if (index < 0 || index >= array.length) {
				throw new RangeError('Index out of bounds: ' + index);
			}

			var oldItem = array[index];

			if (item === oldItem) {
				return; // no change
			}

			if (itemIsModel && item.isRelatedModel(this)) {
				throw new Error('Item is a model in a cyclic relationship with this model');
			}

			if (oldItem instanceof AbstractModel) {
				this.unsubscribeChild(index);
			}

			array[index] = item;

			if (itemIsModel) {
				this.subscribeChild(index, item);
			}

			this.publish(index, 'change', item);

			return oldItem;
		},

		/**
		 * Remove item with specified index from this model.
		 *
		 * @param {number} [index=0] Index from which to remove item
		 * @throws {RangeError} If index is out of bounds
		 * @returns {mixed} Removed item for garbage collection etc.
		 */
		remove: function(index) {
			var array = this.array;

			if (arguments.length < 1) {
				index = 0;
			} else if (index < 0 || index >= array.length) {
				throw new RangeError('Index out of bounds: ' + index);
			}

			var oldItem = array[index];

			if (oldItem instanceof AbstractModel) {
				this.unsubscribeChild(index);
			}

			array.splice(index, 1);
			this.childSubscriptions.splice(index, 1);

			this.publish(index, 'remove', oldItem);

			return oldItem;
		},

		/**
		 * Remove all items that match the specified condition from this model.
		 *
		 * @param {Function|mixed} condition Evaluation function or value to match items with
		 * @returns {Array} Removed items for garbage collection etc.
		 */
		removeWhere: function(condition) {
			if (!(condition instanceof Function)) {
				var value = condition;
				condition = function(candidate) {
					return candidate === value;
				};
			}

			var removed = [];
			var array = this.array;
			for (var i = array.length - 1; i >= 0; i--) {
				if (condition(array[i])) {
					removed.push(this.remove(i));
				}
			}
			return removed;
		},

		/**
		 * Remove all items from this model.
		 *
		 * @returns {Array} Removed items for garbage collection etc.
		 */
		clear: function() {
			var removed = [];
			for (var i = this.array.length - 1; i >= 0; i--) {
				removed.push(this.remove(i));
			}
			return removed;
		},

		/**
		 * Either calls {@link playtotv.model.ArrayModel#change} or {@link playtotv.model.ArrayModel#add} depending on
		 * whether index is smaller then array length or not.
		 *
		 * @param {number} index Argument to #add or #change
		 * @param {mixed} item Argument to #add or #change
		 * @returns {mixed} Removed item (if any) for garbage collection etc.
		 */
		set: function(index, item) {
			if (index < this.array.length) {
				return this.change(index, item);
			} else {
				this.add(item, index);
			}
		},

		/**
		 * Dispose array and child subscriptions.
		 *
		 * Note: Do not forget to also dispose any child models that are no longer used.
		 */
		dispose: function() {
			this.array.length = 0;

			this.childSubscriptions.forEach(function(childSubscription) {
				if (childSubscription) {
					childSubscription.model.unsubscribe(childSubscription.subscription);
				}
			});

			AbstractModel.prototype.dispose.call(this);
		}
	});
});
