playtotv.define('playtotv.user.LocalUser', [
	'playtotv.model.ObjectModel',
	'playtotv.user.User',
	'playtotv.user.usersAPI'
], function(
	ObjectModel,
	User,
	usersAPI
) {

	'use strict';

	var ptv = playtotv;

	return new ptv.Class({
		/** @lends playtotv.user.LocalUser.prototype */

		extend: User,

		/**
		 * Local user data model class.
		 *
		 * @extends playtotv.user.User
		 * @constructs
		 * @param {playtotv.server.Server} server Server
		 * @param {Object} user {@link playtotv.user.User} constructor data
		 * @param {Object} [data] LocalUser data to initialize with
		 * @param {Object} [data.privateProperties={}] LocalUser private property map
		 * @param {string} [data.authenticationToken] LocalUser authentication token
		 * @param {string} [data.pttvSession] LocalUser PlayToTV session token
		 * @param {string} [data.email] LocalUser email
		 * @param {string} [data.username] LocalUser username
		 * @param {string} [data.facebookId] LocalUser Facebook ID
		 *
		 * @property {playtotv.storage.Storage} storage LocalUser storage
		 * @property {playtotv.model.ObjectModel} privateProperties LocalUser private property map
		 * @property {string} authenticationToken LocalUser authentication token
		 * @property {string} pttvSession LocalUser PlayToTV session token
		 * @property {string} email LocalUser email
		 * @property {string} username LocalUser username
		 * @property {string} facebookId LocalUser Facebook ID
		 */
		initialize: function(server, user, data) {
			data = ptv.params(data, {
				privateProperties: {},
				authenticationToken: undefined,
				pttvSession: undefined,
				email: undefined,
				username: undefined,
				facebookId: undefined
			});

			this._super(server, user);

			this.storage = server.storage.subStorage('user/' + user.id + '/');

			this.copyProperties({
				privateProperties: new ObjectModel(data.privateProperties),
				authenticationToken: data.authenticationToken,
				pttvSession: data.pttvSession,
				email: data.email,
				username: data.username,
				facebookId: data.facebookId
			});
		},

		/**
		 * Calls {@link playtotv.user.usersAPI.updateProfile} with this LocalUser's displayName,
		 * avatarUrl, profileProperties and privateProperties.
		 *
		 * @returns {Promise} Promise of server response
		 */
		updateProfile: function() {
			return this.server.call(usersAPI.updateProfile, {
				displayName: this.displayName || null,
				avatarUrl: this.avatarUrl || null,
				properties: this.profileProperties.toObject(),
				privateProperties: this.privateProperties.toObject()
			});
		},

		/** Dispose */
		dispose: function() {
			this.privateProperties.dispose();
			User.prototype.dispose.call(this);
		}
	});
});
