playtotv.define('playtotv.analytics.AnalyticsWidget', [
	'playtotv.widget.Widget'
], function(
	Widget
) {

	'use strict';

	var ptv = playtotv;

	/**
	 * Widget facilitating tracking of node insertion, and events through an AnalyticsController.
	 *
	 * Option values are split by separator (e.g: comma) in arguments (e.g: category, action, label).
	 * Type of logging is equal to the option key or in case of key "event" to the first argument.
	 * The action argument is optional and will default to type.
	 * The label argument is optional and will default to undefined.
	 *
	 * Note: You will need to ensure that category, action and label values do not match the separator!
	 *
	 * @example
	 * <span {{widgets (widget "analytics-track" view="question")}}></span>
	 * <div {{widgets (widget "analytics-track" load="banner")}}> ..banner.. </div>
	 * <div {{widgets (widget "analytics-track" click="answer")}}> ..answer.. </div>
	 * <form {{widgets (widget "analytics-track" submit="profile, update, avatar")}}>..</form>
	 * <input {{widgets (widget "analytics-track" change="rating")}}>
	 * <div {{widgets (widget "analytics-track" event="eventType, category, action, label")}}>..</div>
	 *
	 * @extends playtotv.widget.Widget
	 * @param {Node} node Widget node
	 * @param {Object} options Widget options
	 * @param {playtotv.analytics.AnalyticsController} options.controller AnalyticsController instance to use
	 * @param {RegExp} [options.nonInteraction=/view|load/] Matching types are considered not to be interactions
	 * @param {RegExp} [options.separator=/,\s+/] Option value separator
	 * @param {string} [options.view=''] "[name]" controller.view()
	 * @param {string} [options.load=''] "[category], [action], [label]" controller.event() on load
	 * @param {string} [options.click=''] "[category], [action], [label]" controller.event() on click events
	 * @param {string} [options.submit=''] "[category], [action], [label]" controller.event() on submit events
	 * @param {string} [options.change=''] "[category], [action], [label]" controller.event() on change events
	 * @param {string} [options.event=''] "[type], [category], [action], [label]" controller.event() on type events
	 */
	return class AnalyticsWidget extends Widget {

		constructor(node, options) {
			options = ptv.params(options, {
				app: null,
				controller: ptv.REQUIRED,
				nonInteraction: /view|load/,
				separator: /,\s+/,
				view: '',
				load: '',
				click: '',
				submit: '',
				change: '',
				event: ''
			});

			var controller = options.controller;
			var nonInteractionRE = options.nonInteraction;
			var separator = options.separator;

			super(node);

			var eventOptions = ptv.omit(options, [ 'controller', 'nonInteraction', 'separator' ]);
			ptv.each(eventOptions, (value, key) => {
				if (!value) {
					return;
				}

				var parts = value.split(separator);
				var type = key === 'event' ? parts.shift() : key;
				var nonInteraction = nonInteractionRE.test(type);

				if (type === 'view') {
					controller.view(value, { nonInteraction: nonInteraction });
				} else {
					var onEvent = function() {
						controller.event(parts[0], parts[1] || type, {
							eventLabel: parts[2],
							nonInteraction: nonInteraction
						});
					};
					if (type === 'load') {
						onEvent();
					} else {
						this.addEventListener(node, type, onEvent);
					}
				}
			});
		}
	};
});
