playtotv.define('playtotv.analytics.ComScoreAnalytics', [], function() {

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;

	return ptv.Class({
		/** @lends playtotv.analytics.ComScoreAnalytics.prototype */

		/**
		 * ComScore Analytics class.
		 *
		 * This will define window.udm_() as specified by ComScore if it has not been defined already.
		 *
		 * @constructs
		 * @param {Object} options
		 * @param {string} options.url Base UDM log URL
		 *
		 * @property {string} url Base UDM URL
		 */
		initialize: function(config) {
			ptv.params(config, {
				url: ptv.REQUIRED
			});

			this.url = config.url;

			if (!window.udm_) {
				// Following is fugly code as specified by ComScore
				// Note: it is slightly modified to leak ns_p to window as intended
				/*jshint ignore:start*/
				/*jscs:disable*/
				window.udm_ = function udm_(e) {var t="comScore=",n=document,r=n.cookie,i="",s="indexOf",o="substring",u="length",a=2048,f,l="&ns_",c="&",h,p,d,v,m=window,g=m.encodeURIComponent||escape;if(r[s](t)+1)for(d=0,p=r.split(";"),v=p[u];d<v;d++)h=p[d][s](t),h+1&&(i=c+unescape(p[d][o](h+t[u])));e+=l+"_t="+ +(new Date)+l+"c="+(n.characterSet||n.defaultCharset||"")+"&c8="+g(n.title)+i+"&c7="+g(n.URL)+"&c9="+g(n.referrer),e[u]>a&&e[s](c)>0&&(f=e[o](0,a-8).lastIndexOf(c),e=(e[o](0,f)+l+"cut="+g(e[o](f+1)))[o](0,a)),n.images?(h=new Image,m.ns_p||(m.ns_p=h),h.src=e):n.write("<","p","><",'img src="',e,'" height="1" width="1" alt="*"',"><","/p",">")};
				/*jscs:enable*/
				/*jshint ignore:end*/
			}
		},

		/**
		 * Calls window.udm_() (Unified Digital Measurement) with this.url and optional tag and params.
		 *
		 * @param {string} [tag=''] Occurrences of '__TAG__' in URL will be replaced by this value
		 * @param {Object} [params={}] Map of parameters to add to URL
		 */
		udm: function(tag, params) {
			var url = this.url.replace(/__TAG__/g, tag || '');
			url = util.addURLParams(url, params || {});
			window.udm_(url);
		}
	});
});
