playtotv.define('playtotv.net.jsonp', [], function() {

	'use strict';

	var nr = 0;
	var id = this.id;

	/**
	 * Loads a json response via jsonp, and returns a Promise that - when resolved - receives the json data.
	 *
	 * @name jsonp
	 * @memberOf playtotv.net
	 * @function
	 * @param {string} url
	 * @param {object} [params] Parameters to add to url
	 * @param {number} [timeout=20000] Timeout in ms after which to reject this promise
	 * @returns {Promise} Promise of JSON callback data
	 */
	var jsonp = function(url, params, timeout) {
		params = params || {};
		timeout = timeout || 20000;

		// TODO: Rewrite without use of Promise.defer() and as Cancellable for optional timeout
		var deferred = Promise.defer();
		var callback = 'cb' + (nr += 1);
		params.callback = id + '.' + callback;
		var request = playtotv.util.addURLParams(url, params);
		var timeoutId;

		jsonp[callback] = function(json) {
			deferred.resolve(json);
			delete jsonp[callback];
			clearTimeout(timeoutId);
		};

		playtotv.loadScript(request).then(function(script) {
			playtotv.dom.remove(script);
		});

		timeoutId = setTimeout(function() {
			deferred.reject(new Error('Timeout'));
			delete jsonp[callback];
		}, timeout);

		return deferred.promise;
	};

	return jsonp;
});
