playtotv.define('playtotv.facebook.FacebookAdapter', [], function() {

	'use strict';

	var ptv = playtotv;

	return new ptv.Class({
		/** @lends playtotv.facebook.FacebookAdapter.prototype */

		/**
		 * Adapter between {@link playtotv.facebook.Facebook} and PlayToTV API call parameters.
		 *
		 * @constructs
		 * @param {playtotv.facebook.Facebook} facebook Facebook instance to use
		 */
		initialize: function(facebook) {
			this.facebook = facebook;
		},

		/**
		 * Returns a params object to pass to {@link playtotv.user.UserManager#loginByFacebook}.
		 *
		 * In addition to the basic PlayToTV user properties, all of the Facebook user data is returned as private
		 * properties for advanced usage purposes. This enables you to use Facebook's extended "scope" properties like
		 * email. Please note that PlayToTV property values are strings, so any object values are JSON stringified.
		 *
		 * Note: Picture size URL parameters can optionally be appended to resulting avatarUrl:
		 * <a href="http://developers.facebook.com/docs/reference/api/using-pictures/#sizes">Pictures API</a>
		 *
		 * @param {boolean} [interactive] {@link playtotv.facebook.Facebook#login} interactive
		 * @param {number} [timeout] {@link playtotv.facebook.Facebook#call} timeout for fetching Facebook name
		 * @return {Promise} Promise of params object
		 */
		getLoginParams: function(interactive, timeout) {
			var facebook = this.facebook;
			return facebook.login(interactive).then(function(id) {
				return facebook.call('api', timeout, '/me').then(function(data) {
					return {
						facebookId: id,
						fbsr: facebook.signedRequest,
						displayName: data.name,
						avatarUrl: '//graph.facebook.com/' + id + '/picture',
						privateProperties: Object.keys(data).reduce(function(props, key) {
							var value = data[key];
							props[key] = typeof value === 'object' ? JSON.stringify(value) : value;
							return props;
						}, {})
					};
				});
			});
		},

		/**
		 * Returns a params object to pass to {@link playtotv.user.UserManager#addFacebookCredentials}.
		 *
		 * @param {boolean} [interactive] {@link playtotv.facebook.Facebook#login} interactive
		 * @return {Promise} Promise of params object
		 */
		getCredentialParams: function(interactive) {
			var facebook = this.facebook;
			return facebook.login(interactive).then(function(id) {
				return {
					facebookId: id,
					fbsr: facebook.signedRequest
				};
			});
		}
	});
});
