playtotv.define('playtotv.relation.RelationManager', [
	'playtotv.pubsub.PubSub',
	'playtotv.relation.RelationSubscription'
], function(
	PubSub,
	RelationSubscription
) {

	'use strict';

	var ptv = playtotv;
	var dom = ptv.dom;
	var event = ptv.event;

	return new ptv.Class({
		/** @lends playtotv.relation.RelationManager.prototype */

		extend: PubSub,

		/**
		 * Enables subscribing to clicks on nodes with "rel" attribute values matching strings or regular expressions.
		 *
		 * @extends playtotv.pubsub.PubSub
		 * @constructs
		 * @param {Node} [node=document] Root node on which to listen to click events
		 */
		initialize: function(node) {
			node = node || document;
			this._super();
			this.node = node;
			event.addListener(node, 'click', this.onClick);
		},

		/**
		 * Subscribe handler to specified selector.
		 *
		 * This overrides {@link playtotv.pubsub.PubSub#subscribe} to return a
		 * {@link playtotv.relation.RelationSubscription} to facilitate matching multiple rels.
		 *
		 * P.e: If you subscribe to "foo" then a click on a link with rel="foo bar" will result in handler being called
		 * with rel channel "foo".
		 *
		 * @param {string|RegExp} selector Channel selector
		 * @param {Function} handler Subscription callback
		 * @throws {TypeError} If selector or handler is invalid
		 * @returns {playtotv.relation.RelationSubscription} Added subscription
		 */
		subscribe: function(selector, handler) {
			var subscription = new RelationSubscription(selector, handler);
			this.subscriptions.push(subscription);
			return subscription;
		},

		/**
		 * Click handler that publishes link node to rel attribute value channel.
		 *
		 * @param {Event} e DOM Event
		 */
		onClick: function(e) {
			var link = dom.closest('a[rel]', e.target);
			var rel = link ? link.rel : null;
			if (rel) {
				e.preventDefault();
				// Publish synchronously to allow for privileged callbacks (e.g. play audio on iOS and Android)
				// Besides, we are already in an async event listener callback
				this.publish(rel, link, true);
			}
		},

		/**
		 * Dispose.
		 */
		dispose: function() {
			event.removeListener(this.node, 'click', this.onClick);

			PubSub.prototype.dispose.call(this);
		},

		callbacks: [
			'onClick'
		]
	});
});
