playtotv.define('playtotv.widget.TimerWidget', [
	'playtotv.css.style',
	'playtotv.fx.Transition',
	'playtotv.widget.Widget'
], function(
	style,
	Transition,
	Widget
) {

	'use strict';

	var ptv = playtotv;
	var dom = ptv.dom;

	/**
	 * TimerWidget bar that resizes an indicator from 0 to 100% width over the given time.
	 * Time may be stated using meaningful combinations of start, time and/or end options.
	 * If no start time is given, the current time is used.
	 * Also note that the end option takes precedence over the time option when determining duration.
	 * It IS possible to only specify an end timestamp on a TimerWidget node.
	 *
	 * TODO: enable a switch for CSS transitions vs physical width modification.
	 *
	 * @class
	 * @name TimerWidget
	 * @memberOf playtotv.widget
	 * @param {Node} node
	 * @param {Object} [options]
	 * @param {string} [options.indicator='.indicator'] TimerWidget indicator selector
	 * @param {string} [options.property='width'] The CSS property to modify, must support a percentage value.
	 * @param {boolean} [options.inverted=false] Set to true to size down instead of up
	 * @param {boolean} [options.autoplay=true] Defines whether or not the timer starts automatically
	 * @param {number} [options.fps=1] Animation fps, should not be too high for mobiles due to reflows
	 */
	return class TimerWidget extends Widget {

		constructor(node, options) {
			options = ptv.params(options, {
				app: null,
				indicator: '.indicator',
				property: 'width',
				inverted: false,
				autoplay: true,
				fps: 1,
				start: 0,
				time: 0,
				end: 0
			});

			super(node);

			this.node = node;
			this.bar = dom.first(options.indicator, node);
			this.property = options.property;

			var inv = options.inverted;
			this.transition = new Transition({
				callback: this.handleFrame.bind(this),
				duration: 1000,
				from: inv ? 1 : 0,
				to: inv ? 0 : 1,
				fps: options.fps
			});

			if (options.autoplay) {
				var { start, time, end } = options;
				var now = Date.now();
				start = start || now;
				time = time;
				end = end || (start + time);
				this.start(end - start, start);
			}
		}

		/**
		 * Start the timer, running it for the given duration. An optional start time may be given.
		 *
		 * @param {number} duration in ms
		 * @param {number} [start] timestamp
		 */
		start(duration, start) {
			if (isNaN(duration)) {
				throw new TypeError('Duration is not a number');
			}

			this.stop();

			this.starttime = start || Date.now();
			this.duration = duration;

			this.transition.play({
				start: this.starttime,
				duration: this.duration
			});
		}

		/**
		 * Stops the timer
		 *
		 */
		stop() {
			this.transition.stop();
		}

		reset() {
			this.stop();
			var from = this.transition.settings.from * 100;
			style.set(this.bar, this.property, from + '%');
		}

		handleFrame(n) {
			style.set(this.bar, this.property, (n * 100) + '%');
		}

		dispose() {
			this.stop();

			super();
		}
	};
});
