playtotv.define('playtotv.handlebars.helpers', [
	'playtotv.handlebars.hbsUtil'
], function(
	hbsUtil
) {
	/*global Handlebars:false*/

	// TODO: JSDOC

	'use strict';

	// For more helper inspiration see:
	// http://assemble.io/helpers
	// http://elving.github.io/swag
	// https://github.com/shannonmoeller/handlebars-layouts (OO Templating)
	// https://github.com/wycats/handlebars.js/issues/208 (Inline partial overriding/inheritance)

	var ptv = playtotv;
	var util = ptv.util;

	var helpers = {
		/** @lends playtotv.handlebars.helpers */

		/**
		 * Change Handlebars variable values by hash, making them available through @key data references.
		 * Calling this as a block helper is usually the best thing to do as it will not leak outside of it's block.
		 * As a non-block helper this will change the variable value within the current render frame context.
		 *
		 * Note: Variables will be available in child blocks with different contexts like {{#with}} as well.
		 *
		 * @example
		 * Template: {{#var x="foo"}} {{@x}}bar {{/var}}
		 * Template: {{var x="foo"}} {{@x}}bar
		 * Output: foobar
		 */
		var: function() {
			var { options, hash } = hbsUtil.getArgs(this, arguments, '');
			var { fn, data } = options;

			// If called as block helper then frame our data so that we do not override variables in our parent block
			// If not called as block helper then let the variable changes operate through side effects
			if (fn) {
				data = Handlebars.createFrame(data);
			}

			ptv.extend(data, hash);

			return fn ? fn(this, { data: data }) : '';
		},

		/**
		 * @example
		 * Template: {{#is state "ok"}} Cool {{else}} Oh no! {{/is}}
		 */
		is: function() {
			var { value1, value2, options } = hbsUtil.getArgs(this, arguments, 'value1, value2');
			return hbsUtil.delegateIfBlock('if', this, value1 === value2, options);
		},

		/**
		 * @example
		 * Template: {{#isnt state "bad"}} Cool {{else}} Oh no! {{/isnt}}
		 */
		isnt: function() {
			var { value1, value2, options } = hbsUtil.getArgs(this, arguments, 'value1, value2');
			return hbsUtil.delegateIfBlock('if', this, value1 !== value2, options);
		},

		default: function() {
			var { value, def } = hbsUtil.getArgs(this, arguments, 'value, def');
			return value === undefined ? def : value;
		},

		/**
		 * Note: The def argument is optional.
		 *
		 * @example
		 * Data: { state: 'y' }
		 * Template: {{switch state "Default" x="Foo" y="Bar"}}
		 * Output: Bar
		 */
		switch: function() {
			var { value, def, hash } = hbsUtil.getArgs(this, arguments, 'value, def', 1);
			if (Object.keys(hash).indexOf(value) >= 0) {
				return hash[value];
			} else if (def === undefined) {
				return hbsUtil.softException('No matching case or default for value: ' + value);
			} else {
				return def;
			}
		},

		/**
		 * @example
		 * Data: { isFoo: true, isBar: false }
		 * Template: &lt;li class="{{classes foo=isFoo bar=isBar}}">
		 * Output: &lt;li class="foo">
		 */
		classes: function() {
			var { hash } = hbsUtil.getArgs(this, arguments);
			var str = '';
			ptv.each(hash, function(value, key) {
				if (value) {
					str = str ? str + ' ' + key : key;
				}
			}.bind(this));
			return str;
		},

		/**
		 * Data: { x: 3 }
		 *
		 * Example: {{inc x}}
		 * Output: 4
		 *
		 * Example: {{inc x 2}}
		 * Output: 5
		 */
		inc: function() {
			var { x, y } = hbsUtil.getArgs(this, arguments, 'x, y', 1);
			y = y === undefined ? 1 : y;
			return x + y;
		},

		/**
		 * Data: { x: 3 }
		 *
		 * Example: {{dec x}}
		 * Output: 2
		 *
		 * Example: {{dec x 2}}
		 * Output: 1
		 */
		dec: function() {
			var { x, y } = hbsUtil.getArgs(this, arguments, 'x, y', 1);
			y = y === undefined ? 1 : y;
			return x - y;
		},

		/**
		 * Example: {{#gt x 10}} Wow! {{else}} Meh.. {{/gt}}
		 * Can also be used to compose sub-expressions.
		 */
		gt: function() {
			var { x, y, options } = hbsUtil.getArgs(this, arguments, 'x, y');
			return hbsUtil.delegateIfBlock('if', this, x > y, options);
		},

		/**
		 * Example: {{#lt x 10}} Meh.. {{else}} Wow! {{/lt}}
		 * Can also be used to compose sub-expressions.
		 */
		lt: function() {
			var { x, y, options } = hbsUtil.getArgs(this, arguments, 'x, y');
			return hbsUtil.delegateIfBlock('if', this, x < y, options);
		},

		/**
		 * Example: {{#not x}} is equal to {{#unless x}}
		 * Can also be used to compose sub-expressions.
		 */
		not: function() {
			var { value, options } = hbsUtil.getArgs(this, arguments, 'value');
			return hbsUtil.delegateIfBlock('if', this, !value, options);
		},

		/**
		 * Example: {{#or x y z}} is equal to {{#if (eval 'x || y || z')}}
		 * Can also be used to compose sub-expressions.
		 */
		or: function() {
			var { args, options } = hbsUtil.getArgs(this, arguments);
			var result = args.find(arg => arg);
			return hbsUtil.delegateIfBlock('if', this, result, options);
		},

		/**
		 * Example: {{#and x y z}} is equal to {{#if (eval 'x && y && z')}}
		 * Can also be used to compose sub-expressions.
		 */
		and: function() {
			var { args, options } = hbsUtil.getArgs(this, arguments);
			var result = args.reduce((result, arg) => result && arg, true);
			return hbsUtil.delegateIfBlock('if', this, result, options);
		},

		/**
		 * Example: {{#inArray (Array 2 3 5 7) x}}small prime{{/inArray}}
		 * Can also be used to compose sub-expressions.
		 */
		inArray: function() {
			var { list, value, options } = hbsUtil.getArgs(this, arguments, 'list, value');
			return hbsUtil.delegateIfBlock('if', this, list.indexOf(value) >= 0, options);
		},

		Array: function() {
			var { args, options } = hbsUtil.getArgs(this, arguments);
			return hbsUtil.delegateIfBlock('each', this, args, options);
		},

		Object: function() {
			var { options, hash } = hbsUtil.getArgs(this, arguments);
			return hbsUtil.delegateIfBlock('with', this, hash, options);
		},

		toNumber: function() {
			var { str } = hbsUtil.getArgs(this, arguments, 'str');
			return parseFloat(str);
		},

		toBool: function() {
			var { str } = hbsUtil.getArgs(this, arguments, 'str');
			return str === 'true' ? true : false;
		},

		// To: 'foo bar'
		lowerCase: function() {
			var { text } = hbsUtil.getArgs(this, arguments, 'text');
			return ('' + text).toLowerCase();
		},

		// To: 'FOO BAR'
		upperCase: function() {
			var { text } = hbsUtil.getArgs(this, arguments, 'text');
			return ('' + text).toUpperCase();
		},

		// To: 'foo-bar'
		dashCase: function() {
			var { text } = hbsUtil.getArgs(this, arguments, 'text');
			return util.dashCase('' + text);
		},

		// To: 'Foo bar'
		sentenceCase: function() {
			var { text } = hbsUtil.getArgs(this, arguments, 'text');
			text = '' + text;
			return text[0].toUpperCase() + text.substr(1).toLowerCase();
		},

		// To: 'Foo Bar'
		titleCase: function() {
			var { text } = hbsUtil.getArgs(this, arguments, 'text');
			return util.titleCase('' + text);
		},

		concat: function() {
			var { args } = hbsUtil.getArgs(this, arguments);
			return args.join('');
		},

		/**
		 * @example
		 * Data: { names: [ 'John', 'Mary', 'Elizabeth' ] }
		 * Template 1: {{join names last=" and "}}
		 * Template 2: {{#join names last=" and "}}{{.}}{{/join}}
		 * Output: John, Mary and Elizabeth
		 */
		join: function() {
			var { array, hash, options } = hbsUtil.getArgs(this, arguments, 'array');
			var { fn, inverse } = options;
			var separator = hash.separator || ', ';
			var last = hash.last || separator;
			array = fn ? array.map(fn) : array.slice(0);
			var len = array.length;
			return len === 0 ? (inverse ? inverse(this) : '') :
				len === 1 ? array[0] :
				array.slice(0, -1).join(separator) + last + array[len - 1];
		},

		/**
		 * @example
		 * Data: { str: 'John, Mary,Elizabeth' }
		 * Template: -{{#split str}}{{.}}-{{/split}}
		 * Output: -John-Mary-Elizabeth-
		 */
		split: function() {
			var { str, hash, options } = hbsUtil.getArgs(this, arguments, 'str');
			var separator = hash.separator || /,\s?/;
			var array = str.length > 0 ? str.split(separator) : [];
			return hbsUtil.delegateIfBlock('each', this, array, options, true);
		},

		/**
		 * @example
		 * Template: {{inflect nrEnemies "one enemy" "%d enemies"}}
		 * Output: 3 enemies
		 */
		inflect: function() {
			var { count, singular, plural } = hbsUtil.getArgs(this, arguments, 'count, singular, plural');
			var result = count === 1 ? singular : plural;
			return result.replace(/%d/g, count);
		},

		/**
		 * @example
		 * Template: {{truncate "Bender should not be allowed on tv." 10}}
		 * Template: {{truncate "Bender should not be allowed on tv." 10 dots=".."}}
		 * Output: Bender s..
		 */
		truncate: function() {
			var { str, len, hash } = hbsUtil.getArgs(this, arguments, 'str, len');
			var dots = hash.dots || '..';
			if (str.length > len) {
				str = str.substr(0, len - dots.length) + dots;
			}
			return str;
		},

		/**
		 * Returns URL with hash added as query parameters.
		 * The url argument defaults to location.href.
		 */
		addURLParams: function() {
			var { url, hash } = hbsUtil.getArgs(this, arguments, 'url', 0);
			url = url || window.location.href;
			return util.addURLParams(url, hash);
		},

		/**
		 * Returns hash of url's query parameters.
		 * The url argument defaults to location.href.
		 */
		getURLParams: function() {
			var { url } = hbsUtil.getArgs(this, arguments, 'url', 0);
			url = url || window.location.href;
			return util.getURLParams(url);
		},

		/**
		 * Returns value of url's query parameter with specified key.
		 * You can call this with two arguments (url, key) or with one (key).
		 * In the latter case the url argument defaults to location.href.
		 */
		getURLParam: function() {
			var { urlOrKey, key } = hbsUtil.getArgs(this, arguments, 'urlOrKey, key', 1);
			var url = key ? urlOrKey : '';
			key = key || urlOrKey;
			return util.getURLParams(url)[key];
		},

		/**
		 * Returns specified or current date formatted with specified pattern using {@link playtotv.util.formatDate}.
		 */
		formatDate: function() {
			var { pattern, date } = hbsUtil.getArgs(this, arguments, 'pattern, date', 1);
			return util.formatDate(pattern, date);
		},

		/**
		 * @example
		 * Template: {{addCommas 1000}}
		 * Output: 1,000
		 *
		 * Template: {{addCommas 1000 "."}}
		 * Output: 1.000
		 *
		 * Template: {{addCommas 1000.123}}
		 * Output: 1,000.123
		 */
		addCommas: function() {
			var { number, separator } = hbsUtil.getArgs(this, arguments, 'number, separator', 1, 1);

			if (separator === undefined) {
				separator = ',';
			}

			var decimal = (separator === ',') ? '.' : ',';
			var parts = number.toString().split('.');

			return parts[0].replace(/\B(?=(\d{3})+(?!\d))/g, separator) + (parts[1] ? decimal + parts[1] : '');
		},

		/**
		 * @example
		 * Template: {{toFixed 1.23456}}
		 * Output: 1.23
		 *
		 * Template: {{toFixed 1.23456 4}}
		 * Output: 1.2345
		 *
		 * Template: {{toFixed 1.23456 0}}
		 * Output: 1
		 */
		toFixed: function() {
			var { value, digits } = hbsUtil.getArgs(this, arguments, 'value, digits', 1, 1);

			if (digits === undefined) {
				digits = 2;
			}

			return value.toFixed(digits);
		},

		/**
		 * Transform value to string
		 *
		 * @example
		 * Template: {{toString 1.23}}
		 * Output: 1.23
		 */
		toString: function() {
			var { value } = hbsUtil.getArgs(this, arguments, 'value', 1, 0);
			return String(value);
		},

		/**
		 * @example
		 * Template: {{pad 1}}
		 * Output: 01
		 *
		 * Template: {{pad 1 10}}
		 * Output: 0000000001
		 *
		 * Template: {{pad 1 10 "-"}}
		 * Output: ---------1
		 */
		pad: function() {
			var { value, len, ch } = hbsUtil.getArgs(this, arguments, 'value, len, ch', 1, 2);
			return util.pad(value, len, ch);
		},

		// TODO: More helpers like:
		//   L10n: (see also: mozilla l20n, cordova globalization, etc.)
		//     {{l10n "Translate this"}}
		//     {{ordinalize 3}} => 3rd
		//     {{toPrecision 555.322 4}} => 555.3
		//     {{formatNumber number type="decimal|percent|currency"}}
		//     {{formatPhoneNumber 4444444444}} => (444) 444-4444

		seek: function() {
			var { path, context } = hbsUtil.getArgs(this, arguments, 'path, context', 1);
			context = context || this;
			return util.seek(context, path);
		},

		eval: function() {
			var { script, context, hash, options } = hbsUtil.getArgs(this, arguments, 'script, context', 0);

			// Default to this render context, but if hash properties are passed, use those as context instead
			context = context || this;
			if (Object.keys(hash).length > 0) {
				context = hash;
			}

			// If called with a block then the block is the script
			if (options.fn) {
				script = options.fn(this);
			}

			return util.eval(script, context);
		},

		/**
		 * Returns list filtered by value specified by item path matching value by specified mode.
		 * If used as block helper this delegates to "each" helper.
		 */
		filter: function() {
			var { list, path, value, hash, options } = hbsUtil.getArgs(this, arguments, 'list, path, value', 1);
			var { mode } = hash;
			var filtered = util.filter(list, path, value, mode);
			return hbsUtil.delegateIfBlock('each', this, filtered, options, true);
		},

		/**
		 * Returns list ordered by value specified by item path, optionally descending.
		 * If used as block helper this delegates to "each" helper.
		 */
		order: function() {
			var { list, path, hash, options } = hbsUtil.getArgs(this, arguments, 'list, path', 1);
			var { desc } = hash;
			var ordered = util.order(list, path, desc);
			return hbsUtil.delegateIfBlock('each', this, ordered, options, true);
		},

		/**
		 * Returns specified page of list with added properties: page, prev, next and last.
		 * If used as a block helper this delegates to "each" helper.
		 */
		paginate: function() {
			var { list, page, size, options } = hbsUtil.getArgs(this, arguments, 'list, page, size', 1);
			page = parseInt(page, 10);
			size = parseInt(size, 10);
			var paged = util.page(list, page, size);
			return hbsUtil.delegateIfBlock('each', this, paged, options, true);
		},

		json: function() {
			var { obj, hash } = hbsUtil.getArgs(this, arguments, 'obj');
			return hash.pretty ? JSON.stringify(obj, null, '	') :  JSON.stringify(obj);
		},

		/**
		 * Returns hash path relative to current hash or current hash if no path is specified
		 *
		 * @example
		 * From: #/episodes/ep1/schedule
		 * Data: { interactionId: 'abc123' }
		 *
		 * Template: {{hash}}
		 * Output: #/episodes/ep1/schedule
		 *
		 * Template: {{hash "interaction/create"}}
		 * Output: #/episodes/ep1/interaction/create
		 *
		 * Template: {{hash "../ep2/"}}
		 * Output: #/episodes/ep2/
		 *
		 * Template: {{hash "interaction/" interactionId "/edit"}}
		 * Output: #/episodes/ep1/interaction/abc123/edit
		 */
		hash: function() {
			var hash = window.location.hash;
			var { args } = hbsUtil.getArgs(this, arguments);
			var path = args.join('');
			if (!path) {
				return hash;
			}
			var from = hash.slice(1); // strip leading #
			if (from) {
				path = hbsUtil.pathResolve(from, path);
			}
			return '#' + path;
		},

		helperMissing: function() {
			var options = arguments[arguments.length - 1];
			hbsUtil.softException('Unknown field or helper: ' + options.name);
		}
	};

	return helpers;
});
