playtotv.define('playtotv.rating.RatingInputWidget', [
	'playtotv.widget.Widget'
], function(
	Widget
) {

	'use strict';

	var ptv = playtotv;
	var REQUIRED = ptv.REQUIRED;

	return class RatingInputWidget extends Widget {

		constructor(node, options) {
			ptv.params(options, {
				app: null,
				controller: REQUIRED,
				id: REQUIRED,
				code: REQUIRED
			});

			super(node);

			ptv.extend(this, options);

			this.addEventListener(node, 'change', this.onChange.bind(this));
		}

		onChange() {
			this.controller.submit(this.id, this.code, this.node.value);
		}
	};
});
