playtotv.define('playtotv.native.NativeController', [
	'playtotv.mvc.Controller'
], function(
	Controller
) {

	'use strict';

	var ptv = playtotv;
	var REQUIRED = ptv.REQUIRED;
	var util = ptv.util;
	var commandIdCounter = 1;

	return class NativeController extends Controller {
		constructor(context) {
			super(context);

			this.addMethods([
				'getGatekeeper',
				'getWrapper',
				'sendMessage',
				'sendCommand',
				'popup',
				'browser',
				'overlay',
				'dialog',
				'isTwitterAvailable',
				'tweet',
				'fbLogin',
				'fbLogout',
				'fbGraph',
				'fbShare',
				'loadAudio',
				'playAudio',
				'stopAudio'
			]);

			this.wrapperPromise = this.when('Event.BridgeReady').then(this.onWrapper.bind(this));
			this.gatekeeperPromise = this.when('Event.GatekeeperReady').then(this.onGatekeeper.bind(this));

			this.subscribe('Event.BackButton', this.onBackButton.bind(this));

			this.subscribe('Event.PushEnabled', this.onPushEnabled.bind(this));
			this.model.subscribe('change:pushEnabled', this.onPushEnabledChange.bind(this));

			var wrapper = window.nativeWrapper;
			if (wrapper) {
				this.publish('Event.BridgeReady');
				this.sendMessage({ type: 'Event', subject: 'AppReady' });
			} else {
				wrapper = window.nativeWrapper = {};
			}

			if (wrapper.onMessage) {
				throw new Error('onMessage handler already defined');
			}
			wrapper.onMessage = this.onMessage.bind(this);

			// Fake AppResume, Wrappers do not (yet) send these
			this.scope.pauseDog.add(10 * 1000, () => this.publish('Event.AppResume', {}));
		}

		onMessage(message) {
			var type = message.type || 'Event';
			var subject = message.subject;
			var channel = type + '.' + subject;

			if (type === 'CommandResult') {
				channel += '.' + message.commandId;
			}

			this.publish(channel, message);
		}

		sendMessage(message) {
			ptv.params(message, {
				type: REQUIRED,
				subject: REQUIRED,
				commandId: -1,
				payload: {}
			});
			return this.wrapperPromise.then(wrapper => wrapper.sendMessage(message));
		}

		sendCommand(subject, payload) {
			var commandId = commandIdCounter++;

			// Note: iOS wrapper sometimes synchronously calls onMessage so we need to start listening before we send
			var resultPromise = this.when('CommandResult.' + subject + '.' + commandId).then(message => {
				var error = message.error;
				if (error) {
					throw ptv.extend(new Error(error.message || 'Command rejected: ' + error.code), error);
				}

				return message.payload || {};
			});

			this.sendMessage({
				type: 'Command',
				subject: subject,
				commandId: commandId,
				payload: payload
			});

			return resultPromise;
		}

		onWrapper() {
			var wrapper = window.nativeWrapper;

			this.model.copyProperties(ptv.omit(wrapper, [
				'sendMessage', 'onMessage', 'gatekeeperResponse', 'gatekeeperTimestamp'
			]));

			if (wrapper.gatekeeperResponse) {
				this.publish('Event.GatekeeperReady', {
					payload: ptv.pick(wrapper, [ 'gatekeeperResponse', 'gatekeeperTimestamp' ])
				});
			}

			return wrapper;
		}

		onGatekeeper(message) {
			var payload = message.payload;
			return {
				url: window.nativeWrapper.serverUrl,
				gatekeeperData: payload.gatekeeperResponse,
				gatekeeperDataTime: payload.gatekeeperTimestamp
			};
		}

		onBackButton() {
			if (window.location.hash === '#' + this.scope.router.root) {
				return this.dialog({
					message: 'Are you sure you want to close ' + this.model.name + '?',
					buttons: [ 'YES', 'NO' ]
				}).then(result => {
					if (result.pressed === 'YES') {
						this.sendCommand('Close');
					}
				});
			}

			this.scope.back();
		}

		onPushEnabled(message) {
			this.set('pushEnabled', message.payload.value);
		}

		onPushEnabledChange(enabled) {
			// Effectuate in wrapper or rollback local tentative state
			this.sendCommand('SetPushEnabled', { value: enabled }).catch(() => {
				// E.g: reason.error === 'CannotEnablePush'
				this.set('pushEnabled', false);
			});
		}

		getWrapper() {
			return this.wrapperPromise;
		}

		getGatekeeper() {
			return this.gatekeeperPromise;
		}

		dialog(options) {
			options = ptv.params(options, {
				title: this.model.name,
				message: '',
				buttons: [ 'OK' ]
			});
			return this.sendCommand('OpenDialog', options);
		}

		popup(url, target) {
			return target === 'overlay' ? this.overlay(url) : this.browser(url);
		}

		browser(url) {
			return this.sendCommand('BrowserOpen', { url: url });
		}

		overlay(url) {
			return this.sendCommand('OverlayOpen', { url: url });
		}

		// TODO: Replace by iTunesOpen Command or something once that becomes available
		itunes(id, options) {
			var { affiliate } = ptv.params(options, {
				affiliate: undefined
			});
			window.location = 'https://itunes.apple.com/dummy/id' + id +
				affiliate ? '?at=' + affiliate : '';
		}

		/* TWITTER */

		isTwitterAvailable() {
			return this.sendCommand('IsTwitterAccountAvailable', {}).then(result => result.available);
		}

		// TODO: Replace by TwitterTweet Command or something once that becomes available
		// Legal option keys right now are: text, url, hashtags, via, related and in-reply-to
		tweet(options) {
			var url = util.addURLParams('https://twitter.com/intent/tweet', options);
			util.popup(url, 'tweet', { width: 550, height: 420 });
		}

		/* FACEBOOK */

		fbLogin(options) {
			options = ptv.params(options, {
				interactive: false,
				permissions: []
			});

			// Facilitate calling by also allowing permissions to be specified as a comma separated string
			if (typeof options.permissions === 'string') {
				options.permissions = options.permissions.split(/\s*,\s*/);
			}

			return this.sendCommand('FacebookLogin', options);
		}

		fbLogout() {
			return this.sendCommand('FacebookLogout');
		}

		fbGraph(path, method = 'get', params = {}) {
			return this.sendCommand('FacebookGraphRequest', {
				path: path,
				parameters: params,
				httpMethod: method
			});
		}

		fbShare(options) {
			options = ptv.params(options, {
				title: '',
				description: '',
				url: REQUIRED,
				image: null
			});
			var params = {
				contentTitle: options.title,
				contentDescription: options.description,
				contentURL: options.url
			};
			if (options.image) {
				params.imageURL = options.image;
			}
			return this.sendCommand('FacebookShare', params);
		}

		/* AUDIO */

		loadAudio(url) {
			return this.sendCommand('LoadAudio', { url: url });
		}

		playAudio(url) {
			return this.sendCommand('PlayAudio', { url: url });
		}

		stopAudio(url) {
			return this.sendCommand('StopAudio', { url: url });
		}
	};
});
