/*
Defines Promise.Cancellable, CancellationError and TimeoutError to facilitate cancelling functions returning Promises.

Passes promises-aplus-tests (https://github.com/promises-aplus/promises-tests).
 */
(function() {

	'use strict';

	function extend(dst, src) {
		for (var key in src) {
			if (src.hasOwnProperty(key)) {
				dst[key] = src[key];
			}
		}
		return dst;
	}

	// Minimal Object.create shim for IE8
	var objectCreate = Object.create || function(proto) {
		function F() {}
		F.prototype = proto;
		return new F();
	};

	function createClass(Extend, Constructor, stat, proto) {
		extend(Constructor, Extend);
		extend(Constructor, stat);
		proto = extend(objectCreate(Extend.prototype), proto);
		Constructor.prototype = proto;
		proto.constructor = Constructor;
		return Constructor;
	}

	/**
	 * Error class that can be used to recognize rejection due to cancellation.
	 *
	 * @name CancellationError
	 * @memberOf Promise
	 * @class
	 * @extends Error
	 * @param {string} [message='Cancelled']
	 */
	var CancellationError = createClass(Error, function(message) {
		this.message = message || 'Cancelled';
		// Workarounds for not being able to properly extend Error
		if (Error.captureStackTrace) {
			Error.captureStackTrace(this, this.constructor);
		} else {
			this.stack = new Error().stack;
		}
	}, null, { name: 'CancellationError' });

	/**
	 * Error class that can be used to recognize cancellation due to timeout.
	 *
	 * @name TimeoutError
	 * @memberOf Promise
	 * @class
	 * @extends Promise.CancellationError
	 * @param {string} [message='Timed out']
	 */
	var TimeoutError = createClass(CancellationError, function(message) {
		CancellationError.call(this, message || 'Timed out');
	}, null, { name: 'TimeoutError' });

	/**
	 * A special kind of Promise that can be cancelled.
	 *
	 * The fn callback will be called with the Cancellable instance as 'this' argument.
	 * This facilitates immediately registering a cancellation callback with access to your function scope.
	 *
	 * @example
	 * function request(method, url) {
	 *   return new Cancellable(function(resolve, reject) {
	 *     var xhr = new XMLHttpRequest();
	 *     xhr.addEventListener('load', function() {
	 *       resolve(xhr.responseText);
	 *     });
	 *     xhr.addEventListener('error', function() {
	 *       reject(xhr.status);
	 *     });
	 *     xhr.open(method, url, true);
	 *     xhr.send();
	 *     this.catchClass(Promise.CancellationError, function() {
	 *       xhr.abort();
	 *     });
	 *   });
	 * }
	 *
	 * @name Cancellable
	 * @memberOf Promise
	 * @class
	 * @extends Promise
	 * @param {Function} fn
	 */
	var Cancellable = createClass(Promise, function(fn) {

		if (!(this instanceof Cancellable)) {
			throw new TypeError('Failed to construct "Cancellable": Please use the "new" operator.');
		}
		if (typeof fn !== 'function') {
			throw new TypeError('Cancellable constructor takes a function argument');
		}

		var resolve, reject;
		this._promise = new Promise(function(_resolve, _reject) {
			resolve = _resolve;
			reject = _reject;
		});
		this._parentPromises = [];
		this._reject = reject;

		try {
			fn.call(this, resolve, reject);
		} catch (e) {
			reject(e);
		}

	}, {
		/** @lends Promise.Cancellable */

		// Note: Need to manually link these static methods since they do not show up in extend: for (i in Promise)
		resolve: Promise.resolve,
		reject: Promise.reject,
		defer: Promise.defer,

		/**
		 * Extended version of {@link Promise.all} that adds all Cancellable values as parents to the returned
		 * Cancellable.
		 *
		 * @param {Array} values
		 * @returns {Cancellable}
		 */
		all: function(values) {
			var cancellable = Promise.all.call(this, values);

			var parents = cancellable._parentPromises;
			var i, value, length = values.length;
			for (i = 0; i < length; i++) {
				value = values[i];
				if (value instanceof Cancellable) {
					parents.push(value);
				}
			}

			return cancellable;
		},

		/**
		 * Extended version of {@link Promise.race} that adds all Cancellable values as parents to the returned
		 * Cancellable.
		 *
		 * @param {Array} values
		 * @returns {Cancellable}
		 */
		race: function(values) {
			var cancellable = Promise.race.call(this, values);

			var parents = cancellable._parentPromises;
			var i, value, length = values.length;
			for (i = 0; i < length; i++) {
				value = values[i];
				if (value instanceof Cancellable) {
					parents.push(value);
				}
			}

			return cancellable;
		}

	}, {
		/** @lends Promise.Cancellable.prototype */

		/**
		 * Extended version of {@link Promise#then} that adds this Cancellable and any Cancellable returned by
		 * onResolve or onReject to the returned Cancellable's parents.
		 *
		 * @param {Function} [onResolve]
		 * @param {Function} [onReject]
		 * @returns {Cancellable}
		 */
		then: function(onResolve, onReject) {
			var onThenResolve = typeof onResolve !== 'function' ? onResolve : function(value) {
				var result = onResolve(value);
				if (result === thenCancellable) {
					throw new TypeError('You can not resolve a promise with itself');
				}
				if (result instanceof Cancellable) {
					parents.push(result);
				}
				return result;
			};
			var onThenReject = typeof onReject !== 'function' ? onReject : function(value) {
				var result = onReject(value);
				if (result === thenCancellable) {
					throw new TypeError('You can not reject a promise with itself');
				}
				if (result instanceof Cancellable) {
					parents.push(result);
				}
				return result;
			};

			var promise = this._promise;
			var thenCancellable = new Cancellable(function(resolve) {
				resolve(promise.then(onThenResolve, onThenReject));
			});

			var parents = thenCancellable._parentPromises;
			parents.push(this);

			return thenCancellable;
		},

		/**
		 * If this Cancellable has parents then cancel those, otherwise reject this promise, with specified reason.
		 *
		 * @param {Object} [reason=new Promise.CancellationError()]
		 */
		cancel: function(reason) {
			reason = reason || new CancellationError();
			var parents = this._parentPromises;
			var parentsLen = parents.length;
			if (parentsLen === 0) {
				this._reject(reason);
			} else {
				for (var i = 0; i < parentsLen; i++) {
					parents[i].cancel(reason);
				}
			}
		},

		/**
		 * Cancel with specified reason after specified time.
		 *
		 * @param {number} ms Time in milliseconds after which to time out
		 * @param {Object} [reason=new Promise.TimeoutError('Timed out after ' + ms + 'ms')]
		 * @returns {number} setTimeout() id, can be cleared with clearTimeout()
		 */
		setTimeout: function(ms, reason) {
			reason = reason || new TimeoutError('Timed out after ' + ms + 'ms');

			return setTimeout(function() {
				this.cancel(reason);
			}.bind(this), ms);
		},

		/**
		 * Returns a plain Promise version of this Cancellable.
		 *
		 * This can be useful if you want to prevent another scope from cancelling this Cancellable.
		 *
		 * @returns {Promise}
		 */
		toPromise: function() {
			return this._promise;
		}
	});

	// Expose Cancellable etc. as static properties on Promise
	extend(Promise, {
		Cancellable: Cancellable,
		CancellationError: CancellationError,
		TimeoutError: TimeoutError
	});
}());
