playtotv.define('playtotv.analytics.GoogleAnalytics', [], function() {

	'use strict';

	var ptv = playtotv;
	var log = ptv.Logger.get(this.id);

	return new ptv.Class({
		/** @lends playtotv.analytics.GoogleAnalytics.prototype */

		/**
		 * Google Analytics class.
		 *
		 * <ol>
		 * <li>Loads window.ga() if necessary using the standard asynchronous snippet
		 * <li>Creates a tracker with specified fields
		 * <li>If a name is specified then the tracker will be created with and this instance scoped to that name
		 * <li>If setFields are specified then they are set
		 * <li>If requires are specified then they are required
		 * <li>If skipView is not true then immediately calls {@link playtotv.analytics.GoogleAnalytics#sendView}
		 * </ol>
		 *
		 * When fields are passed to setFields or send then if a dimension or metric property is specified
		 * it will be mapped to dimension1, metric1 etc. by the processFields method.
		 * This facilitates use of custom dimensions and metric by making them named
		 * and by facilitating different mappings per GoogleAnalytics instance.
		 *
		 * @see <a href="developers.google.com/analytics/devguides/collection/analyticsjs/">Google Dev Guide</a>
		 * @see <a href="developers.google.com/analytics/devguides/collection/analyticsjs/field-reference">Fields</a>
		 *
		 * @constructs
		 * @param {Object} options
		 * @param {string} options.id Tracker ID
		 * @param {string} [options.name] Tracker name
		 * @param {Object} [options.fields={}] Tracker create fields (cookieDomain, sampleRate, ..)
		 * @param {Object} [options.setFields={}] Tracker set fields (appName, appId, appVersion, ..)
		 * @param {Object} [options.dimensionMap={}] Map of dimension names to indexes
		 * @param {Object} [options.metricMap={}] Map of metric names to indexes
		 * @param {[string]} [options.requires=[]] Array of plugin name strings or argument arrays to require
		 * @param {string} [options.skipView=false] If true then skip sending initial screen or page view
		 *
		 * @property {string} prefix Tracker name prefix to use before method names
		 * @property {string} mode Tracker mode, if setFields.appId was defined then 'app' otherwise 'web'
		 * @property {Object} dimensionMap Map of dimension names to indexes
		 * @property {Object} metricMap Map of metric names to indexes
		 * @property {Promise} trackerPromise Promise of GA tracker reference used by this instance
		 * @property {Object} tracker GA tracker reference used by this instance
		 */
		initialize: function(options) {
			ptv.params(options, {
				id: ptv.REQUIRED,
				name: null,
				fields: {},
				setFields: null,
				dimensionMap: {},
				metricMap: {},
				requires: [],
				skipView: false
			});

			if (!window.ga) {
				/*jshint -W030, -W033, -W069, -W072*/
				/*jscs:disable*/
				(function(i,s,o,g,r,a,m) {i['GoogleAnalyticsObject']=r;i[r]=i[r]||function() {
				(i[r].q=i[r].q||[]).push(arguments)},i[r].l=1*new Date();a=s.createElement(o),
				m=s.getElementsByTagName(o)[0];a.async=1;a.src=g;m.parentNode.insertBefore(a,m)
				})(window,document,'script','//www.google-analytics.com/analytics.js','ga');
				/*jscs:enable*/
			}

			var name = options.name;
			var fields = options.fields;
			var setFields = options.setFields;
			var prefix = '';
			if (name) {
				prefix = name + '.';
				fields.name = name;
			}

			this.prefix = prefix;

			this.mode = setFields && setFields.appId ? 'app' : 'web';
			this.dimensionMap = options.dimensionMap;
			this.metricMap = options.metricMap;

			window.ga('create', options.id, fields);

			if (setFields) {
				this.setFields(setFields);
			}

			options.requires.forEach(function(args) {
				args = [ 'require' ].concat(args);
				this.ga.apply(this, args);
			}.bind(this));

			// TODO: Rewrite without use of Promise.defer()
			var deferred = Promise.defer();
			this.trackerPromise = deferred.promise;
			window.ga(function(tracker) {
				tracker = name ? window.ga.getByName(name) : tracker;
				this.tracker = tracker;
				deferred.resolve(tracker);
			}.bind(this));

			if (!options.skipView) {
				this.sendView();
			}
		},

		/**
		 * Send a view using either sendScreenview (in 'app' mode) or sendPageview.
		 *
		 * @param {string} [name] The name of the view
		 * @param {Object} [fields={}] An object containing one or more field/value pairs
		 * @returns {Promise} Promise that resolves once the tracker has completed sending data
		 */
		sendView: function(name, fields) {
			if (this.mode === 'app') {
				return this.sendScreenview(name, fields);
			} else {
				return this.sendPageview(name, null, fields);
			}
		},

		/**
		 * Send an error using either sendException (in 'app' mode) or sendEvent.
		 *
		 * The error description is formatted by combining error name, message and location.
		 * Error location is determined by examining error stack or falling back to document location.
		 *
		 * @param {Error} error The error to send
		 * @param {boolean} [isFatal=false] Whether the error was fatal
		 * @param {Object} [fields={}] An object containing one or more field/value pairs
		 * @returns {Promise} Promise that resolves once the tracker has completed sending data
		 */
		sendError: function(error, isFatal, fields) {
			fields = fields || {};
			var loc = ((error.stack || '').match(/[^\s\(@]*(:\d+)+/) || [])[0] || document.location;
			var description = error.name + ': ' + error.message + ' [' + loc + ']';
			if (this.mode === 'app') {
				return this.sendException(description, isFatal, fields);
			} else {
				fields.nonInteraction = true;
				return this.sendEvent(isFatal ? 'fatal' : 'error', description, fields);
			}
		},

		/**
		 * Send a pageview.
		 *
		 * @param {string} [page=pathname+search] The page path and query string (must start with a / character)
		 * @param {string} [title=document.title] The title of the page
		 * @param {Object} [fields={}] An object containing one or more field/value pairs
		 * @param {string} [fields.location=protocol+'//'+hostname+pathname+search] URL of the page being tracked
		 * @returns {Promise} Promise that resolves once the tracker has completed sending data
		 */
		sendPageview: function(page, title, fields) {
			fields = fields || {};
			if (page) {
				fields.page = page;
			}
			if (title) {
				fields.title = title;
			}
			return this.send('pageview', fields);
		},

		/**
		 * Send an App screenview.
		 *
		 * @param {string} [name] The name of the screen
		 * @param {Object} [fields={}] An object containing one or more field/value pairs
		 * @returns {Promise} Promise that resolves once the tracker has completed sending data
		 */
		sendScreenview: function(name, fields) {
			fields = fields || {};
			if (name) {
				fields.screenName = name;
			}
			return this.send('screenview', fields);
		},

		/**
		 * Send an App exception.
		 *
		 * @param {string} description The description of the exception
		 * @param {boolean} [isFatal=false] Whether the exception was fatal
		 * @param {Object} [fields={}] An object containing one or more field/value pairs
		 * @returns {Promise} Promise that resolves once the tracker has completed sending data
		 */
		sendException: function(description, isFatal, fields) {
			fields = fields || {};
			fields.exDescription = description;
			fields.exFatal = isFatal ? true : false;
			return this.send('exception', fields);
		},

		/**
		 * Measure amount of milliseconds it takes a promise to resolve and send it as user timing data.
		 *
		 * @param {Promise} promise Promise to time
		 * @param {string} category A string for categorizing all user timing variables into logical groups
		 * @param {string} variable A string to identify the variable being recorded. (e.g. JavaScript Load)
		 * @param {number} value The number of milliseconds in elapsed time to report to Google Analytics
		 * @param {Object} [fields={}] An object containing one or more field/value pairs
		 * @param {string} [fields.timingLabel] Used to add flexibility in visualizing user timings in the reports
		 * @returns {Promise} Original promise to facilitate chaining
		 */
		time: function(promise, category, variable, fields) {
			var start = Date.now();
			promise.then(function() {
				this.sendTiming(category, variable, Date.now() - start, fields);
			}.bind(this));
			return promise;
		},

		/**
		 * Send user timing data.
		 *
		 * @param {string} category A string for categorizing all user timing variables into logical groups
		 * @param {string} variable A string to identify the variable being recorded. (e.g. JavaScript Load)
		 * @param {number} value The number of milliseconds in elapsed time to report to Google Analytics
		 * @param {Object} [fields={}] An object containing one or more field/value pairs
		 * @param {string} [fields.timingLabel] Used to add flexibility in visualizing user timings in the reports
		 * @returns {Promise} Promise that resolves once the tracker has completed sending data
		 */
		sendTiming: function(category, variable, value, fields) {
			fields = fields || {};
			fields.timingCategory = category;
			fields.timingVar = variable;
			fields.timingValue = value;
			return this.send('timing', fields);
		},

		/**
		 * Send an event.
		 *
		 * @param {string} category Typically the object that was interacted with (e.g. button)
		 * @param {string} action The type of interaction (e.g. click)
		 * @param {Object} [fields={}] An object containing one or more field/value pairs
		 * @param {string} [fields.eventLabel] Useful for categorizing events (e.g. nav buttons)
		 * @param {number} [fields.eventValue] Values must be non-negative. Useful to pass counts (e.g. 4 times)
		 * @param {boolean} [fields.nonInteraction] Specifies that a hit be considered non-interactive
		 * @returns {Promise} Promise that resolves once the tracker has completed sending data
		 */
		sendEvent: function(category, action, fields) {
			fields = fields || {};
			fields.eventCategory = category;
			fields.eventAction = action;
			return this.send('event', fields);
		},

		/**
		 * Send a social interaction.
		 *
		 * @param {string} network The network on which the action occurs (e.g. Facebook, Twitter)
		 * @param {string} action The type of action that happens (e.g. Like, Send, Tweet)
		 * @param {string} target Specifies the target of a social interaction (e.g. URL)
		 * @param {Object} [fields={}] An object containing one or more field/value pairs
		 * @returns {Promise} Promise that resolves once the tracker has completed sending data
		 */
		sendSocial: function(network, action, target, fields) {
			fields = fields || {};
			fields.socialNetwork = network;
			fields.socialAction = action;
			fields.socialTarget = target;
			return this.send('social', fields);
		},

		/**
		 * Returns a Promise of the value currently associated with the given field.
		 *
		 * TODO: Add reverse dimension and metric index mapping support?
		 *
		 * @param {string} fieldName Name of the field to retrieve
		 * @returns {Promise} Promise of the value of the field
		 */
		get: function(fieldName) {
			return this.trackerPromise.then(function(tracker) {
				return tracker.get(fieldName);
			});
		},

		/**
		 * Updates the value currently associated with the given field.
		 *
		 * This delegates to: {@link playtotv.analytics.GoogleAnalytics#setFields}
		 *
		 * @param {string} fieldName Name of the field to set
		 * @param {string|number|Object} value New value to associate with the given field
		 */
		set: function(fieldName, value) {
			var fields = {};
			fields[fieldName] = value;
			this.setFields(fields);
		},

		/**
		 * Updates multiple field/value pairs.
		 *
		 * This calls: {@link playtotv.analytics.GoogleAnalytics#processFields} before setting.
		 *
		 * @param {Object} fields An object containing one or more field/value pairs to set
		 */
		setFields: function(fields) {
			fields = this.processFields(fields);
			this.ga('set', fields);
		},

		/**
		 * Returns a copy of fields with dimension and metric object properties replaced by
		 * dimension1 and metric1 etc. properties based on dimensionMap and metricMap.
		 *
		 * @param {Object} fields An object containing one or more field/value pairs
		 * @returns {Object} Field object with dimension and metric properties mapped to their indices
		 */
		processFields: function(fields) {
			var keys = [ 'dimension', 'metric' ];
			var processed = ptv.omit(fields, keys);
			keys.forEach(function(key) {
				var map = this[key + 'Map'];
				ptv.each(fields[key], function(value, name) {
					var index = map[name];
					if (index) {
						processed[key + index] = value;
					}
				});
			}, this);
			return processed;
		},

		/**
		 * Sends a tracking beacon to Googles collection servers.
		 *
		 * The optional fields object allows users to override one or more field values for this hit only.
		 *
		 * A hitCallback field is automatically added to the fields to resolve the returned data sending Promise.
		 *
		 * This calls: {@link playtotv.analytics.GoogleAnalytics#processFields} before sending.
		 *
		 * @param {string} hitType The type of hit to send (pageview/event/social/timing/..)
		 * @param {Object} [fields={}] An object containing one or more field/value pairs
		 * @returns {Promise} Promise that resolves once the tracker has completed sending data
		 */
		send: function(hitType, fields) {
			fields = this.processFields(fields || {});
			// TODO: Rewrite without use of Promise.defer()
			var deferred = Promise.defer();
			fields.hitCallback = deferred.resolve;
			this.ga('send', hitType, fields);
			return deferred.promise;
		},

		/**
		 * Prefixes the first argument (method) and then applies arguments to window.ga().
		 */
		ga: function() {
			var prefix = this.prefix;
			var args = ptv.toArray(arguments);
			var method = args.shift();
			log.s(prefix + method, args);
			args.unshift(prefix + method);
			window.ga.apply(window, args);
		}
	});
});
