(function() {

	'use strict';

	// Reliably get a global reference on all platforms by looking at a non-strict function's "this"
	var global = /*jshint evil:true*/ new Function('return this')(); /*jshint evil:false*/
	var window = global.window || {};
	var MutationObserver = window.MutationObserver || window.WebKitMutationObserver;
	var tasks = [];

	function tick() {
		// Note current length, any tasks beyond should be processed in the next tick
		var i = 0;
		var len = tasks.length;
		var task;

		// Catch exceptions without actually catching them to leave stack traces nice and pristine
		try {
			for (; i < len; i++) {
				task = tasks[i];
				task.cb.apply(task.on, task.args);
			}
		} finally {
			// If we did not cleanly finish the iteration then move past the task that threw the exception
			// and request another tick if necessary to finish processing the remaining tasks
			if (i < len && ++i < len) {
				requestTick();
			}

			// Remove tasks that were called in this tick
			tasks.splice(0, i);
		}
	}

	var requestTick =

		// MutationObserver is crazy fast (micro task)
		// Supported by: Chrome, FF, Safari, IE11+
		MutationObserver ?
		(function() {
			var el = document.createElement('div');
			new MutationObserver(tick).observe(el, { attributes: true });
			// MutationObserver already takes care of batching observer callbacks itself so forget about pending
			return function() {
				el.setAttribute('x', 'x');
			};
		}()) :

		// setImmediate is 3 times faster then message events in IE10 and very fast in Node
		// Supported by: IE10+, Node 0.10+
		global.setImmediate ?
		(function() {
			var pending = false;
			return function() {
				if (!pending) {
					pending = global.setImmediate(function() {
						pending = false;
						tick();
					});
				}
			};
		}()) :

		// Window message events are about 25 times slower then MutationObserver, but still fast
		// Supported by: IE9, IE10, Android
		// Note: IE8 does have message support using attachEvent instead of addEventListener, but is not asynchronous
		window.addEventListener ?
		(function() {
			var pending = false;
			var msg = 'nextTick';
			function onMessage(e) {
				if (e.source === window && e.data === msg) {
					e.stopPropagation();
					pending = false;
					tick();
				}
			}
			window.addEventListener('message', onMessage, true);
			return function() {
				if (!pending) {
					window.postMessage(msg, '*');
					pending = true;
				}
			};
		}()) :

		// setTimeout is about 500 times slower then MutationObserver
		// Supported by: everything
		(function() {
			var pending = false;
			return function() {
				if (!pending) {
					pending = setTimeout(function() {
						pending = false;
						tick();
					}, 0);
				}
			};
		}());

	/**
	 * Call the specified callback asynchronously, but as soon as possible.
	 *
	 * The name is inspired by Node.js' process.nextTick.
	 *
	 * The similar setImmediate function was drafted for W3C but remains impopular with FireFox and Chrome developers
	 * (<a href="http://www.nczonline.net/blog/2013/07/09/the-case-for-setimmediate/">more info</a>).
	 *
	 * This either uses MutationObserver, setImmediate, window message events or plain old setTimeout.
	 *
	 * Callbacks are batched to improve performance.
	 * If an exception is thrown by a callback it is not caught to prevent losing valuable stack trace information.
	 * In such a case the remainder of the batch of callbacks will still be executed, but not on the same tick to
	 * prevent issues due to excessive recursion.
	 *
	 * Note: Do not immediately call nextTick again from each tick callback.
	 *
	 * TODO: Replace by setImmediate shim (is also in https://github.com/zloirock/core-js)
	 *
	 * @param {Function} callback
	 * @param {Object} [thisArg=undefined]
	 * @param {Array} [args=[]]
	 */
	function nextTick(callback, thisArg, args) {
		tasks.push({ cb: callback, on: thisArg, args: args || [] });
		requestTick();
	}

	// Expose function
	global.nextTick = nextTick;

}());
