playtotv.define('playtotv.mvc.Router', [
	'playtotv.pubsub.PubSub'
], function(
	PubSub
) {

	// TODO: JSDOC

	'use strict';

	var ptv = playtotv;

	return new ptv.Class({
		extend: PubSub,

		initialize: function(options) {
			options = ptv.params(options, {
				root: 'index',
				rescue: 'route-not-found',
				patterns: ptv.REQUIRED
			});

			this._super();

			this.root = options.root;
			this.rescue = options.rescue;
			this.routes = options.patterns.map(function(pattern) {
				return {
					pattern: pattern,
					regex: new RegExp('^' + pattern.replace(/__\w+__/g, '([^/]*)') + '$'),
					keys: (pattern.match(/__\w+__/g) || []).map(function(key) {
						return key.substr(2, key.length - 4); // strip surrounding __ and __
					})
				};
			});

			window.onhashchange = this.update.bind(this);
			this.update();
		},

		find: function(path) {
			return ptv.find(this.routes, function(candidate) {
				return candidate.pattern === path;
			}) || ptv.find(this.routes, function(candidate) {
				return candidate.regex.test(path);
			});
		},

		getParams: function(route, path) {
			var values = (path.match(route.regex) || []).slice(1);
			return values.reduce(function(params, value, i) {
				params[route.keys[i]] = value;
				return params;
			}, {});
		},

		update: function() {
			var loc = window.location;
			var path = loc.hash.substr(1); // strip leading #
			if (!path) {
				path = this.root;
				loc.hash = '#' + path;
			}

			// Add 'index' to paths that end with '/' (like on a web server)
			if (/\/$/.test(path)) {
				path += 'index';
			}

			if (path === this.path) {
				return;
			}
			this.path = path;

			var route = this.find(path);
			var params;
			if (route) {
				params = this.getParams(route, path);
			} else {
				var rescue = this.rescue;
				route = this.find(rescue);
				if (!route) {
					throw new Error('Could not find rescue route: ' + rescue);
				}
				params = ptv.extend(this.getParams(route, rescue), { path: path });
			}

			var pattern = route.pattern;
			this.pattern = pattern;
			this.params = params;
			this.publish(pattern, params);
		}
	});
});
