(function() {

	'use strict';

	// Abort if we are not running in a browser (e.g: in Node)
	if (typeof window === 'undefined') {
		return;
	}

	var document = window.document;

	/**
	 * @name Event
	 * @memberOf playtotv.event
	 * @class
	 * @param {String} type
	 * @param {object} params
	*/
	var Event = window.Event;

	/**
	 * @name MouseEvent
	 * @memberOf playtotv.event
	 * @class
	 * @param {String} type
	 * @param {object} params
	*/
	var MouseEvent = window.MouseEvent;

	/**
	 * @name CustomEvent
	 * @memberOf playtotv.event
	 * @class
	 * @param {String} type
	 * @param {object} params
	*/
	var CustomEvent = window.CustomEvent;

	// determine whether Event(s) as constructor are supported
	var supportsCustom = (function() {
		try {
			var custom = new CustomEvent('test');
			custom.preventDefault();
			return true;
		} catch (e) {
			return false;
		}
	}());

	// IE9 and up support for Event(s) as constructor. Note that these Event classes are exposed via playtotv.event
	// for manually dispatching them on nodes if so needed. For convenience, event.triggerNative may be used.
	if (!supportsCustom) {
		var eventProto = Event.prototype;

		Event = function(event, params) {
			params = playtotv.params(params, {
				bubbles: false,
				cancelable: false
			});

			var evt = document.createEvent('HTMLEvents');
			evt.initEvent(event, params.bubbles, params.cancelable);
			return evt;
		};

		Event.prototype = eventProto;

		CustomEvent = function(event, params) {
			params = playtotv.params(params, {
				bubbles: false,
				cancelable: false,
				detail: null
			});

			var evt = document.createEvent( 'CustomEvent' );
			evt.initCustomEvent( event, params.bubbles, params.cancelable, params.detail );
			return evt;
		};

		CustomEvent.prototype = eventProto;

		MouseEvent = function(event, params) {
			params = playtotv.params(params, {
				bubbles: false,
				cancelable: false,
				screenX: 0,
				screenY: 0,
				clientX: 0,
				clientY: 0,
				ctrlKey: false,
				altKey: false,
				shiftKey: false,
				metaKey: false,
				button: 0,
				buttons: 0,
				relatedTarget: null
			});

			var evt = document.createEvent('MouseEvent');
			evt.initMouseEvent(
				event, params.bubbles, params.cancelable, window, 1,
				params.screenX, params.screenY, params.clientX, params.clientY,
				params.ctrlKey, params.altKey, params.shiftKey, params.metaKey,
				params.button,
				params.relatedTarget
			);
			return evt;
		};

		MouseEvent.prototype = eventProto;
	}

	/**
	 * EventListener, represents a listener function for an event on the given node. Returned by
	 * {@link playtotv.event.addListener} for easy removal at {@link playtotv.event.removeListener}, instead of passing
	 * the separate node, type and listener arguments.
	 *
	 * @name EventListener
	 * @memberOf playtotv.event
	 * @class
	 * @param {Node} node
	 * @param {String} type
	 * @param {Function} listener
	 */
	class EventListener {
		constructor(node, type, listener) {
			this.node = node;
			this.type = type;
			this.listener = listener;
		}

		dispose() {
			delete this.node;
			delete this.type;
			delete this.listener;
		}
	}

	/**
	 * Event namespace
	 *
	 * @namespace Public event namespace, enables binding, unbinding and dispatching of events.
	 */
	playtotv.event = {

		/**
		 * Adds an event listener of the given type to a Node and returns an {@link playtotv.event.EventListener}
		 * listener object for easy removal at {@link playtotv.event#removeListener}.
		 *
		 * @param {Node} node
		 * @param {string} type
		 * @param {Function} listener
		 * @returns {playtotv.event.EventListener} listener
		 */
		addListener: function(node, type, listener) {
			if (!node) {
				throw new ReferenceError('Node is undefined at event.addListener for "' + type + '"');
			}

			node.addEventListener(type, listener, false);

			// return optional listener object for removeListener
			return new EventListener(node, type, listener);
		},

		/**
		 * Removes an event listener of the given type from a Node. A single {@link playtotv.event.EventListener}
		 * listener object may be passed as returned by {@link playtotv.event#addListener}, instead of calling this
		 * function with separate node, type and listener parameters.
		 *
		 * @param {Node|playtotv.event.EventListener} subject
		 * @param {string} [type]
		 * @param {Function} [listener]
		 */
		removeListener: function(subject, type, listener) {
			var node = subject;

			// subject may either be a node, or an EventListener object
			if (subject instanceof EventListener) {
				listener = subject.listener;
				type = subject.type;
				node = subject.node;
				subject.dispose();
			}

			if (!node) {
				throw new ReferenceError('Node is undefined at event.removeListener for "' + type + '"');
			}

			node.removeEventListener(type, listener, false);
		},

		/**
		 * Dispatches a CustomEvent on the given Node, and returns a boolean whether any handler that listened to it
		 * called preventDefault on the event object (if cancelable), as it traversed the DOM.
		 *
		 * @function
		 * @param {Node} node
		 * @param {string} type
		 * @param {boolean} bubbles
		 * @param {boolean} cancelable
		 * @param {Object} detail
		 * @returns {boolean} unprevented
		 */
		dispatchEvent: function(node, event, bubbles, cancelable, detail) {
			return node.dispatchEvent(new CustomEvent(event, {
				bubbles: bubbles,
				cancelable: cancelable,
				detail: detail
			}));
		},

		/**
		 * Triggers a native HTML event or mouse event on a node. This method is more or less just convenience for
		 * dispatching a (Mouse)Event instance with node.dispatchEvent, although using this method does ensure
		 * IE9 compatibility by using the deprecated event.initMouseEvent if needed.
		 *
		 * @param {Node} node
		 * @param {string} type
		 * @param {object} params
		 * @returns {boolean} unprevented
		 */
		triggerNative: function(node, type, params) {
			params = playtotv.extend({
				bubbles: true,
				cancelable: true
			}, params);

			var event = /mouse|click/.test(type) ?
				new MouseEvent(type, params) : new Event(type, params);

			return node.dispatchEvent(event);
		},

		/**
		 * Prevents the default action of an event. May be passed to addListener for shorthand purposes.
		 * @param {playtotv.event.Event} event
		 * @returns {boolean} false
		 */
		preventDefault: function(e) {
			e.preventDefault();
			return false;
		},

		/**
		 * Stops event propagation through the dom. May be passed to addListener for shorthand purposes.
		 * @param {playtotv.event.Event} event
		 */
		stopPropagation: function(e) {
			e.stopPropagation();
		},

		/**
		 * Set window.onerror to a function that calls any previously set window.onerror function
		 * and then calls specified callback with the Error instance.
		 *
		 * If error is not provided by browser then an approximation is constructed using the other arguments.
		 *
		 * @param {Function} callback Function to call with uncaught error
		 */
		addWindowOnError: function(callback) {
			var originalOnError = window.onerror;

			window.onerror = function(errorMsg, url, line, column, error) {
				/*jshint maxcomplexity:10*/
				var originalReturnValue;
				if (originalOnError) {
					originalReturnValue = originalOnError.apply(this, arguments);
				}

				if (!error) {
					error = new Error((errorMsg.match(/Error: (.*)/) || [])[1] || errorMsg);
					error.name = (errorMsg.match(/\w+Error/) || [])[0] || 'Error';
					error.stack = url + ':' + line + (column === undefined ? '' : ':' + column);
				}

				return callback(error) || originalReturnValue;
			};
		}
	};

	/**
	 * add local objects to public namespace
	 *
	 */

	playtotv.extend(playtotv.event, {
		CustomEvent: CustomEvent,
		Event: Event,
		EventListener: EventListener,
		MouseEvent: MouseEvent
	});

}());
