playtotv.define('playtotv.logUtil', [
	'playtotv.model.modelUtil'
], function(
	modelUtil
) {

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;

	/**
	 * @name playtotv.logUtil
	 * @namespace Log utility functions
	 */
	return {
		/** @lends playtotv.logUtil */

		/**
		 * Log publishes made to specified PubSub with specified log function.
		 *
		 * @param {playtotv.pubsub.PubSub} pubsub PubSub to subscribe to
		 * @param {Function} fn Log function to call on publish
		 * @param {Object} [options={}] Options
		 * @param {[string]} [options.blacklist=[]] Array of channel globs to ignore
		 * @param {string} [options.name] Name of PubSub to prefix log string with
		 * @returns {playtotv.pubsub.Subscription} PubSub subscription
		 */
		logPubSub: function(pubsub, fn, options) {
			options = ptv.params(options, { blacklist: [], name: '' });
			var blacklist = options.blacklist.map(glob => util.globToRegEx(glob));
			var prefix = options.name;
			prefix = prefix ? prefix + '.' : '';
			return pubsub.subscribe(/.*/, function(data, channel) {
				if (blacklist.find(regex => regex.test(channel))) {
					return;
				}

				if (data && data.toObject) {
					data = modelUtil.toObject(data);
				}

				var pc = prefix + channel;
				if (typeof data === 'object') {
					fn(pc, data);
				} else {
					fn(pc + ': ' + data);
				}
			});
		},

		/**
		 * Log mutations made to specified Model with specified log function.
		 *
		 * @param {playtotv.model.AbstractModel} model Model to subscribe to
		 * @param {Function} fn Log function to call on mutation
		 * @param {Object} [options={}] Options
		 * @param {[string]} [options.blacklist=[]] Array of key globs to ignore
		 * @param {string} [options.name] Name of Model to prefix log string with
		 * @returns {playtotv.model.ModelSubscription} Model subscription
		 */
		logModel: function(model, fn, options) {
			options = ptv.params(options, { blacklist: [], name: '' });
			var blacklist = options.blacklist.map(glob => util.globToRegEx(glob));
			var prefix = options.name;
			prefix = prefix ? prefix + ' ' : '';
			return model.subscribe('*', function(v, a, k) {
				if (blacklist.find(regex => regex.test(k))) {
					return;
				}

				if (v && v.toObject) {
					v = modelUtil.toObject(v);
				}

				var pak = prefix + a + ' ' + k;
				if (typeof v === 'object') {
					fn(pak, v);
				} else {
					fn(pak + ' => ' + v);
				}
			});
		},

		/**
		 * Log calls made to specified instance method with specified log function.
		 *
		 * Note: This replaces specfified instance method by another function that calls the original function.
		 *
		 * @param {Object} instance Instance to which the method belongs
		 * @param {string} methodName Name of instance method
		 * @param {Function} fn Log function to call with logName and call arguments
		 * @param {string} [logName=methodName] Method name to use for logging
		 */
		logMethod: function(instance, methodName, fn, logName) {
			logName = logName || methodName;
			var method = instance[methodName];
			instance[methodName] = function() {
				var args = ptv.toArray(arguments);
				fn(logName, args);
				return method.apply(instance, args);
			};
		},

		/**
		 * Log calls made to methods of specified instance with specified log function.
		 *
		 * Note: This replaces specfified instance methods by other functions that call the original functions.
		 *
		 * @param {Object} instance Instance to which the method belongs
		 * @param {Function} fn Log function to call with name and call arguments
		 * @param {Object} [options={}] Options
		 * @param {[string]} [options.blacklist=[]] Array of method name globs to ignore
		 * @param {string} [options.name] Name of instance to prefix method names with
		 */
		logMethods: function(instance, fn, options) {
			options = ptv.params(options, { blacklist: [], name: '' });
			var blacklist = options.blacklist.map(glob => util.globToRegEx(glob));
			var prefix = options.name;
			prefix = prefix ? prefix + '.' : '';
			/*jshint forin:false*/
			for (var key in instance) {
				if (instance[key] instanceof Function && !blacklist.find(regex => regex.test(key))) {
					this.logMethod(instance, key, fn, prefix + key);
				}
			}
		}
	};
});
