playtotv.define('playtotv.storage.MockAsyncStorage', [
	'playtotv.storage.AsyncStorage',
	'playtotv.storage.MockStorage',
	'playtotv.storage.Storage'
], function(
	AsyncStorage,
	MockStorage,
	Storage
) {

	'use strict';

	return class MockAsyncStorage extends AsyncStorage {
		constructor(prefix) {
			super(prefix);

			this.ready = Promise.resolve({ storage: new Storage(new MockStorage(), prefix) });
		}
	};
});
