playtotv.define('playtotv.server.MockServerController', [
	'playtotv.server.ServerController',
	'playtotv.server.MockServer'
], function(
	ServerController,
	MockServer
) {

	// TODO: Merge MockServer into this
	// TODO: JSDoc

	'use strict';

	return class MockServerController extends ServerController {
		getServer(/*options*/) {
			return this.server || (this.server = new MockServer());
		}

		callAsync() {
			return this.call.apply(this, arguments);
		}
	};
});
