playtotv.define('playtotv.mvc.helpers', [
	'playtotv.handlebars.hbsUtil',
	'playtotv.handlebars.helpers'
], function(
	hbsUtil,
	helpers
) {
	/*global Handlebars:false*/

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;
	var templateStack = [];

	return ptv.extend(helpers, {
		/** @lends playtotv.mvc.helpers */

		// TODO: JSDOC
		// E.g: {{call "controller.method" arg1 .. key1=value1 ..}}
		// => controller.method(arg, { key1: value1 })
		call: function() {
			var { args, hash, options } = hbsUtil.getArgs(this, arguments);
			var methodPath = args.shift();
			var context = options.data.root;

			// First argument is optional context argument
			if (typeof methodPath === 'object') {
				context = methodPath;
				methodPath = args.shift();
			}

			if (!methodPath) {
				return hbsUtil.softException('Call methodPath argument missing');
			}

			// If called with a block then add that as an argument
			if (options.fn) {
				args.push(options.fn(this));
			}

			// If called with hash key-value pairs then add the hash as an argument
			if (Object.keys(hash).length > 0) {
				args.push(hash);
			}

			var methodRef = util.seekRef(context, methodPath);
			var method = methodRef && methodRef.value;
			if (!method) {
				return hbsUtil.softException('Call method not found: ' + methodPath);
			}

			try {
				return method.apply(methodRef.object, args);
			} catch (error) {
				return hbsUtil.softException('Call ' + error.name + ':' + error.message);
			}

			// TODO: de-promisify return value if necessary
			/*
			// Wrap method to deal with Promise return values
			var shardMap = {};
			// Note: If promise value does not depends on arguments or arguments are not JSON stringifyable
			// then method has to define a getShard() property
			var getShard = method.getShard || function() {
				return JSON.stringify(ptv.toArray(arguments));
			};
			var scope = this;
			hbsUtil.addMethod(name, function() { // note: fat arrow (=>) would mess with "arguments"
				var returnValue;
				try {
					returnValue = method.apply(null, arguments);
				} catch (error) {
					scope.onError(error);
				}
				if (!(returnValue instanceof Promise)) {
					return returnValue;
				}

				// Note: In case of rejection the returned lastValue is left unchanged (e.g: undefined)
				var shardKey = getShard.apply(null, arguments);
				var shard = shardMap[shardKey] || (shardMap[shardKey] = {});
				returnValue.then(value => {
					if (JSON.stringify(value) !== JSON.stringify(shard.lastValue)) {
						shard.lastValue = value;
						this.view.update();
					}
				}, error => scope.onError(error));
				return shard.lastValue;
			}.bind(this));
			 */
		},

		// TODO: JSDOC
		script: function() {
			var { script, hash, options } = hbsUtil.getArgs(this, arguments, 'script', 0);

			// If called with a block then the block is the script
			if (options.fn) {
				script = options.fn(this);
			}

			// Compact script for the sake of compact render output
			// Note: You can circumvent tab chars etc. getting changed by passing them in a hash (context) value
			script = script.trim().replace(/\s+/g, ' ');

			var serialized = Object.keys(hash).length > 0 ? JSON.stringify([ script, hash ]) : script;
			serialized = Handlebars.Utils.escapeExpression(serialized);
			return new Handlebars.SafeString(serialized);
		},

		// TODO: JSDOC
		on: function() {
			var args = ptv.toArray(arguments);
			var type = args.shift();
			var script = helpers.script.apply(this, args);
			return new Handlebars.SafeString('on-' + type + '="' + script + '"');
		},

		// TODO: JSDOC
		/**
		 * {{widgets (widget ..) (widget ..)}}
		 */
		widgets: function() {
			var { args } = hbsUtil.getArgs(this, arguments);
			var serialized = JSON.stringify(args);
			serialized = Handlebars.Utils.escapeExpression(serialized);
			return new Handlebars.SafeString('widgets="' + serialized + '"');
		},

		// TODO: JSDOC
		/**
		 * (widget "rating.Input" bar=bar)
		 */
		widget: function() {
			var { name, hash } = hbsUtil.getArgs(this, arguments, 'name');
			return [ name, hash ];
		},

		/**
		 * Returns path relative to current path or just current path if path argument is undefined.
		 *
		 * The base path is determined using the render context provided by App.
		 */
		templatePath: function() {
			var { path, options } = hbsUtil.getArgs(this, arguments, 'path', 0);
			var current = templateStack[templateStack.length - 1] || options.data.root.router.pattern;
			return path === undefined ? current : util.resolvePath(current, path);
		},

		/**
		 * Renders and returns the partial specified by name relative to the current template path.
		 *
		 * Partial is rendered with specified or current render context.
		 *
		 * TODO: Update JSDOC
		 * Any hash properties are added as @ variables within this block similar to using {{#var}}.
		 *
		 * If this is called as a block helper then the block will be available as the @partial-block partial.
		 * E.g: {{#partial "_x"}}..{{/partial}} is equal to {{#> _x}}..{{/x}} in Handlebars v4 and up.
		 *
		 * Note: If a partial with the specified name is not found but a template is then that will be used instead.
		 * This facilitates "aliasing" from one template to another.
		 *
		 * @example
		 * Data: { template: 'episodes/__episodeCode__/index', episodePartial: '_episode', state: 'y' }
		 *
		 * Template: {{partial "_interaction"}}
		 * Output: {{> episodes/__episodeCode__/_interaction}}
		 *
		 * Template: {{partial (eval '"../" + episodePartial')}}
		 * Output: {{> episodes/_episode}}
		 *
		 * Template: {{partial (switch state "_default" x="_foo" y="_bar")}}
		 * Output: {{> episodes/__episodeCode__/_bar}}
		 */
		partial: function() {
			var { path, context, hash, options } = hbsUtil.getArgs(this, arguments, 'path, context', 1);

			// Default to this render context, but if hash properties are passed, use those as context instead
			context = context || this;
			if (Object.keys(hash).length > 0) {
				context = hash;
				options.hash = {};
			}

			var partialBlock = options.fn;
			if (partialBlock) {
				Handlebars.registerPartial('@partial-block', partialBlock);
				delete options.fn;
			}

			var name = hbsUtil.callHelper('templatePath', this, path, options);
			var partial = Handlebars.partials[name] || Handlebars.templates[name];
			if (!partial) {
				return hbsUtil.softException('Partial not found: ' + name);
			}

			templateStack.push(name);
			var output = new Handlebars.SafeString(partial(context, options));
			templateStack.pop();

			if (partialBlock) {
				Handlebars.unregisterPartial('@partial-block');
			}

			return output;
		},

		/**
		 * Register block as partial with name based on path relative to current template.
		 *
		 * TODO: Use new Handlebars v4 features instead.
		 *
		 * @example
		 * Data: { dude: { name: 'Peter' } }
		 * Template:
		 *   {{#registerPartial "greet"}}{{greeting}} {{name}}!{{/registerPartial}}
		 *   {{partial "greet" dude greeting="Yo"}}
		 * Output: Yo Peter!
		 */
		registerPartial: function() {
			var { path, options } = hbsUtil.getArgs(this, arguments, 'path');
			var name = hbsUtil.callHelper('templatePath', this, path, options);
			Handlebars.registerPartial(name, options.fn);
		}
	});
});
