playtotv.define('playtotv.PauseDog', [], function() {

	'use strict';

	var ptv = playtotv;
	var log = new ptv.Logger(this.id);

	/**
	 * PauseDog is a watchdog for monitoring JS pauses.
	 * E.g: When you have switched to another tab/app on a mobile device.
	 *
	 * Note: Safari and Firefox also pause/slow down JS after they are left in the background for a minute or two.
	 */
	return class PauseDog {
		constructor() {
			this.pauseCallbacks = [];
			this.interval = 1000;
			this.scheduleTick();
		}

		scheduleTick() {
			this.lastTick = Date.now();
			this.timeout = setTimeout(this.onTick.bind(this), this.interval);
		}

		add(threshold, callback) {
			var pauseCallback = {
				threshold: threshold,
				callback: callback
			};
			this.pauseCallbacks.push(pauseCallback);
			return pauseCallback;
		}

		remove(pauseCallback) {
			var pauseCallbacks = this.pauseCallbacks;
			var index = pauseCallbacks.indexOf(pauseCallback);
			if (index !== -1) {
				pauseCallbacks.splice(index, 1);
			}
		}

		onTick() {
			var now = Date.now();
			var pause = now - this.interval - this.lastTick;

			var desc = 'Paused: ' + Math.round(pause / 1000) + ' seconds';
			if (pause > 10000) {
				log.d(desc);
			} else if (pause > 1000) {
				log.s(desc);
			}

			this.pauseCallbacks.
				filter(pcb => pause > pcb.threshold).
				forEach(pcb => pcb.callback(pause));

			this.scheduleTick();
		}

		dispose() {
			this.pauseCallbacks.length = 0;
			clearTimeout(this.timeout);
			ptv.dispose(this);
		}
	};
});
