playtotv.define('playtotv.rating.RatingController', [
	'playtotv.mvc.Controller',
	'playtotv.rating.ratingAPI',
	'playtotv.rating.RatingInputWidget'
], function(
	Controller,
	ratingAPI,
	RatingInputWidget
) {

	'use strict';

	var FACTOR = 10;

	var ptv = playtotv;

	return class RatingController extends Controller {
		constructor(context) {
			super(context);

			this.addMethods([
				'submit',
				'decorateSubjects'
			]);

			this.addWidgets([
				[ 'input', RatingInputWidget ]
			]);
		}

		submit(id, code, value) {
			var valueMap = {};
			valueMap[code] = Math.round(value * FACTOR);

			return this.scope.call('episode.submitInteraction', ratingAPI.submitRating, id, {
				ratingId: id,
				values: valueMap
			}, submittedValueMap => {
				submittedValueMap = submittedValueMap || {};
				submittedValueMap[code] = value;
				return submittedValueMap;
			});
		}

		decorateSubjects(rating) {
			var stats = rating.stats;
			var subjectAverages = (stats && stats.values) || [];
			var submittedValueMap = rating.data || {};

			return rating.content.subjects.map(subject => {
				var subjectCode = subject.code;
				var subjectAverage = subjectAverages.find(subjectAverage => subjectAverage.code === subjectCode);
				return ptv.extend({
					rating: rating,
					submitted: submittedValueMap[subjectCode],
					average: subjectAverage && subjectAverage.value / FACTOR
				}, subject);
			});
		}
	};
});
