playtotv.define('playtotv.widget.ScrollableWidget', [
	'playtotv.widget.Widget',
	'playtotv.widget.DraggableWidget',
	'playtotv.css.Vector',
	'playtotv.css.style'
], function(
	Widget,
	DraggableWidget,
	Vector,
	style
) {

	'use strict';

	var dom = playtotv.dom;

	/**
	 * Creates a scrollable area from a given node and its content childnode (as defined by options.content), that may
	 * be scrolled by dragging anywhere in the content by either mouse or touch. The root node acts as the viewport and
	 * must define a hidden overflow in the direction for which scrolling is enabled, in order to hide any content in
	 * excess of the viewport's dimensions. This requires a block level display value on the viewport. The content
	 * node's position is updated using CSS translations. This may conflict with transformations that are applied to
	 * the content node from CSS.
	 *
	 * Non-interactive track thumbs indicating the relative size and position of the content in respect to the viewport
	 * are inserted (by default, see options) as span elements and are updated dynamically while scrolling. Thumbs must
	 * be styled using CSS. The vertical thumb should be positioned in the top right corner of the viewport and
	 * minimally define a fixed width to be visible; the height is calculated and applied dynamically. The horizontal
	 * thumb should be positioned in the bottom left corner and define a fixed height. This may require a position
	 * value on the viewport other than static (e.g. relative).
	 *
	 * Vertical scrolling minimally requires a limited height value for the viewport, and an auto height value for the
	 * content node. As normal CSS flow is vertically oriented, this doesn't require any styling other than possible
	 * block display on the content node.
	 *
	 * Horizontal scrolling minimally requires a limited width value for the viewport, and either a fixed width value
	 * or non-wrapping white space for the content node. This may require a block level display value on the content
	 * node.
	 *
	 * Orientation free scrolling requires limited width and height for the viewport, as well as and fixed width and
	 * height for the content node.
	 *
	 * @name ScrollableWidget
	 * @memberOf playtotv.widget
	 * @class
	 * @extends playtotv.widget.Widget
	 * @param {Node} node
	 * @param {Object} [options]
	 * @param {number} [options.orientation=Vector.VERTICAL] Content orientation.
	 * @param {string} [options.restrict='outside'] DraggableWidget content restriction.
	 * @param {string} [options.content='.content'] Selector indicating the draggable content node.
	 * @param {string} [options.thumbH='thumb-horizontal'] Horizontal thumb class, false for no thumb.
	 * @param {string} [options.thumbV='thumb-vertical'] Vertical thumb class, false for no thumb.
	 * @param {string} [options.active='scrolling'] Active class when scrolling.
	 * @param {string} [options.disabled='disabled'] Disabled class for thumbs when view exceeds content.
	 * @param {boolean} [options.elastic=true] ScrollableWidget is elastic by default.
	 * @param {boolean} [options.inertia=true] ScrollableWidget has inertia by default.
	 */
	var ScrollableWidget = new playtotv.Class({
		extend: Widget,

		initialize: function(node, options) {
			this._super(node);

			options = playtotv.params(options, {
				orientation: Vector.VERTICAL,
				restrict: 'outside',
				content: '.content',
				thumbH: 'thumb-horizontal',
				thumbV: 'thumb-vertical',
				active: 'scrolling',
				disabled: 'disabled',
				elastic: true,
				inertia: true,
				friction: 50,
				priority: false,
				handle: null
			});

			// draggable content node MUST be present (its actual content may be added dynamically)
			var content = dom.first(options.content, node);
			if (!content) {
				throw new ReferenceError('Content node "' + options.content + '" expected');
			}

			// node display SHOULD be set to "block" or "inline-block"
			// TODO: test more display values including flex types and run-in.
			var display = style.get(node, 'display');
			if (!/block/i.test(display)) {
				throw new Error('Display "block" or "inline-block" is required for scrollable nodes');
			}

			// node overflow MUST be set to "hidden"
			var overflow = style.get(node, 'overflow');
			if (overflow !== 'hidden') {
				throw new Error('Overflow "hidden" is required for scrollable nodes');
			}

			this.content = content;
			this.activeClass = options.active;
			this.disabledClass = options.disabled;

			var dir = options.orientation;

			this.pane = new DraggableWidget(content, {
				orientation: dir,
				restrict: options.restrict,
				elastic: options.elastic,
				inertia: options.inertia,
				friction: options.friction,
				priority: options.priority,
				handle: options.handle
			});

			if (options.thumbH && (dir === Vector.HORIZONTAL || dir === Vector.FREE)) {
				this.thumbH = dom.create('span', { 'class': options.thumbH }, { appendTo: node });
			}

			if (options.thumbV && (dir === Vector.VERTICAL || dir === Vector.FREE)) {
				this.thumbV = dom.create('span', { 'class': options.thumbV }, { appendTo: node });
			}

			this.addEventListener(content, 'em:dragstart', this.handleStart);
			this.addEventListener(content, 'em:dragmove', this.handleDrag);
			this.addEventListener(content, 'em:dragend', this.handleEnd);

			this.updateThumbs();
		},

		/** @lends playtotv.widget.ScrollableWidget.prototype */

		/**
		 * Moves and resizes the thumb to reflect the content's position
		 *
		 */
		updateThumbs: function() {
			var pane = this.pane;

			var relative = pane.getRelative();
			var pos = pane.position;
			var min = pane.min;
			var view = pane.view;
			var content = pane.content;
			var scale = view.divide(content.max(view));
			var disabled = this.disabledClass;

			var elasticity = pos.max(min.subtract(pos)).max(Vector.ZERO);
			var thumbSize = view.multiply(scale).subtract(elasticity);
			var thumbSpace = view.subtract(thumbSize);
			var thumbPos = relative.multiply(thumbSpace).limit(Vector.ZERO, thumbSpace);

			// If present, update the horizontal thumb's size, positition and state class.
			var horizontal = this.thumbH;
			if (horizontal) {
				style.set(horizontal, 'width', thumbSize.x + 'px');
				style.setTranslate(horizontal, thumbPos.x + 'px, 0');
				horizontal.classList.toggle(disabled, scale.x >= 1);
			}

			// Same for vertical.
			var vertical = this.thumbV;
			if (vertical) {
				style.set(vertical, 'height', thumbSize.y + 'px');
				style.setTranslate(vertical, '0, ' + thumbPos.y + 'px');
				vertical.classList.toggle(disabled, scale.y >= 1);
			}
		},

		/**
		 * Toggles the active class when scrolling for styling purposes.
		 * @param {boolean} toggle
		 */
		toggleActive: function(toggle) {
			if (toggle !== this.scrolling) {
				this.scrolling = toggle;
				this.node.classList.toggle(this.activeClass, toggle);
			}
		},

		/**
		 * Animates the given node into view using {@link playtotv.widget.DraggableWidget#moveIntoView}
		 * @param {Node} target
		 * @param {number} [time=1000]
		 */
		scrollIntoView: function(target, time) {
			this.pane.moveIntoView(target, time);
		},

		/**
		 * Animates the content to align the given point (relative to content) to the top-left edge of the view.
		 *
		 * @param {playtotv.css.Vector} point
		 * @param {number} [time=1000]
		 */
		scrollTo: function(point, time) {
			this.pane.swipeTo(Vector.ZERO.subtract(point), time);
		},

		/**
		 * Internal dragstart handler, checks if thumbs are needed, corrects for native scrolling offsets, and stops
		 * propagation to higher nodes.
		 * @param {Event|playtotv.event.Event} event
		 */
		handleStart: function(e) {
			e.stopPropagation();

			var pane = this.pane;

			// check if thumbs are needed
			this.indicate = pane.content.exceeds(pane.view);

			// compensate for anchor jumps
			var node = this.node;
			var sx = node.scrollLeft;
			var sy = node.scrollTop;
			if (sx || sy) {
				node.scrollTop = node.scrollLeft = 0;
				pane.moveBy(new Vector(-sx, -sy));
				pane.start = pane.position;
			}
		},

		/**
		 * Internal dragmove handler, updates thumb positions, and adds the active class after scrolling at least 20px.
		 * @param {Event|playtotv.event.Event} event
		 */
		handleDrag: function(e) {
			e.stopPropagation();

			if (this.indicate) {

				var pane = this.pane;
				var start = pane.start;
				var pos = pane.position;
				var magnitude = pos.subtract(start).magnitude();

				if (!this.scrolling && magnitude > 20) {
					this.toggleActive(true);
				}

				this.updateThumbs();
			}
		},

		/**
		 * Internal dragend handler. Removes the active class.
		 * @param {Event|playtotv.event.Event} event
		 */
		handleEnd: function(e) {
			e.stopPropagation();
			this.toggleActive(false);
		},

		callbacks: [
			'handleStart',
			'handleDrag',
			'handleEnd'
		]
	});

	return ScrollableWidget;
});
