playtotv.define('playtotv.episode.EpisodeController', [
	'playtotv.mvc.Controller',
	'playtotv.episode.episodeSessionAPI'
], function(
	Controller,
	episodeSessionAPI
) {

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;
	var log = ptv.Logger.get(this.id);

	return class EpisodeController extends Controller {
		constructor(context, options) {
			options = ptv.params(options, {
				timeZone: util.getTimeZone(),
				showName: undefined,
				showId: undefined,
				episodeSessionId: undefined
			});

			var stored = context.stored || {};
			super(context, {
				model: {
					current: null,
					waiting: false,
					// Note: Used as a backup for server side storage of user data
					episodeInteractionUserDataMap: stored.episodeInteractionUserDataMap || {}
				}
			});

			this.options = options;

			this.addMethods([
				'getEpisode',
				'getInteraction',
				'getCurrentInteraction',
				'getNextAsyncInteraction',
				'submitInteraction'
			]);

			this.predictionTypes = [ 'PREDICTION' ];

			this.scope.observe('user.online', this.start.bind(this));

			this.scope.subscribe('change:push.stream', this.onStreamChange.bind(this));
			this.scope.subscribe('push.EPISODESESSION-STATECHANGE', this.onChangeEvent.bind(this));
			this.scope.subscribe('push.EPISODESESSION-INTERACTIONSTATEUPDATE', this.onUpdateEvent.bind(this));
		}

		// Note: Currently no support for going offline and online again, but that does not seem necessary
		start(online) {
			if (this.hasStarted || !online) {
				return;
			}
			this.join();
			this.hasStarted = true;
		}

		adaptEpisode(data) {
			return ptv.extend(data, {
				interactionMap: {},
				interactionQueue: [],
				asyncInteractions: {},
				position: 'BEFORE'
			});
		}

		join() {
			return this.scope.call('server.call', episodeSessionAPI.joinAndGetState, this.options).then(data => {
				var episodeData = data.episodeSession;
				if (!episodeData) {
					this.set('waiting', true);

					return Promise.race([
						this.scope.when('push.EPISODESESSION-OPENED'),
						this.scope.when('change:push.stream')
					]).then(() => this.join());
				}

				var episode = this.adaptEpisode(episodeData);

				this.set('waiting', false);
				this.set('current', episode);

				data.state.stateChanges.forEach(change => {
					// Quietly work up to current state (already publishing current episode etc)
					this.onStateChange(change, true);
				});

				return episode;
			}).catch(error => {
				log.e('join()', error);
				this.set('current', null);
				return Promise.delay(5000).then(() => this.join());
			});
		}

		checkState() {
			var episode = this.getEpisode();

			if (this.checkingState) {
				return;
			}
			this.checkingState = true;

			// Note: For some reason this returns changes including specified version, hence the +1
			this.scope.call('server.call', episodeSessionAPI.getEpisodeSessionStateAsChangesSince, {
				episodeSessionId: episode.id,
				sinceVersion: episode.version ? episode.version + 1 : 1
			}).then(data => {
				data.stateChanges.forEach(change => this.onStateChange(change));
			}).finally(() => {
				this.checkingState = false;
			});
		}

		getEpisode() {
			return this.model.current;
		}

		getInteraction(id) {
			var episode = this.getEpisode();
			return episode && episode.interactionMap[id];
		}

		getCurrentInteraction() {
			var episode = this.getEpisode();
			return episode && episode.interactionQueue[0];
		}

		/**
		 * Returns a Promise of an array of interactions from specified async phase.
		 *
		 * Interactions are stored in {@link playtotv.episode.Episode#asyncInteractions}.
		 * If interactions for specified phase were already previously stored there, they are returned.
		 * Otherwise {@link playtotv.episode.episodeSessionAPI.getAsyncDataForEpisodeSession} is called.
		 *
		 * @param {string} [phase=Current episode phase]
		 * @returns {Promise} Promise of an array of interactions from specified async phase
		 */
		getAsyncInteractions(phase) {
			var episode = this.getEpisode();

			// TODO: Also add support for getting async data for all phases (phase param has become optional)?
			phase = phase || episode.currentPhase;

			var interactions = episode.asyncInteractions[phase];
			if (interactions) {
				return Promise.resolve(interactions);
			}

			return this.scope.call('server.call', episodeSessionAPI.getAsyncDataForEpisodeSession, {
				episodeSessionId: episode.id,
				phase: phase
			}).then(data => {
				interactions = data.map(item => {
					var interaction = this.upsertInteraction(item);
					// Change state to ANSWER to enable answering
					interaction.state = 'ANSWER';
					return interaction;
				});

				episode.asyncInteractions[phase] = interactions;

				return interactions;
			});
		}

		/**
		 * Returns a Promise of the first interaction from specified async phase that has no user data.
		 * E.g: For which no answer has been submitted yet.
		 *
		 * Uses: {@link playtotv.episode.EpisodeController#getAsyncInteractions}
		 *
		 * @param {string} [phase=Current episode phase]
		 * @returns {Promise} Promise of the first interaction from specified async phase that has no user data
		 */
		getNextAsyncInteraction(phase) {
			phase = phase || this.getEpisode().currentPhase;
			return this.getAsyncInteractions(phase).then(interactions => {
				return interactions.find(interaction => !interaction.data);
			});
		}

		/**
		 * TODO: JSDoc
		 */
		submitInteraction(method, interactionId, params, adaptor) {
			return this.scope.call('server.toServerTime').then(serverTime => {
				var episode = this.getEpisode();
				var interaction = episode.interactionMap[interactionId];
				var stateTime = interaction.stateTimeMap.ANSWER;

				params.episodeSessionId = episode.id;
				if (params.clientAnswerTime) {
					params.clientAnswerTime = serverTime - stateTime;
				}

				return this.scope.call('server.call', method, params).then(data => {
					if (adaptor) {
						data = adaptor(interaction.data, data);
					}
					this.setInteractionUserData(episode.id, interaction, data);
					this.publish('interaction-submitted-' + interactionId, data);
					return data;
				});
			});
		}

		getInteractionUserData(episodeId, interactionId) {
			var interactionUserDataMap = this.model.episodeInteractionUserDataMap[episodeId];
			return interactionUserDataMap && interactionUserDataMap[interactionId];
		}

		setInteractionUserData(episodeId, interaction, data) {
			// Note: No need for more then one episode
			var episodeInteractionUserDataMap = ptv.pick(this.model.episodeInteractionUserDataMap, [ episodeId ]);
			var interactionUserDataMap = episodeInteractionUserDataMap[episodeId] ||
				(episodeInteractionUserDataMap[episodeId] = {});
			interaction.data = interactionUserDataMap[interaction.id] = data;
			this.set('episodeInteractionUserDataMap', episodeInteractionUserDataMap, { force: true });
		}

		upsertInteraction(payload) {
			var id = payload.interactionId;
			var data = payload.data || {};
			var episode = this.getEpisode();
			var interactionMap = episode.interactionMap;
			var interaction = interactionMap[id];

			if (!interaction) {
				interaction = interactionMap[id] = {
					id: id,
					type: payload.type,
					stateTimeMap: {},
					stateDurationMap: {},
					data: this.getInteractionUserData(episode.id, id)
				};
			}

			if (data.interactionContent) {
				interaction.content = data.interactionContent;
			}
			if (data.interactionStats) {
				interaction.stats = data.interactionStats;
			}
			if (data.interactionUserData) {
				this.setInteractionUserData(episode.id, interaction, data.interactionUserData);
			}

			return interaction;
		}

		queueInteraction(interaction) {
			var episode = this.getEpisode();
			var queue = episode.interactionQueue;
			var index = queue.indexOf(interaction);

			// Remove interaction from wherever it was in the queue
			if (index !== -1) {
				queue.splice(index, 1);
			}

			// Add interaction back to beginning of queue unless it is finished
			// Note: Prediction interactions behave differently, popping off while AWAITING_RESULTS
			var state = interaction.state;
			var type = interaction.type;
			if (
				state === 'FINISHED' ||
				state === 'AWAITING_RESULTS' && this.predictionTypes.indexOf(type) >= 0
			) {
				return;
			}
			queue.unshift(interaction);
		}

		updateInteractionPosition(interaction) {
			var content = interaction.content || {};
			var episode = this.getEpisode();
			// TODO: Check with Mark whether we can replace lastquestion by lastInteraction
			episode.position =
				interaction.state === 'FINISHED' && content.lastquestion === 'true' ? 'AFTER' : 'BETWEEN';
		}

		onStreamChange() {
			if (this.getEpisode()) {
				this.checkState();
			}
		}

		onChangeEvent(event) {
			var payload = event.payload;
			var episode = this.getEpisode();
			if (!episode || payload.episodeSessionId !== episode.id) {
				return;
			}
			this.onStateChange(payload);
		}

		onStateChange(change, quiet) {
			var episode = this.getEpisode();
			var version = episode.version;
			var subject = change.subject;
			var newVersion = change.version;

			// Catch any unusual changes
			if (newVersion <= version) {
				log.d('onStateChange: Skipping: ' + newVersion + ' (already at: ' + version + ')');
				return;
			} else if (newVersion > version + 1) {
				log.d('onStateChange: Missed changes between: ' + version + ' and ' + newVersion);
				this.checkState();
				return;
			}

			var publishIfNotQuiet = function(channel) {
				if (!quiet) {
					this.publish(channel, change);
				}
			}.bind(this);

			switch (subject) {
				case 'PHASE': this.onPhaseChange(change, quiet); break;
				case 'INTERACTION': this.onInteractionChange(change, quiet); break;
				case 'LEADERBOARD_UPDATED': publishIfNotQuiet('leaderboard-update'); break;
				case 'LEADERBOARD_FINAL': publishIfNotQuiet('leaderboard-final'); break;
				default: log.d('onStateChange: No handler defined for subject: ' + subject);
			}

			episode.version = newVersion;
		}

		onPhaseChange(change, quiet) {
			var episode = this.getEpisode();
			var phase = change.sessionPhase;

			episode.currentPhase = phase;
			if (!quiet) {
				this.publish('phase-change-' + phase, change);
			}

			if (phase === 'CLOSED') {
				this.set('current', null);
				this.join();
			}
		}

		onInteractionChange(change, quiet) {
			var interaction = this.upsertInteraction(change);

			var state = change.interactionState;
			interaction.state = state;
			interaction.stateTimeMap[state] = change.time;
			interaction.stateDurationMap[state] = change.duration;

			change.interaction = interaction;

			this.queueInteraction(interaction);

			this.updateInteractionPosition(interaction);

			if (!quiet) {
				this.publish('interaction-change-' + change.interactionId, change);
			}
		}

		onUpdateEvent(event) {
			var payload = event.payload;
			var episode = this.getEpisode();

			if (!episode || payload.episodeSessionId !== episode.id) {
				return;
			}

			var oldJSON = JSON.stringify(this.getInteraction(payload.interactionId));

			var interaction = this.upsertInteraction(payload);

			if (JSON.stringify(interaction) === oldJSON) {
				return; // nothing changed
			}

			payload.interaction = interaction;

			this.publish('interaction-update-' + payload.interactionId, payload);
		}

		serialize(data) {
			return ptv.pick(data, [ 'episodeInteractionUserDataMap' ]);
		}
	};
});
