playtotv.define('playtotv.fx.Animation', [
	'playtotv.animationFrame'
], function(
	animationFrame
) {
	'use strict';

	/**
	 * Animation super class. Increments an animation fraction from 0 to 1 over a given duration, and passes the
	 * current value to the onFrame function using a requestAnimationFrame callback. The onFrame function MUST be
	 * implemented by subclasses. Super class for {@link playtotv.fx.Transition} and {@link playtotv.fx.Timeline}.
	 *
	 * @name Animation
	 * @class
	 * @memberOf playtotv.fx
	 * @param {Object} options
	 * @param {boolean} [options.alternate=false] Whether an animation with multiple cycles alternates direction
	 * @param {number} [options.cycles=1] Amount of repeating animation cycles
	 * @param {number} [options.duration=1000] Duration of one animation cycle in ms
	 * @param {number} [options.fps=0] Fps limit on requestAnimationFrame callbacks
	 * @param {number} [options.rate=1] Time rate multiplier
	 * @param {number} [options.start] Alternate start timestamp in ms; past starts mid-animation, future delays start
	 */
	var Animation = new playtotv.Class({
		initialize: function(options) {
			this.settings = playtotv.params(options, {
				alternate: false,
				cycles: 1,
				duration: playtotv.REQUIRED,
				fps: 0,
				rate: 1,
				start: 0
			});
		},

		/** @lends playtotv.fx.Animation.prototype */

		/**
		 * Internal requestAnimationFrame
		 * @private
		 */
		requestFrame: function() {
			this.playing = animationFrame.request(this.onTick);
		},

		/**
		 * Internal cancelAnimationFrame
		 * @private
		 */
		cancelFrame: function() {
			if (this.playing) {
				animationFrame.cancel(this.playing);
				delete this.playing;
			}
		},

		/**
		 * Public play function. Options that are passed will overrule the instance's, but only for this animation. If
		 * an animation was currently playing, it will be stopped. This will NOT trigger onFinish(). Calls onStart().
		 * @param {Object} options See instance options.
		 */
		play: function(options) {
			if (this.playing) {
				this.stop();
			}

			this.animation = options = playtotv.params(options, this.settings);
			this.frame = options.fps ? 1000 / options.fps : 0;
			this.startTime = options.start || Date.now();
			this.frameTime = 0;
			this.reversed = false;

			this.onStart();

			this.requestFrame();
		},

		/**
		 * Pauses the current animation.
		 */
		pause: function() {
			if (this.playing) {
				this.paused = true;
				this.pauseTime = Date.now();
				this.cancelFrame();
			}
		},

		/**
		 * Resumes a currently paused animation.
		 */
		resume: function() {
			if (this.paused) {
				delete this.paused;
				this.startTime = this.startTime + (Date.now() - this.pauseTime);
				this.requestFrame();
			}
		},

		/**
		 * Stops a currently playing animation. A reset boolean may be passed to indicate whether the onFrame function
		 * should be called with the initial animation fraction value of 0.
		 * @param {boolean} reset
		 */
		stop: function(reset) {
			this.cancelFrame();
			if (reset) {
				this.onFrame(0);
			}
		},

		/**
		 * Internal tick. Calculates the current fraction value, and passes it to onFrame.
		 * @private
		 */
		onTick: function() {
			/*jshint maxcomplexity:10*/

			var time = Date.now();
			var anim = this.animation;
			var cycles = anim.cycles;
			var totalTime = (time - this.startTime) * anim.rate;
			var cycle = Math.max(0, totalTime / anim.duration);
			var finished = cycle >= cycles;

			var at = (cycles > 1) ? cycle % 1 : Math.min(1, cycle);
			if (anim.alternate) {
				var reversed = cycle % 2 > 1;
				if (reversed !== this.reversed) {
					this.reversed = reversed;
					this.onCycle(reversed);
				}

				at = reversed ? 1 - at : at;
			}

			if (!this.frame || finished || (time - this.frameTime) > this.frame) {
				this.frameTime = time;
				this.onFrame(at);
			}

			if (!finished) {
				this.requestFrame();
			} else {
				this.onFinish();
			}
		},

		/**
		 * Frame callback that MUST be implemented by subclasses. Receives the animation fraction as a number between
		 * 0 and 1, which indicates how much of the animation has passed.
		 * @param {number} fraction
		 */
		onFrame: playtotv.AbstractFunction,

		/**
		 * Start callback that may be implemented by subclasses. Called immediately before an animation is started
		 * using play().
		 */
		onStart: function() {},

		/**
		 * Cycle callback that may be implemented by subclasses. Called immediately before subsequent animation cycles
		 * start. Receives a boolean indicating whether this cycle is a reversed one. This function is called only when
		 * an animation has more than one animation cycle.
		 * @param {boolean} reversed
		 */
		onCycle: function() {},

		/**
		 * End callback that may be implemented by subclasses. Called immediately after an animation ended.
		 */
		onFinish: function() {},

		callbacks: [
			'onTick'
		]
	});

	return Animation;
});
