/*
For lack of possibility of extending Promise (https://github.com/slightlyoff/Promises/issues/71) we will extend the
global Promise instead. It's not ideal, but it's the best we can do for now.

Still passes promises-aplus-tests (https://github.com/promises-aplus/promises-tests).
*/
(function() {

	'use strict';

	function extend(dst, src) {
		for (var key in src) {
			if (src.hasOwnProperty(key)) {
				dst[key] = src[key];
			}
		}
	}

	extend(Promise.prototype, {
		/** @lends Promise.prototype */

		/**
		 * Extended version of {@link Promise#catch} with predicate function argument to filter by.
		 *
		 * If predicate returns true for rejection reason then onReject will be called and returned
		 * otherwise the original reason will be re-thrown.
		 *
		 * @param {Function} predicate
		 * @param {Function} onReject
		 * @returns {Promise}
		 */
		catchIf: function(predicate, onReject) {
			return this.catch(function(reason) {
				if (predicate(reason)) {
					return onReject(reason);
				} else {
					throw(reason);
				}
			});
		},

		/**
		 * Extended version of {@link Promise#catch} with Class argument to filter by.
		 *
		 * If rejection reason is an instance of specified Class then onReject will be called and returned
		 * otherwise the original reason will be re-thrown.
		 *
		 * @param {Function} Class
		 * @param {Function} onReject
		 * @returns {Promise}
		 */
		catchClass: function(Class, onReject) {
			return this.catch(function(reason) {
				if (reason instanceof Class) {
					return onReject(reason);
				} else {
					throw(reason);
				}
			});
		},

		/**
		 * Call specified callback when this Promise either resolves or rejects.
		 * Returns a Promise that resolves/rejects with the original value/reason when the callback result is resolved.
		 * If the callback throws or the result is rejected the returned Promise will reject with that reason.
		 *
		 * @param {Function} callback
		 * @returns {Promise}
		 */
		finally: function(callback) {
			var Constructor = this.constructor;
			return this.then(function(value) {
				return Constructor.resolve(callback()).then(function() {
					return value;
				});
			}, function(reason) {
				return Constructor.resolve(callback()).then(function() {
					throw reason;
				});
			});
		}
	});

	extend(Promise, {
		/** @lends Promise */

		/**
		 * Returns a deferred object with promise, resolve and reject properties.
		 *
		 * Use of this is generally discouraged, but can be practical in specific cases and eases porting of old code.
		 *
		 * @returns {Object}
		 */
		defer: function() {
			var deferred = {};
			deferred.promise = new this(function(resolve, reject) {
				deferred.resolve = resolve;
				deferred.reject = reject;
			});
			return deferred;
		},

		/**
		 * Returns a Promise that resolves after specified amount of milliseconds.
		 *
		 * @param {number} ms
		 * @returns {Promise}
		 */
		delay: function(ms) {
			return new this(function(resolve) {
				setTimeout(resolve, ms);
			});
		},

		/**
		 * Returns a function that will unstack calls using {@link nextTick} and then call callback after any previous
		 * callback has resolved/rejected and with at least the specified interval in ms between callbacks.
		 * When callback is called it will use the arguments of the most recent call.
		 * Any extra calls made in the meantime are ignored; they will not cause callback to be called and their
		 * arguments will be discarded.
		 *
		 * TODO: Improve this JSDOC and also JSDOC limit() and debounce()
		 * TODO: Add support for getShard argument like in limit()
		 *
		 * @param {Function} callback
		 * @param {number} interval
		 * @returns {Function}
		 */
		throttle: function(callback, interval) {
			// TODO: Ensure this and limit and debounce also work like they should in Cancellable
			// E.g: throttled.cancel() should cancel any Cancellable returned by callback

			var Promise = this;
			var thisArg, args, timeout, promise;
			var deferred = Promise.defer();

			function call() {
				if (!args || promise || timeout) {
					return; // wait for timeout/promise to call when it is finished
				}

				promise = Promise.resolve(callback.apply(thisArg, args));
				thisArg = null;
				args = null;

				promise.then(deferred.resolve, deferred.reject).finally(function() {
					promise = null;
					call();
				});

				deferred = Promise.defer();

				timeout = setTimeout(function() {
					timeout = null;
					call();
				}, interval);
			}

			return function() {
				thisArg = this;
				if (args || promise || timeout) { // already waiting for tick, promise or timeout
					args = arguments;
				} else {
					args = arguments;
					nextTick(call);
				}
				return deferred.promise;
			};
		},

		// Like throttle, but without the trailing call
		// Returns previous call's promise when called during wait
		// Otherwise calls and returns that promise
		limit: function(callback, interval, getShard) {
			getShard = getShard || callback.getShard || function() {
				return JSON.stringify(Array.from(arguments));
			};

			var Promise = this;
			var shardMap = {};

			function limited() {
				/*jshint validthis: true */
				var key = getShard.apply(this, arguments);
				var shard = shardMap[key] || (shardMap[key] = { last: 0 });
				var now = Date.now();

				if (!shard.busy && now > shard.last + interval) {
					shard.busy = true;
					shard.last = now;

					shard.promise = Promise.resolve(callback.apply(this, arguments)).finally(function() {
						shard.busy = false;
					});
				}

				return shard.promise;
			}

			limited.getShard = getShard;

			return limited;
		},

		// Waits for an interval to pass without being called again before calling callback
		// Returns promise of that call's return value
		debounce: function(callback, interval, getShard) {
			getShard = getShard || callback.getShard || function() {
				return JSON.stringify(Array.from(arguments));
			};

			var Promise = this;
			var shardMap = {};

			function debounced() {
				/*jshint validthis: true */
				var thisArg = this;
				var args = arguments;
				var key = getShard.apply(thisArg, args);
				var shard = shardMap[key] || (shardMap[key] = {});

				if (!shard.deferred) {
					shard.deferred = Promise.defer();
				}
				if (shard.timeout) {
					clearTimeout(shard.timeout);
				}
				// TODO: Double check that this acts reasonably when callback takes longer to resolve then interval
				shard.timeout = setTimeout(function() {
					shard.timeout = 0;
					Promise.resolve(callback.apply(thisArg, args)).
						then(shard.deferred.resolve, shard.deferred.reject).
						finally(function() {
							shard.deferred = 0;
						});
				}, interval);

				return shard.deferred.promise;
			}

			debounced.getShard = getShard;

			return debounced;
		}
	});
}());
