playtotv.define('playtotv.touch.Tracker', [
	'playtotv.touch.Sequence'
], function(Sequence) {

	'use strict';

	var nativeMap = [].map;

	/**
	 * Tracker
	 *
	 */
	var Tracker = new playtotv.Class({
		initialize: function() {
			this.map = {};
		},

		add: function(touches, target) {
			return nativeMap.call(touches, function(touch) {
				var id = touch.pointerId || touch.identifier;
				var sequence = this.map[id] = new Sequence(target, touch);
				return sequence;
			}, this);
		},

		update: function(touches) {
			return nativeMap.call(touches, function(touch) {
				var id = touch.pointerId || touch.identifier;
				var item = this.map[id];
				item.update(touch);
				return item;
			}, this);
		},

		remove: function(touches) {
			return nativeMap.call(touches, function(touch) {
				var id = touch.pointerId || touch.identifier;
				var item = this.map[id];
				delete this.map[id];
				return item;
			}, this);
		},

		toArray: function() {
			var result = [];
			var map = this.map;
			for (var id in map) {
				if (map.hasOwnProperty(id)) {
					result.push(map[id]);
				}
			}
			return result;
		},

		empty: function() {
			var map = this.map;
			for (var id in map) {
				if (map.hasOwnProperty(id)) {
					return false;
				}
			}

			return true;
		}
	});

	return Tracker;
});
