playtotv.define('playtotv.handlebars.hbsUtil', [], function() {
	/*global Handlebars:false*/

	'use strict';

	var ptv = playtotv;

	var hbsUtil = {
		// Throw Exception, but in separate tick so as not to break down subsequent code execution
		softException: function(message) {
			var exception = new Handlebars.Exception(message);
			window.nextTick(function() {
				throw exception;
			});
			return '';
		},

		// Call if value is a getter function otherwise just return value as-is
		getValue: function(context, value) {
			return value instanceof Function ? value.call(context) : value;
		},

		getHash: function(context, hash) {
			return Object.keys(hash).reduce(function(newHash, key) {
				newHash[key] = this.getValue(context, hash[key]);
				return newHash;
			}.bind(this), {});
		},

		getArgs: function(context, args, names, nrRequired, nrOptional) {
			args = ptv.toArray(args);
			var options = args.pop();
			args = args.map(arg => this.getValue(context, arg));

			var map = {
				args: args,
				options: options,
				hash: this.getHash(context, options.hash)
			};

			// If defined then validate and map required and optional arguments to their names
			if (names) {
				names = names.split(', ');

				var nrNames = names.length;
				nrRequired = nrRequired !== undefined ? nrRequired : nrNames;
				nrOptional = nrOptional !== undefined ? nrOptional : nrNames - nrRequired;
				var nrArgs = args.length;
				if (nrArgs < nrRequired || nrArgs > nrRequired + nrOptional) {
					var requiredNames = names.slice(0, nrRequired);
					var optionalNames = names.slice(nrRequired, nrOptional);
					this.softException(
						options.name + ' got invalid number of arguments, ' +
						' expected: ' + nrRequired + (nrOptional ? ' to ' + (nrRequired + nrOptional) : '') + ' (' +
						requiredNames.concat(optionalNames.map(function(n) { return '[' + n + ']'; })).join(', ') +
						'), but got: ' + nrArgs + ' (' + args.join(', ') + ')'
					);
				}

				names.forEach(function(name, i) {
					map[name] = args[i];
				});
			}

			return map;
		},

		extendContext: function(context, hash) {
			// Note: Using hash context extending will break any context prototype chaining
			return hash instanceof Object && Object.keys(hash).length > 0 ?
				ptv.extend({}, context, hash) :
				context;
		},

		callHelper: function(name, context, ...args) {
			return Handlebars.helpers[name].apply(context, args);
		},

		delegateIfBlock: function(helperName, context, value, options, clearHash) {
			if (options.fn) {
				if (clearHash) {
					options = ptv.extend({}, options, { hash: {} });
				}
				return this.callHelper(helperName, context, value, options);
			}

			return value;
		}
	};

	return hbsUtil;
});
