playtotv.define('playtotv.widget.ScalableWidget', [
	'playtotv.css.style',
	'playtotv.widget.Widget'
], function(
	style,
	Widget
) {

	'use strict';

	/**
	 * Scales a node to fit its parent's dimensions, scaling in either width or height, and resizing in (respectively)
	 * height or width to completely fill the parent's space. Use min- and/or max- css dimensions on the parent to set
	 * hard limits on scaling. Note that {@link playtotv.css.style.fromPageToNode} may be needed to convert coordinate
	 * spaces when working with user input, as transformed nodes will return their original position and dimensions.
	 *
	 * @name ScalableWidget
	 * @memberOf playtotv.widget
	 * @class
	 * @param {Node} node
	 * @param {Object} [options] ScalableWidget options
	 * @param {Object} [options.mode='portrait'] The scaling mode, either "portrait" or "landscape"
	 * @param {Object} [options.resize=true] Resizing may be set to false to have nodes scale only, not resize
	 */
	var ScalableWidget = new playtotv.Class({
		extend: Widget,
		initialize: function(node, options) {
			options = playtotv.params(options, {
				mode: 'portrait',
				resize: true
			});

			this._super(node);

			this.mode = options.mode;
			this.resize = options.resize;

			this.addEventListener(window, 'resize', playtotv.debounce(this.onResize, 100));
			this.onResize();
		},

		/** @lends playtotv.widget.ScalableWidget.prototype */

		/**
		 * Resize handler, determines and applies scale and resizing.
		 *
		 * @param {Event} resize
		 */
		onResize: function() {
			var node = this.node;
			if (!node) {
				return; // disposed during debounce
			}

			var parent = node.parentNode;
			var scale = (this.mode === 'landscape') ?
				parent.offsetWidth / node.offsetWidth :
				parent.offsetHeight / node.offsetHeight;

			this.setScale(scale);

			if (this.resize) {
				this.sizeToScale();
			}
		},

		/**
		 * Resizes the widget's node to fill its parent, based on this instance's mode, the current scale, and the
		 * parent's dimensions. When resizing is enabled, this method is called via onResize immediately following
		 * setScale.
		 *
		 */
		sizeToScale: function() {
			var node = this.node;
			var parent = node.parentNode;
			var landscape = (this.mode === 'landscape');

			var offside = landscape ? 'height' : 'width';
			var space = landscape ?
				parent.offsetHeight / this.scale :
				parent.offsetWidth / this.scale;

			style.set(node, offside, space + 'px');
		},

		/**
		 * Applies the scale to the widget's node as a CSS scale() transformation.
		 *
		 * @param {number} scale
		 */
		setScale: function(scale) {
			this.scale = scale;
			style.setTransform(this.node, 'scale(' + scale + ')');
		},

		/**
		 * Returns the current scale
		 *
		 * @returns {number} scale
		 */
		getScale: function() {
			return this.scale;
		},

		callbacks: [
			'onResize'
		]
	});

	return ScalableWidget;
});
