playtotv.define('playtotv.pubsub.Subscription', [], function() {

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;

	return new ptv.Class({
		/** @lends playtotv.pubsub.Subscription.prototype */

		/**
		 * Class used to represent a {@link playtotv.pubsub.PubSub} Subscription.
		 *
		 * When a selector string contains star (*) characters it is converted to a basic glob RegExp.
		 *
		 * @constructs
		 * @param {string|RegExp} selector Channel selector
		 * @param {Function} handler Subscription callback
		 * @throws {TypeError} If selector or handler is invalid
		 *
		 * @property {string|RegExp} selector
		 * @property {Function} handler
		 */
		initialize: function(selector, handler) {
			if (!(typeof selector === 'string' || selector instanceof RegExp)) {
				throw new TypeError('Invalid selector (not a string or RegExp)');
			}
			if (!(handler instanceof Function)) {
				throw new TypeError('Invalid handler (not a function)');
			}

			if (typeof selector === 'string' && selector.indexOf('*') >= 0) {
				selector = new RegExp('^' + util.escapeRegEx(selector).replace(/\\\*/g, '.*?') + '$');
			}

			this.selector = selector;
			this.handler = handler;
		},

		/**
		 * Returns true if this subscription's selector matches specified channel.
		 *
		 * @param {string} channel Channel to match
		 * @returns {boolean} True if this subscription's selector matches specified channel
		 */
		match: function(channel) {
			var selector = this.selector;
			return typeof selector === 'string' ? selector === channel : selector.test(channel);
		},

		/**
		 * Deliver data to handler on specified channel.
		 *
		 * If subscription has been disposed then handler will no longer be called.
		 *
		 * Handler will be called with this subscription as thisArg.
		 *
		 * @param {mixed} [data] Data to deliver
		 * @param {string} channel Channel we are delivering to
		 */
		deliver: function(data, channel) {
			if (this.handler) {
				this.handler(data, channel);
			}
		},

		dispose: function() {
			ptv.dispose(this);
		}
	});
});
