playtotv.define('playtotv.widget.FormWidget', [
	'playtotv.widget.Widget'
], function(
	Widget
) {

	'use strict';

	var ptv = playtotv;
	var dom = ptv.dom;
	var event = ptv.event;

	/**
	 * ValidityTest class, validates an input using either the native checkValidity method, or a specified fallback
	 * when native support is not detected by the given test method. For example; with a "pattern" attribute, the test
	 * method should test native support, and the fallback should implement a regex check on the input's value.
	 *
	 * Since native checkValidity errors on any illegal value, it may invalidate an input outside of the scope of a
	 * test's instance, but this merely makes validation fail early, and prevents subsequent tests on the same input.
	 */
	var ValidityTest = new ptv.Class({
		initialize: function(options) {
			options = ptv.params(options, {
				selector: ptv.REQUIRED,
				test: ptv.REQURIED,
				fallback: ptv.REQUIRED
			});

			this.selector = options.selector;
			this.supported = options.test();
			this.fallback = options.fallback;
		},

		validate: function(input) {
			// this test doesn't apply, consider valid
			if (!dom.matches(this.selector, input)) {
				return true;
			}

			// validate native if supported
			if (this.supported) {
				return input.checkValidity();
			}

			// otherwise, return the fallback implementation value
			return this.fallback(input);
		}
	});

	/**
	 * Implemented tests. Instances are created when FormWidget intances are, as to not execute any tests by loading
	 * this module.
	 */
	var validityTests = [ {
		selector: '[required]',
		test: function() {
			var input = document.createElement('input');
			input.setAttribute('required', true);
			return input.checkValidity && input.checkValidity() === false;
		},
		fallback: function(input) {
			if (/radio|check/i.test(input.type)) {
				// one or more radios or checks: check at least one of [name]
				var list = input.form.elements[input.name];
				return list.length ?
					// multiple inputs sharing a name
					ptv.toArray(list).some(item => item.checked) :
					// one input
					input.checked;
			} else {
				// otherwise, check the current value
				return !!input.value;
			}
		}
	}, {
		selector: '[pattern]',
		test: function() {
			var input = document.createElement('input');
			input.setAttribute('pattern', '\\d+');
			input.value = 'test';
			return input.checkValidity && input.checkValidity() === false;
		},
		fallback: function(input) {
			return input.value ?
				// with value: test on the pattern
				(new RegExp('^' + input.getAttribute('pattern') + '$')).test(input.value) :
				// without value: valid, required is another test.
				true;
		}
	}, {
		selector: '[min],[max]',
		test: function() {
			var input = document.createElement('input');
			input.setAttribute('type', 'number');
			input.setAttribute('min', 0);
			input.value = -1;
			// when supported, value will be overruled, or validity will be false
			// min and max for unsupported types (date in firefox) is ignored, and will validate
			// min and max for types that don't support it is ignored, and will validate
			return input.value === '0' || (input.checkValidity && input.checkValidity() === false);
		},
		fallback: function(input) {
			if (input.value) {
				var value = parseFloat(input.value);
				var min = input.getAttribute('min');
				var max = input.getAttribute('max');

				return !isNaN(value) &&
					(!min || value >= parseFloat(min)) &&
					(!max || value <= parseFloat(max));
			}

			return true;
		}
	}, {
		selector: '[type=date]',
		test: function() {
			var input = document.createElement('input');
			input.setAttribute('type', 'date');
			input.value = 'invalid';
			// test for browsers that may support min and max for numbers, but not the date input
			// when supported, value will be overruled or validity will be false
			return !input.value || (input.checkValidity && input.checkValidity() === false);
		},
		fallback: function(input) {
			if (input.value) {
				var value = Date.parse(input.value);
				var min = input.getAttribute('min');
				var max = input.getAttribute('max');

				return !isNaN(value) &&
					(!min || value >= Date.parse(min)) &&
					(!max || value <= Date.parse(max));
			}

			return true;
		}
	} ];

	/**
	 * The FormWidget provides convenient hooks to validation and custum submit actions for form nodes.
	 * The widget node does not need to be a form itself, but if not MUST contain it.
	 * By default this widget does not implement a custom submit action and only serves as a super class for specific
	 * form widgets, which in turn MUST implement custom submit handling.
	 *
	 * @name FormWidget
	 * @memberOf playtotv.widget
	 * @class
	 * @param {Node} node
	 * @param {Object} [options] FormWidget options
	 * @param {boolean} [options.preventNative=true] Indicates whether or not to preventDefault the native submit
	 * @param {boolean} [options.validateChange=true] Indicates whether or not to validate inputs onchange
	 */
	var FormWidget = new ptv.Class({
		extend: Widget,

		initialize: function(node, options) {
			if (!dom.matches('form', node)) {
				throw new Error('Form expected');
			}

			options = ptv.params(options, {
				app: null,
				controller: null,
				highlight: 'error',
				field: '.field',
				preventNative: true,
				validateChange: true
			});

			this._super(node);

			this.field = options.field;
			this.highlightClass = options.highlight;
			this.preventNative = options.preventNative;

			this.addEventListener(node, 'submit', this.onSubmit);

			if (options.validateChange) {
				this.addEventListener(node, 'change', this.onChange);
			}

			// Disable native onSubmit validation to allow the onSubmit event to fire. This generates a validity object
			// which is forwarded to the {@link playtotv.widget.FormWidget#submit} method for custom error handling.
			node.setAttribute('novalidate', true);

			// get test instances for validateInput
			this.tests = validityTests.map(
				test => new ValidityTest(test)
			);
		},

		/** @lends playtotv.widget.FormWidget.prototype */

		/**
		 * Native submit handler. Calls validateForm and passes the returned validity object to the
		 * {@link playtotv.widget.FormWidget#submit} function for custom validation feedback. The native submit event is
		 * prevented if the instance's preventNative option is set to true (default), or if submit returns false.
		 *
		 * @param {Event} submit
		 */
		onSubmit: function(e) {
			if (this.preventNative) {
				e.preventDefault();
			}

			var result = this.validateForm();
			result.elements.forEach(input => {
				this.highlight(input, false);
			}, this);

			result.errors.forEach(input => {
				this.highlight(input, true);
			}, this);

			if (result.valid) {
				event.dispatchEvent(this.node, 'submit-valid', true, true, {
					data: this.serialize(),
					validity: result,
					originalEvent: e
				});
			}
		},

		/**
		 * Handles native onchange events to live-update input validity feedback as the user enters values.
		 *
		 * @param {Event} change
		 */
		onChange: function(e) {
			var input = e.target;
			var valid = this.validateInput(input);
			this.highlight(input, !valid);
		},

		/**
		 * Toggle error on input
		 */
		highlight: function(input, toggle) {
			var field = dom.closest(this.field, input);
			if (field) {
				field.classList.toggle(this.highlightClass, toggle);
			}
		},

		/**
		 * Validates the input of this form using HTML5 validation properties including input types, required, pattern
		 * and min max attributes, etc. Returns a validity object with an "elements" array of all the form's elements,
		 * an "errors" array of invalid elemements, and a "valid" boolean indicating whether the form's current input
		 * is valid.
		 *
		 * @returns {Object} validity Validity object with elements array, errors array and valid boolean.
		 */
		validateForm: function() {
			var elements = this.getElements();
			var errors = [];

			elements.forEach(function(input) {
				if (!this.validateInput(input)) {
					errors.push(input);
				}
			}, this);

			return {
				elements: elements,
				errors: errors,
				valid: !errors.length
			};
		},

		/**
		 * Validates a single input, and returns a boolean indicating whether the value is valid.
		 *
		 * @param {Node} input
		 * @returns {boolean} valid
		 */
		validateInput: function(input) {
			// start with an initial checkValidity if supported, otherwise assume valid before testing
			var valid = input.checkValidity ? input.checkValidity() : true;

			// test until false (if not already) or all tests passed
			this.tests.forEach(test => {
				valid = valid && test.validate(input);
			});

			return valid;
		},

		/**
		 * Returns an array of the form's elements at the time of calling this method. As an array representation of
		 * the native form.elements HTMLFormControlsCollection, this WILL include possible fieldset nodes.
		 *
		 * @returns {Array} elements
		 */
		getElements: function() {
			return ptv.toArray(this.node.elements);
		},

		/**
		 * Returns an object with name-value pairs of the form's current input. The values of range and number inputs
		 * are parsed to a number. Inputs without a name attribute are ignored.
		 *
		 * @returns {Object} data Current input as name-value pairs.
		 */
		serialize: function() {
			var elements = this.getElements();
			var data = {};
			elements.forEach(function(element) {
				var name = element.name;
				var type = element.type;
				var value = element.value;

				// TODO: aggregate multiple inputs sharing a name

				// can't store value without a name
				if (!name) {
					return;
				}

				// only store the value of checked elements
				if (/check|radio/i.test(type) && !element.checked) {
					return;
				}

				// parseFloat number elements
				if (value && /number|range/i.test(type)) {
					value = parseFloat(value);
				}

				// Retrieve selected option values as array
				if (type === 'select-multiple') {
					value = playtotv.toArray(element.options)
						.filter(option => option.selected)
						.map(option => option.value);
				}

				// store
				data[name] = value;
			});

			return data;
		},

		callbacks: [
			'onChange',
			'onSubmit'
		]
	});

	return FormWidget;
});
