playtotv.define('playtotv.widget.ThemeWidget', [
	'playtotv.widget.Widget'
], function(
	Widget
) {

	'use strict';

	var ptv = playtotv;
	var dom = ptv.dom;

	return class ThemeWidget extends Widget {
		constructor(node, options) {
			ptv.params(options, {
				app: null,
				theme: ptv.REQUIRED,
				target: 'body'
			});

			super(node);

			var theme = this.theme = options.theme;
			var target = this.target = dom.first(options.target);
			if (target) {
				target.classList.add(theme);
			}
		}

		dispose() {
			if (this.target) {
				this.target.classList.remove(this.theme);
			}

			super();
		}
	};
});
