playtotv.define('playtotv.server.Server', [
	'playtotv.server.gatekeeperAPI',
	'playtotv.model.ObjectModel',
	'playtotv.net.request',
	'playtotv.storage.Storage'
], function(
	gatekeeperAPI,
	ObjectModel,
	request,
	Storage
) {

	'use strict';

	// TODO: Once playtotv.net.request() returns Promise.Cancellable have this also deal properly with cancellation
	// TODO: Cache service URL(s) (for a limited amount of time) to enable fast resume
	// TODO: replace onRetry callback option, resolveTaps and rejectTaps by publish-ing (and publish responseTime?)
	// TODO: Cleanup gatekeeper code (e.g: getServiceUrls, request, ..)

	////////// Private variables

	var ptv = playtotv;
	var REQUIRED = ptv.REQUIRED;
	var util = ptv.util;
	var log = ptv.Logger.get(this.id);
	var event = ptv.event;
	var gatekeeperInit = gatekeeperAPI.getInit;

	////////// Class declaration

	// Patch gatekeeperAPI.getInit method while this is not taken care of by server API generation
	gatekeeperInit.gatekeeper = true;

	return new ptv.Class({
		/** @lends playtotv.server.Server.prototype */

		extend: ObjectModel,

		/**
		 * PlayToTV Server Class, used to manage a client-server connection.
		 *
		 * This requests service urls from the gatekeeper server and then sends server requests to the first url.
		 * When a server request times out, that service url is scratched and the request is retried using the next.
		 * When no more service urls are available, the client will request new service urls from the gatekeeper.
		 *
		 * @extends playtotv.model.ObjectModel
		 * @constructs
		 * @param {Object} options
		 * @param {string} [options.url=''] URL of gatekeeper server, will be prefixed with '//' protocol if necessary
		 * @param {number} [options.badDelay=5000] Number of ms to delay status BAD by to prevent unnecessary warnings
		 * @param {Function} [options.onRetry] Callback(url, reason) for monitoring when this class retries a request
		 * @param {Object} [options.gatekeeperData] Gatekeeper data to use (rather then request it ourselves)
		 * @param {number} [options.gatekeeperDataTime] Unix time at which gatekeeperData was received
		 *
		 * @property {playtotv.model.ObjectModel} urls Subscribable: Map of service types to their current urls
		 * @property {string} status Subscribable: Current server connection status: GOOD, BAD or BUSY
		 * @property {number} timeOffset Subscribable: Client to server time offset in ms (add to server time)
		 * @property {Object} clientMinimalVersion Subscribable: Client minimal version values returned by gatekeeper
		 */
		initialize: function(options) {
			this._super({
				urls: new ObjectModel({}),
				status: undefined,
				timeOffset: undefined,
				clientMinimalVersion: {}
			});

			// TODO: extend options or add extra arguments for initializing with other settings, retryDelay etc.
			options = ptv.params(options, {
				url: '',
				badDelay: 5000,
				onRetry: function(url, reason) {
					log.d('Retry: ' + url + ' (' + reason + ')');
				},
				gatekeeperData: undefined,
				gatekeeperDataTime: undefined
			});

			// If it looks like a hostname without a protocol prefix then prefix with //.
			// If it does not look like a hostname it is probably a relative path.
			var url = options.url;
			if (url.length > 0 && /^localhost|.+\..+/.test(url) && url.indexOf('//') === -1) {
				url = '//' + url;
			}

			////////// Undocumented properties

			this.badDelay = options.badDelay;
			this.onRetry = options.onRetry;
			this.serverUrl = url;
			this.serviceUrls = {};
			this.urlPromises = {};
			this.gatekeeperUrl = this.getGatekeeperServiceUrl(gatekeeperInit.service || 'API') + gatekeeperInit.url;
			this.gatekeeperData = options.gatekeeperData;
			this.gatekeeperDataTime = options.gatekeeperDataTime;

			////////// Documented properties

			/**
			 * Default {@link playtotv.net.request} settings:
			 * { cache: false, dataType: 'json' timeout: 20000, headers: {}, urlParams: {} }
			 *
			 * @type Object
			 */
			this.settings = {
				cache: false,
				dataType: 'json',
				timeout: 20000,
				headers: {},
				urlParams: {}
			};

			/**
			 * Time in ms to delay before retrying a request that fails due to a lack of connection or unavailability
			 * of servers.
			 *
			 * @type number
			 * @default 5000
			 */
			this.retryDelay = 5000;

			/**
			 * Array of callbacks to call upon each request resolve.
			 * Callbacks will be called with the API method object as 'this' and resolve data object as argument.
			 *
			 * @type [Function]
			 */
			this.resolveTaps = [];

			/**
			 * Array of callbacks to call upon each request reject.
			 * Callbacks will be called with the API method object as 'this' and reject reason object as argument.
			 *
			 * @type [Function]
			 */
			this.rejectTaps = [];

			/**
			 * Storage instance prefixed with server URL.
			 *
			 * @type {playtotv.storage.Storage}
			 */
			this.storage = new Storage(window.localStorage, url + '/');

			event.addListener(window, 'offline', function() {
				log.d('Event: offline');
				// Improve speed of feedback by immediately changing to BAD without waiting for timeout.
				this.setStatus('BAD');
			}.bind(this));

			event.addListener(window, 'online', function() {
				log.d('Event: online');
				// Experiments with immediate request retries here resulted in status 0 and thus worse recovery speed.
				// So instead of adding complexity here, I simply fake immediate recovery by changing status to GOOD.
				// In case things are not GOOD after all, this will change back to BAD upon the next request issue.
				this.setStatus('GOOD');
			}.bind(this));

			// Immediately start by fetching service urls
			// Go straight for API for now since that will immediately define this.urlPromises.API and prevent another
			// call to getServiceUrls from any subsequent requests. And since API is the only service we really use :P
			this.getServiceUrl('API');
		},

		////////// Undocumented methods

		setStatus: (function() {
			// Try not to bother users with BAD connection warnings unnecessarily by delaying that status.
			// But do not delay other statuses (GOOD and BUSY) since we want to confirm those as quickly as possible.
			var badPending = false;

			function checkBad() {
				if (badPending) {
					badPending.changeProperty('status', 'BAD');
					badPending = null;
				}
			}

			return function(status) {
				if (status === 'BAD') {
					if (!badPending) {
						badPending = this;
						setTimeout(checkBad, this.badDelay);
					}
				} else {
					badPending = false;
					this.changeProperty('status', status);
				}
			};
		}()),

		urlKeys: function(url) {
			var placeholders = url.match(/\{(\w+)\}/g) || [];
			return placeholders.map(function(placeholder) {
				return placeholder.substr(1, placeholder.length - 2);
			});
		},

		validateCall: function(method, params, urlKeys) {
			urlKeys = urlKeys || this.urlKeys(method.url);

			var label = method.label;
			var dataKeys = method.dataKeys;
			var keys = Object.keys(params || {});

			var validKeys = urlKeys.concat(method.queryKeys).concat(dataKeys);
			var invalidKeys = [];
			keys.forEach(function(key) {
				if (validKeys.indexOf(key) === -1) {
					invalidKeys.push(key);
				}
			});
			if (invalidKeys.length > 0) {
				throw new Error(label + ': invalid parameters: ' + invalidKeys.join(', '));
			}

			// URL (a.k.a: path) and data keys are required, query keys are optional
			var requiredKeys = urlKeys.concat(dataKeys);
			var undefinedKeys = [];
			requiredKeys.forEach(function(key) {
				if (params[key] === undefined) {
					undefinedKeys.push(key);
				}
			});
			if (undefinedKeys.length > 0) {
				throw new Error(label + ': required parameters are undefined: ' + undefinedKeys.join(', '));
			}
		},

		_getServiceUrls: function() {
			// Use externally provided gatekeeper response data if available, otherwise call/request it ourselves
			var gatekeeperData = this.gatekeeperData;
			var gatekeeperDataTime = this.gatekeeperDataTime;
			var dataPromise;
			if (gatekeeperData && gatekeeperDataTime) {
				// Compensate for time that has passed since receiving gatekeeperData
				gatekeeperData.serverTime += Date.now() - gatekeeperDataTime;
				dataPromise = Promise.resolve(gatekeeperData);
				delete this.gatekeeperData;
				delete this.gatekeeperDataTime;
			} else {
				dataPromise = this.call(gatekeeperInit);
			}

			return dataPromise.then(function(data) {
				// If status is undefined assume it's AVAILABLE (for same origin CF cached init)
				if (!data.status || data.status === 'AVAILABLE') {
					this.setStatus('GOOD');
				} else {
					this.setStatus('BUSY');
					this.onRetry(this.gatekeeperUrl, 'busy-' + data.status.toLowerCase());
					return Promise.delay(this.retryDelay).then(this._getServiceUrls);
				}

				this.changeProperty('timeOffset', Date.now() - data.serverTime);
				this.changeProperty('clientMinimalVersion', data.clientMinimalVersion);

				return data.services.reduce(function(map, service) {
					// Randomly shuffle service urls to evenly distribute client load
					map[service.serviceType] = ptv.shuffle(
						service.serviceUrls.map(function(url) {
							// Strip API version (belongs to method descriptors, not service urls)
							return url.replace(/\d+\/$/, '');
						})
					);
					return map;
				}.bind(this), {});
			}.bind(this), function(reason) {
				// We should only end up here if error is considered unrecoverable by this.call/request() (e.g. 404)
				// However, we still want to keep retry-ing gatekeeper in such cases to allow for auto recovery
				// Because sometimes servers being restarted etc. (e.g. during up/down-scale) will return such errors
				log.i('getServiceUrls: retry: ' + reason.status);
				this.setStatus('BAD');
				this.onRetry(this.gatekeeperUrl, 'status-' + reason.status);
				return Promise.delay(this.retryDelay).then(this._getServiceUrls);
			}.bind(this));
		},

		/**
		 * Get service URLs from server.
		 *
		 * Has the side effect that status, timeOffset and clientMinimalVersion properties are updated.
		 *
		 * This is throttled (2s) to prevent too many or parallel requests.
		 *
		 * @function
		 * @returns {Promise} Promise of service URL map object
		 */
		getServiceUrls: Promise.throttle(function() {
			return this._getServiceUrls();
		}, 2000),

		getGatekeeperServiceUrl: function(service) {
			return this.serverUrl + '/' + service.toLowerCase() + '/';
		},

		getServiceUrl: function(service) {
			var urlPromises = this.urlPromises;
			if (!urlPromises[service]) {
				var serviceUrls = this.serviceUrls;
				var myServiceUrls = serviceUrls[service] || [];
				if (myServiceUrls.length > 0) {
					// Just pop next url of the stack
					urlPromises[service] = Promise.resolve(myServiceUrls.shift());
				} else {
					// Get new service urls
					urlPromises[service] = this.getServiceUrls().then(function(newServiceUrls) {
						// Store service urls for all services; could save on making another request later
						// Override any previously fetched urls since some of those might be deprecated
						ptv.each(newServiceUrls, function(urls, urlsService) {
							serviceUrls[urlsService] = urls;
						});
						var myNewServiceUrls = newServiceUrls[service] || [];
						if (myNewServiceUrls.length === 0) {
							// Default to gatekeeper service url (e.g. single node cluster)
							return this.getGatekeeperServiceUrl(service);
						} else {
							// Return first service url (and remember the rest)
							return serviceUrls[service].shift();
						}
					}.bind(this));
				}
			}
			return urlPromises[service];
		},

		invalidateServiceUrl: function(service, promise) {
			var urlPromises = this.urlPromises;
			if (urlPromises[service] === promise) { // only invalidate if the promise has not changed already
				urlPromises[service] = null;
			}
		},

		request: function(url, settings, retry) {
			// Default to simply retry-ing with same url and settings
			retry = retry || function() {
				return this.request(url, settings, retry);
			}.bind(this);

			var currentSettings = ptv.extend({}, this.settings, settings);

			return request(url, currentSettings).then(function(data) {
				// Prevent unnecessary status change on successful gatekeeperInit
				if (settings.label === gatekeeperInit.label && data.status && data.status !== 'AVAILABLE') {
					return data;
				}
				this.setStatus('GOOD');
				return data;
			}.bind(this), function(reason) {
				var status = reason.status;
				// TODO: Add setting option to retry after a delay (true=>always, callback=>if true)?
				// Retry requests that were most likely rejected without delay after a minimum delay
				if (status === 0 || status === 'no-xhr-proxy' || (status > 12000 && status < 13000)) { // IE uses 12xxx
					this.onRetry(url, 'status-' + status);
					// TODO: Double check that status 0 does not lead to false positive BAD statusses
					// TODO: Only look at gatekeeper requests and offline events for BAD?
					this.setStatus('BAD');
					return Promise.delay(this.retryDelay).then(retry);
				// Retry temporary server error after a delay
				} else if (status === 503) {
					this.onRetry(url, 'status-' + status);
					return Promise.delay(this.retryDelay).then(retry);
				// Retry requests that rejected after waiting for timeout straight away
				} else if (status === 'timeout') {
					this.onRetry(url, 'timeout');
					this.setStatus('BAD');
					return retry();
				// Unrecoverable
				} else {
					// TODO: Facilitate retry-ing from rejection handlers (reason.retry = retry)?
					return Promise.reject(reason);
				}
			}.bind(this));
		},

		getUrlAndRequest: function(serviceType, methodUrl, settings) {
			var urlPromise = this.getServiceUrl(serviceType);
			return urlPromise.then(function(serviceUrl) {
				this.urls.setProperty(serviceType, serviceUrl);
				return this.request(serviceUrl + methodUrl, settings, function retry() {
					this.invalidateServiceUrl(serviceType, urlPromise);
					return this.getUrlAndRequest(serviceType, methodUrl, settings);
				}.bind(this));
			}.bind(this));
		},

		tap: function(method, requestPromise) {
			requestPromise.then(function(data) {
				this.resolveTaps.forEach(function(tap) {
					tap.call(method, data);
				});
			}.bind(this), function(reason) {
				this.rejectTaps.forEach(function(tap) {
					tap.call(method, reason);
				});
			}.bind(this));

			return requestPromise;
		},

		////////// Documented methods

		/**
		 * Call specified method on this server with optional method parameters and request settings.
		 *
		 * TODO: Add abort() function to returned promise for aborting this call.
		 *
		 * @example
		 *
		 *var server = new Server({ host: demo-backend.playtotv.com });
		 *
		 *var testAPI = {
		 *	createA: {
		 *		label: 'test.createA',
		 *		type: 'POST',
		 *		url: 'test/a',
		 *		dataKeys: [ 'x', 'y' ]
		 *	},
		 *	readA: {
		 *		label: 'test.readA',
		 *		url: 'test/a/{id}'
		 *	},
		 *	updateA: {
		 *		label: 'test.updateA',
		 *		type: 'PUT',
		 *		url: 'test/a/{id}',
		 *		dataKeys: [ 'x', 'y' ]
		 *	},
		 *	deleteA: {
		 *		label: 'test.deleteA',
		 *		type: 'DELETE',
		 *		url: 'test/a/{id}'
		 *	},
		 *};
		 *
		 *server.call(testAPI.createA, { x: 'foo', y: 'bar' }).then(function(a) {
		 *	// created a
		 *	return server.call(testAPI.deleteA, { id: a.id }, { timeout: -1 });
		 *}).catch(function(reason) {
		 *	// reason.label can be either 'test.createA' or 'test.deleteA'
		 *	// response status and data are returned as reason.status and reason.data
		 *});
		 *
		 * @param {Object} method Definition of server method to call
		 * @param {string} [method.label] Request label to be used to identify this request
		 * @param {string} [method.service='API'] Service type
		 * @param {boolean} [method.gatekeeper=false] Request directly from gatekeeper server
		 * @param {string} [method.type='GET'] Request type (GET/POST/PUT/DELETE)
		 * @param {string} method.url Request URL including path variables
		 * @param {Array} [method.queryKeys=[]] Array of request query parameter keys
		 * @param {Array} [method.dataKeys=[]] Array of request data parameter keys
		 * @param {Object} [params] Parameters to pass to method
		 * @param {Object} [settings] Extra settings/overrides to apply to {@link playtotv.net.request}
		 * @return {Promise} Promise of server response to method call
		 */
		call: function(method, params, settings) {
			ptv.params(method, {
				label: undefined,
				service: 'API',
				gatekeeper: false,
				type: 'GET',
				url: REQUIRED,
				queryKeys: [],
				dataKeys: []
			});
			params = params || {};
			settings = ptv.extend({
				label: method.label,
				type: method.type
			}, settings);

			var methodUrl = method.url;
			var urlKeys = this.urlKeys(methodUrl);

			this.validateCall(method, params, urlKeys);

			urlKeys.forEach(function(key) {
				methodUrl = methodUrl.replace('{' + key + '}', encodeURIComponent(params[key]));
			});

			method.queryKeys.forEach(function(key) {
				if (params[key] !== undefined) {
					methodUrl = util.addURLParam(methodUrl, key, params[key]);
				}
			});

			var dataKeys = method.dataKeys;
			if (dataKeys.length > 0) {
				settings.data = {};
				dataKeys.forEach(function(key) {
					settings.data[key] = params[key];
				});
			}

			var methodService = method.service;
			if (method.gatekeeper) {
				var gatekeeperMethodUrl = this.getGatekeeperServiceUrl(methodService) + methodUrl;
				return this.tap(method, this.request(gatekeeperMethodUrl, settings));
			} else {
				return this.tap(method, this.getUrlAndRequest(methodService, methodUrl, settings));
			}
		},

		/**
		 * Converts a server time into a local client time by adding the server time offset.
		 *
		 * @param {number} serverTime Server time in ms since Unix time epoch
		 * @return {number} Client time in ms since Unix time epoch
		 */
		toClientTime: function(serverTime) {
			return serverTime + this.timeOffset;
		},

		/**
		 * Converts a local client time into a server time by subtracting the server time offset.
		 *
		 * @param {number} [clientTime=Date.now()] Client time in ms since Unix time epoch
		 * @return {number} Server time in ms since Unix time epoch
		 */
		toServerTime: function(clientTime) {
			clientTime = clientTime || Date.now();
			return clientTime - this.timeOffset;
		},

		/**
		 * Shortcut to make a server call and log the resolve/reject value.
		 *
		 * @param {Object} method {@link playtotv.server.Server#call} method
		 * @param {Object} [params] {@link playtotv.server.Server#call} params
		 * @param {Object} [settings] {@link playtotv.server.Server#call} settings
		 * @param {string} [level='DEBUG'] {@link playtotv.Logger} level
		 * @return {Promise} {@link playtotv.server.Server#call} promise
		 */
		log: function(method, params, settings, level) {
			var promise = this.call(method, params, settings);
			log.log(level || 'DEBUG', [ method.label, params, promise ]);
			return promise;
		},

		callbacks: [
			'_getServiceUrls'
		]
	});
});
