(function() {
	/*jshint maxcomplexity:100, maxstatements:100*/

	'use strict';

	// Abort if we are not running in a browser (e.g: in Node)
	if (typeof window === 'undefined') {
		return;
	}

	var ptv = playtotv;
	var document = window.document;
	var documentElement = document.documentElement;
	var dataAttr = 'data-em-uid';
	var dataMap = {};

	//
	// polyfills
	//

	// matches implementation for dom.matches
	var matchesImplementation =
		documentElement.matches ||
		documentElement.webkitMatchesSelector ||
		documentElement.mozMatchesSelector ||
		documentElement.msMatchesSelector ||
		documentElement.oMatchesSelector;

	// node.contains polyfill for firefox < 9
	var Node = window.Node;
	if (Node && Node.prototype && !Node.prototype.contains) {
		Node.prototype.contains = function(arg) {
			/*jshint bitwise:false*/
			return !!(this.compareDocumentPosition(arg) & 16);
		};
	}

	// classList.toggle polyfill for IE10, ff < 24 and Android < 4.4, that don't support the force argument
	(function(DOMTokenList, node) {
		node.classList.toggle('test', false);
		if (node.classList.contains('test')) {
			var _toggle = DOMTokenList.prototype.toggle;
			DOMTokenList.prototype.toggle = function(token, force) {
				return (1 in arguments && this.contains(token) === force) ?
					force : _toggle.call(this, token);
			};
		}
	})(window.DOMTokenList, document.createElement('_'));

	/**
	 * playtotv.dom
	 */
	ptv.dom = {
		/** @lends playtotv.dom */

		/**
		 * Returns an Array of matching elements for the given selector. An optional context Node
		 * may be used to search within, otherwise the whole document is searched.
		 *
		 * TODO: Remove and replace uses of this by $$()
		 *
		 * @function
		 * @param {string} selector
		 * @param {Node} context
		 * @returns {Array} result
		 */
		find: function(selector, context) {
			return ptv.toArray(
				(context || document).querySelectorAll(selector)
			);
		},

		/**
		 * Returns a boolean whether the given Node matches the selector string.
		 *
		 * @function
		 * @param {string} selector
		 * @param {Node} node
		 * @returns {boolean} matches
		 */
		matches: function(selector, context) {
			return (context && context !== document) ?
				matchesImplementation.call(context, selector) : false;
		},

		/**
		 * Returns the first Node matching the given selector.
		 *
		 * TODO: Remove and replace uses of this by $()
		 *
		 * @param {string} selector
		 * @param {Node} context
		 * @returns {Node} node
		 */
		first: function(selector, context) {
			return (context || document).querySelector(selector);
		},

		/**
		 * Returns the last Node matching the given selector.
		 *
		 * @param {string} selector
		 * @param {Node} context
		 * @returns {Node} node
		 */
		last: function(selector, context) {
			var result = this.find(selector, context);
			var index = Math.max(0, result.length - 1);
			return result[index];
		},

		/**
		 * Traverses the dom from a given context until the selector is matched or no sibling is found,
		 * and returns the result.
		 *
		 * @param {string} selector
		 * @param {Node} context
		 * @param {string} sibling
		 * @returns {Node} node
		 */
		traverse: function(selector, context, sibling) {
			while (context) {
				if (this.matches(selector, context)) {
					return context;
				}
				context = context[sibling];
			}
			return null;
		},

		/**
		 * Returns the first matching sibling traversing forward from the given context.
		 *
		 * @param {string} selector
		 * @param {Node} context
		 * @returns {Node} node
		 */
		next: function(selector, context) {
			return this.traverse(selector, context.nextSibling, 'nextSibling');
		},

		/**
		 * Returns the first matching sibling traversing backward from the given context.
		 *
		 * @param {string} selector
		 * @param {Node} context
		 * @returns {Node} node
		 */
		previous: function(selector, context) {
			return this.traverse(selector, context.previousSibling, 'previousSibling');
		},

		/**
		 * Traverses upward in the DOM from the given context Node, and returns the first Node matching the given
		 * selector (including the given node).
		 *
		 * @param {string} selector
		 * @param {Node} context
		 * @returns {Node} node
		 */
		closest: documentElement.closest ?
			function(selector, context) {
				return context.closest(selector);
			} :

			function(selector, context) {
				return this.traverse(selector, context, 'parentNode');
			},

		/**
		 * Like find, but includes the context itself (as first result) if it matches the given selector
		 *
		 * @param {string} selector
		 * @param {Node} context
		 * @returns {Array} result
		 */
		any: function(selector, context) {
			var nodes = this.find(selector, context);
			if (context && this.matches(selector, context)) {
				nodes.splice(0, 0, context);
			}

			return nodes;
		},

		/**
		 * Filters an Array or NodeList, and returns an array that contains only those nodes that match the selector.
		 *
		 * @param {string} selector
		 * @param {Array|NodeList} nodes
		 * @returns {Array} result
		 */
		filter: function(selector, nodes) {
			var result = [];
			var i, item, l = nodes.length;
			for (i = 0; i < l; i++) {
				item = nodes[i];
				if (this.matches(selector, item)) {
					result.push(item);
				}
			}

			return result;
		},

		/**
		 * Returns a boolean whether the given object is a Node
		 *
		 * @param {Object} object
		 * @returns {boolean} isNode
		 */
		isNode: function(node) {
			return node && node.nodeType && (node.nodeType === 1);
		},

		/**
		 * Returns a boolean whether the given Node is a child of the given context Node
		 *
		 * @param {Node} node
		 * @param {Node} context
		 * @returns {boolean} isChild
		 */
		isChild: function(node, context) {
			return this.isNode(context) ? context.contains(node) : false;
		},

		/**
		 * Creates a Node with optional attributes and options for inserting and/or writing content.
		 *
		 * @param {string} name
		 * @param {Object} attributes
		 * @param {Object} options
		 * @returns {Node} node
		 */
		create: function(name, attributes, options) {

			var node = document.createElement(name);

			if (attributes) {
				ptv.each(attributes, function(value, attr) {
					node.setAttribute(attr, value);
				});
			}

			if (options) {

				if (options.html !== undefined) {
					this.innerHTML = options.html;
				}

				[ 'appendTo', 'insertBefore', 'insertAfter', 'prependTo', 'replace' ].forEach(function(type) {
					var target = options[type];
					if (target !== undefined) {
						ptv.dom[type](node, target);
					}
				});

				if (options.text !== undefined) {
					node.appendChild(document.createTextNode(options.text));
				}
			}

			return node;
		},

		/**
		 * Removes the a Node from the DOM, and returns it.
		 *
		 * @param {Node} node
		 * @returns {Node} node
		 */
		remove: function(node) {
			return node.parentNode.removeChild(node);
		},

		/**
		 * Appends the Node to the given reference Node, and returns it.
		 *
		 * @param {Node} node
		 * @param {Node} refnode
		 * @returns {Node} node
		 */
		appendTo: function(node, refnode) {
			return refnode.appendChild(node);
		},

		/**
		 * Inserts the Node before the given reference Node, and returns it.
		 *
		 * @param {Node} node
		 * @param {Node} refnode
		 * @returns {Node} node
		 */
		insertBefore: function(node, refnode) {
			return refnode.parentNode.insertBefore(node, refnode);
		},

		/**
		 * Inserts the Node after the given reference Node, and returns it.
		 *
		 * @param {Node} node
		 * @param {Node} refnode
		 * @returns {Node} node
		 */
		insertAfter: function(node, refnode) {
			if (refnode.nextSibling) {
				return this.insertBefore(node, refnode.nextSibling);
			} else {
				return this.appendTo(node, refnode);
			}
		},

		/**
		 * Inserts the Node as first child of the given reference Node, and returns it.
		 *
		 * @param {Node} node
		 * @param {Node} refnode
		 * @returns {Node} node
		 */
		prependTo: function(node, refnode) {
			if (refnode.firstChild) {
				return this.insertBefore(node, refnode.firstChild);
			} else {
				return this.appendTo(node, refnode);
			}
		},

		/**
		 * Replaces reference Node for the the given Node, and returns the removed Node.
		 *
		 * @param {Node} node
		 * @param {Node} refnode
		 * @returns {Node} node
		 */
		replace: function(node, refnode) {
			return refnode.parentNode.replaceChild(node, refnode);
		},

		/**
		 * Returns a DocumentFragment with the specified HTML.
		 *
		 * @param {string} html
		 * @returns {DocumentFragment}
		 */
		parseHTML: function(html) {
			var div = document.createElement('div');
			div.innerHTML = html;

			var fragment = document.createDocumentFragment();
			var i, l = div.childNodes.length;
			for (i = 0; i < l; i++) {
				fragment.appendChild(div.firstChild);
			}
			return fragment;
		},

		/**
		 * Returns the value of a key/value pair that was saved to this node earlier. Values may be of any type, as
		 * the data is stored in an object literal that is linked to the node via a custom identifier. Note that this
		 * data is not accessible via attributes or native datasets, and does not span multiple sessions or views.
		 *
		 * @param {Node} node
		 * @param {string} key
		 * @returns {mixed} value
		 */
		getData: function(node, key) {
			var uid = node[dataAttr];
			var data = uid ? dataMap[uid] : null;

			return data ? data[key] : null;
		},

		/**
		 * Stores a key/value pair on a node. Values may be of any type, as the data is stored in an object literal
		 * that is linked to the node via a custom identifier. Note that this data is not accessible via attributes
		 * or native datasets, and does not span multiple sessions or views.
		 *
		 * @param {Node} node
		 * @param {string} key
		 * @param {mixed} value
		 * @returns {mixed} value
		 */
		setData: function(node, key, value) {
			var uid = node[dataAttr];
			if (!uid) {
				uid = ptv.getUID();
				node[dataAttr] = uid;
			}

			var data = dataMap[uid];

			if (!data) {
				dataMap[uid] = data = {};
			}

			data[key] = value;

			return value;
		},

		/**
		 * Removes data with specified key from specified node, stored by earlier call to {@link playtotv.dom.setData}.
		 *
		 * @param {Node} node
		 * @param {string} key
		 */
		removeData: function(node, key) {
			var uid = node[dataAttr];
			var data = dataMap[uid];
			if (data) {
				delete data[key];
			}
		},

		/**
		 * Returns the attribute value of the closest node that has specified attribute.
		 *
		 * @param {Node} node Node from which to start looking
		 * @param {string} key Attribute key to look for
		 * @return {string} Attribute value of the closest node
		 */
		getClosestAttr: function(node, key) {
			node = this.closest('[' + key + ']', node);
			return node ? node.getAttribute(key) : undefined;
		},

		/**
		 * Returns dataset or a read-only dataset like data attribute map Object.
		 *
		 * @param {Node} node
		 * @returns {Object} dataset
		 */
		getDataSet: function(node) {
			var dataset = node.dataset;

			if (!dataset) {
				return ptv.toArray(node.attributes).reduce(function(dataset, attr) {
					var name = attr.name;
					if (name.indexOf('data-') !== 0) {
						return dataset;
					}

					// Remove data- prefix and convert to camel case
					name = name.substr(5).replace(/-([a-z])/g, function(match, letter) {
						return letter.toUpperCase();
					});

					dataset[name] = attr.value;
					return dataset;
				}, {});
			}

			// Workaround for Android 4.3 that returns an "object" without hasOwnProperty() method etc.
			if (!dataset.hasOwnProperty) {
				var key, copy = {};
				for (key in dataset) {
					/*jshint forin:false*/
					copy[key] = dataset[key];
				}
				dataset = copy;
			}

			return dataset;
		}
	};

}());
