playtotv.define('playtotv.model.modelUtil', [
	'playtotv.model.AbstractModel',
	'playtotv.model.ArrayModel',
	'playtotv.model.ObjectModel'
], function(
	AbstractModel,
	ArrayModel,
	ObjectModel
) {

	'use strict';

	/**
	 * @name playtotv.model.modelUtil
	 * @namespace Namespace for model utility functions
	 */
	return {
		/** @lends playtotv.model.modelUtil */

		/**
		 * Returns a recursively modelified copy of specified value.
		 *
		 * Note: This may create many models, so take care, especially with regards to memory leaks.
		 *
		 * @param {mixed} value Value to modelify
		 * @param {boolean} [doNotThrow=false] If true then just return original value instead of throwing an exception
		 * @throws {TypeError} If value is already a Model
		 * @throws {TypeError} If value can not be modelified (it is neither Array nor Object)
		 * @returns {playtotv.model.ArrayModel|playtotv.model.ObjectModel} Modelified copy
		 */
		create: function(value, doNotThrow) {
			if (value instanceof AbstractModel) {
				if (doNotThrow) {
					return value;
				}
				throw new TypeError('Value is already a Model');
			} else if (value instanceof Array) {
				return new ArrayModel(value.map(function(item) {
					return this.create(item, true);
				}.bind(this)));
			} else if (typeof value === 'object' && value !== null) {
				return new ObjectModel(Object.keys(value).reduce(function(model, key) {
					model[key] = this.create(value[key], true);
					return model;
				}.bind(this), {}));
			} else {
				if (doNotThrow) {
					return value;
				}
				throw new TypeError('Value can not be modelified');
			}
		},

		/**
		 * Returns a recursively de-modelified copy of specified model.
		 *
		 * @param {playtotv.model.AbstractModel} model Model to de-modelify
		 * @return {Object} De-modelified copy
		 */
		toObject: function(model) {
			// This code works on both normal and Array objects
			var obj = model.toObject();
			playtotv.each(obj, function(value, key) {
				if (value instanceof AbstractModel) {
					obj[key] = this.toObject(value);
				}
			}.bind(this));
			return obj;
		},

		/**
		 * Recursively updates a model using {@link playtotv.model.AbstractModel#updateModel}.
		 *
		 * Note: Any child models removed in this process will be assumed to no longer be used and disposed.
		 *
		 * @example
		 * var m = modelUtil.create({
		 *   a: 1,
		 *   b: { p: 'p' },
		 *   c: [ { q: 'q' } ],
		 *   d: { x: 2 }
		 * });
		 *
		 * modelUtil.update(m, {
		 *   a: 2,
		 *   b: { p: 'P' },
		 *   c: [ { q: 'Q' }, { r: 'R' } ]
		 * });
		 *
		 * // Is equivalent to:
		 * // m.b.changeProperty('p', 'P');
		 * // m.c.array[0].changeProperty('q', 'Q');
		 * // m.c.add(new ObjectModel({ r: 'R' }));
		 * // d = m.d;
		 * // m.removeProperty('d');
		 * // d.dispose();
		 * // m.changeProperty('a', 2);
		 *
		 * @param {playtotv.model.AbstractModel} model Model to update
		 * @param {Object} source Source data to update model to
		 * @throws {TypeError} If model is not an {@link playtotv.model.AbstractModel}
		 * @throws {TypeError} If source is already an {@link playtotv.model.AbstractModel}
		 */
		update: function(model, source) {
			if (!(model instanceof AbstractModel)) {
				throw new TypeError('Model argument is not a model');
			}
			if (source instanceof AbstractModel) {
				throw new TypeError('Source is already a Model');
			}

			var isArray = model instanceof ArrayModel;
			var updatedSource = isArray ? [] : {};

			// Recurse into children updates or modelifications
			playtotv.each(source, function(sValue, key) {
				if (sValue instanceof AbstractModel) {
					throw new Error('Source value is already a Model');
				}

				var mValue = isArray ? model.array[key] : model[key];
				var svIsArray = sValue instanceof Array;
				var svIsObject = !svIsArray && typeof sValue === 'object' && sValue !== null;

				if ((svIsObject && mValue instanceof ObjectModel) || (svIsArray && mValue instanceof ArrayModel)) {
					this.update(mValue, sValue);
					updatedSource[key] = mValue;
				} else if (svIsObject || svIsArray) {
					updatedSource[key] = this.create(sValue);
				} else {
					updatedSource[key] = sValue;
				}
			}.bind(this));

			var removedValues = model.updateModel(updatedSource);
			removedValues.filter(function(rValue) {
				return rValue instanceof AbstractModel;
			}).forEach(function(rModel) {
				this.dispose(rModel);
			}.bind(this));
		},

		/**
		 * Recursively dispose a model and all of it's child models.
		 *
		 * @param {playtotv.model.AbstractModel} model Model to dispose
		 */
		dispose: function(model) {
			playtotv.each(model.toObject(), function(value) {
				if (value instanceof AbstractModel) {
					this.dispose(value);
				}
			}.bind(this));
			model.dispose();
		}
	};
});
