playtotv.define('playtotv.animationFrame', [], function() {

	'use strict';

	// Abort if we are not running in a browser (e.g: in Node)
	if (typeof window === 'undefined') {
		return;
	}

	var request = window.requestAnimationFrame;
	var cancel = window.cancelAnimationFrame;

	var vendors = [ 'webkit', 'moz', 'ms', 'o' ];
	var i, vendor, l = vendors.length;
	for (i = 0; i < l && !request; i++) {
		vendor = vendors[i];
		request = window[vendor + 'RequestAnimationFrame'];
		cancel = window[vendor + 'CancelAnimationFrame'] || window[vendor + 'CancelRequestAnimationFrame'];
	}

	var callTime = 0;

	request = request ? request.bind(window) : function(callback) {
		var time = Date.now();
		var delay = Math.max(0, 16 - (time - callTime));

		callTime = time + delay;
		return setTimeout(function() {
			callback(callTime);
		}, delay);
	};

	cancel = cancel ? cancel.bind(window) : function(id) {
		clearTimeout(id);
	};

	/**
	 * This module facilitates working with requestAnimationFrame, providing a 'shim' if necessary, but without
	 * actually shimming window.requestAnimationFrame as that might have some averse side effects when combined with
	 * other libraries.
	 *
	 * @name animationFrame
	 * @memberOf playtotv
	 * @namespace
	 */

	return /** @lends playtotv.animationFrame */ {

		/**
		 * Either window.requestAnimationFrame, a vendor prefixed version or a shim.
		 *
		 * @function
		 * @param {Function} callback
		 * @returns {number} id
		 */
		request: request,

		/**
		 * Either window.cancelAnimationFrame, a vendor prefixed version or a shim.
		 *
		 * @function
		 * @param {number} id
		 */
		cancel: cancel
	};
});
