playtotv.define('playtotv.mvc.BindWidget', [
	'playtotv.widget.Widget'
], function(
	Widget
) {

	'use strict';

	var ptv = playtotv;
	var event = ptv.event;

	return class BindWidget extends Widget {

		constructor(node, options) {
			super(node);

			var { controller, path } = ptv.params(options, {
				app: null,
				controller: ptv.REQUIRED,
				path: ptv.REQUIRED
			});

			ptv.extend(this, options);

			// If controller value is defined then initialize input to match
			// else initialize controller to match input
			var value = controller.seek(path);
			if (value !== undefined) {
				this.updateInput(value);
			} else {
				this.updateController();
			}

			// Bind input to controller
			// TODO: Only updateController after input and/or form are validated?
			// See if/how this can tie in with our FormWidget widget
			var onInputChange = () => this.updateController();
			this.addEventListener(node, 'change', onInputChange);
			this.addEventListener(node, 'keyup', onInputChange);

			// Bind controller to input
			this.subscribe(controller, path, value => this.updateInput(value));
		}

		updateController() {
			var node = this.node;
			var value = node.type === 'checkbox' ? node.checked : node.value;
			this.controller.set(this.path, value);
		}

		updateInput(value) {
			var node = this.node;
			if (node.type === 'checkbox') {
				if (value !== node.checked) {
					node.checked = value;
					event.triggerNative(node, 'change');
				}
			} else {
				if (value !== node.value) {
					node.value = value;
				}
			}
		}
	};
});
