playtotv.define('playtotv.css.style', [
	'playtotv.css.Matrix',
	'playtotv.css.Vector'
], function(
	Matrix,
	Vector
) {
	/*jshint maxcomplexity:100*/

	'use strict';

	// Abort if we are not running in a browser (e.g: in Node)
	if (typeof window === 'undefined') {
		return;
	}

	var ua = navigator.userAgent;
	var browser = {
		gecko: /gecko\//i.test(ua),
		trident: /trident\//i.test(ua),
		webkit: /webkit\//i.test(ua),
		presto: /presto\//i.test(ua)
	};

	var PRIORITY = '';

	var prefix =
		browser.webkit ? '-webkit-' :
		browser.gecko ? '-moz-' :
		browser.presto ? '-o-' :
		browser.trident ? '-ms-' : '';

	var cssTransform = prefix + 'transform';
	var cssOrigin = prefix + 'transform-origin';

	var webkitConvert = window.WebKitPoint && window.webkitConvertPointFromPageToNode;

	/**
	 * Style modifications for nodes, including transforms and transitions
	 *
	 * @name style
	 * @namespace
	 * @memberOf playtotv.css
	 */
	var style = {

		/** @lends playtotv.css.style */

		/**
		 * Sets a css property of a node's style to the given value.
		 *
		 * @function
		 * @param {Node} node
		 * @param {string} property
		 * @param {string} value
		 */
		set:
			window.getComputedStyle ?

			function(node, property, value) {
				node.style.setProperty(property, value, PRIORITY);
			} :

			function(node, property, value) {
				node.style[property] = value;
			},

		/**
		 * Attempts a variety of methods to retreive the calculated style property value of
		 * a given node.
		 *
		 * @function
		 * @param {Node} node
		 * @param {string} property
		 * @returns {string} value
		 */
		get:

			window.getComputedStyle ?

			function(node, property) {
				var computed = window.getComputedStyle(node, null);
				return computed.getPropertyValue(property);
			} :

			function(node, property) {
				var computed = node.currentStyle || node.style;
				return computed[property];
			},

		/**
		 * Removes a style property from a node
		 *
		 * @function
		 * @param {Node} node
		 * @param {string} property
		 */
		remove:

			window.getComputedStyle ?

			function(node, property) {
				node.style.removeProperty(property);
			} :

			function(node, property) {
				node.style[property] = '';
			},

		/**
		 * Returns the css vendor prefixed versions of a property. Note that it does not check if the property
		 * requires one, it merely prefixes.
		 *
		 * @param {string} property
		 * @returns {string} prefixed
		 */
		prefix: function(property) {
			return prefix + property || '';
		},

		// metrics

		/**
		 * Calculates the size of a Node, and returns an Object with x and y properties. Though not a Vector itself,
		 * the returned object is compatible with existing Vectors.
		 *
		 * @param {Node} node
		 * @returns {playtotv.css.Vector} size
		 */
		getSize: function(node) {
			return new Vector(
				node.offsetWidth,
				node.offsetHeight
			);
		},

		/**
		 * Calculates the inner size of a Node, excluding borders, and returns a Vector compatible object with x and y
		 * properties.
		 *
		 * @param {Node} node
		 * @returns {playtotv.css.Vector} size
		 */
		getInnerSize: function(node) {
			return new Vector(
				node.clientWidth,
				node.clientHeight
			);
		},

		/**
		 * Calculates the offset of a Node, optionally in respect to a parent Node, and returns an object with x and y
		 * properties. Though not a Vector itself, the returned object is compatible with existing Vectors.
		 *
		 * @param {Node} node
		 * @param {Node} parent
		 * @returns {playtotv.css.Vector} offset
		 */
		getOffset: function(node, parent) {
			var x = 0, y = 0;
			while (node && (node !== parent)) {
				x += node.offsetLeft;
				y += node.offsetTop;
				node = node.offsetParent;
			}

			return new Vector(x, y);
		},

		/**
		 * Converts a point from (local) node coordinate space to the same point in page coordinate space. This is
		 * especially helpful when nodes have css transformations applied to them like scaling and rotation.
		 * See also http://www.w3.org/TR/2009/WD-css3-2d-transforms-20090320/#dom-interfaces
		 *
		 * @function
		 * @param {Node} node
		 * @param {playtotv.css.Vector} vector
		 */
		fromNodeToPage:
			webkitConvert ?

			function(node, vector) {
				var point = new window.WebKitPoint(vector.x, vector.y);
				var converted = window.webkitConvertPointFromNodeToPage(node, point);
				return new Vector(converted.x, converted.y);
			} :

			function(node, vector) {
				var transform = this.getFullTransform(node);
				return transform.transformPoint(vector).toVector();
			},

		/**
		 * Converts a point from page coordinate space to the same point in (local) node coordinate space. This is
		 * especially helpful when nodes have css transformations applied to them like scaling and rotation.
		 * See also http://www.w3.org/TR/2009/WD-css3-2d-transforms-20090320/#dom-interfaces
		 *
		 * @function
		 * @param {Node} node
		 * @param {playtotv.css.Vector} vector
		 */
		fromPageToNode:
			webkitConvert ?

			function(node, vector) {
				var point = new window.WebKitPoint(vector.x, vector.y);
				var converted = window.webkitConvertPointFromPageToNode(node, point);
				return new Vector(converted.x, converted.y);
			} :

			function(node, vector) {
				var transform = this.getFullTransform(node);
				return transform.inverse().transformPoint(vector).toVector();
			},

		// Matrices

		/**
		 * Sets the node style's transform property (vendor specific if needed) to the given value.
		 *
		 * @param {Node} node
		 * @param {string|playtotv.css.Matrix} transform
		 */
		setTransform: function(node, transform) {
			if (transform instanceof Matrix) {
				transform = transform.toString();
			}
			this.set(node, cssTransform, transform);
		},

		/**
		 * Returns a matrix representing the current node style's transform property value.
		 *
		 * @param {Node} node
		 * @returns {playtotv.css.Matrix} transform
		 */
		getTransform: function(node) {
			var transform = this.get(node, cssTransform);
			return new Matrix(transform);
		},

		/**
		 * Removes any transforms on the given node's style.
		 *
		 * @param {Node} node
		 */
		removeTransform: function(node) {
			this.remove(node, cssTransform);
		},

		/**
		 * Returns a matrix representing the node's complete transformation (translation, scale and rotation) in
		 * respect to the document's origin. This includes any transformations inherited from ascendant nodes.
		 *
		 * @param {Node} node
		 * @returns {playtotv.css.Matrix} transformation
		 */
		getFullTransform: function(node) {
			var nodeTransform, transform = new Matrix();
			while (node) {
				nodeTransform = this.getTransform(node);
				if (nodeTransform) {
					var origin = this.getTransformOrigin(node);
					if (origin) {
						nodeTransform = new Matrix().
							translate(origin.inverse()).
							multiply(nodeTransform).
							translate(origin);
					}

					transform = transform.multiply(nodeTransform);
				}

				transform = transform.translate(
					new Vector(node.offsetLeft, node.offsetTop)
				);

				node = node.offsetParent;
			}

			return transform;
		},

		/**
		 * Returns a vector representing the transformation origin of the given node.
		 *
		 * @param {Node} node
		 * @returns {playtotv.css.Vector} origin
		 */
		getTransformOrigin: function(node) {
			var origin = (this.get(node, cssOrigin) || '0 0').split(' ');
			return Vector.parseFloat(origin[0], origin[1]);
		},

		// Vectors

		/**
		 * Sets the given node style's left and top position to the given vector.
		 *
		 * @param {Node} node
		 * @param {playtotv.css.Vector} position
		 */
		setPosition: function(node, vector) {
			var css = node.style;
			css.left = vector.x + 'px';
			css.top = vector.y + 'px';
		},

		/**
		 * Returns a vector representing the node style's left and top position.
		 *
		 * @param {Node} node
		 * @returns {playtotv.css.Vector} position
		 */
		getPosition: function(node) {
			return Vector.parseFloat(
				this.get(node, 'left'),
				this.get(node, 'top')
			);
		},

		/**
		 * Moves a node by the given vector using translate3d.
		 *
		 * @function
		 * @param {Node} node
		 * @param {playtotv.css.Vector} translate
		 */
		setTranslate: function(node, vector) {
			this.set(node, cssTransform, 'translate3d(' + vector + ',0)');
		},

		/**
		 * Returns a vector representing the current translation of the given node.
		 *
		 * @param {Node} node
		 * @returns {playtotv.css.Vector} translate
		 */
		getTranslate: function(node) {
			var transform = this.getTransform(node);
			return transform.transformPoint(Vector.ZERO).toVector();
		}
	};

	return style;
});
