playtotv.define('playtotv.model.ModelSubscription', [
	'playtotv.pubsub.Subscription'
], function(
	Subscription
) {

	'use strict';

	var util = playtotv.util;

	return new playtotv.Class({
		/** @lends playtotv.model.ModelSubscription.prototype */

		extend: Subscription,

		/**
		 * RegExp used to test validity of selector strings.
		 * @type {RegExp}
		 */
		selectorRegex: /^([\w\*]+:[\w\*\.]+(=.*)?)|\*$/,

		/**
		 * Class used to represent an {@link playtotv.model.AbstractModel} Subscription.
		 *
		 * @extends playtotv.pubsub.Subscription
		 * @constructs
		 * @param {string} selector Model selector string
		 * @param {Function} handler Subscription callback
		 * @throws {Error} If selector string is invalid
		 *
		 * @property {string|RegExp} selector Model selector string converted to channel string or RegExp
		 * @property {Function} handler Subscription callback
		 */
		initialize: function(selector, handler) {
			if (!this.selectorRegex.test(selector)) {
				throw new Error('Invalid selector string: ' + selector);
			}

			// Convert model selector into RegExp
			if (selector === '*') {
				selector = /.*/;
			} else {
				var noValue = selector.indexOf('=') === -1;
				if (noValue) {
					selector += '=';
				}
				if (noValue || selector.indexOf('*') !== -1) {
					selector = util.escapeRegEx(selector);
					selector = selector.replace(/\\\*/g, '[^:\\.=]+');
					selector = new RegExp('^' + selector);
				}
			}

			this._super(selector, handler);
		},

		/**
		 * Deliver data to handler.
		 *
		 * If subscription has been disposed then handler will no longer be called.
		 *
		 * Handler will be called with this subscription as thisArg and data.value, data.action, data.key as arguments.
		 *
		 * @param {mixed} [data] Data to deliver
		 * @param {string} channel Channel we are delivering to
		 */
		deliver: function(data/*, channel*/) {
			if (this.handler) {
				this.handler(data.value, data.action, data.key);
			}
		}
	});
});
