playtotv.define('playtotv.user.UserManager', [
	'playtotv.pubsub.PubSub',
	'playtotv.user.LocalUser',
	'playtotv.user.usersAPI'
], function(
	PubSub,
	LocalUser,
	usersAPI
) {

	'use strict';

	// TODO: Check that is alright that we no longer bother about X-PlayToTV-Session header
	// TODO: Add: get(id) // get user from cache or server

	var ptv = playtotv;
	var REQUIRED = ptv.REQUIRED;

	return new ptv.Class({
		/** @lends playtotv.user.UserManager.prototype */

		extend: PubSub,

		/**
		 * User manager class.
		 *
		 * <ul>
		 * <li>Facilitates making registration, login and logout server calls.
		 * <li>Will instantiate and keep track of a {@link playtotv.user.LocalUser} instance reference.
		 * <li>Can also keep track of authentication token and method using {@link playtotv.server.Server#storage}.
		 * <li>Will deal with user session expiry (e.g: status 463)
		 * </ul>
		 *
		 * @extends playtotv.pubsub.PubSub
		 * @constructs
		 * @param {playtotv.server.Server} server Server
		 *
		 * @property {playtotv.server.Server} server Server
		 * @property {playtotv.storage.Storage} storage Storage
		 * @property {string} authenticationMethod Persisted last used authentication method
		 * @property {string} authenticationToken Persisted authentication token
		 * @property {playtotv.user.LocalUser} localUser Local user
		 */
		initialize: function(server) {
			this._super();

			this.server = server;
			var storage = this.storage = server.storage;
			this.authenticationMethod = storage.get('authenticationMethod');
			this.authenticationToken = storage.get('authenticationToken');
			this.localUser = null;

			server.rejectTaps.push(this.onServerReject);
		},

		/**
		 * Save authentication method and token and persist them in storage.
		 *
		 * @param {string} method Authentication method
		 * @param {string} token Authentication token
		 */
		saveAuthentication: function(method, token) {
			this.authenticationMethod = method;
			this.storage.set('authenticationMethod', method);
			this.authenticationToken = token;
			this.storage.set('authenticationToken', token);
		},

		/**
		 * Login with specified server method and params and then:
		 * <ul>
		 * <li>If we already have a local user reference then call {@link playtotv.user.UserManager#onLogout}
		 * <li>Create a new LocalUser instance using the returned data and change local user reference to it
		 * <li>Save and persist local user authentication token if remember is true
		 * <li>Publish local user reference to 'login' channel
		 * </ul>
		 *
		 * @param {Object} method Server call method
		 * @param {Object} [params] Server call params
		 * @param {boolean} [remember=false] Whether to save and persist authentication token for future logins
		 * @returns {Promise} Promise of logged in {@link playtotv.user.LocalUser}
		 */
		login: function(method, params, remember) {
			return this.server.call(method, params).then(function(data) {
				var token = data.authenticationToken;
				var profile = data.profile || {};
				var credentials = data.loginCredentialStatus || {};

				if (this.localUser) {
					this.onLogout();
				}

				var localUser = new LocalUser(this.server, {
					id: data.userId,
					displayName: data.displayName,
					avatarUrl: data.avatarUrl,
					profileProperties: profile.profileProperties
				}, {
					privateProperties: profile.privateProperties,
					authenticationToken: token,
					pttvSession: data.pttvSession,
					email: credentials.email,
					username: credentials.username,
					facebookId: credentials.facebookId
				});
				this.localUser = localUser;

				if (remember) {
					this.saveAuthentication(method.label, token);
				}

				this.publish('login', localUser);

				return localUser;
			}.bind(this));
		},

		/**
		 * Base server login and register call params.
		 * @type {Object}
		 */
		baseParams: {
			displayName: null, // data key, so required, but not really required by server, so null
			avatarUrl: null, // data key, so required, but not really required by server, so null
			runtimeId: undefined
		},

		/**
		 * Optional server register call params.
		 * @type {Object}
		 */
		optionalParams: {
			teamTag: undefined,
			emailOptinMode: undefined,
			properties: undefined,
			privateProperties: undefined
		},

		/**
		 * Moves teamTag, emailOptinMode, properties and privateProperties into optionalValues sub params to match
		 * request params structure expected by server.
		 *
		 * @param {Object} params Server register call params
		 * @returns {Object} Server register call params with nested optionalValues
		 */
		processParams: function(params) {
			var optionalKeys = Object.keys(this.optionalParams);
			params.optionalValues = ptv.pick(params, optionalKeys);
			return ptv.omit(params, optionalKeys);
		},

		/**
		 * Shortcut to {@link playtotv.user.UserManager#login} with
		 * {@link playtotv.user.usersAPI.registerAsGuest}.
		 *
		 * @param {Object} [params] Parameters
		 * @param {string} [params.displayName] Display name
		 * @param {string} [params.avatarUrl] Avatar URL
		 * @param {string} [params.runtimeId] Runtime ID
		 * @param {string} [params.teamTag] Team tag
		 * @param {string} [params.emailOptinMode] Email opt-in mode (UNDECIDED/OPTED_IN/OPTED_OUT)
		 * @param {Object} [params.properties] Profile properties
		 * @param {Object} [params.privateProperties] Private properties
		 * @param {boolean} [remember=false] Whether to save and persist authentication token for future logins
		 * @returns {Promise} Promise of logged in {@link playtotv.user.LocalUser}
		 */
		registerAsGuest: function(params, remember) {
			params = ptv.params(params, ptv.extend({}, this.baseParams, this.optionalParams));
			params = this.processParams(params);
			return this.login(usersAPI.registerAsGuest, params, remember);
		},

		/**
		 * Shortcut to {@link playtotv.user.UserManager#login} with
		 * {@link playtotv.user.usersAPI.registerByEmail}.
		 *
		 * @param {Object} params Parameters
		 * @param {string} params.email Email
		 * @param {string} params.password Password
		 * @param {string} [params.username] Username
		 * @param {string} [params.displayName] Display name
		 * @param {string} [params.avatarUrl] Avatar URL
		 * @param {string} [params.runtimeId] Runtime ID
		 * @param {string} [params.teamTag] Team tag
		 * @param {string} [params.emailOptinMode] Email opt-in mode (UNDECIDED/OPTED_IN/OPTED_OUT)
		 * @param {Object} [params.properties] Profile properties
		 * @param {Object} [params.privateProperties] Private properties
		 * @param {boolean} [remember=false] Whether to save and persist authentication token for future logins
		 * @returns {Promise} Promise of logged in {@link playtotv.user.LocalUser}
		 */
		registerByEmail: function(params, remember) {
			params = ptv.params(params, ptv.extend({
				email: REQUIRED,
				password: REQUIRED,
				username: null // data key, so required, but not really required by server, so null
			}, this.baseParams, this.optionalParams));
			params = this.processParams(params);
			return this.login(usersAPI.registerByEmail, params, remember);
		},

		/**
		 * Shortcut to {@link playtotv.user.UserManager#login} with
		 * {@link playtotv.user.usersAPI.loginByEmail}.
		 *
		 * @param {Object} params Parameters
		 * @param {string} params.email Email
		 * @param {string} params.password Password
		 * @param {boolean} [params.rememberMe=false] Cookie based remember-me (token is recommended instead)
		 * @param {string} [params.displayName] Display name
		 * @param {string} [params.avatarUrl] Avatar URL
		 * @param {string} [params.runtimeId] Runtime ID
		 * @param {boolean} [remember=false] Whether to save and persist authentication token for future logins
		 * @returns {Promise} Promise of logged in {@link playtotv.user.LocalUser}
		 */
		loginByEmail: function(params, remember) {
			params = ptv.params(params, ptv.extend({
				email: REQUIRED,
				password: REQUIRED,
				rememberMe: false
			}, this.baseParams));
			return this.login(usersAPI.loginByEmail, params, remember);
		},

		/**
		 * Shortcut to {@link playtotv.user.UserManager#login} with
		 * {@link playtotv.user.usersAPI.loginByFacebook}.
		 *
		 * @param {Object} params Parameters
		 * @param {string} params.facebookId Facebook ID
		 * @param {string} params.fbsr Facebook signed request token
		 * @param {string} [params.displayName] Display name
		 * @param {string} [params.avatarUrl] Avatar URL
		 * @param {string} [params.runtimeId] Runtime ID
		 * @param {string} [params.teamTag] Team tag
		 * @param {string} [params.emailOptinMode] Email opt-in mode (UNDECIDED/OPTED_IN/OPTED_OUT)
		 * @param {Object} [params.properties] Profile properties
		 * @param {Object} [params.privateProperties] Private properties
		 * @param {boolean} [remember=false] Whether to save and persist authentication token for future logins
		 * @returns {Promise} Promise of logged in {@link playtotv.user.LocalUser}
		 */
		loginByFacebook: function(params, remember) {
			params = ptv.params(params, ptv.extend({
				facebookId: REQUIRED,
				fbsr: REQUIRED
			}, this.baseParams, this.optionalParams));
			params = this.processParams(params);
			return this.login(usersAPI.loginByFacebook, params, remember);
		},

		/**
		 * Call {@link playtotv.user.usersAPI.addFacebookCredentials} on server.
		 *
		 * This will reject directly without making a server call in case this.localUser is undefined or in case
		 * this.localUser.facebookId is already defined.
		 *
		 * If the call succeeds this.localUser's facebookId property is changed to specified facebookId.
		 *
		 * @param {Object} params Parameters
		 * @param {string} params.facebookId Facebook ID
		 * @param {string} params.fbsr Facebook signed request token
		 * @returns {Promise} Promise of {@link playtotv.user.LocalUser}
		 */
		addFacebookCredentials: function(params) {
			params = ptv.params(params, {
				facebookId: REQUIRED,
				fbsr: REQUIRED
			});
			var localUser = this.localUser;
			if (!localUser) {
				return Promise.reject(new Error('Not logged in'));
			}
			if (localUser.facebookId) {
				return Promise.reject(new Error('Local user already has Facebook Id'));
			}
			return this.server.call(usersAPI.addFacebookCredentials, params).then(function() {
				localUser.changeProperty('facebookId', params.facebookId);
				return localUser;
			});
		},

		/**
		 * Call {@link playtotv.user.usersAPI.removeFacebookCredentials} on server.
		 *
		 * This will reject directly without making a server call in case this.localUser or this.localUser.facebookId
		 * is undefined.
		 *
		 * If the call succeeds this.localUser's facebookId property is changed to undefined.
		 *
		 * @returns {Promise} Promise of {@link playtotv.user.LocalUser}
		 */
		removeFacebookCredentials: function() {
			var localUser = this.localUser;
			if (!localUser) {
				return Promise.reject(new Error('Not logged in'));
			}
			if (!localUser.facebookId) {
				return Promise.reject(new Error('Local user does not have Facebook Id'));
			}
			return this.server.call(usersAPI.removeFacebookCredentials).then(function() {
				localUser.changeProperty('facebookId', undefined);
			});
		},

		/**
		 * Shortcut to {@link playtotv.user.UserManager#login} with
		 * {@link playtotv.user.usersAPI.loginByAuthenticationToken}.
		 *
		 * If params.authenticationToken and this.authenticationToken are both undefined this will reject directly
		 * without making a server call.
		 *
		 * @param {Object} [params] Parameters
		 * @param {string} [params.authenticationToken=this.authenticationToken] Authentication token
		 * @param {boolean} [params.rememberMe=false] Cookie based remember-me (token is recommended instead)
		 * @param {string} [params.displayName] Display name
		 * @param {string} [params.avatarUrl] Avatar URL
		 * @param {string} [params.runtimeId] Runtime ID
		 * @returns {Promise} Promise of logged in {@link playtotv.user.LocalUser}
		 */
		loginByToken: function(params) {
			params = ptv.params(params, ptv.extend({
				authenticationToken: this.authenticationToken
			}, this.baseParams));
			return params.authenticationToken ?
				this.login(usersAPI.loginByAuthenticationToken, params) :
				Promise.reject(new Error('Missing required parameter: authenticationToken'));
		},

		/**
		 * Logout from server and optionally remove local user authentication token from instance and storage.
		 *
		 * @param {boolean} [forget=false] Whether to remove authentication token that was stored for future logins
		 * @returns {Promise} Promise of logged out {@link playtotv.user.LocalUser}
		 */
		logout: function(forget) {
			if (forget) {
				this.saveAuthentication(null, null);
			}
			return this.server.call(usersAPI.logout).then(function() {
				return this.onLogout();
			}.bind(this));
		},

		/**
		 * Calls this.localUser.dispose and publishes localUser to 'logout' channel.
		 *
		 * If this.localUser is null then this simply returns null.
		 *
		 * @returns {playtotv.user.LocalUser} Logged out local user
		 */
		onLogout: function() {
			var localUser = this.localUser;
			if (localUser) {
				localUser.dispose();
				this.localUser = null;
				this.publish('logout', localUser);
			}
			return localUser;
		},

		/**
		 * User session expiry handler.
		 *
		 * <ul>
		 * <li>Changes this.authenticationMethod and authenticationToken to null
		 * <li>If this.localUser is defined it is published to 'expire'
		 * <li>Calls {@link playtotv.user.UserManager#onLogout}
		 * </ul>
		 */
		onExpire: function() {
			var localUser = this.localUser;
			this.authenticationMethod = null;
			this.authenticationToken = null;
			if (localUser) {
				this.publish('expire', localUser);
				this.onLogout();
			}
		},

		/**
		 * Server rejection tap handler.
		 *
		 * Calls {@link playtotv.user.UserManager#onExpire} if reason.status equals 463.
		 *
		 * TODO: Also facilitate handling of 401 and 403
		 */
		onServerReject: function(reason) {
			if (reason.status === 463) {
				this.onExpire();
			}
		},

		/**
		 * Dispose.
		 */
		dispose: function() {
			var rejectTaps = this.server.rejectTaps;
			rejectTaps.splice(rejectTaps.indexOf(this.onServerReject), 1);

			var localUser = this.localUser;
			if (localUser) {
				localUser.dispose();
			}

			PubSub.prototype.dispose.call(this);
		},

		callbacks: [
			'onServerReject'
		]
	});
});
