playtotv.define('playtotv.touch.TouchProxy', [
	'playtotv.touch.Tracker'
], function(
	Tracker
) {

	'use strict';

	var event = playtotv.event;
	var dom = playtotv.dom;

	var inputReg = /input|textarea|select|option/i;
	var CLICK_DELAY = 500;

	var nativeTouch = 'ontouchstart' in window;
	var nativePointers = 'onpointerdown' in window;
	var maxPointers = navigator.maxTouchPoints || navigator.msMaxTouchPoints || 0;

	// determines whether triggering a click on touchend is needed at all
	function needsFastClick(node) {
		/*jshint maxcomplexity:20*/

		// only touch enabled devices may have a click delay
		if (!nativeTouch && (!nativePointers || (maxPointers === 0))) {
			return false;
		}

		var ua = navigator.userAgent;
		var winMobile = /Windows Phone/.test(ua);
		var iOs = /iP(hone|ad|od)/.test(ua) && !winMobile;

		// iOs never disables the 300ms delay due to double-tap scrolling
		if (iOs) {
			return true;
		}

		var android = /Android/.test(ua) && !winMobile;
		var chrome = /Chrome\/([0-9]+)/.test(ua);
		var firefox = /Firefox\/([0-9]+)/.test(ua);

		// chrome and firefox disable the 300ms delay when user scaling is disabled
		if ((chrome && android) || firefox) {
			var viewMeta = dom.first('meta[name=viewport]');
			if (viewMeta && /user-scalable=no/.test(viewMeta.content)) {
				return false;
			}
		}

		// IE disables the delay if touchAction is set to none or manipulation
		var style = node.currentStyle || node.style;
		var touchAction = style ? (style.touchAction || style.msTouchAction) : null;
		if (touchAction && /none|manipulation/.test(touchAction)) {
			return false;
		}

		// default fastclick to true on otherwise unknown touch-enabled devices
		return true;
	}

	/**
	 * Touch event proxy, fires custom em:touch[type] events by listening to native touch or mouse events, depending
	 * on the device it is running on. Check {@link playtotv.touch.touchManager#captureEvents} on how to manage
	 * TouchProxy instances and how to use the events fired by them.
	 *
	 * @name TouchProxy
	 * @class
	 * @memberOf playtotv.touch
	 * @constructor
	 */
	var TouchProxy = new playtotv.Class({

		initialize: function(node, options) {
			options = playtotv.params(options, {
				fastClick: true,
				preventNative: true,
				propagateNative: false
			});

			this.node = node;
			this.fastClick = options.fastClick && needsFastClick(node);
			this.preventNative = options.preventNative;
			this.propagateNative = options.propagateNative;

			this.startEvent = nativePointers ? 'pointerdown' : nativeTouch ? 'touchstart' : 'mousedown';
			this.moveEvent = nativePointers ? 'pointermove' : nativeTouch ? 'touchmove' : 'mousemove';
			this.endEvent = nativePointers ? 'pointerup' : nativeTouch ? 'touchend' : 'mouseup';

			this.tracker = new Tracker();

			event.addListener(node, this.startEvent, this.touchStart);
		},

		/** @lends playtotv.touch.TouchProxy.prototype */

		/**
		 * Touchstart handler, fires a 'em:touchstart' event, and binds move and release event handlers for the rest of
		 * the touch (or mouse) sequence.
		 *
		 * @param {Event} e
		 */
		touchStart: function(e) {
			var target = e.target;

			// ignore touchstart on form elements; would make entering values impossible
			// TODO: enable as class options to include elements like video, object, etc
			if (inputReg.test(target.nodeName)) {
				return;
			}

			var changed = this.getChangedTouches(e);
			var added = this.tracker.add(changed, target);
			var prevent = false;

			// added.length is > 1 only if two pointers entered the same node in the same callstack, which is unlikely,
			// but possible. Separate native events are fired if the target differs. For unified behavior, custom
			// events are always fired separately.
			var i, l = added.length;
			for (i = 0; i < l; i++) {
				if (this.dispatchEvent('em:touchstart', added[i], e) === false) {
					prevent = true;
				}
			}

			// stop native event behaviour for custom touch enabled elements if allowed and requested
			if (this.preventNative && prevent) {
				e.preventDefault();
			}

			// stop propagation of native event unless explicitly allowed
			if (!this.propagateNative) {
				e.stopPropagation();
			}

			// don't add more listeners when a second finger touches an already tracked node
			if (!this.tracking) {
				this.bindEvents();
			}
		},

		/**
		 * Internal touchmove handler. Fires an 'em:touchmove' event for each individual moving pointer. Handlers that
		 * wish to update the DOM with coordinates derived from this event should do so using interval based callbacks
		 * like {@link playtotv.animationFrame} to minimize redraws.
		 *
		 * @param {Event} e
		 */
		touchMove: function(e) {
			var changed = this.getChangedTouches(e);
			var touches = this.tracker.update(changed);
			var prevent = false;

			// The native touches.length is only > 1 if two or more pointers move within the same target. Separate
			// native events are fired otherwise. The custom events are always fired separately, because multi pointer
			// event handling is just as likely to be needed on complex dom fragments, in which case separate events
			// would natively be fired as well.
			var i, l = touches.length;
			for (i = 0; i < l; i++) {
				if (this.dispatchEvent('em:touchmove', touches[i], e) === false) {
					prevent = true;
				}
			}

			if (this.preventNative && prevent) {
				e.preventDefault();
			}

			if (!this.propagateNative) {
				e.stopPropagation();
			}
		},

		/**
		 * Checks the event sequence and fires a generic em:touchend event, followed by either an em:swipe, or a native
		 * click event.
		 *
		 * @param {Event} e
		 */
		touchEnd: function(e) {
			/*jshint maxcomplexity:20*/

			var changed = this.getChangedTouches(e);
			var removed = this.tracker.remove(changed);

			var preventNative = this.preventNative;
			var prevent = false;

			var touch, i, l = removed.length;
			for (i = 0; i < l; i++) {
				touch = removed[i];

				// default, always: fire em:touchend
				if (this.dispatchEvent('em:touchend', touch, e) === false) {
					prevent = true;
				}

				// trigger a faster click than a native one, which may be delayed for double-tap functionality on
				// various touch enabled devices. This delay can otherwise take up to 350ms, which is undesirable.
				if (touch.isTap && this.fastClick) {

					var click = new event.MouseEvent('click', {
						bubbles: true,
						cancelable: true,
						clientX: touch.point.x,
						clientY: touch.point.y,
						screenX: touch.screen.x,
						screenY: touch.screen.y
					});

					// mark as custom for telling clicks apart
					click.isFastClick = true;

					touch.target.dispatchEvent(click);

					// Prevent any following native clicks if fired.
					this.preventNextClick(touch.point);
				}

				// within swipe timeframe, trigger a swipe
				if (touch.isSwipe) {
					this.dispatchEvent('em:swipe', touch, e);
				}
			}

			if (preventNative && prevent) {
				e.preventDefault();
			}

			if (!this.propagateNative) {
				e.stopPropagation();
			}

			// unbind only after all pointers/fingers left the node
			if (this.tracker.empty()) {
				this.unbindEvents();
			}
		},

		/**
		 * Dispatches a touch event of the given type via event.dispatchEvent, automatically called by the proxy.
		 * The event's detail property contains a reference to the proxy that dispatched it, the touch or mouse
		 * sequence, and the native event from which it originated.
		 *
		 * @param {string} type
		 * @param {playtotv.touch.Sequence} sequence
		 * @param {Event} e
		 */
		dispatchEvent: function(type, sequence, e) {
			return event.dispatchEvent(sequence.target, type, true, true, {
				proxy: this,
				sequence: sequence,
				nativeEvent: e
			});
		},

		/**
		 * Binds move and end events
		 * @private
		 */
		bindEvents: function() {
			if (this.tracking) {
				return;
			}

			event.addListener(document, this.moveEvent, this.touchMove);
			event.addListener(document, this.endEvent, this.touchEnd);
			this.tracking = true;
		},

		/**
		 * Unbinds move and end events
		 * @private
		 */
		unbindEvents: function() {
			if (!this.tracking) {
				return;
			}

			event.removeListener(document, this.moveEvent, this.touchMove);
			event.removeListener(document, this.endEvent, this.touchEnd);
			this.tracking = false;
		},

		/**
		 * Returns the native touches list of the given event, or the event itself as only array value on non-touch
		 * devices, effectively mimicking e.touches.
		 * See https://developer.mozilla.org/en-US/docs/Web/API/TouchEvent.touches
		 *
		 * @param {Event} e
		 * @returns {TouchList|Array} touches
		 */
		getTouches: function(e) {
			return e.touches || [ e ];
		},

		/**
		 * Returns the native changedTouches list of the given event, or the event itself as only array value on
		 * non-touch devices, effectively mimicking the e.changedTouches array.
		 * See https://developer.mozilla.org/en-US/docs/Web/API/TouchEvent.changedTouches
		 *
		 * @param {Event} e
		 * @returns {TouchList|Array} changedTouches
		 */
		getChangedTouches: function(e) {
			return e.changedTouches || [ e ];
		},

		/**
		 * Returns the {@link playtotv.touch.Sequence} that tracks the currently active mouse or touch pointer that is
		 * associated with te given identifier (see https://developer.mozilla.org/en-US/docs/Web/API/Touch.identifier).
		 *
		 * @param {String} identifier
		 * @returns {playtotv.touch.Sequence} sequence
		 */
		getSequence: function(id) {
			return this.tracker.map[id];
		},

		/**
		 * Returns an array of all sequences of currently active mouse or touch pointers.
		 *
		 * @returns {Array} sequences
		 */
		getSequences: function() {
			return this.tracker.toArray();
		},

		/**
		 * Calls preventDefault in the capture phase of any click events that are fired both during ghost time and in
		 * close proximity to the given point. This method is called by the proxy's touchEnd method after a faster
		 * click is triggered on mobile devices, and prevents double click events from firing when a native click does
		 * follow the triggered one.
		 *
		 * @param {playtotv.css.Vector} point
		 */
		preventNextClick: function(point) {
			var nextClick = function(e) {
				var dx = e.clientX - point.x;
				var dy = e.clientY - point.y;
				if (!e.isFastClick && Math.sqrt((dx * dx) + (dy * dy)) < 20) {
					e.preventDefault();
					e.stopPropagation();
				}
			};

			var node = this.node;
			node.addEventListener('click', nextClick, true);
			setTimeout(function() {
				node.removeEventListener('click', nextClick, true);
			}, CLICK_DELAY);
		},

		/**
		 * Unbinds all events and deletes the node reference. Call this method before the page unloads to prevent
		 * any possible memory leaks.
		 */
		dispose: function() {
			this.unbindEvents();
			event.removeListener(this.node, this.startEvent, this.touchStart);
			delete this.node;
		},

		callbacks: [
			'touchStart',
			'touchMove',
			'touchEnd'
		]
	});

	return TouchProxy;
});
