playtotv.define('playtotv.fx.Transition', [
	'playtotv.fx.Animation',
	'playtotv.fx.easing'
], function(
	Animation,
	easing
) {

	'use strict';

	var transitionOptions = [ 'callback', 'easing', 'from', 'to' ];

	/**
	 * Creates a transition that runs a requestAnimationFrame based callback over a given duration when played, passing
	 * the current value between the from and to option values, based on the elapsed time since the transition started.
	 *
	 * @name Transition
	 * @class
	 * @memberOf playtotv.fx
	 * @extends playtotv.fx.Animation
	 * @param {Object} options
	 * @param {boolean} [options.alternate=false] Whether a transition with multiple cycles alternates its direction
	 * @param {Function} options.callback Callback that is called each frame with the current transtion value
	 * @param {number} [options.cycles=1] Amount of animation cycles
	 * @param {number} [options.duration=1000] Transition duration in ms
	 * @param {string} [options.easing='linear'] callback value easing, see {@link playtotv.fx.easing}
	 * @param {number} [options.fps=0] Frames per second cap, 0 for no cap
	 * @param {number} [options.from=0] Starting value
	 * @param {number} [options.rate=1] Time rate multiplier
	 * @param {number} [options.start] Alternate start timestamp in ms; past starts mid-animation, future delays start
	 * @param {number} [options.to=1] End value
	 */
	var Transition = new playtotv.Class({
		extend: Animation,

		initialize: function(options) {
			options = playtotv.params(options, {
				alternate: false,
				callback: playtotv.REQUIRED,
				cycles: 1,
				duration: playtotv.REQUIRED,
				easing: 'linear',
				fps: 0,
				from: 0,
				rate: 1,
				start: 0,
				to: 1
			});

			this._super(playtotv.omit(options, transitionOptions));
			this.settings = options;
		},

		/** @lends playtotv.fx.Transition.prototype */

		/**
		 * Calculates the current transition value based on the given animation fraction and any easing, and passes
		 * this value to the transition's callback. This function is called via requestAnimationFrame (and may be
		 * throttled further with the fps option) for optimized rendering performance.
		 *
		 * @param {number} fraction
		 */
		onFrame: function(n) {
			var animation = this.animation || this.settings;
			var ease = this.ease || (this.ease = easing.get(animation.easing));

			animation.callback(
				animation.from + ((animation.to - animation.from) * ease(n))
			);
		}
	});

	return Transition;
});
