playtotv.define('playtotv.native.MockNativeController', [
	'playtotv.facebook.Facebook',
	'playtotv.native.NativeController',
	'playtotv.net.request'
], function(
	Facebook,
	NativeController,
	request
) {

	'use strict';

	var ptv = playtotv;
	var dom = ptv.dom;
	var util = ptv.util;
	var log = ptv.Logger.get(this.id);

	function getMatch(str, regex, idx) {
		var matches = str.match(regex);
		return matches && matches[idx || 0];
	}

	return class MockNativeController extends NativeController {
		constructor(context, options) {
			ptv.params(options, {
				serverUrl: ptv.REQUIRED,
				facebook: null
				// TODO: Add optional web based support for MixPanel?
			});
			var serverUrl = options.serverUrl;

			super(context);

			this.audioNodeMap = {};

			if (options.facebook) {
				this.facebook = new Facebook(options.facebook);
			}

			var wrapper = window.nativeWrapper;
			ptv.extend(wrapper, this.getDevice(), {
				name: document.title,
				version: '1.0-mock',
				capabilities: [],
				pushEnabled: false,
				serverUrl: serverUrl,
				sendMessage: this.sendMessage.bind(this)
			});

			this.publish('Event.BridgeReady');

			var requestUrl = (/\/\//.test(serverUrl) ? '' : '//') + serverUrl;
			var gatekeeperDataPromise = serverUrl === 'mock' ?
				Promise.resolve({ serverTime: Date.now(), status: 'AVAILABLE', services: [] }) :
				request(requestUrl + '/api/1/gatekeeper/init', { dataType: 'json', cache: false });

			gatekeeperDataPromise.then(data => {
				data = { gatekeeperResponse: data, gatekeeperTimestamp: Date.now() };
				ptv.extend(wrapper, data);
				this.mockMessage({ type: 'Event', subject: 'GatekeeperReady', payload: data });
			});

			this.initLoading();
		}

		initLoading() {
			var loadingNode = dom.create('div', {
				className: 'native-loading'
			}, {
				text: 'Loading..',
				appendTo: document.body
			});

			ptv.extend(loadingNode.style, {
				position: 'absolute',
				top: 0, left: 0, bottom: 0, right: 0,
				background: 'rgba(255, 255, 255, 0.9)', color: '#000',
				'text-align': 'center', 'padding-top': '150px',
				'z-index': 99999
			});

			this.loadingNode = loadingNode;
		}

		getDevice() {
			var ua = navigator.userAgent;
			return ptv.extend({
				deviceName: 'Mock Device',
				deviceModel: ua
			}, /android/i.test(ua) ? {
				os: 'Android',
				osVersion: getMatch(ua, /android ([\d\.]+)/i, 1) || '4.4'
			} : /ip(ad|hone)/i.test(ua) ? {
				os: 'iOS',
				osVersion: getMatch(ua, /OS (\d)_\d/, 1) || '8'
			} : {
				os: 'Browser',
				osVersion: '1.0'
			});
		}

		mockMessage(message) {
			Promise.resolve().then(() => window.nativeWrapper.onMessage(message));
		}

		sendMessage(message) {
			if (message.type === 'Command') {
				var subject = message.subject;
				var handler = this['onCommand' + subject];
				var result = {
					type: 'CommandResult',
					subject: subject,
					commandId: message.commandId
				};

				if (handler) {
					Promise.resolve(handler.call(this, message.payload)).then(payload => {
						this.mockMessage(ptv.extend(result, { payload: payload }));
					}, error => {
						this.mockMessage(ptv.extend(result, { error: error }));
					});
				} else {
					// Let's just be lenient and log instead of rejecting
					/*
					this.mockMessage(ptv.extend(result, {
						error: {
							code: 'UnsupportedCommand',
							message: `Command ${subject} is not supported`
						}
					}));
					*/
					log.w('UnsupportedCommand', subject, message.payload);
				}
			}
			return Promise.resolve();
		}

		popup(url, target, options) {
			var { width, height } = ptv.params(options, { width: 0, height: 0 });
			return util.popup(url, target, { width: width, height: height });
		}

		onCommandHideLoading() {
			dom.remove(this.loadingNode);
		}

		onCommandOpenDialog(payload) {
			// Not a great mock; this blocks JS and does not really support payload.buttons (assumes first is OK)
			var buttons = payload.buttons;
			var text = payload.title + '
' + payload.message + '
' + '[' + buttons.join('], [') + ']';
			var ok = buttons.length > 1 ? window.confirm(text) : window.alert(text) || true;
			return { pressed: buttons[ok ? 0 : 1] };
		}

		onCommandSetPushEnabled(payload) {
			return payload.value && Math.random() > 0.8 ? Promise.reject({}) : {};
		}

		/* TWITTER */

		onCommandIsTwitterAccountAvailable() {
			// This can only be available on iOS, so let's simply mock that it is always available there
			return this.wrapperPromise.then(wrapper => {
				return { available: wrapper.os === 'iOS' };
			});
		}

		/* FACEBOOK */

		onCommandFacebookLogin(options) {
			if (this.facebook) {
				// TODO: Add permissions support
				return this.facebook.login(options.interactive).then(() => {
					return {
						token: this.facebook.accessToken
					};
				});
			}

			// Rudimentary mock for when we have no Facebook (JS SDK based) instance
			return {
				token: 'mock-facebook-token'
			};
		}

		onCommandFacebookLogout() {
			if (this.facebook) {
				return this.facebook.logout();
			}
		}

		onCommandFacebookGraphRequest(options) {
			/*jshint camelcase:false*/
			/*jscs:disable requireCamelCaseOrUpperCaseIdentifiers*/

			if (this.facebook) {
				return this.facebook.api(5000, options.path, options.httpMethod, options.parameters);
			}

			// Rudimentary mock for when we have no Facebook (JS SDK based) instance
			var { path } = options;
			var friends = {
				data: [
					{ name: 'Peter Ex Machina', id: '100002151356216' }
				],
				paging: { next: '' },
				summary: { total_count: 1 }
			};
			return {
				me: {
					id: 'mock-facebook-id',
					permissions: { data: [
						{ permission: 'public_profile', status: 'granted' },
						{ permission: 'user_friends', status: 'granted' }
					] },
					first_name: 'Peter Paul',
					last_name: 'Ex Machina',
					name: 'Peter Paul Ex Machina',
					picture: { data: { url: '//graph.facebook.com/100001721575332/picture' } },
					friends: friends
				},
				'me/friends': friends
			}[path] || Promise.reject(new Error('No mock response for FacebookGraphRequest path: ' + path));
		}

		onCommandFacebookShare(options) {
			/*jshint camelcase:false*/
			/*jscs:disable requireCamelCaseOrUpperCaseIdentifiers*/

			var params = {
				link: options.contentURL,
				name: options.contentTitle,
				description: options.contentDescription,
				picture: options.imageURL
			};

			// Prune empty params
			Object.keys(params).forEach(key => {
				if (!params[key]) {
					delete params[key];
				}
			});

			if (this.facebook) {
				return this.facebook.postByDialog(params);
			}

			window.location = util.addURLParams('https://www.facebook.com/dialog/feed', ptv.extend({
				app_id: '160288680666821', // PlayToTV (works from *.playtotv.com)
				redirect_uri: window.location.href
			}, params));
		}

		/* AUDIO */

		onCommandLoadAudio(payload) {
			var url = payload.url;
			var node = document.createElement('audio');
			node.src = url;
			node.load();
			this.audioNodeMap[url] = node;
		}

		onCommandPlayAudio(payload) {
			var url = payload.url;
			var node = this.audioNodeMap[url];
			node.play();
		}

		onCommandStopAudio(payload) {
			var url = payload.url;
			var node = this.audioNodeMap[url];
			node.stop();
		}
	};
});
