playtotv.define('playtotv.server.MockServer', [
	'playtotv.server.gatekeeperAPI',
	'playtotv.server.Server',
	'playtotv.net.request'
], function(
	gatekeeperAPI,
	Server,
	request
) {

	'use strict';

	return new playtotv.Class({
		/** @lends playtotv.server.MockServer.prototype */

		extend: Server,

		/**
		 * PlayToTV MockServer Class, for mocking server calls.
		 *
		 * Calls will be handled by specified mocks if defined.
		 * If a mock is a function then this will be called with the call arguments and returned as a Promise.
		 * Else a Promise resolved with the mock value will be returned.
		 *
		 * If a string Promise would be returned, the call will recurse using that string as method label instead.
		 *
		 * If no mock is defined a request will be made for JSON file: "<base>/<label>.json".
		 * Then if a render function is specified (e.g. {@link playtotv.mustache.render}) the JSON file content is
		 * rendered as a template using the method params as view data before it is parsed as JSON, e.g:
		 * <code>"pollId" : "{{pollId}}"</code>
		 *
		 * If the JSON request is rejected then the specified default resolve/reject value Promise will be returned.
		 *
		 * A default function is provided for the mock gatekeeper call (gatekeeperAPI.getInit).
		 *
		 * @extends playtotv.server.Server
		 * @constructs
		 * @param {Object} [options]
		 * @param {Object} [options.mocks={}] Map of method labels to mock functions or value promises
		 * @param {string} [options.base='mock'] Base URL to fetch JSON files with mock responses from
		 * @param {Promise|mixed} [options.value={isDefaultValue: true}] Default Promise or resolve value
		 * @param {Function} [render=null] Function for rendering response template file to JSON
		 */
		initialize: function(options) {
			options = playtotv.params(options, {
				mocks: {},
				base: 'mock',
				value: { // this default resolve value can facilitate some simple requests
					isDefaultValue: true
				},
				render: null
			});

			var mockMap = this.mockMap = options.mocks;
			this.baseUrl = options.base;
			this.defaultValue = options.value;
			this.render = options.render;

			// Some default mock functions
			var gatekeeperLabel = gatekeeperAPI.getInit.label;
			var pushPollLabel = 'pushPoll.getEvents'; // See: playtotv.push.EventsPoller
			mockMap[gatekeeperLabel] = mockMap[gatekeeperLabel] || this.mockGatekeeper;
			mockMap[pushPollLabel] = mockMap[pushPollLabel] || this.mockPushPoll;

			this._super();
		},

		/**
		 * Mocking override of {@link playtotv.server.Server#call}
		 */
		call: function(method, params, settings) {
			playtotv.params(method, {
				label: undefined,
				service: 'API',
				gatekeeper: false,
				type: 'GET',
				url: playtotv.REQUIRED,
				queryKeys: [],
				dataKeys: []
			});
			params = params || {};
			settings = playtotv.extend({
				label: method.label,
				type: method.type
			}, settings);

			this.validateCall(method, params);

			var label = method.label || method.url.replace(/[\{\}]/g, '_') + (method.type ? '/' + method.type : '');
			var mock = this.mockMap[label];
			if (mock) {
				if (mock instanceof Function) {
					mock = mock(method, params, settings);
				}
				return this.tap(method, Promise.resolve(mock).then(function(value) {
					return typeof value === 'string' ? this.call({ label: value }, params, settings) : value;
				}.bind(this)));
			}

			var url = this.baseUrl + '/' + label + '.json';
			var render = this.render;
			return this.tap(method, request(url).then(function(template) {
				var view = playtotv.extend({ method: method }, params);
				var json = render ? render(template, view) : template;
				return JSON.parse(json);
			}, function(/* reason */) {
				return this.defaultValue;
			}.bind(this)));
		},

		mockGatekeeper: function() {
			return {
				serverTime: Date.now(),
				status: 'AVAILABLE',
				services: []
			};
		},

		mockPushPoll: function() {
			// Delay to prevent EventsPoller from going nuts
			return Promise.delay(10000).then(function() {
				return {
					stream: 'mock-stream',
					events: []
				};
			});
		}
	});
});
