(function() {

	'use strict';

	/**
	 * Shorthands
	 *
	 */
	var ptv = playtotv;

	// Improved decodeURIComponent with added support for things like: '?key=foo+bar'
	function decodeURIComponent(s) {
		return window.decodeURIComponent(s.replace(/\+/g, ' '));
	}

	ptv.util = {
		/** @lends playtotv.util */

		// Ease up on regex-es
		/*jshint regexdash:true, quotmark:false, maxlen:250*/

		/**
		 * Regular expression to validate the format of an email.
		 * @type RegExp
		 */
		EMAIL_REGEXP: new RegExp("^[A-Za-z0-9!#$%&'*+/=?^_`{|}~-]+(?:\\.[A-Za-z0-9!#$%&'*+/=?^_`{|}~-]+)*@" +
			'(?:[A-Za-z0-9](?:[A-Za-z0-9-]*[A-Za-z0-9])?\\.)+[A-Za-z0-9](?:[A-Za-z0-9-]*[A-Za-z0-9])$'),

		/**
		 * Regular expression for matching URL's in a string.
		 * @type RegExp
		 */
		URL_REGEXP: /((?:[a-z][\w-]+:(?:\/{1,3}|[a-z0-9%])|www\d{0,3}[.]|[a-z0-9.\-]+[.][a-z]{2,4}\/)(?:[^\s()<>]+|\(([^\s()<>]+|(\([^\s()<>]+\)))*\))+(?:\(([^\s()<>]+|(\([^\s()<>]+\)))*\)|[^\s`!()\[\]{};:'".,<>?]))/gi,

		/**
		 * Regular expression to derive the Origin from a URL.
		 * @type RegExp
		 */
		ORIGIN_REGEXP: /(\w+:)?\/{2,}[^\/]+/,

		/**
		 * Returns an HTML escaped version of the given String.
		 *
		 * @param {string} unescaped
		 * @returns {string} HTML escaped version
		 */
		escapeHtml: function(unescaped) {
			return ptv.dom.create('div', null, { text: unescaped }).innerHTML;
		},

		/**
		 * Returns an escaped version of the given string for use with the RegExp class.
		 *
		 * @param {string} text String to escape
		 * @returns {string} RegExp escaped string
		 */
		escapeRegEx: function(text) {
			return text.replace(/[\-\[\]{}()*+?.,\\\^$|#\s]/g, '\\$&');
		},

		/**
		 * Basic glob to RegExp converter, just supports * wildcards for now.
		 *
		 * @param {string} glob
		 * @returns {RegExp}
		 */
		globToRegEx: function(glob) {
			return new RegExp('^' + this.escapeRegEx(glob).replace(/\\\*/g, '(.*)') + '$');
		},

		/**
		 * Glob test specified candidate string.
		 *
		 * @param {string} glob
		 * @param {string} candidate
		 * @returns {boolean} True if candidate matches glob
		 */
		glob: function(glob, candidate) {
			return this.globToRegEx(glob).test(candidate);
		},

		/**
		 * Adds a key value pair to an URL. Checks for existing parameters and a possible hash for insertion.
		 *
		 * @param {string} URL
		 * @param {string} key
		 * @param {string} value
		 * @returns {string} URL with key and value added to it
		 */
		addURLParam: function(url, key, value) {
			var sep = (url.indexOf('?') !== -1) ? '&' : '?';
			return url.replace(/((#.*)?$)/, sep + encodeURIComponent(key) + '=' + encodeURIComponent(value) + '$1');
		},

		/**
		 * Adds multiple URL params via an object literal.
		 *
		 * @param {string} URL
		 * @param {Object} params
		 * @returns {string} URL with params added to it
		 */
		addURLParams: function(url, params) {
			ptv.each(params, function(value, key) {
				url = this.addURLParam(url, key, value);
			}, this);
			return url;
		},

		/**
		 * Returns an object literal of the key value pairs in the specified URL.
		 *
		 * @param {string} [url=window.location.href] Defaults to current window URL
		 * @returns {Object} URL parameters
		 */
		getURLParams: function(url) {
			url = url || window.location.href;
			var query = url.replace(/[^\?]*\??([^#]*)(#.*)?/, '$1');
			var params = {};
			var regex = /([^&=]+)=?([^&]*)/g;
			var match;
			/*jshint boss:true*/ // suppress warning about following line
			while (match = regex.exec(query)) {
				params[ decodeURIComponent(match[1]) ] = decodeURIComponent(match[2]);
			}
			return params;
		},

		/**
		 * Returns the origin of the specified URL.
		 *
		 * @param {string} [url=window.location.href] URL to derive origin from
		 * @param {string} [baseUrl=window.location.href] Base URL to use for deriving a relative URL's origin
		 * @return {string} Origin of the specified URL
		 */
		getOrigin: function(url, baseUrl) {
			var loc = window.location;
			url = url || loc.href;
			baseUrl = baseUrl || loc.href;

			// If URL matches origin regexp then it is absolute, otherwise it is relative
			var matches = url.match(this.ORIGIN_REGEXP);
			var origin = matches ? matches[0] : baseUrl.match(this.ORIGIN_REGEXP)[0];

			// Use location protocol by default if not specified (e.g: '//foo.com')
			if (origin.indexOf('//') === 0) {
				origin = loc.protocol + origin;
			}

			// Origin of file://foo/bar should be file:// instead of file://foo
			if (origin.indexOf('file://') === 0) {
				origin = 'file://';
			}

			return origin;
		},

		/**
		 * Opens a popup window, facilitating specifying window features by an object instead of a string.
		 *
		 * Width and height options are ensured to be smaller then or equal to screen size.
		 * Note: Unfortunately this does not account for space used by bars around content.
		 *
		 * If width and/or height are defined then the window will be automatically centered on screen by calculating
		 * the appropriate values for the left and top options (unless those are already manually defined).
		 *
		 * @param {string} url URL to open in popup window
		 * @param {string} [target='_blank'] Name of target window, '_blank' for new window
		 * @param {Object} [options={}] Object with window features to request
		 * @return {Window} Popup window
		 */
		popup: function(url, target, options) {
			target = target || '_blank';
			options = options || {};

			var width = options.width;
			if (width) {
				var winWidth = window.screen.width;
				options.width = width = Math.min(width, winWidth);
				options.left = options.left || Math.round((winWidth / 2) - (width / 2));
			}

			var height = options.height;
			if (height) {
				var winHeight = window.screen.height;
				options.height = height = Math.min(height, winHeight);
				options.top = options.top || Math.round((winHeight / 2) - (height / 2));
			}

			var features = Object.keys(options).map(function(key) {
				return key + '=' + options[key];
			}).join(',');

			return window.open(url, target, features);
		},

		/**
		 * Pads a string with a leading character (or 0 if omited) up to the given length.
		 *
		 * @param {string} str String to pad
		 * @param {number} [len=2] Length to pad up to
		 * @param {Char} [ch='0'] Character to pad with
		 * @returns {string} Padded string
		 */
		pad: function(str, len, ch) {
			str = String(str);
			len = parseInt(len, 10) || 2;
			ch = ch || '0';
			while (str.length < len) {
				str = ch + str;
			}
			return str;
		},

		/**
		 * Returns a formatted version of the given date, in hh:mm:ss and optional ms.
		 *
		 * @param {Date} [date=new Date()] Defaults to current date
		 * @param {boolean} [ms=false] Whether to show milliseconds
		 * @returns {string} Formatted time string
		 */
		timeStr: function(date, ms) {
			date = date || new Date();
			return this.pad(date.getHours()) + ':' +
				this.pad(date.getMinutes()) + ':' +
				this.pad(date.getSeconds()) +
				(ms ? '.' + this.pad(date.getMilliseconds(), 3) : '');
		},

		/**
		 * Date format function based on Java's SimpleDateFormat.
		 * @see https://docs.oracle.com/javase/8/docs/api/index.html?java/text/SimpleDateFormat.html
		 *
		 * Pattern letters:
		 *   G*  Era designator  Text  AD
		 *   y   Year  Year  1996; 96
		 *   Y*  Week year  Year  2009; 09
		 *   M   Month in year (context sensitive)  Month  July; Jul; 07
		 *   L*  Month in year (standalone form)  Month  July; Jul; 07
		 *   w*  Week in year  Number  27
		 *   W*  Week in month  Number  2
		 *   D*  Day in year  Number  189
		 *   d   Day in month  Number  10
		 *   F*  Day of week in month  Number  2
		 *   E*  Day name in week  Text  Tuesday; Tue
		 *   u*  Day number of week (1 = Monday, ..., 7 = Sunday)  Number  1
		 *   a   Am/pm marker  Text  PM
		 *   H   Hour in day (0-23)  Number  0
		 *   k*  Hour in day (1-24)  Number  24
		 *   K*  Hour in am/pm (0-11)  Number  0
		 *   h   Hour in am/pm (1-12)  Number  12
		 *   m   Minute in hour  Number  30
		 *   s   Second in minute  Number  55
		 *   S   Millisecond  Number  978
		 *   z*  Time zone  General time zone  Pacific Standard Time; PST; GMT-08:00
		 *   Z*  Time zone  RFC 822 time zone  -0800
		 *   X*  Time zone  ISO 8601 time zone  -08; -0800; -08:00
		 *
		 * TODO: Add support for unsupported letters, e.g. marked with (*)
		 * TODO: Add support for number/name depending on length, e.g: MMM => Aug and: MMMM => August
		 * TODO: Add support for escaping, e.g: "hh 'o''clock' a" => "12 o'clock PM"
		 *
		 * @param {string} pattern
		 * @param {Date|number|string} [date] Defaults to current date
		 * @returns {string} Formatted date string
		 */
		formatDate: function(pattern, date = new Date()) {
			// Convert to Date if necessary (e.g: unix timestamp number or date string)
			if (!(date instanceof Date)) {
				date = new Date(date);
			}

			// Note: An attempt was made to optimize this a bit by only calling Date methods when necessary etc.
			var hours = date.getHours();
			return pattern.replace(/([a-zA-Z])\1*/g, letters => {
				/*jshint maxcomplexity:100*/

				var letter = letters[0];
				var len = letters.length;
				var output =
					letter === 'y' ? (len === 2 ? String(date.getFullYear()).substr(2) : date.getFullYear()) :
					letter === 'M' ? date.getMonth() + 1 :
					letter === 'd' ? date.getDate() :
					letter === 'H' ? hours :
					letter === 'h' ? (hours <= 12 ? (hours === 0 ? 12 : hours) : hours - 12) :
					letter === 'm' ? date.getMinutes() :
					letter === 's' ? date.getSeconds() :
					letter === 'S' ? date.getMilliseconds() :
					letter === 'a' ? (hours < 12 ? 'AM' : 'PM') :
					null;

				if (output === null) {
					throw new Error(`Unsupported letter: ${letter} in pattern: ${pattern}`);
				}

				// Pad number with 0's if necessary, e.g: 1 with MM => 01
				if (len > 1) {
					output = this.pad(output, len);
				}

				return output;
			});
		},

		/**
		 * Returns a currency formatted value from the given number.
		 *
		 * @param {number} number Value to format
		 * @param {boolean} [cents=true] Whether to round to cents or whole units
		 * @returns {string} Formatted currency string
		 */
		toCurrency: function(number, cents) {
			// number.toLocaleString() fails in chrome and adds no decimals
			var split = parseFloat(number).toFixed(2).split('.');
			var nr = split[0].split(''), i, l = nr.length - 3;
			for (i = l; i >= 1; i -= 3) {
				nr.splice(i, 0, '.');
			}
			return nr.join('') + ',' + (cents === false ? '-' : split[1]);
		},

		/**
		 * Returns lower case formatted string with words separated by dashes.
		 *
		 * @param {string} str
		 * @returns {string}
		 */
		dashCase: function(str) {
			return str.
				replace(/\W+/g, '-').
				replace(/\w+/g, m => m.toLowerCase());
		},

		/**
		 * Returns string formatted with only the first letters of words upper case and words separated by spaces.
		 *
		 * @param {string} str
		 * @returns {string}
		 */
		titleCase: function(str) {
			return str.
				replace(/[\W_]+/g, ' ').
				replace(/(\w)(\w*)/g, (m, p1, p2) => p1.toUpperCase() + p2.toLowerCase());
		},

		/**
		 * Returns head of path string separated by specified separator.
		 *
		 * @param {string} path Path (e.g: object property keys) string
		 * @param {string} [sep='.'] Path string separator character
		 * @returns {string} Head of path
		 */
		getPathHead: function(path, sep = '.') {
			var i = path.indexOf(sep);
			return i < 0 ? path : path.substr(0, i);
		},

		/**
		 * Returns tail of path string separated by specified separator.
		 *
		 * @param {string} path Path (e.g: object property keys) string
		 * @param {string} [sep='.'] Path string separator character
		 * @returns {string} Tail of path
		 */
		getPathTail: function(path, sep = '.') {
			var i = path.indexOf(sep);
			return i < 0 ? '' : path.substr(i + 1);
		},

		/**
		 * Returns a reference object with object, key and value properties.
		 * Or object if path is falsy (e.g: in case you just want to return the object itself).
		 * Or undefined if object is falsy (e.g: path was not found after recursion).
		 *
		 * @param {Object} [object] Object to seek through
		 * @param {string} [path=''] Path (e.g: object property keys) to seek in object
		 * @param {string} [sep='.'] Path string separator character
		 * @returns {Object} Reference object with object, key and value
		 */
		seekRef: function(object, path = '', sep = '.') {
			if (!path) {
				return { object: object, key: '', value: object };
			}
			if (!object) {
				return undefined;
			}
			var key = this.getPathHead(path, sep);
			var value = object[key];
			return key === path ?
				{ object: object, key: key, value: value } :
				this.seekRef(value, this.getPathTail(path, sep));
		},

		/**
		 * Returns value of property with specified path within specified object.
		 * Or undefined if property at path could not be found.
		 *
		 * @param {Object} [object] Object to seek through
		 * @param {string} [path=''] Path (e.g: object property keys) to seek in object
		 * @param {string} [sep='.'] Path string separator character
		 * @returns {mixed} Value of property with specified path within specified object
		 */
		seek: function(object, path = '', sep = '.') {
			var ref = this.seekRef(object, path, sep);
			return ref && ref.value;
		},

		/**
		 * Resolves a base 'from' and a relative 'to' path to a combined file path.
		 *
		 * Note: This currently only works for a relative from path (not starting with '/').
		 *
		 * TODO: Make this work for absolute paths as well, depending on whether from is absolute or not.
		 *
		 * @param {string} from Path to start from
		 * @param {string} to Relative path to resolve to
		 * @returns {string} Resolved path
		 */
		resolvePath: function(from, to) {
			if (to.indexOf('/') === 0) {
				return to.substr(1);
			}

			var fromDir = from.substr(0, from.lastIndexOf('/') + 1);

			while (to.indexOf('../') === 0) {
				to = to.substr(3);
				fromDir = fromDir.replace(/[^\/]+\/$/, '');
			}

			return fromDir + to;
		},

		/**
		 * Returns a UTC time zone string (e.g: UTC+01:00).
		 *
		 * @param {number} [hours] Defaults to value derived from Date getTimezoneOffset()
		 * @returns {string} UTC time zone string
		 */
		getTimeZone: function(hours) {
			hours = hours === undefined ? -1 * (new Date().getTimezoneOffset() / 60) : hours;
			var sign = hours < 0 ? '-' : '+';
			hours = Math.abs(hours);
			return 'UTC' + sign + this.pad(Math.floor(hours)) + ':' + this.pad((hours * 60) % 60);
		},

		/**
		 * Evaluates specified script within specified context.
		 *
		 * Besides context properties being available like variables, it is also used as the 'this' argument.
		 *
		 * Strict mode is used to prevent leaking onto global scope (e.g: "x = 1" => window.x = 1) etc.
		 *
		 * Script is wrapped in eval to return block value without requiring explicit return.
		 * E.g: script = "x + 1" instead of "return x + 1", like in a do-expression (ECMAScript proposal)
		 * See: https://gist.github.com/DmitrySoshnikov/de4727f57c5acc17e9469d1a91743125
		 *
		 * Notes:
		 * - Be careful when using this! Only use when you are sure it is appropriate and safe!
		 * - Context can not have a property with key: '_script'
		 *
		 * @param {string} [script=''] Script to evaluate
		 * @param {Object} [context={}] Context to evaluate within
		 * @returns {mixed} Script return value
		 */
		eval: function(script = '', context = {}) {
			// Make context properties available through arguments of new Function
			var args = Object.keys(context);
			var values = args.map(arg => context[arg]);

			// Final argument is the Function script
			// Use eval only if necessary (it has a performance impact)
			script = script.trim();
			var semiIndex = script.indexOf(';');
			if (semiIndex >= 0 && semiIndex < script.length) {
				args.push('_script', '"use strict"; return eval(_script);');
				values.push(script);
			} else {
				args.push('"use strict"; return ' + script);
			}

			// Add script to error message to facilitate debugging (try-catch should barely have a performance impact)
			try {
				var fn = Function.apply(null, args);
				return fn.apply(context, values);
			} catch (error) {
				error.message += ' in script: ' + script.trim().replace(/\s+/g, ' ');
				throw error;
			}
		},

		/**
		 * Returns list filtered by value specified by item path matching specified value by specified mode.
		 *
		 * When you pass a string value it is turned into a function for you based on mode.
		 *
		 * If you want a more exact form of matching then provide your own RegExp or Function test value.
		 *
		 * @param {Array} list List to filter
		 * @param {string} path Filter value path, e.g: '' (item) or: 'x.y' (item.x.y)
		 * @param {string|RegExp|Function|mixed} value Value to test for or Function to test with
		 * @params {string} [mode] Filter mode: fuzzy/contains/startsWith/endsWith/equals (default: exact)
		 * @returns {Array} Filtered list
		 */
		filter: function(list, path, value, mode) {
			if (typeof value === 'string') {
				var escapedValue = this.escapeRegEx(value);
				var regexStr = {
					// Very simple fuzzy-like matching algorithm
					fuzzy: value.split('').map(ch => this.escapeRegEx(ch) + '.*').join(''),
					contains: escapedValue,
					startsWith: '^' + escapedValue,
					endsWith: escapedValue + '$',
					equals: '^' + escapedValue + '$'
				}[mode];
				if (regexStr !== undefined) {
					value = new RegExp(regexStr, 'i');
				} // else default to exact string value match
			}

			var filterFn = value instanceof RegExp ? value.test.bind(value) :
				value instanceof Function ? value.bind(list) :
				value === undefined ? function(candidate) { return !!candidate; } :
				function(candidate) { return candidate === value; };

			return list.filter(item => filterFn(this.seek(item, path)));
		},

		/**
		 * Returns a copy of list ordered by value specified by item path.
		 *
		 * @param {Array} list List to order
		 * @param {string} [path=''] Filter value path, e.g: '' (item) or: 'x.y' (item.x.y)
		 * @params {boolean} [desc=false] Whether to order in descencing order (or ascending)
		 * @returns {Array} Ordered list
		 */
		order: function(list, path = '', desc = false) {
			// Do not modify original array
			list = list.slice(0);

			return list.sort((a, b) => {
				a = this.seek(a, path);
				b = this.seek(b, path);
				return (desc ? -1 : 1) * (a < b ? -1 : a > b ? 1 : 0);
			});
		},

		/**
		 * Returns specified page of list with added properties.
		 *
		 * The properties that are added are:
		 * - page = actual page returned (bound between 1 and last page)
		 * - prev = page - 1 or false if page is already <= 1
		 * - next = page + 1 or false if page is already >= last
		 * - last = last page number
		 *
		 * @param {Array} list List to page
		 * @param {number} [page=1] Number of page to return (starting at 1)
		 * @param {number} [size=10] Page size to use
		 * @returns {Array} Specified page of list with added properties
		 */
		page: function(list, page = 1, size = 10) {
			var last = Math.ceil(list.length / size);
			page = Math.max(1, page);
			page = Math.min(last, page);

			var paged = list.slice((page - 1) * size, page * size);

			ptv.extend(paged, {
				page: page,
				prev: page > 1 && page - 1,
				next: page < last && page + 1,
				last: last
			});

			return paged;
		}
	};

}());
