playtotv.define('playtotv.widget.CountdownWidget', [
	'playtotv.widget.Widget'
], function(
	Widget
) {

	'use strict';

	var ptv = playtotv;
	var dom = ptv.dom;
	var util = ptv.util;

	return class CountdownWidget extends Widget {

		constructor(node, options) {
			ptv.params(options, {
				app: null,
				time: ptv.REQUIRED
			});

			super(node);

			this.time = options.time;

			this.dd = dom.first('.dd', node);
			this.hh = dom.first('.hh', node);
			this.mm = dom.first('.mm', node);
			this.ss = dom.first('.ss', node);

			this.update();
		}

		update() {
			if (!this.node) {
				return; // disposed
			}

			var time = Math.max(0, this.time - Date.now()) / 1000;

			this.updateDigit(this.dd, time / 86400);
			this.updateDigit(this.hh, (time % 86400 ) / 3600);
			this.updateDigit(this.mm, (time % 3600) / 60);
			this.updateDigit(this.ss, time % 60);

			setTimeout(() => this.update(), 1000);
		}

		updateDigit(node, time) {
			if (node) {
				node.innerHTML = util.pad(Math.floor(time));
			}
		}
	};
});
