playtotv.define('playtotv.cookie', [], function() {

	'use strict';

	/**
	 * Sets, gets or deletes cookies
	 *
	 * @name cookie
	 * @namespace Cookie module. Sets, gets or deletes cookies
	 * @memberOf playtotv
	 */
	var cookie = {

		/** @lends playtotv.cookie */

		/**
		 * Gets a cookie value by name
		 *
		 * @param {string} name
		 */
		get: function(name) {
			var nameEQ = name + '=';
			var ca = document.cookie.split(';');

			var i, l = ca.length;
			for (i = 0; i < l; i++) {
				var c = ca[i];
				while (c.charAt(0) === ' ') {
					c = c.substring(1, c.length);
				}
				if (c.indexOf(nameEQ) === 0) {
					return c.substring(nameEQ.length, c.length);
				}
			}
			return null;
		},

		/**
		 * Sets a cookie
		 *
		 * @param {string} name
		 * @param {string} value
		 * @param {number} days to expire
		 */
		set: function(name, value, days) {
			var expires = '';
			if (days) {
				var date = new Date();
				date.setTime(date.getTime() + (days * 24 * 60 * 60 * 1000));
				expires = '; expires=' + date.toGMTString();
			}
			document.cookie = name + '=' + value + expires + '; path=/';
		},

		/**
		 * Erases a cookie by name
		 *
		 * @param {string} name
		 */
		remove: function(name) {
			this.set(name, '', -1);
		}
	};

	return cookie;
});
