playtotv.define('playtotv.widget.TimestampWidget', [
	'playtotv.widget.Widget'
], function(
	Widget
) {

	'use strict';

	var DAY_HOURS = 24;
	var HOUR_MINUTES = 60;
	var MINUTE_SECONDS = 60;
	var HOUR_SECONDS = HOUR_MINUTES * MINUTE_SECONDS;
	var DAY_SECONDS = DAY_HOURS * HOUR_SECONDS;

	var ptv = playtotv;

	/**
	 * Widget that dynamically updates a node's innerHTML with a string representing the amount of time passed since
	 * the timestamp passed as time option.
	 *
	 * TODO: add a dateformat option
	 *
	 * @name TimestampWidget
	 * @memberOf playtotv.widget
	 * @class
	 * @extends playtotv.widget.Widget
	 * @param {Node} node
	 */
	return class TimestampWidget extends Widget {

		constructor(node, options) {
			ptv.params(options, {
				app: null,
				time: ptv.REQUIRED
			});

			super(node);

			this.time = options.time;

			this.update();
		}

		update() {
			var seconds = Math.floor((Date.now() - this.time) / 1000);
			var text;

			if (seconds >= DAY_SECONDS) {
				text = Math.floor(seconds / DAY_SECONDS) + 'd';
			} else if (seconds >= HOUR_SECONDS) {
				text = Math.floor(seconds / HOUR_SECONDS) % DAY_HOURS + 'h';
			} else if (seconds >= MINUTE_SECONDS) {
				text = Math.floor(seconds / MINUTE_SECONDS) % HOUR_MINUTES + 'm';
			} else {
				text = seconds % MINUTE_SECONDS + 's';
			}

			if (this.node.innerHTML !== text) {
				this.node.innerHTML = text;
			}

			this.timeout = setTimeout(() => {
				this.update();
			}, seconds > MINUTE_SECONDS ? 60000 : 1000);
		}

		dispose() {
			clearTimeout(this.timeout);

			super();
		}
	};
});
