playtotv.define('playtotv.fx.Timeline', [
	'playtotv.fx.Animation'
], function(
	Animation
) {

	'use strict';

	/**
	 * Creates a timeline on which actions may be added. Like its super class Animation, timelines may be run at
	 * different rates, and can cycle with or without alternating direction.
	 *
	 * @name Timeline
	 * @class
	 * @memberOf playtotv.fx
	 * @extends playtotv.fx.Animation
	 * @param {Object} options
	 * @param {boolean} [options.alternate=false] Whether a timeline with multiple cycles alternates direction
	 * @param {number} [options.cycles=1] Amount of repeating timeline cycles
	 * @param {number} [options.duration=1000] Duration of one timeline cycle in ms
	 * @param {number} [options.fps=0] Fps limit on requestAnimationFrame callbacks
	 * @param {number} [options.rate=1] Time rate multiplier
	 * @param {number} [options.start] Alternate start timestamp in ms; past starts mid-timeline, future delays start
	 * @param {number} [options.to=1] End value
	 */
	var Timeline = new playtotv.Class({
		extend: Animation,

		initialize: function(options) {
			this._super(options);
			this.actions = [];
		},

		/**
		 * Adds an action to the timeline at the given time, in the form of a callback. Actions are executed from the
		 * timeline's onFrame() function, which is subject to the fps option value. Because of this, the given time is
		 * an approximation of the actual time at which the action will be performed (with lower fps increasing the
		 * possible deviation). Considerably lowering the fps or queuing actions closer to another than the time
		 * between two consecutive frames may result in multiple actions being called in the same onFrame() callstack.
		 *
		 * @param {number} time Time relative to the start of the timeline at which the action is executed
		 * @param {Object} action
		 * @param {Function} action.callback Function to execute
		 * @param {Object} [action.context] Optional context the callback will be applied to
		 * @param {Array} [action.args] Optional array of callback arguments
		 * @returns {Object} action
		 */
		add: function(time, action) {
			action = playtotv.params(action, {
				callback: playtotv.REQUIRED,
				context: undefined,
				args: []
			});

			this.actions.push(playtotv.extend(action, {
				time: time,
				run: true
			}));

			return action;
		},

		/**
		 * Removes an action from the timeline. Accepts actions as given to or returned by add()
		 *
		 * @param {Object} action
		 */
		remove: function(action) {
			var index = this.actions.indexOf(action);
			if (index >= 0) {
				this.actions.splice(index, 1);
			}
		},

		/**
		 * Resets all actions when the timeline starts or cycles, allowing them to be run at their respective time.
		 *
		 */
		reset: function() {
			this.actions.forEach(function(frame) {
				frame.run = false;
			});
		},

		/**
		 * Called when the timeline starts, triggers a reset()
		 *
		 */
		onStart: function() {
			this.reset();
		},

		/**
		 * Called when a timeline with multiple cycles cycles, triggers a reset()
		 *
		 */
		onCycle: function() {
			this.reset();
		},

		/**
		 * Checks the elapsed time, and executes any actions of which the indicated time matches or has passed.
		 *
		 * @param {number} fraction
		 */
		onFrame: function(n) {
			var anim = this.animation;
			var current = n * anim.duration;
			var reverse = this.reversed;

			this.actions.forEach(function(frame) {
				var passed = current >= frame.time;
				if (!frame.run && ((reverse && !passed) || (!reverse && passed))) {
					frame.run = true;
					frame.callback.apply(frame.context, frame.args);
				}
			});
		}
	});

	return Timeline;
});
