playtotv.define('playtotv.mvc.Controller', [
	'playtotv.mvc.BindWidget',
	'playtotv.mvc.UpdateWidget',
	'playtotv.pubsub.PubSub'
], function(
	BindWidget,
	UpdateWidget,
	PubSub
) {

	'use strict';

	var ptv = playtotv;
	var util = ptv.util;
	var REQUIRED = ptv.REQUIRED;

	return class Controller extends PubSub {
		/** @lends playtotv.mvc.Controller.prototype */

		/**
		 * PlayToTV MVC Controller.
		 *
		 * A base context object will be provided by App.addController().
		 * A specific Controller can add or customize the context.store property as desired.
		 *
		 * @constructs
		 * @param {Object} context Controller context
		 * @param {string} context.name Name of Controller within App
		 * @param {playtotv.mvc.App} context.app App instance this controller belongs to
		 * @param {Object} [context.stored] Any stored data to deserialize
		 * @param {[string]} [context.store={}] Properties with default values to be serialized and persisted
		 *
		 * @property {string} name Name of Controller within App
		 * @property {playtotv.mvc.App} app App instance this controller belongs to
		 * @property {[string]} [storeKeys=Object.keys(store)] Keys of properties to be serialized and persisted
		 */
		constructor(context) {
			var { name, app, stored, store } = ptv.params(context, {
				name: REQUIRED,
				app: REQUIRED,
				stored: undefined,
				store: {}
			});

			super();

			this.name = name;
			this.app = app;
			this.storeKeys = Object.keys(store);

			this.deserialize(store, stored);

			this.setWidgets([
				[ 'bind', BindWidget ],
				[ 'update', UpdateWidget ]
			]);
		}

		/**
		 * Returns any JSON stringify-able data to be persisted in storage.
		 * Called by App when it wants to persist this Controller's data.
		 * Returns this instance's properties with keys matching this.storekeys.
		 *
		 * @returns {Object} Data to be persisted in storage
		 */
		serialize() {
			return ptv.pick(this, this.storeKeys);
		}

		/**
		 * Called by constructor with any previously stored data.
		 * Extends this instance with data properties or default values.
		 *
		 * @param {Object} defaults Default values
		 * @param {Object} data Previously stored data
		 */
		deserialize(defaults, data) {
			ptv.extend(this, defaults, data);
		}

		/**
		 * Registers widget named [this.name]-[name] to specified Widget class and base options.
		 *
		 * @param {string} name Widget name (within this Controller)
		 * @param {[type]} WidgetClass Class to use for Widget instantiation
		 * @param {Object} [options={}] Base options
		 */
		setWidget(name, WidgetClass, options = {}) {
			options.controller = this;
			return this.app.setWidget(this.name + '-' + name, WidgetClass, options);
		}

		/**
		 * Registers an array of Widgets.
		 *
		 * @param {[args]} widgets Array of array of setWidget arguments
		 */
		setWidgets(widgets) {
			widgets.forEach(args => this.setWidget.apply(this, args));
		}

		/**
		 * Persists this controller's data to storage.
		 *
		 * @returns {Promise} Promise of data being persisted
		 */
		flush() {
			return this.app.flush(this.name);
		}

		/**
		 * Returns value of property with specified path.
		 *
		 * @param {string} path Property path
		 * @returns {mixed} Property value or undefined
		 */
		seek(path) {
			return util.seek(this, path);
		}

		/**
		 * Set controllery property with specified path to specified value and publish it.
		 *
		 * If value already equals current value then this does nothing.
		 *
		 * TODO: Move set() method up to App and call that prefixed with controller name from here?
		 * Likewise with toggle() below.
		 * Also add Array add() and remove() and number inc() and dec() convenience methods alongside toggle()?
		 *
		 * @param {string} path Path of property to set
		 * @param {mixed} value Value to change property to
		 * @param {options={}} [options={}]
		 * @param {boolean} [options.json] Compare values using JSON stringify (default true if typeof value 'object')
		 * @param {boolean} [options.force=false] Assume value to be changed
		 */
		set(path, value, options) {
			options = ptv.params(options, {
				json: typeof value === 'object',
				force: false
			});

			var ref = util.seekRef(this, path);
			if (!ref) {
				throw new Error('Can not set property on undefined object: ' + path);
			}

			var current = ref.value;
			if (
				options.force ||
				(!options.json && value !== current) ||
				(options.json && JSON.stringify(value) !== JSON.stringify(current))
			) {
				ref.object[ref.key] = value;
				this.publish(path, value);
			}
		}

		/**
		 * Toggle boolean value of property with specified path.
		 *
		 * If the value argument is undefined then this will change the property to !x where x is the current value.
		 * If typeof value is 'string' then this will change to false unless value equals 'true'.
		 * Otherwise this will change to true if value is truthy or false otherwise.
		 *
		 * @param {string} path Path of property to toggle
		 * @param {boolean} [value] Value to change to
		 */
		toggle(path, value) {
			value = value === undefined ? !this.seek(path) :
				typeof value === 'string' ? value === 'true' :
				value ? true : false;

			this.set(path, value);
		}
	};
});
